import * as types from './mutation-types'
import { localStore } from '@/utils/storage'
const FEED_SETTINGS_KEY = 'feedSettings'

const actions = {
  loadSettings: ({ commit, getters }) => {
    const feedSettings = localStore.getJSON(FEED_SETTINGS_KEY) || getters.feedTypes
    commit(types.SET_FEED_TYPES, feedSettings)
  },

  setUser: ({ commit }, firstName) => {
    commit(types.SET_USER, firstName)
  },

  setLocale: ({ commit }, locale) => {
    commit(types.SET_LOCALE, locale)
  },

  setCulture: ({ commit }, culture) => {
    commit(types.SET_CULTURE, culture)
  },

  setMetrics: ({ commit }, metrics) => {
    commit(types.SET_METRICS, metrics)
  },

  showAdvancedMetrics: ({ commit }, show) => {
    commit(types.SET_SHOW_ADVANCED, show)
  },

  setDaysShown: ({ commit }, days) => {
    commit(types.SET_INTERVAL, days)
  },

  showMenu: ({ commit }, show) => {
    commit(types.SHOW_MENU, show)
  },

  showBundle: ({ commit }, payload) => {
    commit(types.SHOW_BUNDLE, payload)
  },

  setFeed: ({ commit }, feed) => {
    commit(types.SET_FEED, feed)
  },

  setFeedTypes: ({ commit }, types) => {
    commit(types.SET_FEED_TYPES, types)
  },

  toggleFeedType: ({ commit, getters }, type) => {
    commit(types.TOGGLE_FEED_TYPE, type)
    localStore.set(FEED_SETTINGS_KEY, JSON.stringify(getters.feedTypes))
  },

  setLicenseError: ({ commit }, error) => {
    commit(types.SET_LICENSE_ERROR, error)
  },
}

export default actions
