<template>
  <div
    class="input-group-cap"
    :class="cssClassObject">
    <slot />
  </div>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

export const SizeProp = {
  SM: 'sm',
  MD: 'md',
}

export default {
  name: 'InputGroupCap',
  props: {
    size: {
      type: String,
      default: SizeProp.SM,
      validator: validateAgainstEnum(SizeProp),
    },
  },
  computed: {
    cssClassObject () {
      return {
        'input-group-cap__size-sm': this.size === SizeProp.SM,
        'input-group-cap__size-md': this.size === SizeProp.MD,
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.input-group-cap
  display flex
  align-items center
  justify-content center
  padding 0 $base-unit * 1.5
  text-align center
  cursor default
  user-select none

.input-group-cap__size-sm
  min-width $base-unit * 6

.input-group-cap__size-md
  min-width $base-unit * 10
</style>
