    class="legacy-link"
    target="_self"
    :href="href"
    :title="translations.title">
    {{ translations.label }}
  </a>
</template>

<script>
export default {
  name: 'LegacyLink',
  props: {
    href: {
      type: String,
      required: true,
    },
  },
  computed: {
    translations () {
      return {
        title: 'For missing features, click here to edit in old version',
        label: 'Edit in old version',
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.legacy-link
  text-decoration none
