<script>
const ComponentEvent = {
  NEXT: 'next',
  PREVIOUS: 'previous',
}

export default {
  name: 'UseSteps',
  props: {
    steps: {
      type: Number,
      required: true,
    },
  },
  data () {
    return {
      step: 1,
      history: [1],
    }
  },
  computed: {
    stepsArray () {
      return Array.from(Array(this.steps), (_step, index) => index + 1)
    },
    previousStep () {
      return this.history[Math.max(0, this.history.length - 2)]
    },
  },
  methods: {
    offset (offset) {
      const step = this.step + offset

      if (!this.stepsArray.includes(step)) {
        return
      }

      this.history.push(step)
      this.step = step
    },
    next () {
      this.offset(1)
      this.$emit(ComponentEvent.NEXT, this.step)
    },
    previous () {
      this.offset(-1)
      this.$emit(ComponentEvent.PREVIOUS, this.step)
    },
  },
  render () {
    return this.$scopedSlots.default({
      step: this.step,
      next: this.next,
      previous: this.previous,
      history: this.history,
      previousStep: this.previousStep,
    })
  },
}
</script>
