<template>
  <form
    class="uri-whitelist-create__form"
    @submit.prevent="createOneAsync">
    <uc-modal
      :title="$t('title')"
      @hide="cancel">
      <p class="uri-whitelist-create__description">
        We support wildcards in all of your redircet URIs. A wildcard is a catch-all functionality, you just need to place * in your registered URI. <br> ex. https://mydomain.com/ucommerce-callback/*
      </p>
      <div class="uri-whitelist-create__form-fields">
        <uc-text-input
          v-model="payload.uri"
          :label="$t('uriPlaceholder')" />
      </div>
      <!-- Error message -->
      <div
        v-show="isError"
        class="uri-whitelist-create__error"
        data-test="uri-whitelist-create__error">
        {{ errorMessage }}
      </div>
      <template #footer>
        <!-- Add button-->
        <uc-button
          :disabled="!canSubmit"
          :type="buttonTypeSubmit">
          {{ $t('global.add') }}
          <template #after>
            <loading-spinner v-show="isPosting" />
          </template>
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<i18n>
{
  "en": {
    "title": "Add URI",
    "uriPlaceholder": "URI"
  }
}
</i18n>

<script>
import { UcModal } from '@/components/widgets/uc-modal/'
import { UcButton, UcButtonType } from '@/components/forms/uc-button/'
import { LoadingSpinner } from '@/components/widgets/loading-spinner/'
import { uriWhitelistService } from '@/services/'
import { UcTextInput } from '@/components/forms/uc-text-input/'

export const ComponentEvent = {
  CANCEL: 'cancel',
  CREATE: 'create',
}

const ComponentState = {
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

export default {
  name: 'UriWhitelistCreate',
  components: {
    UcTextInput,
    UcModal,
    UcButton,
    LoadingSpinner,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    storeGuid: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
      payload: {
        uri: '',
      },
      errorMessage: '',
      state: ComponentState.IDLE,
    }
  },
  computed: {
    buttonTypeSubmit: () => UcButtonType.SUBMIT,
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    canSubmit () {
      return this.payload.uri.length > 0
    },
    isError () {
      return this.state === ComponentState.ERROR
    },
  },
  methods: {
    cancel () {
      this.state = ComponentState.IDLE
      this.$emit(ComponentEvent.CANCEL)
    },
    async createOneAsync () {
      try {
        this.state = ComponentState.POSTING
        await uriWhitelistService.createOneAsync({
          cultureCode: this.contentLanguage,
          storeGuid: this.storeGuid,
          uri: this.payload.uri,
        })
        this.$emit(ComponentEvent.CREATE)
      } catch (error) {
        this.state = ComponentState.ERROR
        if (error?.response?.data?.message) {
          this.errorMessage = error.response.data.message
        } else {
          throw error
        }
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.uri-whitelist-create__form-fields
  display grid
  row-gap 3 * $base-unit
  padding-top 3 * $base-unit

.uri-whitelist-create__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()

.uri-whitelist-create__description
  color #b4b4c6
</style>
