<template>
  <uc-infinite-scroller
    :disabled="disableInfiniteScroll"
    @next="onNext">
    <template #default>
      <table
        class="uc-data-grid"
        :class="cssClassObject">
        <!-- Define column width -->
        <slot name="columns">
          <colgroup>
            <col
              v-for="column in columns"
              :key="column.key"
              data-test="table-col"
              :width="column.width">
          </colgroup>
        </slot>

        <!-- Table head -->
        <thead v-if="!headingless">
          <slot
            name="head"
            :columns="columns">
            <tr class="uc-data-grid__header">
              <uc-data-grid-cell-header
                v-for="column in columns"
                :key="column.key"
                :style="column.headerCellStyle || column.cellStyle">
                {{ column.value }}
              </uc-data-grid-cell-header>
            </tr>
          </slot>
        </thead>

        <!-- Table body -->
        <tbody data-test="body">
          <slot
            name="body"
            :rows="sortedRows"
            :allowed-keys="allowedKeys">
            <tr
              v-for="(row, index) in sortedRows"
              :key="row[rowKey] || index"
              data-test="table-row"
              class="uc-data-grid__row"
              @click="onClick(row)">
              <template v-for="(cell, key) in row">
                <uc-data-grid-cell
                  v-if="allowedKeys.includes(key)"
                  :key="key"
                  :style="setCellStyle(key)">
                  <slot
                    :name="key"
                    v-bind="{ cell, row }">
                    {{ cell }}
                  </slot>
                </uc-data-grid-cell>
              </template>
            </tr>
          </slot>
        </tbody>
      </table>
    </template>

    <!-- Trigger -->
    <template #trigger>
      <div class="uc-data-grid__loading-indicator">
        <loading-indicator is-loading />
      </div>
    </template>
  </uc-infinite-scroller>
</template>

<script>
import { difference } from '@/utils/array.utils.js'
import UcInfiniteScroller from '@/components/generic/uc-infinite-scroller/'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import UcDataGridCellHeader from './components/uc-data-grid-cell-header.vue'
import UcDataGridCell from './components/uc-data-grid-cell.vue'

const ComponentEvent = {
  NEXT: 'next',
  ROW_CLICK: 'row-click',
}

export default {
  name: 'UcDataGrid',
  components: {
    UcInfiniteScroller,
    LoadingIndicator,
    UcDataGridCellHeader,
    UcDataGridCell,
  },
  props: {
    columns: {
      type: Array,
      default: () => [], // Array of objects { key, value, width }. Width is optional.
    },
    rows: {
      type: Array,
      default: () => [],
    },
    rowKey: {
      type: [Number, String],
      default: null,
    },
    disableInfiniteScroll: {
      type: Boolean,
      default: false,
    },
    headingless: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    cssClassObject () {
      return {
        'is-clickable': this.$listeners && this.$listeners[ComponentEvent.ROW_CLICK],
      }
    },
    allowedKeys () {
      return this.columns.map(column => column.key)
    },
    sortedRows () {
      return this.rows.map(row => {
        // Add properties to row from columns, that were missing there originally.
        // This way, we get an empty cell, which can be customized if needed
        const newRow = difference(this.allowedKeys, Object.keys(row))
          .reduce((object, key) => {
            object[key] = ''
            return object
          }, { ...row })

        return Object.keys(newRow)
          // Sort columns in row to reflect order in columns prop
          .sort((a, b) => {
            const currentKeyIndex = this.allowedKeys.indexOf(a)
            const nextKeyIndex = this.allowedKeys.indexOf(b)

            return currentKeyIndex - nextKeyIndex
          })
        // Tie it all together again :)
          .reduce((object, key) => {
            object[key] = newRow[key]
            return object
          }, {})
      })
    },
  },
  methods: {
    setCellStyle (key) {
      return this.columns.find(column => column.key === key)?.cellStyle
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    onClick (row) {
      this.$emit(ComponentEvent.ROW_CLICK, row)
    },
  },
}
</script>

<style lang="stylus" scoped>
$table-cell-height = $base-unit * 10
$component-z-index-header = 1

.uc-data-grid
  position relative
  width 100%
  border-collapse collapse
  border-spacing 0
  table-layout fixed
  isolation isolate
  user-select none

.uc-data-grid__header
  // https://css-tricks.com/position-sticky-and-table-headers/
  position sticky
  top -1px
  z-index $component-z-index-header

.uc-data-grid__loading-indicator
  display flex
  align-items center
  justify-content center
  height $table-cell-height
  background-color $base-color-white

.is-clickable
  cursor pointer
</style>
