<template>
  <label
    class="uc-toggle switch"
    :class="{readonly}"
    data-test="uc-toggle-switch">
    <input
      v-show="!compact"
      :id="id"
      v-model="checkedValue"
      type="checkbox"
      :name="id"
      :disabled="readonly"
      :value="guid"
      @change="onChange">
    <span class="slider round" />
    <span
      v-if="!compact"
      v-truncate
      class="label">{{ label }}</span>
  </label>
</template>

<script>
import getId from '@/utils/id-generator.js'

export default {
  name: 'UcToggle',
  props: {
    label: {
      type: String,
      default: '',
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    checked: {
      type: Boolean,
      default: false,
    },
    guid: {
      type: String,
      default: '',
    },
    compact: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      checkedValue: '',
    }
  },
  computed: {
    id () {
      return getId()
    },
  },
  watch: {
    checked: {
      immediate: true,
      handler (value) {
        this.checkedValue = value ? 'checked' : ''
      },
    },
  },
  methods: {
    onChange (event) {
      this.$emit('change', { value: event.target.checked, guid: this.guid })
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-toggle
  position relative
  display flex
  align-items center
  cursor pointer
  user-select none

  &.readonly
    cursor not-allowed

.slider
  position relative
  display flex
  width 40px
  height 100%
  background-color #ccc
  cursor pointer
  transition .4s

  &::before
    position absolute
    bottom 2px
    left 2px
    width 20px
    height 20px
    background-color white
    transition .4s
    content ""

  &.round
    border-radius 12.8px

    &::before
      border-radius 50%

input
  &:checked
    & ~ .slider
      background-color $base-color-brand

      &::before
        transform translateX(16px)

  &:focus
    & ~ .slider
      box-shadow 0 0 1px #2196f3

.switch
  position relative
  display flex
  min-width 40px
  height 24px

  &.readonly
    .slider
      cursor not-allowed

  input
    width 0
    height 0
    opacity 0

.label
  max-width 700px
  margin-left 8px
  primary-body-text()
</style>
