import { rest } from 'msw'
import { addressDefinitions } from '@/mocks/entities/addresses/'

const RESPONSE_DELAY = 200

export const addressHandlers = [
  rest.get('/ucommerceapi/v2/addresses/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(addressDefinitions),
    )
  }),
]
