export function generateMockOrderLines (count = 100) {
  const products = generateProducts(count, true)
  const orderLines = products.map(product => {
    const sku = product.sku.value
    const variantSku = product.variantSku.value || ''
    const label = `${sku} - ${variantSku}`

    return {
      guid: product.guid.value,
      sku,
      variantSku,
      label,
    }
  })

  const anyOption = {
    guid: 'mock-15c4fc8c-8dc9-4819-b213-1a5e5dfef1db',
    label: 'Any',
    sku: 'Any',
    variantSku: '',
  }

  return [anyOption, ...orderLines]
}
