<template>
  <div class="order-audits">
    <fields-section
      paddingless
      :heading="$tc('heading', resultCount)">
      <template #actions>
        <uc-button
          :size="buttonSizeSmall"
          @click="toggleModal">
          {{ $t('new') }}
        </uc-button>
      </template>
      <uc-section-wrapper
        v-bind="{ state, errorMessage }"
        :empty-message="$t('emptyMessage')">
        <uc-audit-list
          v-bind="{
            audits,
            totalCount: resultCount,
            contentLanguage
          }"
          @next="requestNextPage" />
      </uc-section-wrapper>
    </fields-section>

    <!-- Create new entry modal -->
    <order-audits-create-modal
      v-if="isModalOpen"
      @add="addEntryAsync"
      @cancel="toggleModal" />
  </div>
</template>

<i18n>
{
  "en": {
    "heading": "Logbook | Logbook ({count}) | Logbook ({count})",
    "emptyMessage": "No Audits have been added to this order yet. Click the button to get started.",
    "new": "Add"
  }
}
</i18n>

<script>
import { auditService } from '@/services/'
import { FieldsSection } from '@/components/sections/fields-section/'
import { UcSectionWrapper, UcSectionWrapperState } from '@/components/generic/uc-section-wrapper/'
import { UcButton, UcButtonSize } from '@/components/forms/uc-button/'
import { UcAuditList } from '../uc-audit-list/'
import { OrderAuditsCreateModal } from './components/order-audits-create-modal/'

export default {
  name: 'OrderAudits',
  components: {
    UcSectionWrapper,
    UcAuditList,
    FieldsSection,
    UcButton,
    OrderAuditsCreateModal,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    parentGuid: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
      state: UcSectionWrapperState.LOADING,
      errorMessage: '',
      audits: [],
      totalCount: 0,
      resultCount: 0,
      startAt: 0,
      currentPage: 1,
      pageSize: 18,
      isModalOpen: false,
    }
  },
  computed: {
    buttonSizeSmall: () => UcButtonSize.SMALL,
  },
  async created () {
    await this.fetchAuditsAsync()
  },
  methods: {
    async fetchAuditsAsync (startAt = 0, currentPage = 1) {
      try {
        const response = await auditService.fetchManyAsync({
          orderGuid: this.parentGuid,
          cultureCode: this.contentLanguage,
          startAt,
          limit: this.pageSize,
        })
        const audits = response.data
        this.audits = currentPage > 1 ? [...this.audits, ...audits] : audits
        this.totalCount = Number(response.headers['x-total-count'])
        this.resultCount = Number(response.headers['x-result-count'])
        this.currentPage = currentPage
        this.state = this.audits.length > 0 ? UcSectionWrapperState.IDLE : UcSectionWrapperState.EMPTY
      } catch (error) {
        this.state = UcSectionWrapperState.ERROR
        this.errorMessage = error?.response?.data?.message || this.$t('global.errorMessage')
      }
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1
      await this.fetchAuditsAsync(startAt, page)
    },
    toggleModal () {
      this.isModalOpen = !this.isModalOpen
    },
    async addEntryAsync (message) {
      await auditService.createOneAsync({
        orderGuid: this.parentGuid,
        cultureCode: this.contentLanguage,
        message,
      })

      await this.fetchAuditsAsync()
      this.toggleModal()
    },
  },
}
</script>
