<template>
  <div
    v-show="!muted"
    class="feed__item-wrapper">
    <feed-card :notification="notification" />
    <transition name="fade">
      <div
        v-if="hasBundle && !bundleShown"
        class="feed__item-stack one" />
      <div
        v-if="hasBundle && notification.bundle.length > 1 && !bundleShown"
        class="feed__item-stack two" />
      <div
        v-if="hasBundle && notification.bundle.length > 2 && !bundleShown"
        class="feed__item-stack three" />
    </transition>
    <transition-group name="flip-list">
      <feed-card
        v-for="subItem in bundle"
        :key="subItem.id"
        :notification="subItem"
        :is-child="true" />
    </transition-group>
  </div>
</template>

<script>
import FeedCard from './feed-card'
export default {
  name: 'FeedItem',
  components: {
    FeedCard,
  },
  props: {
    notification: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    hasBundle () {
      return this.notification.bundle.length > 0
    },
    bundleShown () {
      return this.notification.showBundle
    },
    bundle () {
      return this.notification.showBundle ? this.notification.bundle : []
    },
    muted () {
      return this.feedSettings[this.notification.type].muted
    },
    feedSettings () {
      return this.$store.getters.feedTypes
    },
  },
}
</script>

<style lang="stylus" scoped>
.feed__item-wrapper
  position relative

.feed__item-stack
  position relative
  height 8px
  margin-bottom 2px
  border-radius 3px
  background-color #fff
  box-shadow: 0px 2px 5px -2px rgba(0,0,0,0.3)

  &.one
    z-index 4
    margin -8px 1px 4px

  &.two
    z-index 3
    margin -10px 2px 5px

  &.three
    z-index 2
    margin -15px 3px 6px

</style>
