<template>
  <uc-grid
    :items="nodes"
    :columns="2"
    :grid-gap="gridGap">
    <template #default="{ item }">
      <slot :node="item" />
    </template>
  </uc-grid>
</template>

<script>
import UcGrid, { GridGapProp } from '@/components/generic/uc-grid/uc-grid'
import { validateAgainstEnum } from '@/utils/validate-against'

export default {
  name: 'UcNavigatorViewGridSpacious',
  components: {
    UcGrid,
  },
  props: {
    nodes: {
      type: Array,
      required: true,
    },
    gridGap: {
      type: String,
      default: GridGapProp.MD,
      validator: validateAgainstEnum(GridGapProp),
    },
  },
}
</script>
