import { LayoutType } from '@/layouts'
import Catalogs from './views/catalogs.vue'

export function CatalogsApp ({ router, store }) {
  router.addRoute({
    path: '/catalogs/',
    name: 'catalogs',
    component: Catalogs,
    beforeEnter: (_to, _from, next) => {
      store.commit('details/resetDetails')
      next()
    },
    meta: {
      layout: LayoutType.STICKY_HEADER,
    },
  })
}
