import faker from 'faker'

function mockGuid () {
  return `mock-${faker.datatype.uuid()}`
}

function priceGroupFactory () {
  return {
    name: `${faker.finance.currencyCode()} ${faker.datatype.number({ min: 1, max: 100 })} pct`,
    guid: mockGuid(),
  }
}

export function generatePriceGroups (count = 1) {
  const sequence = [...Array(count).keys()]
  return sequence.map(priceGroupFactory)
}

export const orderPriceGroups = generatePriceGroups(200)
