<template>
  <form
    class="order-discounts-create__form"
    @submit.prevent="addEntryAsync">
    <uc-modal
      :title="$t('title')"
      @hide="cancel">
      <div class="order-discounts-create__form-fields">
        <uc-numeric-input
          v-model.number="payload.amount"
          :label="$t('amountPlaceholder')" />
        <uc-text-input
          v-model="payload.campaign"
          :label="$t('campaignPlaceholder')" />
        <uc-text-input
          v-model="payload.promotion"
          :label="$t('promotionPlaceholder')" />
        <uc-text-area
          v-model="payload.details"
          :label="$t('detailsPlaceholder')" />
      </div>
      <!-- Error message -->
      <div
        v-show="isError"
        class="discount-create__error"
        data-test="discount-create__error">
        {{ errorMessage }}
      </div>
      <template #footer>
        <!-- Add button-->
        <uc-button
          :disabled="isDisabled"
          :type="buttonTypeSubmit">
          {{ $t('global.add') }}
          <template #after>
            <loading-spinner v-show="isPosting" />
          </template>
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<i18n>
{
  "en": {
    "title": "Add discount",
    "detailsPlaceholder": "Details",
    "campaignPlaceholder": "Campaign",
    "promotionPlaceholder": "Promotion",
    "amountPlaceholder": "Discount amount"
  }
}
</i18n>

<script>
import { UcModal } from '@/components/widgets/uc-modal/'
import { UcButton, UcButtonType } from '@/components/forms/uc-button/'
import { LoadingSpinner } from '@/components/widgets/loading-spinner/'
import { orderDiscountService } from '@/services/'
import { UcTextArea } from '@/components/forms/uc-text-area/'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import { UcNumericInput } from '@/components/forms/uc-numeric-input/'

const ComponentEvent = {
  ADD: 'add',
  CANCEL: 'cancel',
  CREATED: 'created',
}

const ComponentState = {
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

export default {
  name: 'OrderDiscountCreateModal',
  components: {
    UcNumericInput,
    UcTextInput,
    UcModal,
    UcButton,
    UcTextArea,
    LoadingSpinner,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    parentGuid: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
      payload: {
        details: '',
        amount: null,
        campaign: '',
        promotion: '',
      },
      errorMessage: '',
      state: ComponentState.IDLE,
    }
  },
  computed: {
    buttonTypeSubmit: () => UcButtonType.SUBMIT,
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    isDisabled () {
      return this.payload.amount === null
    },
    isError () {
      return this.state === ComponentState.ERROR
    },
  },
  methods: {
    cancel () {
      this.state = ComponentState.IDLE
      this.$emit(ComponentEvent.CANCEL)
    },
    async addEntryAsync () {
      try {
        this.state = ComponentState.POSTING
        const { campaign, promotion, amount, details } = this.payload
        await orderDiscountService.createOneAsync({
          orderGuid: this.parentGuid,
          cultureCode: this.contentLanguage,
          campaign,
          promotion,
          amount,
          details,
        })
        this.$emit(ComponentEvent.CREATED)
      } catch (error) {
        this.state = ComponentState.ERROR
        if (error?.response?.data?.message) {
          this.errorMessage = error.response.data.message
        } else {
          throw error
        }
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.order-discounts-create__form-fields
  display grid
  row-gap 3 * $base-unit
  padding-top 3 * $base-unit

.discount-create__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()
</style>
