import Fuse from 'fuse.js'

export default {
  name: 'UseFuzzySearch',
  props: {
    defaultAll: {
      type: Boolean,
      default: true,
    },
    // Sets fuse.js options
    list: {
      type: Array,
      required: true,
    },
    keys: {
      type: Array,
      default: undefined,
    },
    caseSensitive: {
      type: Boolean,
      default: undefined,
    },
    includeScore: {
      type: Boolean,
      default: undefined,
    },
    includeMatches: {
      type: Boolean,
      default: undefined,
    },
    tokenize: {
      type: Boolean,
      default: undefined,
    },
    tokenSeparator: {
      type: RegExp,
      default: undefined,
    },
    matchAllTokens: {
      type: Boolean,
      default: undefined,
    },
    findAllMatches: {
      type: Boolean,
      default: undefined,
    },
    id: {
      type: String,
      default: undefined,
    },
    shouldSort: {
      type: Boolean,
      default: undefined,
    },
    threshold: {
      type: Number,
      default: 0.4,
    },
    location: {
      type: Number,
      default: undefined,
    },
    distance: {
      type: Number,
      default: undefined,
    },
    maxPatternLength: {
      type: Number,
      default: undefined,
    },
    minMatchCharLength: {
      type: Number,
      default: undefined,
    },
  },
  data () {
    return {
      fuse: null,
      value: '',
      result: [],
    }
  },
  computed: {
    options () {
      return {
        keys: this.keys,
        caseSensitive: this.caseSensitive,
        includeScore: this.includeScore,
        includeMatches: this.includeMatches,
        tokenize: this.tokenize,
        tokenSeparator: this.tokenSeparator,
        matchAllTokens: this.matchAllTokens,
        findAllMatches: this.findAllMatches,
        shouldSort: this.shouldSort,
        threshold: this.threshold,
        location: this.location,
        distance: this.distance,
        maxPatternLength: this.maxPatternLength,
        minMatchCharLength: this.minMatchCharLength,
        id: this.id,
      }
    },
  },
  watch: {
    list () {
      this.fuse.list = this.list
      this.fuseSearch()
    },
    value () {
      this.$emit('search-input-changed', this.value)
      this.fuseSearch()
    },
    result () {
      this.$emit('search', this.result)
    },
  },
  mounted () {
    this.initFuse()
  },
  methods: {
    initFuse () {
      this.fuse = new Fuse(this.list, this.options)

      if (this.defaultAll) {
        this.result = this.list
      }
    },
    fuseSearch () {
      if (this.value.trim() === '') {
        this.result = this.defaultAll ? this.list : []
      } else {
        this.result = this.fuse.search(this.value.trim())
      }
    },
    setValue (value) {
      this.value = value
    },
  },
  render () {
    return this.$scopedSlots.default({
      value: this.value,
      setValue: this.setValue,
    })
  },
}
