<template>
  <div class="promotion-criteria">
    <uc-entity-list v-bind="{ promotionGuid: parentGuid, contentLanguage, entityType }" />
  </div>
</template>

<script>
import { criterionService, EntityType } from '@/services/'
import { UcEntityList } from '../uc-entity-list/'

export default {
  name: 'PromotionCriteria',
  components: {
    UcEntityList,
  },
  provide: {
    entityService: criterionService,
  },
  props: {
    // TODO(ch12695): Should be removed once we have sub-resource links in place
    parentGuid: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  computed: {
    entityType () {
      return EntityType.CRITERION
    },
  },
}
</script>
