<template>
  <div class="uc-time-frame">
    <uc-select
      v-bind="{options, placeholder, selected, readonly}"
      @selected="selectTimeFrame">
      <template #default="{ option }">
        {{ option.label }}
      </template>
    </uc-select>
    <template v-if="customDate">
      <uc-popup-calendar
        data-test="startDate"
        :label="$t('startDate')"
        :placeholder="$t('startDate')"
        :value="startDate"
        :readonly="readonly"
        :disabled-dates="disabledStartDate"
        @selected="selectStartDate" />
      <uc-popup-calendar
        data-test="endDate"
        :label="$t('endDate')"
        :placeholder="$t('endDate')"
        :value="endDate"
        :readonly="readonly"
        :disabled-dates="disabledEndDate"
        @selected="selectEndDate" />
    </template>
  </div>
</template>

<i18n>
{
  "en": {
    "startDate": "Start date",
    "endDate": "End date"
  },
  "da": {
  },
  "sv": {
  },
  "de": {
  }
}
</i18n>

<script>

import UcSelect from '@/components/forms/uc-select/'
import UcPopupCalendar from '@/components/forms/uc-popup-calendar/'

const ComponentEvent = {
  DATE_SELECTED: 'date-selected',
  TIME_FRAME_SELECTED: 'time-frame-selected',
}

export default {
  name: 'UcTimeFrame',
  components: {
    UcSelect,
    UcPopupCalendar,
  },
  props: {
    options: {
      validator: prop => Array.isArray(prop) || prop === undefined,
      required: true,
    },
    selected: {
      validator: prop => typeof prop === 'string' || prop === undefined,
      default: undefined,
    },
    customDate: {
      type: Boolean,
      default: false,
    },
    startDate: {
      type: [Date, String],
      default: null,
    },
    endDate: {
      type: [Date, String],
      default: null,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: '',
    },
  },
  computed: {
    disabledStartDate () {
      return this.endDate ? { from: new Date(this.endDate) } : undefined
    },
    disabledEndDate () {
      return this.startDate ? { to: new Date(this.startDate) } : undefined
    },
  },
  methods: {
    selectStartDate (startDate) {
      this.$emit(ComponentEvent.DATE_SELECTED, { startDate, endDate: this.endDate })
    },
    selectEndDate (endDate) {
      this.$emit(ComponentEvent.DATE_SELECTED, { startDate: this.startDate, endDate })
    },
    selectTimeFrame (option) {
      this.$emit(ComponentEvent.TIME_FRAME_SELECTED, option)
    },
  },
}
</script>

<style scoped lang="stylus">
.uc-time-frame > *
  padding $base-unit 0
</style>
