import faker from 'faker'
import { orderDefinitionsShallow } from './order-definitions.mock.js'
import { getRandomFromArray } from '@/utils/array.utils.js'
import { storesShallow } from '@/mocks/entities/stores/'

export const orderStatuses = [
  {
    guid: mockGuid(),
    name: 'basket',
  },
  {
    guid: mockGuid(),
    name: 'new',
  },
  {
    guid: mockGuid(),
    name: 'customNew',
  },
  {
    guid: mockGuid(),
    name: 'completed',
  },
  {
    guid: mockGuid(),
    name: 'invoiced',
  },
  {
    guid: mockGuid(),
    name: 'paid',
  },
  {
    guid: mockGuid(),
    name: 'cancelled',
  },
  {
    guid: mockGuid(),
    name: 'manuallyCancelled',
  },
  {
    guid: mockGuid(),
    name: 'requiresAttention',
  },
  {
    guid: mockGuid(),
    name: 'processing',
  },
]

export const orderInitStatuses = [
  {
    guid: mockGuid(),
    name: 'new',
  },
  {
    guid: mockGuid(),
    name: 'customNew',
  },
]

export const orderAvailableStatuses = [
  {
    guid: mockGuid(),
    name: 'completed',
  },
  {
    guid: mockGuid(),
    name: 'cancelled',
  },
]

function mockGuid () {
  return `mock-${faker.datatype.uuid()}`
}

function orderFactory () {
  const statusName = getRandomFromArray(orderStatuses.map(status => status.name))
  const definition = getRandomFromArray(orderDefinitionsShallow)
  const entityGuid = mockGuid()
  const store = getRandomFromArray(storesShallow)

  return {
    meta: {
      definition,
      entityGuid,
      nodeType: 'order',
      createdBy: faker.internet.email(),
      createdOn: faker.datatype.datetime(),
      modifiedBy: faker.internet.email(),
      modifiedOn: faker.datatype.datetime(),
    },
    definition: {
      value: definition.name,
      selected: [
        definition.guid,
      ],
      guid: mockGuid(),
    },
    editable: {
      value: true,
      guid: 'mock-7903983b-1059-43e1-9624-789c8b64ef24',
    },
    orderNumber: {
      value: faker.datatype.number().toString(),
      guid: mockGuid(),
    },
    status: {
      value: statusName,
      guid: 'mock-0acf6455-d521-45e6-8a6b-c533c404465d',
    },
    customer: {
      value: `${faker.name.firstName()} ${faker.name.lastName()}`,
      guid: mockGuid(),
    },
    email: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    orderDate: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
    store: {
      value: store.name,
      guid: store.guid,
    },
    total: {
      currencyIsoCode: faker.finance.currencyCode(),
      value: faker.commerce.price().toString(),
      guid: mockGuid(),
    },
    subTotal: {
      currencyIsoCode: faker.finance.currencyCode(),
      value: faker.commerce.price().toString(),
      guid: mockGuid(),
    },
    taxTotal: {
      currencyIsoCode: faker.finance.currencyCode(),
      value: faker.commerce.price().toString(),
      guid: mockGuid(),
    },
    shippingTotal: {
      currencyIsoCode: faker.finance.currencyCode(),
      value: faker.commerce.price().toString(),
      guid: mockGuid(),
    },
    paymentTotal: {
      currencyIsoCode: faker.finance.currencyCode(),
      value: faker.commerce.price().toString(),
      guid: mockGuid(),
    },
    discountTotal: {
      currencyIsoCode: faker.finance.currencyCode(),
      value: faker.commerce.price().toString(),
      guid: mockGuid(),
    },
    createdBy: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    createdOn: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
    modifiedBy: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    modifiedOn: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
  }
}

export function generateOrders (count = 1) {
  let items = []

  for (let i = 0; i < count; i++) {
    items = [...items, orderFactory()]
  }

  return items
}

export const orders = generateOrders(1000)
