<template>
  <div
    class="uc-dropdown"
    :class="cssClassObject"
    data-test="uc-dropdown">
    <div
      v-on-clickaway="closeDropdown"
      class="uc-dropdown__wrapper">
      <div
        v-if="isPending"
        class="uc-dropdown__pending">
        <slot name="pending" />
      </div>
      <uc-dropdown-input
        ref="dropdownInput"
        :value="!isEmpty ? placeholder : $t('global.noOptionsFound')"
        :disabled="readonly || isEmpty"
        :enabled="shouldShowList"
        :selected="hasSelected"
        :direction="dropDirection"
        :name="name"
        @toggle="onToggle"
        @open="openList" />
      <transition :name="dropDirection">
        <div
          v-show="shouldShowList"
          ref="dropdownList"
          class="uc-dropdown__container">
          <div
            v-if="isLongList"
            class="uc-dropdown__search">
            <use-fuzzy-search
              :keys="searchKeys"
              :list="options"
              @search="onSearch"
              @search-input-changed="onSearchChanged">
              <template #default="{ value, setValue }">
                <uc-search
                  dark
                  dense
                  :value="value"
                  :focus="shouldShowList"
                  :clear="shouldShowList"
                  @input="setValue($event, value)" />
              </template>
            </use-fuzzy-search>
          </div>

          <!-- Options -->
          <uc-dropdown-list
            :style="cssStyleObject"
            :options="searchResults">
            <template #default="{ option }">
              <slot v-bind="{ option, searchTerm }" />
            </template>
          </uc-dropdown-list>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { directive as onClickaway } from 'vue-clickaway'
import UcSearch from '@/components/forms/uc-search'
import { UcDropdownInput, UcDropdownInputDirection } from './uc-dropdown-input/'
import { UcDropdownList } from './uc-dropdown-list/'
import UseFuzzySearch from '@/components/wrappers/use-fuzzy-search'
import { validateAgainstEnum } from '@/utils/validate-against'

export const DirectionProp = {
  UP: UcDropdownInputDirection.UP,
  DOWN: UcDropdownInputDirection.DOWN,
}

const MAX_LIST_HEIGHT = 230

const ComponentEvent = {
  TOGGLE: 'toggle',
}

export default {
  name: 'UcDropdown',
  components: {
    UcSearch,
    UcDropdownInput,
    UcDropdownList,
    UseFuzzySearch,
  },
  directives: {
    onClickaway,
  },
  props: {
    options: {
      validator: prop => Array.isArray(prop) || prop === undefined,
      default: () => ([]),
    },
    selected: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: function () {
        return this.$t('global.selectPlaceholder')
      },
    },
    collapse: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
      default: function () {
        return this.placeholder
      },
    },
    direction: {
      type: String,
      default: null,
      validator: validateAgainstEnum(DirectionProp) || null,
    },
  },
  data () {
    return {
      shouldShowList: false,
      searchKeys: ['label'],
      searchResults: this.options,
      searchTerm: '',
      hasSelected: false,
      dropDirection: this.direction ?? DirectionProp.DOWN,
    }
  },
  computed: {
    hasOptions () {
      return !!this.options && this.options.length >= 0
    },
    isLongList () {
      return !!this.options && this.options.length > 6
    },
    isEmpty () {
      return !!this.options && this.options.length === 0
    },
    hasSearchResults () {
      return this.searchResults.length > 0
    },
    isPending () {
      return !this.hasOptions
    },
    cssClassObject () {
      return {
        'is-dropup': this.dropDirection === DirectionProp.UP,
        'is-dropdown': this.dropDirection === DirectionProp.DOWN,
      }
    },
    cssStyleObject () {
      // computed prop can't be used, since it won't trigger
      const hasLongList = this.searchResults.length > 6
      return {
        height: hasLongList ? `${MAX_LIST_HEIGHT}px` : 'auto',
      }
    },
  },
  watch: {
    selected: {
      handler (newValue) {
        this.hasSelected = newValue
      },
      immediate: true,
    },
    collapse (value) {
      this.shouldShowList = !value
    },
    options () {
      this.searchResults = this.options
    },
  },
  methods: {
    openList () {
      // If direction prop is explicitly set, no automatic flip
      if (this.direction) {
        return
      }

      // Get height of dropdownList and quickly hide it again
      this.$refs.dropdownList.style.display = ''
      const domRect = this.$refs.dropdownList.getBoundingClientRect()
      this.$refs.dropdownList.style.display = 'none'

      const domRectInput = this.$refs.dropdownInput.$el.getBoundingClientRect()

      const isOverflowing = domRectInput.bottom + domRect.height > document.documentElement.clientHeight
      this.dropDirection = isOverflowing ? DirectionProp.UP : DirectionProp.DOWN
    },
    onToggle (isEnabled) {
      this.shouldShowList = isEnabled
      this.$emit(ComponentEvent.TOGGLE, this.shouldShowList)
    },
    onSearch (results) {
      this.searchResults = results
    },
    onSearchChanged (result) {
      this.searchTerm = result
    },
    closeDropdown () {
      this.shouldShowList = false
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-dropdown
  position relative
  width 100%
  user-select none

.uc-dropdown__container
  position absolute
  z-index 3
  width 100%
  border 1px solid $base-border-color
  background-color $base-color-white

  .is-dropdown &
    border-top none
    border-radius 0 0 ($base-unit / 2) ($base-unit / 2)

  .is-dropup &
    bottom $base-unit * 6
    border-bottom none
    border-radius ($base-unit / 2) ($base-unit / 2) 0 0

.uc-dropdown__search
  padding (2 * $base-unit) (2 * $base-unit) 0

.uc-dropdown__pending
  position absolute
  top 0
  right 0
  bottom 0
  left 0
  z-index 100
  display flex
  align-items center
  justify-content center
  background-color rgba(255, 255, 255, 0.5)
</style>
