<template>
  <div class="content-navigator-item">
    <uc-node-row>
      <template #before>
        <div class="content-navigator-item__image">
          <content-branch-icon v-if="item.hasChildren" />
          <content-leaf-icon v-else />
        </div>
      </template>

      <uc-node-title>
        {{ item.name }}
      </uc-node-title>
      <uc-node-subtitle v-if="item.url">
        {{ item.url }}
      </uc-node-subtitle>

      <template #after>
        <div
          v-if="$slots.actions"
          class="content-navigator-item__actions">
          <slot name="actions" />
        </div>
      </template>
    </uc-node-row>
  </div>
</template>

<script>
import {
  UcNodeRow,
  UcNodeTitle,
  UcNodeSubtitle,
} from '@/components/generic/uc-node-row/'
import {
  ContentBranch24Thick as ContentBranchIcon,
  ContentLeaf24Thick as ContentLeafIcon,
} from '@/assets/icons'

export default {
  name: 'ContentNavigatorItem',
  components: {
    UcNodeRow,
    UcNodeTitle,
    UcNodeSubtitle,
    ContentLeafIcon,
    ContentBranchIcon,
  },
  props: {
    item: {
      type: Object, // { name, url, hasChildren }
      default: () => ({}),
    },
  },
}
</script>

<style lang="stylus" scoped>
$content-spacing = $base-unit * 3

.content-navigator-item
  display flex
  align-items center
  height 6 * $base-unit

.content-navigator-item__image
  width $content-spacing
  height $content-spacing
  margin-right $content-spacing

.content-navigator-item__actions
  flex-shrink 1
  margin-left $content-spacing
</style>
