import { rest } from 'msw'
import { products } from '@/mocks/handlers/products/'
import { difference, intersection } from '@/utils/array.utils.js'
import { ApiStatusCodes } from '@/constants.js'

const RESPONSE_DELAY = 200
let selectedProducts = []

export const productCriterionHandlers = [
  rest.post('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/products/:cultureCode', (req, res, ctx) => {
    const selectedProductsGuids = selectedProducts.map(product => product.meta.entityGuid)

    const duplicateGuids = intersection(req.body, selectedProductsGuids)
    const newGuids = difference(req.body, selectedProductsGuids)
    const newProducts = products.filter(product => newGuids.includes(product.meta.entityGuid))

    selectedProducts = [...selectedProducts, ...newProducts]

    const responseDuplicates = duplicateGuids.map(guid => ({ guid, message: 'duplicate', statusCode: ApiStatusCodes.ALREADY_EXIST }))
    const responseNewProducts = newGuids.map(guid => ({ guid, message: 'success', statusCode: ApiStatusCodes.OK }))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json([...responseDuplicates, ...responseNewProducts]),
    )
  }),

  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/products/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const response = selectedProducts.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', selectedProducts.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.delete('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/products', (req, res, ctx) => {
    selectedProducts = selectedProducts.filter(product => !req.body.includes(product.meta.entityGuid))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
]
