<template>
  <div
    class="overlay-modal"
    data-test="overlay-modal">
    <div class="overlay-modal__container">
      <slot />
    </div>
  </div>
</template>

<script>
export default {
  name: 'OverlayModal',
  created () {
    document.body.classList.add('overlay-open')
  },
  destroyed () {
    document.body.classList.remove('overlay-open')
  },
}
</script>

<style lang="stylus" scoped>
.overlay-modal
  position absolute
  top 0
  right 0
  left 0
  z-index 1000
  display flex
  flex-direction column
  background-color $base-background-color
</style>
