<template>
  <header
    ref="header"
    class="sticky-header"
    :class="cssClassObject"
    :style="{ 'z-index': layer }">
    <div class="sticky-header__container">
      <slot />
      <div
        v-if="$slots.actions"
        class="sticky-header__actions">
        <slot name="actions" />
      </div>
    </div>
  </header>
</template>

<script>
const ComponentEvent = {
  SCROLL: 'scroll',
}

export default {
  name: 'StickyHeader',
  props: {
    expand: {
      type: Boolean,
      default: false,
    },
    layer: {
      type: Number,
      default: 150,
    },
    flat: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      isScrolled: false,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-scrolled': this.isScrolled,
        'is-expanded': this.expand,
        'is-flat': this.flat,
      }
    },
  },
  created () {
    window.addEventListener('scroll', this.onScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.onScroll)
  },
  methods: {
    onScroll () {
      const headerOffsetTop = this.$refs.header.offsetTop
      const scrollPosition = window.pageYOffset || document.documentElement.scrollTop
      this.isScrolled = scrollPosition > headerOffsetTop

      this.$emit(ComponentEvent.SCROLL, this.isScrolled)
    },
  },
}
</script>

<style lang="stylus" scoped>
$background-color = $base-background-color
$header-height = $base-unit * 12
$header-width = 100%
$scrollbar-width = 6px

.sticky-header
  position sticky
  top -1px
  left 0
  display flex
  align-items center
  min-height $header-height
  background $background-color

  // For smooth box-shadow animation: https://tobiasahlin.com/blog/how-to-animate-box-shadow/
  &::after
    position absolute
    top 0
    left 0
    display block
    width $header-width
    height 100%
    box-shadow $base-box-shadow
    opacity 0
    transition opacity 150ms ease-in-out
    content ''
    pointer-events none

.sticky-header__container
  display flex
  align-items flex-start
  justify-content space-between
  width $header-width
  min-width $base-layout-width
  margin-inline $base-layout-margin-inline

.sticky-header__actions
  display flex
  align-items center
  justify-content flex-end

// state .is-scrolled
:not(.is-flat).is-scrolled::after
  opacity 1

// state .is-expanded
.is-expanded
  margin 0 'calc(-50vw + %s + %s)' % (($header-width / 2) ($scrollbar-width / 2))
</style>
