<template>
  <div class="uc-catalog-editor">
    <uc-dropdown-paged
      v-bind="{
        options,
        collapse,
        readonly,
        selected,
        isLoading,
        hasLoadedAllItems,
      }"
      :placeholder="label"
      @toggle="onToggle"
      @next="requestNextPage"
      @search="search">
      <template #default="{ option }">
        <div class="uc-dropdown-paged__option">
          <uc-radio
            :label="option.label"
            :value="option.guid"
            :checked="setChecked(option.guid)"
            @change="setOneAsync(option.guid)">
            <!-- eslint-disable-next-line vue/no-v-html -->
            <span v-html="highlight(option.label, searchTerm)" />
            <template #icon>
              <span class="uc-select__icon">
                <check-icon />
              </span>
            </template>
          </uc-radio>
        </div>
      </template>
    </uc-dropdown-paged>
  </div>
</template>

<script>
import { UcDropdownPaged } from '@/components/forms/uc-dropdown/'
import { UcRadio } from '@/components/forms/uc-radio/'
import { Check24Normal as CheckIcon } from '@/assets/icons'

import { isEmpty } from '@/utils/object.utils.js'
import highlight from '@/utils/word-highlight'

import http from '@/services/http.js'
import { CatalogCriterionService } from './catalog-criterion.service.js'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  LOADING: 'loading',
  ERROR: 'error',
}

export default {
  name: 'UcCatalogEditor',
  components: {
    UcDropdownPaged,
    UcRadio,
    CheckIcon,
  },
  props: {
    language: {
      type: String,
      required: true,
    },
    promotionGuid: {
      type: String,
      required: true,
    },
    entityGuid: {
      type: String,
      required: true,
    },
    definition: {
      type: Object,
      required: true,
    },
  },
  data () {
    return {
      collapse: false,
      selectedOption: null,

      catalogs: [], // of { guid, name }
      catalogCriterionService: null,
      state: ComponentState.INITIALIZING,

      currentPage: 1,
      pageSize: 18,
      totalCount: 0,
      searchTerm: '',
    }
  },
  computed: {
    placeholder () {
      return this.definition.label || this.$t('global.selectPlaceholder')
    },
    label () {
      return (this.selectedOption && this.selectedOption.label) || this.placeholder
    },
    hasSelectedOption () {
      return !isEmpty(this.selectedOption)
    },
    // put selected option (if any) at the top
    options () {
      if (this.hasSelectedOption) {
        const filteredOptions = this.catalogs.filter(option => option.guid !== this.selectedOption.guid)
        return [this.selectedOption, ...filteredOptions]
      }
      return this.catalogs
    },
    readonly () {
      return this.definition.readonly
    },
    selected () {
      return !!this.selectedOption
    },

    isLoading () {
      return this.state === ComponentState.LOADING
    },
    hasLoadedAllItems () {
      return this.catalogs.length === this.totalCount
    },
  },
  async created () {
    this.catalogCriterionService = new CatalogCriterionService(http, this.promotionGuid, this.entityGuid)
    await this.fetchSelectedAsync()
  },
  methods: {
    highlight,
    setChecked (optionGuid) {
      return this.hasSelectedOption && this.selectedOption.guid === optionGuid
    },
    onToggle (event) {
      this.collapse = !event
    },

    async fetchSelectedAsync () {
      this.state = ComponentState.LOADING

      try {
        const response = await this.catalogCriterionService.fetchSelectedAsync({ cultureCode: this.language })
        this.selectedOption = response.data[0]

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
      }
    },

    async fetchManyAsync (startAt = 0, currentPage = 1, reset = false) {
      this.state = ComponentState.LOADING

      try {
        const response = await this.catalogCriterionService.fetchManyAsync({
          startAt,
          limit: this.pageSize,
          cultureCode: this.language,
          searchTerm: this.searchTerm,
        })

        if (reset) {
          this.catalogs = []
        }

        this.catalogs = [...this.catalogs, ...response.data]

        this.totalCount = Number(response.headers['x-total-count'])
        this.currentPage = currentPage

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
      }
    },
    async setOneAsync (optionGuid) {
      this.collapse = true
      this.state = ComponentState.LOADING

      const isSameOption = this.selectedOption && this.selectedOption.guid === optionGuid
      if (isSameOption) {
        return
      }

      try {
        const catalog = this.selectedOption = this.options.find(option => option.guid === optionGuid)
        await this.catalogCriterionService.patchOneAsync({ catalog, cultureCode: this.language })

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle http request errors
        // Following should be handled: 304 and 400
      }
    },

    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1
      await this.fetchManyAsync(startAt, page)
    },
    search (searchTerm) {
      this.searchTerm = searchTerm
      this.fetchManyAsync(0, 1, true)
    },
  },
}
</script>

<style lang="stylus" scoped>
// necessary to ensure the user can click anywhere on the item
.uc-dropdown-paged__option
  width 100%
  height 100%

>>> .highlight
  font-weight bold

.uc-select__icon
  margin-top $base-unit * 0.5
</style>

