<template>
  <div
    v-if="stores"
    class="store-selector"
    data-test="store-selector">
    <uc-selector
      v-model="selected"
      :items="stores"
      :search-keys="['name']"
      item-key="guid"
      @input="onStoreSelect">
      <template #default="{ item }">
        {{ item.name }}
      </template>
    </uc-selector>
  </div>
</template>

<script>
import UcSelector from '@/components/generic/uc-selector'
import { createNamespacedHelpers } from 'vuex'

const builderStore = createNamespacedHelpers('builder')
const persistedStore = createNamespacedHelpers('persisted')

const ComponentEvent = {
  SELECT: 'select',
}

export default {
  name: 'StoreSelector',
  components: {
    UcSelector,
  },
  computed: {
    ...builderStore.mapGetters(['stores']),
    ...persistedStore.mapGetters(['selectedStoreGuidName', 'selectedStoreGuid']),
    selected: {
      get () {
        return this.selectedStoreGuidName
      },
      set (store) {
        this.setSelectedStoreGuid(store.guid)
      },
    },
  },
  methods: {
    ...persistedStore.mapActions(['setSelectedStoreGuid']),
    onStoreSelect (store) {
      this.$emit(ComponentEvent.SELECT, store)
    },
  },
}
</script>
