<template>
  <div
    v-if="items.length"
    data-test="breadcrumbs"
    class="breadcrumbs">
    <!-- Breadcrumbs collapsed -->
    <ol
      v-show="isCollapsed"
      :style="{visibility: isCollapsed ? 'visible' : 'hidden'}"
      class="breadcrumbs__list is-collapsed">
      <span
        v-for="(item, index) in collapsedItems"
        :key="item.id"
        class="breadcrumbs__container">
        <li
          v-if="index === 1"
          class="breadcrumbs__item">
          <span class="force-delimiter">
            <chevron-forward />
          </span>
          <span>...</span>
        </li>
        <li
          class="breadcrumbs__item"
          @click="$emit('click', item)">
          <span class="delimiter">
            <chevron-forward />
          </span>
          <span>{{ item.name }}</span>
        </li>
      </span>
    </ol>

    <!-- Breadcrumbs expanded -->
    <ol
      ref="breadcrumbs"
      :style="{visibility: isCollapsed ? 'hidden' : 'visible'}"
      class="breadcrumbs__list">
      <li
        v-for="item in items"
        :key="item.id"
        class="breadcrumbs__item"
        @click="$emit('click', item)">
        <span class="delimiter">
          <chevron-forward />
        </span>
        <span>{{ item.name }}</span>
      </li>
    </ol>
  </div>
</template>

<script>
import ChevronForward from '@/assets/icons/16/chevron-forward--16--normal.vue'
export default {
  name: 'UcBreadcrumbs',
  components: {
    ChevronForward,
  },
  props: {
    items: {
      type: Array,
      default: function () {
        return []
      },
    },
    widthBeforeCollapse: {
      type: Number,
      default: 500,
    },
  },
  data () {
    return {
      isCollapsed: false,
    }
  },
  computed: {
    collapsedItems () {
      if (this.items.length <= 3) {
        return this.items
      }

      // Collapsed: Only display the first and last 2 elements.
      return [this.items[0], ...this.items.slice(-2)]
    },
  },
  watch: {
    items: {
      handler: function () {
        this.setCollapsed()
      },
    },
  },
  mounted () {
    this.setCollapsed()
  },
  methods: {
    setCollapsed () {
      // Depending on the width of the breadcrumbs, we collapse the middle section into '...'
      this.$nextTick(() => {
        if (this.$refs && this.$refs.breadcrumbs) {
          const elementWidth = this.$refs.breadcrumbs.getBoundingClientRect().width
          this.isCollapsed = elementWidth >= this.widthBeforeCollapse
        }
      })
    },
  },
}
</script>

<style lang="stylus" scoped>
.breadcrumbs
  user-select none

.breadcrumbs__list
  display inline-flex
  margin 0
  padding 0
  primary-details-text()
  overflow hidden
  white-space nowrap
  text-overflow ellipsis

.breadcrumbs__list:not([style*="visibility: visible"])
  position absolute
  height 0

.breadcrumbs__item
  display inline-flex
  cursor pointer

.delimiter
  display none

.breadcrumbs__item + .breadcrumbs__item .delimiter,
.force-delimiter,
.breadcrumbs__container + .breadcrumbs__container .delimiter
  display flex
  align-items center
  padding 0 10px
</style>
