import { rest } from 'msw'
import { generateMockCatalogs } from '@/mocks/entities/criteria/catalog-criterion.mock'

const catalogs = generateMockCatalogs()
let selected = []

const baseUrl = '/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid'
const URLs = {
  GET_CATALOGS: `${baseUrl}/catalogs/:cultureCode`,
  PATCH_CATALOGS: `${baseUrl}/:cultureCode`,
  GET_SELECTED: `${baseUrl}/catalogs/selected/:cultureCode`,
}

const RESPONSE_DELAY = 200

export const catalogCriterionHandlers = [
  rest.get(URLs.GET_CATALOGS, (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))
    const searchTerm = params.get('searchTerm')

    const response = searchTerm === ''
      ? catalogs.slice(startAt, startAt + limit)
      : catalogs
        .filter(catalog => {
          const term = searchTerm.toLowerCase()
          const matchesName = catalog.name.toLowerCase().includes(term)
          return matchesName
        })
        .slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', catalogs.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.patch(URLs.PATCH_CATALOGS, (req, res, ctx) => {
    selected = catalogs.find(catalog => req.body.catalog.selected.includes(catalog.guid))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),

  rest.get(URLs.GET_SELECTED, (_req, res, ctx) => {
    const response = selected

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
]
