<template>
  <div class="uc-free-gift-editor">
    <!-- Add button -->
    <uc-button
      :theme="buttonTheme"
      :disabled="isSelectButtonDisabled"
      block
      @click="toggleModal">
      {{ translations.add }}
    </uc-button>

    <!-- FreeGift editor -->
    <div
      v-show="products.length > 0"
      class="uc-free-gift-editor__editor">
      <uc-data-grid
        v-bind="{ columns }"
        :rows="mappedRows"
        row-key="guid">
        <!-- Image column -->
        <template #image="{ cell }">
          <div class="uc-free-gift-editor__image">
            <uc-image
              :src="cell"
              :fit="imageFit" />
          </div>
        </template>
        <!-- Remove button column -->
        <template #remove="{ cell }">
          <uc-button-round
            theme="ghost"
            size="s"
            @click="deleteFreeGiftAsync(cell)">
            <close-icon />
          </uc-button-round>
        </template>
      </uc-data-grid>

      <!-- Loading spinner -->
      <uc-overlay v-if="isLoading">
        <div class="uc-free-gift-editor__loading-indicator">
          <loading-indicator :is-loading="isLoading" />
        </div>
      </uc-overlay>
    </div>

    <!-- FreeGift picker -->
    <uc-modal
      v-if="isModalOpen"
      :title="translations.heading"
      :size="modalSize"
      @hide="toggleModal">
      <uc-free-gift-picker
        v-bind="{ language }"
        @add="addFreeGiftsAsync" />
    </uc-modal>
  </div>
</template>

<script>
import http from '@/services/http.js'
import UcButton, { UcButtonTheme } from '@/components/forms/uc-button/'
import { UcDataGrid } from '@/components/generic/uc-data-grid/'
import { UcImage, UcImageFitProp } from '@/components/generic/uc-image/'
import UcButtonRound from '@/components/forms/uc-button-round'
import { Close16Normal as CloseIcon } from '@/assets/icons'
import { UcOverlay } from '@/components/widgets/uc-overlay/'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import { UcModal, UcModalSize } from '@/components/widgets/uc-modal/'
import { UcFreeGiftPicker } from './components/uc-free-gift-picker/'
import { FreeGiftService } from './free-gift.service.js'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  LOADING: 'loading',
  ERROR: 'error',
}

export default {
  name: 'UcFreeGiftEditor',
  components: {
    UcButton,
    UcButtonRound,
    CloseIcon,
    UcDataGrid,
    UcOverlay,
    UcImage,
    UcFreeGiftPicker,
    UcModal,
    LoadingIndicator,
  },
  props: {
    language: {
      type: String,
      required: true,
    },
    promotionGuid: {
      type: String,
      required: true,
    },
    entityGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      isModalOpen: false,
      products: [],
      state: ComponentState.INITIALIZING,
      freeGiftService: null,
    }
  },
  computed: {
    buttonTheme: () => UcButtonTheme.SECONDARY,
    imageFit: () => UcImageFitProp.CONTAIN,
    modalSize: () => UcModalSize.LG,
    columns () {
      return [
        { key: 'image', value: this.$t('global.columns.image'), width: '90px' },
        { key: 'name', value: this.$t('global.columns.name') },
        { key: 'sku', value: this.$t('global.columns.sku') },
        { key: 'variantSku', value: this.$t('global.columns.variantSku') },
        { key: 'remove', width: '72px' },
      ]
    },
    mappedRows () {
      return this.products.map(product => {
        return {
          ...product,
          remove: product.guid,
        }
      })
    },
    translations () {
      return {
        add: this.$t('ucFreeGiftEditor.add'),
        heading: this.$t('ucFreeGiftEditor.heading'),
      }
    },
    isSelectButtonDisabled () {
      // The button should be disabled on initial load + if a free gift has been already selected
      return this.isLoading || this.products.length > 0
    },
    isLoading () {
      return this.state === ComponentState.LOADING
    },
  },
  async created () {
    this.freeGiftService = new FreeGiftService(http, this.promotionGuid, this.entityGuid)
    await this.fetchFreeGiftsAsync()
  },
  methods: {
    toggleModal () {
      this.isModalOpen = !this.isModalOpen
    },
    async fetchFreeGiftsAsync () {
      this.state = ComponentState.LOADING

      try {
        const response = await this.freeGiftService.fetchManyAsync({ cultureCode: this.language })
        const products = response.data.map(item => ({
          image: item.primaryImage?.url,
          name: item.name?.value,
          sku: item.sku?.value,
          variantSku: item.variantSku?.value,
          guid: item.meta?.entityGuid,
        }))

        this.products = [...products]
        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
      }
    },
    async deleteFreeGiftAsync (productGuid) {
      this.state = ComponentState.LOADING

      try {
        const guids = [productGuid]
        await this.freeGiftService.deleteManyAsync({ guids })

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
        // Following should be handled: 400, 401 and 404
      }

      await this.fetchFreeGiftsAsync()
    },
    async addFreeGiftsAsync (rows) {
      this.state = ComponentState.LOADING

      try {
        const guids = rows.map(row => row.meta.entityGuid)
        await this.freeGiftService.createManyAsync({ guids, cultureCode: this.language })

        this.state = ComponentState.IDLE
      } catch (error) {
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle http request errors
        // Following should be handled: 304 and 400
      }

      this.toggleModal()
      await this.fetchFreeGiftsAsync()
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-free-gift-editor__editor
  position relative
  display flex
  justify-content center
  margin-top $base-unit * 3
  border $base-border
  border-bottom none

.uc-free-gift-editor__button
  position absolute
  bottom $base-unit * 4
  z-index 1

.uc-free-gift-editor__image
  display flex
</style>
