import PromotionEdit from './views/promotion-edit.vue'
import PromotionCreate from './views/promotion-create.vue'
import Campaigns from './views/campaigns.vue'
import CampaignCreate from './views/campaign-create.vue'
import CampaignEdit from './views/campaign-edit.vue'
import { LayoutType } from '@/layouts'

export function PromotionsApp ({ router }) {
  router.addRoute({
    path: '/campaigns/',
    name: 'campaigns',
    component: Campaigns,
    props: true,
  })

  router.addRoute('campaigns', {
    path: 'new',
    name: 'campaign-create',
    component: CampaignCreate,
  })

  router.addRoute({
    path: '/campaigns/:campaignGuid',
    name: 'campaign-edit',
    component: CampaignEdit,
    props: true,
  })

  router.addRoute('campaign-edit', {
    path: 'promotions/new',
    name: 'promotion-create',
    component: PromotionCreate,
    props: true,
  })

  router.addRoute({
    path: '/campaigns/:campaignGuid/promotions/:promotionGuid',
    name: 'promotion-edit',
    component: PromotionEdit,
    props: true,
    meta: {
      key: 'promotionGuid',
      layout: LayoutType.BLANK,
    },
  })
}
