<template>
  <component
    :is="layout"
    v-if="isLoaded">
    <router-view
      :key="$route.params[$route.meta.key] || ''"
      :content-language="selectedContentLanguage"
      :selected-store="selectedStoreGuid" />
  </component>
</template>

<script>
import { getLayoutComponentFromRoute } from '@/layouts'
import { storeService } from '@/services'
import { mapMutations, createNamespacedHelpers } from 'vuex'

const persistedStore = createNamespacedHelpers('persisted')
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'RootApp',
  data () {
    return {
      isLoaded: false,
    }
  },
  computed: {
    ...persistedStore.mapGetters(['selectedContentLanguage', 'selectedStoreGuid']),
    layout () {
      return getLayoutComponentFromRoute(this.$route)
    },
  },
  async created () {
    // Set system info
    const response = await this.axios.get('system')
    const systemInfo = response.data
    const cmsUiLanguage = systemInfo.currentCultureCode ? systemInfo.currentCultureCode.substr(0, 2) : null
    this.setSystemInfo(systemInfo)
    this.setUiLanguage(cmsUiLanguage)
    this.setUser(systemInfo.currentUserFirstName)
    this.$i18n.locale = cmsUiLanguage

    // Set stores data
    const storesResponse = await storeService.fetchEntityListAsync()
    const stores = storesResponse.data
    this.setStores(stores)

    this.isLoaded = true
  },
  methods: {
    ...mapMutations(['setSystemInfo', 'setUiLanguage', 'setUser']),
    ...builderStore.mapMutations(['setStores']),
  },
}
</script>
