<template>
  <div class="uc-category-picker">
    <uc-stepper
      :render-steps-after="0"
      @beforeLeave="onBeforeLeaveStep">
      <!-- Back button -->
      <template #top-left="{ previous, step }">
        <transition name="fade">
          <uc-button
            v-show="step > 1"
            :theme="button.link"
            :size="button.small"
            @click="previous">
            {{ translations.back }}
            <template #before>
              <arrow-back-icon />
            </template>
          </uc-button>
        </transition>
      </template>

      <template #step-1="{ next }">
        <div class="uc-category-picker__step">
          <use-data-provider v-bind="{ services, language, mapEntityUrl, mapEntity }">
            <template #default="{ data, getData, isLoading, totalNodeCount }">
              <fragment>
                <p
                  v-truncate
                  class="uc-category-picker__sub-heading">
                  {{ translations.navigatorSubheader }}
                </p>

                <uc-category-navigator
                  v-bind="{ isLoading, totalNodeCount, selection, blacklistedNodeTypes }"
                  :nodes="data"
                  @requestPage="getData"
                  @openNode="getData"
                  @selectNode="selectNode">
                  <template #topRight>
                    <!-- Button: Show selection -->
                    <div class="uc-category-picker__preview-button">
                      <uc-button
                        :size="button.small"
                        :disabled="selection.length === 0"
                        :theme="button.ghost"
                        @click="next">
                        {{ translations.previewButtonLabel }}
                      </uc-button>
                    </div>
                  </template>
                </uc-category-navigator>
              </fragment>
            </template>
          </use-data-provider>
        </div>
      </template>

      <template #step-2>
        <p
          v-truncate
          class="uc-category-picker__sub-heading">
          {{ translations.previewHeader }}
        </p>
        <!-- Selected categories -->
        <div class="uc-category-picker__data-grid">
          <h2 class="uc-category-picker__header">
            {{ translations.previewSubheader }}
          </h2>
          <uc-category-picker-selection
            :rows="categories"
            has-loaded-all-items
            @change="onChange" />
        </div>
      </template>
    </uc-stepper>

    <!-- Add button -->
    <transition name="fade-in-up">
      <div
        class="uc-category-picker__add-button">
        <uc-button
          floating
          :disabled="selection.length === 0"
          @click="submitSelection">
          {{ translations.addButtonLabel }}
        </uc-button>
      </div>
    </transition>
  </div>
</template>

<script>
import { Fragment } from 'vue-fragment'

import { UcStepper } from '@/components/generic/uc-stepper/'
import { UcButton, UcButtonTheme, UcButtonSize } from '@/components/forms/uc-button/'
import { ArrowBack16Normal as ArrowBackIcon } from '@/assets/icons/'
import { UseDataProvider } from '@/components/generic/uc-navigator/'

import { UcCategoryNavigator } from '../uc-category-navigator/'
import { UcCategoryEditorService } from '../../uc-category-editor-service.js'
import { UcCategoryPickerSelection } from '../uc-category-picker-selection/'

import { difference } from '@/utils/array.utils.js'
import { toggleObjectInArray } from '@/components/generic/uc-image-picker/uc-image-picker.utils'

const ComponentEvent = {
  ADD: 'add',
}

export default {
  name: 'UcCategoryPicker',
  components: {
    Fragment,
    UcStepper,
    UcButton,
    ArrowBackIcon,
    UseDataProvider,
    UcCategoryNavigator,
    UcCategoryPickerSelection,
  },
  props: {
    language: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      categories: [],
      selection: [],
    }
  },
  computed: {
    services () {
      return UcCategoryEditorService.services
    },
    blacklistedNodeTypes () {
      return UcCategoryEditorService.blacklistedNodeTypes
    },
    button: () => ({
      small: UcButtonSize.SMALL,
      ghost: UcButtonTheme.GHOST,
      link: UcButtonTheme.LINK,
    }),
    translations () {
      return {
        back: this.$tc('global.back'),
        previewButtonLabel: `${this.$t('global.selected')} • ${this.selection.length}`,
        addButtonLabel: `${this.$t('global.add')} (${this.selection.length})`,
        navigatorSubheader: this.$t('ucCategoryEditor.navigatorSubheader'),
        previewHeader: this.$t('ucCategoryEditor.previewHeader'),
        previewSubheader: `${this.$t('ucCategoryEditor.previewSubheader')} • ${this.selection.length}`,
      }
    },
  },
  methods: {
    mapEntity (entity) {
      return UcCategoryEditorService.mapEntity(entity)
    },
    mapEntityUrl (entity) {
      return UcCategoryEditorService.mapEntityUrl(entity)
    },
    selectNode (entity) {
      const node = UcCategoryEditorService.mapEntityForDisplay(entity)
      toggleObjectInArray(this.categories, node)
      this.selection = this.categories
    },
    onBeforeLeaveStep (to) {
      if (to === 2) {
        this.selection = [...this.categories]
      }

      if (to === 1) {
        this.categories = [...this.selection]
      }
    },
    onChange (event) {
      this.selection = difference(this.categories, event)
    },
    submitSelection () {
      this.$emit(ComponentEvent.ADD, this.selection)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-category-picker
  display flex
  flex-direction column
  height 100%

.uc-category-picker__step
  display flex
  flex 1 0 auto
  flex-direction column
  width 100%

.uc-category-picker__preview-button
  align-self flex-end

.uc-category-picker__sub-heading
  secondary-details-text()
  max-width 100%
  margin 0
  margin-bottom $base-unit * 3
  text-align center

.uc-category-picker__data-grid
  display flex
  flex-direction column
  align-items center
  height 100%
  border $base-border
  overflow hidden

.uc-category-picker__header
  align-self flex-start
  margin $base-unit * 3
  primary-heading-2()

.uc-category-picker__add-button
  position absolute
  bottom $base-unit * 5
  left 50%
  z-index 1
  transform translateX(-50%)
</style>
