<template>
  <div class="welcome">
    <h1>
      <span>{{ greeting }}, {{ user }}</span>
      <div class="welcome__emoji">
        <wave-icon />
      </div>
    </h1>

    <p class="welcome__message">
      {{ $tc('welcome.welcomeMessage', numberOfStores) }}
    </p>
  </div>
</template>

<script>
import WaveIcon from '@/assets/icons/legacy/waving-hand.vue'
export default {
  name: 'Welcome',
  components: {
    WaveIcon,
  },
  computed: {
    greeting () {
      const hour = new Date().getHours()
      if (hour > 4 && hour < 11) return this.$t('welcome.greetings.morning')
      if (hour > 10 && hour < 15) return this.$t('welcome.greetings.day')
      if (hour > 14 && hour < 18) return this.$t('welcome.greetings.afternoon')
      if (hour > 17 && hour < 22) return this.$t('welcome.greetings.evening')
      return this.$t('welcome.greetings.night')
    },
    user () {
      return this.$store.getters.user
    },
    numberOfStores () {
      if (this.$store.getters.metrics.storeRevenueStatistics) {
        return this.$store.getters.metrics.storeRevenueStatistics.length
      }
      return 1
    },
  },
}
</script>

<style lang="stylus" scoped>
.welcome
  position relative

  h1
    display flex
    align-items center
    margin-top 0

.welcome__emoji
  width 31px
  height 31px
  margin-left 15px

.welcome__message
  margin-top 2rem
  color $legacy-color-dark-gray-100
  font-size 1.5rem
</style>
