import { EntityService, EntityType } from './_entity.service.js'

export class StoreService extends EntityService {
  constructor (httpClient) {
    super(EntityType.STORE, httpClient)
  }

  async fetchDefinitionsAsync (...args) {
    const definitionsResponse = await super.fetchDefinitionsAsync(...args)

    return {
      ...definitionsResponse,
      getDomains: () => definitionsResponse.data.find(definition => 'domain' in definition).domain.options,
    }
  }

  createEntityAsync ({ name, definitionGuid, domainId }) {
    return super.createEntityAsync({ name, definitionGuid, params: { domainId } })
  }
}
