<template>
  <div class="uc-category-editor">
    <!-- Category Editor -->
    <uc-button
      :theme="buttonTheme"
      block
      @click="toggleModal">
      {{ translations.navigatorHeader }}
    </uc-button>

    <div
      v-show="categories.length > 0"
      class="uc-category-editor__list">
      <!-- Selection -->
      <uc-category-editor-selection
        :rows="categories"
        :has-loaded-all-items="hasLoadedAllItems"
        @next="requestNextPage"
        @remove="deleteCategoriesAsync" />

      <!-- Loading spinner -->
      <uc-overlay v-if="isLoading">
        <div class="uc-category-editor__list__loading-indicator">
          <loading-indicator :is-loading="isLoading" />
        </div>
      </uc-overlay>

      <!-- Notifications -->
      <template v-if="notifications.length > 0">
        <div
          v-for="(notification, index) in notifications"
          :key="index"
          class="uc-category-editor__notifications">
          <uc-snackbar
            v-bind="notification"
            @click="clearNotification(index)"
            @hide="clearFiniteNotifications" />
        </div>
      </template>
    </div>

    <!-- Category navigator -->
    <uc-modal
      v-if="isModalOpen"
      :title="translations.navigatorHeader"
      :size="modalSize"
      @hide="toggleModal">
      <div class="uc-category-editor__navigator">
        <uc-category-picker
          v-bind="{ language }"
          @add="addCategoriesAsync" />
      </div>
    </uc-modal>
  </div>
</template>

<script>
import { UcButton, UcButtonTheme } from '@/components/forms/uc-button/'
import { UcOverlay } from '@/components/widgets/uc-overlay/'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { UcSnackbar, UcSnackbarTheme, UcSnackbarDuration } from '@/components/widgets/uc-snackbar/'
import { UcModal, UcModalSize } from '@/components/widgets/uc-modal/'

import { UcCategoryEditorSelection } from './components/uc-category-editor-selection/'
import { UcCategoryPicker } from './components/uc-category-picker/'

import http from '@/services/http.js'
import { CategoryCriterionService } from './category-criterion.service.js'
import { UcCategoryEditorService } from './uc-category-editor-service.js'
import { ApiStatusCodes } from '@/constants.js'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  LOADING: 'loading',
  ERROR: 'error',
}

const apiStatusCodes = {
  ADDED: ApiStatusCodes.OK,
  UNAUTHORIZED: ApiStatusCodes.NOT_PERMITTED,
  NOT_FOUND: ApiStatusCodes.NOT_FOUND,
  DUPLICATE: ApiStatusCodes.ALREADY_EXIST,
}

export default {
  name: 'UcCategoryEditor',
  components: {
    UcButton,
    UcCategoryEditorSelection,
    UcOverlay,
    LoadingIndicator,
    UcSnackbar,
    UcModal,
    UcCategoryPicker,
  },
  props: {
    language: {
      type: String,
      required: true,
    },
    promotionGuid: {
      type: String,
      required: true,
    },
    entityGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      isModalOpen: false,
      categoryCriterionService: null,
      state: ComponentState.INITIALIZING,
      categories: [],
      currentPage: 1,
      pageSize: 18,
      totalCount: 0,
      notifications: [],
    }
  },
  computed: {
    buttonTheme: () => UcButtonTheme.SECONDARY,
    modalSize: () => UcModalSize.LG,
    translations () {
      return {
        navigatorHeader: this.$t('ucCategoryEditor.header'),
      }
    },
    isEmpty () {
      return this.categories.length === 0
    },
    isLoading () {
      return this.state === ComponentState.LOADING
    },
    hasLoadedAllItems () {
      return this.categories.length === this.totalCount
    },
  },
  async created () {
    this.categoryCriterionService = new CategoryCriterionService(http, this.promotionGuid, this.entityGuid)
    await this.fetchCategoriesAsync()
  },
  methods: {
    toggleModal () {
      this.isModalOpen = !this.isModalOpen
    },

    async fetchCategoriesAsync (startAt = 0, currentPage = 1) {
      this.state = ComponentState.LOADING

      try {
        const result = await this.categoryCriterionService.fetchManyAsync({ startAt, limit: this.pageSize, cultureCode: this.language })
        const entities = result.data.length > 0 ? result.data : []
        const categories = entities.map(UcCategoryEditorService.mapEntityForDisplay)

        this.categories = currentPage > 1 ? [...this.categories, ...categories] : categories
        this.totalCount = Number(result.headers['x-total-count'])
        this.currentPage = currentPage

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
      }
    },

    async deleteCategoriesAsync (categories) {
      this.state = ComponentState.LOADING

      try {
        const guids = categories.map(category => category.guid)
        await this.categoryCriterionService.deleteManyAsync({ guids })

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
      }

      await this.fetchCategoriesAsync()
    },

    async addCategoriesAsync (rows) {
      this.state = ComponentState.LOADING

      try {
        const guids = rows.map(row => row.meta?.entityGuid || row.guid)
        const response = await this.categoryCriterionService.createManyAsync({ guids, cultureCode: this.language })
        this.showCategoriesNotification(response.data)

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle http request errors
        // Following should be handled: 304 and 400
      }

      await this.fetchCategoriesAsync()
      this.toggleModal()
    },

    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1
      await this.fetchCategoriesAsync(startAt, page)
    },

    // Methods related to showing notifications. TODO: Move to separate component
    createNotificationMessage (message, count) {
      return this.$tc(message, count, { count })
    },
    filterCategoriesByStatusCode (categories, statusCode) {
      const matchesStatusCode = category => category.statusCode === statusCode
      return categories.filter(matchesStatusCode)
    },
    showCategoriesNotification (categories) {
      const numberOfCategories = {
        [apiStatusCodes.ADDED]: this.filterCategoriesByStatusCode(categories, apiStatusCodes.ADDED).length,
        [apiStatusCodes.DUPLICATE]: this.filterCategoriesByStatusCode(categories, apiStatusCodes.DUPLICATE).length,
        [apiStatusCodes.NOT_FOUND]: this.filterCategoriesByStatusCode(categories, apiStatusCodes.NOT_FOUND).length,
        [apiStatusCodes.UNAUTHORIZED]: this.filterCategoriesByStatusCode(categories, apiStatusCodes.UNAUTHORIZED).length,
      }

      const message = {
        [apiStatusCodes.ADDED]: this.createNotificationMessage('ucCategoryEditor.messages.success', numberOfCategories[apiStatusCodes.ADDED]),
        [apiStatusCodes.DUPLICATE]: this.createNotificationMessage('ucCategoryEditor.messages.alreadyExist', numberOfCategories[apiStatusCodes.DUPLICATE]),
        [apiStatusCodes.NOT_FOUND]: this.createNotificationMessage('ucCategoryEditor.messages.notFound', numberOfCategories[apiStatusCodes.NOT_FOUND]),
        [apiStatusCodes.UNAUTHORIZED]: this.createNotificationMessage('ucCategoryEditor.messages.unauthorized', numberOfCategories[apiStatusCodes.UNAUTHORIZED]),
      }

      const hasErrors = numberOfCategories[apiStatusCodes.NOT_FOUND] || numberOfCategories[apiStatusCodes.UNAUTHORIZED]
      const hasCategories = numberOfCategories[apiStatusCodes.ADDED] || numberOfCategories[apiStatusCodes.DUPLICATE]

      if (hasErrors) {
        const errorMessage = `${message[apiStatusCodes.NOT_FOUND]} ${message[apiStatusCodes.UNAUTHORIZED]}`
        this.showErrorNotification(errorMessage)
      }

      if (hasCategories) {
        const notificationMessage = `${message[apiStatusCodes.ADDED]} ${message[apiStatusCodes.DUPLICATE]}`
        const theme = numberOfCategories[apiStatusCodes.ADDED] === 0 ? UcSnackbarTheme.NEUTRAL : UcSnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showNotification ({ text, theme, duration = UcSnackbarDuration.SHORT }) {
      const newNotification = {
        text,
        dismissable: duration === UcSnackbarDuration.INFINITE,
        duration,
        theme,
      }

      this.notifications = [...this.notifications, newNotification]
    },
    showErrorNotification (text) {
      this.showNotification({ text, theme: UcSnackbarTheme.ERROR, duration: UcSnackbarDuration.INFINITE })
    },
    clearFiniteNotifications () {
      this.notifications = this.notifications.filter(notification => notification.duration === UcSnackbarDuration.INFINITE)
    },
    clearNotification (index) {
      this.notifications = this.notifications.filter((_notification, i) => i !== index)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-category-editor__list
  position relative
  margin-top $base-unit * 4
  border $base-border

.uc-category-editor__list__loading-indicator
  margin-top $base-unit * 6 // due to `data-grid` header misaligning the spinner

.uc-category-editor__navigator
  height $base-unit * 75

.uc-category-editor__notifications
  position absolute
  top $base-unit * 5
  width 100%
  padding $base-unit * 2

.uc-category-editor__notifications ~ .uc-category-editor__notifications
  top $base-unit * 12
</style>
