// To be removed when we stop supporting Umbraco 7 (Usage of ID instead of Guid)
export function getGuidFromNode (node) {
  const emptyGuid = '00000000-0000-0000-0000-000000000000'
  return node.guid !== emptyGuid ? node.guid : node.id
}
export const toggleObjectInArray = (array, value) => {
  if (value && array) {
    const index = array.findIndex(node => getGuidFromNode(node) === getGuidFromNode(value))
    if (index === -1) {
      array.push(value)
    } else {
      array.splice(index, 1)
    }
  }
}
export const addObjectToArray = (array, value) => {
  if (value && array) {
    const index = array.findIndex(node => getGuidFromNode(node) === getGuidFromNode(value))
    if (index === -1) {
      array.push(value)
    }
  }
}
export const removeObjectFromArray = (array, value) => {
  if (value && array) {
    const index = array.findIndex(node => getGuidFromNode(node) === getGuidFromNode(value))
    if (index !== -1) {
      array.splice(index, 1)
    }
  }
}
