<template>
  <label
    class="uc-checkbox"
    :class="{ readonly }"
    data-test="uc-checkbox"
    @click.stop>
    <input
      v-show="!compact"
      :id="id"
      ref="input"
      type="checkbox"
      :name="id"
      :disabled="readonly"
      :checked="checkedValue"
      :value="guid"
      @change="onChange">
    <span
      class="uc-checkbox__check"
      :class="{ compact }">
      <check-dual v-if="dual" />
      <check v-else />
    </span>
    <span
      v-if="!compact && label"
      v-truncate
      class="uc-checkbox__label">
      <slot>{{ label }}</slot>
    </span>
  </label>
</template>

<script>
import Check from '@/assets/icons/legacy/checkbox-checked.vue'
import CheckDual from '@/assets/icons/legacy/check-dual.vue'
import getId from '@/utils/id-generator.js'

export default {
  name: 'UcCheckbox',
  components: {
    Check,
    CheckDual,
  },
  props: {
    label: {
      type: String,
      default: '',
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    checked: {
      type: Boolean,
      default: false,
    },
    guid: {
      type: String,
      default: '',
    },
    indeterminate: {
      type: Boolean,
      default: false,
    },
    dual: {
      type: Boolean,
      default: false,
    },
    compact: {
      type: Boolean,
      default: false,
    },
    id: {
      type: [String, Number],
      default: () => getId(),
    },
  },
  computed: {
    checkedValue () {
      return this.checked ? 'checked' : ''
    },
  },
  watch: {
    indeterminate () {
      this.$refs.input.indeterminate = this.indeterminate
    },
  },
  methods: {
    onChange (event) {
      this.$emit('change', { value: event.target.checked, label: this.label, guid: this.guid })
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-checkbox
  position relative
  display flex
  align-items center
  justify-content center
  width 100%
  height 100%
  font-size $base-font-size-sm
  cursor pointer
  user-select none
  fill none

  &.readonly
    cursor not-allowed
    fill $base-border-color

  .uc-checkbox__label
    width 100%
    margin-left 8px
    primary-details-text()

  .uc-checkbox__check
    display flex

    .compact &
      margin-top -9px
      margin-left 6px

    svg
      margin auto
      object-fit contain

    .readonly &
      opacity .6

  input[type=checkbox]
    position absolute
    display none

  input[type=checkbox]:checked
    & ~ .uc-checkbox__check
      color $base-border-color
      fill $base-color-navy

  input[type=checkbox]:not(:checked)
    & ~ .uc-checkbox__check svg
      /deep/ svg
        display none
</style>
