<template>
  <span class="animated-number">{{ animatedNumber }}</span>
</template>

<script>
export default {
  name: 'AnimatedNumber',
  props: {
    number: {
      type: Number,
      required: true,
    },
    steps: {
      type: Number,
      default: 50,
    },
    formatted: {
      type: Boolean,
      default: false,
    },
    fixed: {
      type: Number,
      default: 0,
    },
  },
  data () {
    return {
      bufferNumber: 0,
    }
  },
  computed: {
    animatedNumber () {
      const fixedOutput = +this.bufferNumber.toFixed(this.fixed)
      return this.formatted ? fixedOutput.toLocaleString() : fixedOutput
    },
  },
  watch: {
    number () {
      this.animate()
    },
  },
  mounted () {
    this.animate()
  },
  methods: {
    animate () {
      if (this.number === this.bufferNumber) return
      const fraction = (this.number - this.bufferNumber) / this.steps
      const incrementing = fraction > 0
      const step = incrementing ? Math.ceil(fraction) : Math.floor(fraction)

      const incrementor = () => {
        const newValue = this.bufferNumber + step
        if (incrementing && newValue > this.number) {
          this.bufferNumber = this.number
          return
        }
        if (!incrementing && newValue < this.number) {
          this.bufferNumber = this.number
          return
        }
        this.bufferNumber = newValue
        window.requestAnimationFrame(incrementor)
      }
      incrementor()
    },
  },

}
</script>
