<template>
  <div>
    <list
      v-show="!loading && tableHasRows"
      :id="listId"
      :ref="listId"
      v-bind="{ dataUrl }"
      show-variants
      :header="$tc('list.variants')"
      node-type="productVariant"
      :default-columns-configuration="{
        'variantSku': { 'width': '200px' },
        'displayName': { 'width': '180px' },
        'createdOn': { 'width': '220px' },
        'eur 15 pct': { 'width': '200px' }
      }"
      @sortBy="onSortBy"
      @filter="onFilter"
      @initialLoadStarted="loading === true"
      @initialLoadFinished="onInitialLoadFinished"
      @row-clicked="openVariantList">
      <template #actionsHeader>
        <uc-button
          size="sm"
          @click="openDialog">
          {{ $t('list.newButton') }}
        </uc-button>
      </template>
    </list>
    <create-variant
      :is-dialog-open="isDialogOpen"
      :content-language="contentLanguage"
      :product-guid="productGuid"
      @hideDialog="closeDialog"
      @addProductVariantToListAndOpenItsDetails="variantCreated" />
    <div
      v-show="!loading && !tableHasRows"
      class="empty-state">
      <h1
        class="empty-state__header"
        data-test="variant-list-empty-state-header">
        {{ $tc('list.variants') }}
      </h1>
      <p class="empty-state__text">
        {{ $tc('list.variantsEmptyState') }}
      </p>
      <uc-button
        size="sm"
        @click="openDialog">
        {{ $t('list.newVariant') }}
      </uc-button>
    </div>
    <div
      v-if="loading"
      class="loading-container">
      <loading-indicator :is-loading="loading" />
    </div>
  </div>
</template>
<script>
import CreateVariant from './create-product-variant.vue'
import List from '@/components/generic/generic-list/list'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import UcButton from '@/components/forms/uc-button/uc-button'
import { createNamespacedHelpers } from 'vuex'

const detailsStore = createNamespacedHelpers('details')

export default {
  name: 'VariantList',
  components: {
    UcButton,
    LoadingIndicator,
    List,
    CreateVariant,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    productGuid: {
      type: String,
      required: true,
    },
    listId: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      loading: true,
      tableHasRows: false,
      isDialogOpen: false,
      sortBy: '',
      priceGroups: [],
      dataUrl: null,
    }
  },
  computed: {
    metadataUrl () {
      return '/products/definitions/full'
    },
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails']),
    setTableHasRows (totalItems) {
      this.tableHasRows = totalItems > 0
      this.loading = false
    },
    openVariantList (row) {
      this.$emit('openVariantList', row)
    },
    onInitialLoadFinished (totalItems) {
      this.setTableHasRows(totalItems)
      this.$emit('initialLoadFinished')
    },
    onSortBy (sortBy) {
      this.sortBy = sortBy
      this.setDataUrl()
    },
    onFilter (priceColumns) {
      if (priceColumns) {
        this.priceGroups = priceColumns.map(priceColumn => priceColumn.guid)
      } else {
        this.priceGroups = []
      }
      this.setDataUrl()
    },
    setDataUrl () {
      let url = `/products/${this.productGuid}/variants/${this.contentLanguage}`

      const params = new URLSearchParams()
      params.set('sort', this.sortBy)

      if (this.priceGroups.length) {
        this.priceGroups.forEach(pg => params.append('priceGroups', pg))
      } else {
        params.set('priceGroups', '')
      }

      url = `${url}?${params.toString()}`
      this.dataUrl = url
    },
    openDialog () {
      this.isDialogOpen = true
    },
    closeDialog () {
      this.isDialogOpen = false
    },
    variantCreated (createdVariant) {
      this.openEntityEditor(createdVariant)
    },
    removeItems (items) {
      this.$refs[this.listId].removeItems(items)
    },
    openEntityEditor (row) {
      window.scroll(0, 0)
      this.setDetails({ details: row })
    },
  },
}
</script>

<style lang="stylus" scoped>
.empty-state
  display flex
  flex-direction column
  align-items center
  justify-content center
  min-height 346px
  padding 48px 100px 48px 100px

.empty-state__header
  primary-heading-2()
  margin 0

.empty-state__text
  secondary-body-text()

.loading-container
  display flex
  align-items center
  justify-content center
  height 346px
</style>
