const getters = {
  user: state => state.user,
  metrics: state => state.metrics,
  pollInterval: state => state.pollInterval,
  locale: state => state.locale,
  daysDisplayed: state => state.daysDisplayed,
  feed: state => state.feed.filter(x => !x.priority),
  priorityFeed: state => state.feed.filter(x => x.priority),
  feedTypes: state => state.feedTypes,
  showMenu: state => state.showMenu,
  licenseError: state => state.licenseError,
}

export default getters
