import createSaveCommand from '@/utils/create-save-command'
import EditorWrapper from './editor-wrapper'
import { mapGetters } from 'vuex'

export default {
  components: {
    EditorWrapper,
  },
  props: {
    name: {
      type: String,
      default: '',
    },
    definition: {
      type: Object,
      default: () => ({}),
    },
    compact: {
      type: Boolean,
      default: false,
    },
    language: {
      type: String,
      default: function () {
        return this.selectedContentLanguage
      },
    },
  },
  computed: {
    ...mapGetters({ selectedContentLanguage: 'persisted/selectedContentLanguage' }),
    value () {
      return this.definition.value
    },
    guid () {
      return this.definition.guid || ''
    },
    isMultilingual () {
      return this.definition.multilingual
    },
    options () {
      return this.definition.options || []
    },
    selected () {
      return this.definition.selected || []
    },
    placeholder () {
      return this.definition.placeholder || ' '
    },
    isReadonly () {
      return 'readonly' in this.definition && this.definition.readonly
    },
    isRequired () {
      return 'validation' in this.definition && this.definition.validation?.required
    },
    errors () {
      return this.definition.errors
    },
    description () {
      return this.definition.description
    },
    label () {
      const language = this.isMultilingual && this.language ? ` (${this.language})` : ''
      return `${this.definition.label}${language}`
    },
    hasErrors () {
      return this.errors && this.errors.length > 0
    },
  },
  methods: {
    save (options = {}) {
      return createSaveCommand({
        definitionName: this.name,
        definitionGuid: this.guid,
        label: this.label,
        isNullable: !this.isRequired,
        ...options,
      })
    },
    onChange (options = {}, eventName = 'change') {
      this.$emit(eventName, this.save(options))
    },
  },
}
