<template>
  <div class="uc-promo-code-generator-selection">
    <h2 class="uc-promo-code-generator-selection__summary">
      {{ header }}
    </h2>

    <uc-data-grid-selectable
      :value="selection"
      v-bind="{ columns, rows }"
      row-key="code"
      :disable-infinite-scroll="hasLoadedAllItems"
      @next="onNext"
      @deselect="onDeselect"
      @select="onSelect" />
  </div>
</template>

<script>
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { difference } from '@/utils/array.utils'

const ComponentEvent = {
  CHANGE: 'change',
  NEXT: 'next',
}

export default {
  name: 'UcPromoCodeGeneratorSelection',
  components: {
    UcDataGridSelectable,
  },
  props: {
    header: {
      type: String,
      required: true,
    },
    rows: {
      type: Array,
      required: true,
    },
    hasLoadedAllItems: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      deselectedItems: [],
    }
  },
  computed: {
    columns () {
      return [
        {
          key: 'code',
          value: this.$t('ucPromoCodeGenerator.selectionCode'),
        },
        {
          key: 'maxUses',
          value: this.$t('ucPromoCodeGenerator.selectionMaxUses'),
        },
      ]
    },
    selection () {
      return difference(this.rows, this.deselectedItems)
    },
  },
  methods: {
    onDeselect (value) {
      this.deselectedItems = [...this.deselectedItems, value]
      this.$emit(ComponentEvent.CHANGE, this.deselectedItems)
    },
    onSelect (value) {
      this.deselectedItems = difference(this.deselectedItems, [value])
      this.$emit(ComponentEvent.CHANGE, this.deselectedItems)
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-promo-code-generator-selection
  display flex
  flex-direction column
  height 100%
  border $base-border
  overflow hidden

.uc-promo-code-generator-selection__summary
  box-sizing border-box
  width 100%
  margin 0
  padding $base-unit * 3
  primary-heading-2()
</style>
