<template>
  <div
    class="uc-rich-text"
    :class="cssClassObject"
    data-test="uc-rich-text">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :required="isRequired">
      <template #compactValue>
        <!-- eslint-disable vue/no-v-html -->
        <span
          class="uc-rich-text__summary"
          v-html="value" />
      </template>
      <label v-if="label">{{ label }}</label>
      <div class="input-field-container">
        <div class="input-field-wrapper">
          <uc-suneditor
            :value="value"
            @blur="onBlur"
            @focus="onFocus"
            @input="onInput" />
        </div>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import { UcSuneditor } from '@/components/generic/uc-suneditor'
import editorMixin from '../editor-mixin'

function isNullOrContentlessHTML (value) {
  return value === null ||
         value === undefined ||
         value === '' ||
         value === '<p><br></p>'
}

export default {
  name: 'UcRichText',
  components: {
    UcSuneditor,
  },
  mixins: [editorMixin],
  props: {
    theme: {
      type: String,
      default: 'snow',
    },
  },
  data () {
    return {
      editor: null,
      inputValue: this.definition.value || '',
      condition: 'Idle',
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-readonly': this.isReadonly,
        'is-active': this.isActive,
        'is-blurred': !this.isActive,
        'has-content': this.hasInputValue,
      }
    },
    isActive () {
      return this.condition === 'Active'
    },
    hasInputValue () {
      return !isNullOrContentlessHTML(this.inputValue)
    },
  },
  methods: {
    onInput (value) {
      this.inputValue = value
    },
    onFocus () {
      this.condition = 'Active'
    },
    onBlur () {
      const newValue = this.hasInputValue ? this.inputValue : null
      const oldValue = this.definition.value
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null || oldValue === '') && !this.hasInputValue)

      this.condition = this.hasInputValue ? 'Filled' : 'Idle'
      this.inputValue = newValue

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue,
      })
    },
  },
}
</script>
<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
// Content inserted with v-html cannot be targeted when style is scoped
.uc-rich-text__summary *
  primary-body-text()

/* stylelint-disable no-descending-specificity */
// Truncate and hide everything but first child in text in compact mode
.uc-rich-text__summary > *:first-child,
.uc-rich-text__summary > *:first-child > *:first-child
  margin 0
  padding 0
  overflow hidden
  white-space nowrap
  text-overflow ellipsis

.uc-rich-text__summary > *:first-child ~ *,
.uc-rich-text__summary > *:first-child  > *:first-child ~ *
  display none
</style>

<style lang="stylus" scoped>
.input-field-container
  border-radius 4px
  background-color #fff

  .input-field-wrapper
    border 1px solid $base-color-gray-400
    border-radius 4px
    background-color #fff

  &.disabled
    background-color $base-background-color

  &.danger
    background-color $base-color-error

.uc-rich-text
  position relative
  font-family $base-font-family

  label
    position absolute
    top $base-unit * 8
    left 14px
    z-index 5
    padding 0 2px 0 2px
    color $base-color-text-secondary
    font-size 16px
    line-height 30px
    transition-duration 200ms
    user-select none
    pointer-events none

    .error
      color $base-color-error

    .uc-rich-text__tooltip
      margin-left 5px

  &.is-active,
  &.has-content
    label
      z-index 9
      padding 0 4px
      font-size 12px
      line-height 20px
      background-color #fff
      transform translateY(-74px)

  &.is-readonly
    label
      background-color $base-background-color

    .input-field-container
      background-color $base-background-color

      .input-field-wrapper
        border 1px solid $base-color-gray-400
        background-color $base-background-color
</style>
