<template>
  <button
    class="uc-button"
    :class="cssClassObject"
    :disabled="disabled"
    :type="type"
    data-test="uc-button"
    @click="onClick">
    <span
      v-if="$slots.before"
      class="uc-button__icon uc-button__icon--before"
      data-test="uc-button-icon-before">
      <slot name="before" />
    </span>

    <span
      v-truncate
      class="uc-button__text"
      data-test="btn-text">
      <slot />
    </span>

    <span
      v-if="$slots.after"
      class="uc-button__icon uc-button__icon--after"
      data-test="uc-button-icon-after">
      <slot name="after" />
    </span>
  </button>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

const ComponentEvent = {
  CLICK: 'click',
}

export const ThemeProp = {
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  GHOST: 'ghost',
  TEXT: 'text',
  LINK: 'link',
  OUTLINE: 'outline',
}

export const SizeProp = {
  SMALL: 'sm',
  MEDIUM: 'md',
}

export const TypeProp = {
  SUBMIT: 'submit',
  BUTTON: 'button',
}

export default {
  name: 'UcButton',
  props: {
    theme: {
      type: String,
      default: ThemeProp.PRIMARY,
      validator: validateAgainstEnum(ThemeProp),
    },
    size: {
      type: String,
      default: SizeProp.MEDIUM,
      validator: validateAgainstEnum(SizeProp),
    },
    type: {
      type: String,
      default: TypeProp.BUTTON,
      validator: validateAgainstEnum(TypeProp),
    },
    floating: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    block: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    cssClassObject () {
      return {
        'uc-button--theme-primary': this.theme === ThemeProp.PRIMARY,
        'uc-button--theme-secondary': this.theme === ThemeProp.SECONDARY,
        'uc-button--theme-ghost': this.theme === ThemeProp.GHOST,
        'uc-button--theme-outline': this.theme === ThemeProp.OUTLINE,
        'uc-button--theme-text': this.theme === ThemeProp.TEXT,
        'uc-button--theme-link': this.theme === ThemeProp.LINK,
        'uc-button--size-sm': this.size === SizeProp.SMALL,
        'uc-button--size-md': this.size === SizeProp.MEDIUM,
        'is-disabled': this.disabled,
        'is-floating': this.floating,
        'is-block': this.block,
      }
    },
  },
  methods: {
    onClick (event) {
      this.$emit(ComponentEvent.CLICK, event)
    },
  },
}
</script>

<style lang="stylus" scoped>
$uc-button-size-sm = 3 * $base-unit
$uc-button-size-md = 5 * $base-unit

.uc-button
  display flex
  align-items center
  max-width 26 * $base-unit // hotfix for `create-promotion` submit label, TODO: the default width should be unrestricted
  padding 0
  border none
  border-radius 3 * $base-unit
  outline none
  cursor pointer
  transition background-color 150ms ease, text-decoration 150ms ease
  user-select none
  pointer-events auto

  &:active:not(.is-disabled)
    transform 'scale(%s)' % $base-button-transform-scale--active

  &.is-disabled
    text-decoration none
    cursor default

// Icons
.uc-button__icon
  display inline-flex
  flex 1 0 auto
  align-items center
  justify-content center
  box-sizing border-box

.uc-button__text
  width 100%

// Size - sm
.uc-button--size-sm
  height 4 * $base-unit
  primary-details-text(600)

  .uc-button__icon
    min-width $uc-button-size-sm
    padding 0 $base-unit

  .uc-button__text:first-child
    padding-left $uc-button-size-sm

  .uc-button__text:last-child
    padding-right $uc-button-size-sm

// Size - md
.uc-button--size-md
  height 6 * $base-unit
  primary-body-text(600)
  border-width 2 * $base-border-width

  .uc-button__icon
    min-width $uc-button-size-md

  .uc-button__icon--before
    padding 0 $base-unit 0 $base-unit * 2

  .uc-button__icon--after
    padding 0 $base-unit * 2 0 $base-unit

  .uc-button__text:first-child
    padding-left $uc-button-size-md

  .uc-button__text:last-child
    padding-right $uc-button-size-md

// Theme primary
.uc-button--theme-primary
  color $base-color-white
  background-color $base-color-brand

  &:hover:not(.is-disabled)
    background-color $base-color-brand--hover

  &.is-floating
    box-shadow $base-box-shadow

  &.is-disabled
    color $base-button-color--disabled
    background-color $base-button-background-color--disabled
    box-shadow none

// Theme secondary
.uc-button--theme-secondary
  border $base-border-width $base-border-style $base-color-brand
  color $base-color-brand
  background-color $base-color-white

  &.uc-button--size-md
    border-width 2 * $base-border-width

  &.is-floating
    box-shadow $base-box-shadow

  &:hover:not(.is-disabled)
    color $base-color-white
    background-color $base-color-brand

  &.is-disabled
    border-color $base-button-background-color--disabled
    color $base-button-color--disabled
    box-shadow none

// Theme ghost
.uc-button--theme-ghost
  border $base-border-width $base-border-style $base-color-gray-300
  background-color transparent

  &:hover:not(.is-disabled)
    text-decoration underline

  &.is-disabled
    border-color $base-button-background-color--disabled
    color $base-button-color--disabled

// Theme outline
.uc-button--theme-outline
  border $base-border-width $base-border-style $base-color-gray-300
  background-color $base-color-white

  &.uc-button--size-sm
    primary-details-text()

  &.uc-button--size-md
    primary-body-text()

  &:hover:not(.is-disabled)
  &:active:not(.is-disabled)
    border-color $base-color-brand

  &.is-disabled
    border-color $base-button-background-color--disabled
    color $base-button-color--disabled

// Theme link
.uc-button--theme-link
  height auto
  border-width 0
  border-radius 0
  background-color transparent
  primary-details-text()

  .uc-button__icon
    min-width auto

  .uc-button__icon--before
    padding-left 0

  .uc-button__icon--after
    padding-right 0

  .uc-button__text:first-child
    padding-left 0

  .uc-button__text:last-child
    padding-right 0

  &:hover:not(.is-disabled)
    text-decoration underline

  &.is-disabled
    color $base-button-color--disabled

// Theme text
.uc-button--theme-text
  color $base-color-brand
  background-color transparent

  .uc-button__text:first-child
    padding-left 0

  .uc-button__text:last-child
    padding-right 0

  &:hover:not(.is-disabled)
    text-decoration underline

  &.is-disabled
    color $base-button-color--disabled

.is-block
  width 100%
  min-width 100%
</style>
