<template functional>
  <li
    :key="data.key"
    class="uc-list-item"
    :class="{ 'uc-list-item--actionable': props.actionable }"
    data-test="list-item"
    v-on="listeners">
    <slot :isSelected="props.selected" />
  </li>
</template>

<script>
export default {
  name: 'UcListItem',
  props: {
    actionable: {
      type: Boolean,
      default: false,
    },
  },
}
</script>

<style lang="stylus" scoped>
$padding-vertical = $base-unit * 1
$padding-horizontal = $base-unit * 3
$border = $base-border
$border-width = $base-border-width

.uc-list-item
  padding $padding-vertical $padding-horizontal
  padding-bottom $padding-vertical - $border-width
  border-bottom $border
  list-style none
  user-select none

// modifier: actionable
.uc-list-item--actionable
  cursor pointer

</style>
