export class FreeGiftService {
  constructor (httpClient, promotionGuid, discountGuid) {
    this._httpClient = httpClient
    this._promotionGuid = promotionGuid
    this._discountGuid = discountGuid
  }

  get _rootUrl () {
    return `/promotions/${this._promotionGuid}/discounts/${this._discountGuid}/freeGift`
  }

  fetchManyAsync ({ cultureCode }) {
    const url = `${this._rootUrl}/${cultureCode}`
    return this._httpClient.get(url)
  }

  createManyAsync ({ guids, cultureCode }) {
    const url = `${this._rootUrl}/${cultureCode}`
    return this._httpClient.post(url, guids)
  }

  deleteManyAsync ({ guids }) {
    return this._httpClient.delete(this._rootUrl, { data: guids })
  }
}
