<template>
  <div class="uc-navigator-grid-node">
    <uc-navigator-node
      v-bind="{ selected, selectable }"
      data-test="grid-node"
      @click="onSelect">
      <div class="uc-navigator-grid-node__container">
        <div class="uc-navigator-grid-node__left-area">
          <span
            v-if="$slots.branchIcon || $slots.leafIcon"
            class="uc-navigator-grid-node__icon">
            <slot
              v-if="isBranchNode"
              name="branchIcon" />
            <slot
              v-else
              name="leafIcon" />
          </span>
          <div
            v-truncate
            class="uc-navigator-grid-node__name">
            <slot />
          </div>
        </div>
        <div
          v-if="isBranchNode"
          class="uc-navigator-grid-node__right-area">
          <div class="uc-navigator-grid-node__children-count">
            {{ $tc('global.items', childrenCount, { count: childrenCount }) }}
          </div>
          <div class="uc-navigator-grid-node__action">
            <uc-button-round
              theme="ghost"
              size="s"
              @click.stop="onOpen">
              <chevron-forward />
            </uc-button-round>
          </div>
        </div>
      </div>
    </uc-navigator-node>
  </div>
</template>

<script>
import { UcNavigatorNode } from '@/components/generic/uc-navigator'
import UcButtonRound from '@/components/forms/uc-button-round'
import { ChevronForward16Thick as ChevronForward } from '@/assets/icons'

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select',
}

export default {
  name: 'UcNavigatorGridNode',
  components: {
    UcNavigatorNode,
    UcButtonRound,
    ChevronForward,
  },
  props: {
    childrenCount: {
      type: Number,
      default: 0,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    selectable: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    isBranchNode () {
      return this.childrenCount > 0
    },
  },
  methods: {
    onOpen () {
      this.$emit(ComponentEvent.OPEN)
    },
    onSelect () {
      this.$emit(ComponentEvent.SELECT)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-navigator-grid-node__container
  display flex
  flex 1
  justify-content space-between
  min-width 0
  height 6 * $base-unit
  padding $base-unit 2 * $base-unit
  border $base-border
  border-radius ($base-unit / 2)

.uc-navigator-grid-node__left-area
  display flex
  flex 1
  align-items center
  min-width 0

.uc-navigator-grid-node__name
  primary-petit-text()
  color $base-color-text-primary

.uc-navigator-grid-node__right-area
  display flex
  align-items center
  justify-content flex-end
  margin-left 2 * $base-unit

.uc-navigator-grid-node__children-count
  primary-petit-text()
  color $base-color-text-secondary
  line-height inherit
  white-space nowrap

.uc-navigator-grid-node__icon
  width 3 * $base-unit
  height 3 * $base-unit
  margin-right $base-unit

.uc-navigator-grid-node__action
  margin-left (2 * $base-unit)
</style>
