<template>
  <uc-navigator
    v-if="!isEmptyRootState"
    v-bind="{ nodes, isLoading }"
    :path.sync="path"
    @navigate="openNode">
    <template #emptyFolder="navigator">
      <div class="uc-image-picker__empty-folder">
        <h2 class="uc-image-picker__empty-folder__title">
          {{ $t('global.emptyFolder') }}
        </h2>
        <div
          class="uc-image-picker__empty-folder__back"
          @click="navigator.navigateBack">
          <arrow-back />
          <span class="uc-image-picker__empty-folder__back-label">{{ $t('global.back') }}</span>
        </div>
      </div>
    </template>

    <template #topLeft="navigator">
      <uc-breadcrumbs
        :items="navigator.path"
        @click="navigator.navigate" />
    </template>

    <template #topRight>
      <div class="uc-image-picker__actions">
        <slot name="topRight" />
      </div>
    </template>

    <template #default="navigator">
      <uc-infinite-scroller @next="nextPage">
        <uc-navigator-view-grid :nodes="nodesPaged">
          <template #default="{ node }">
            <!-- Node -->
            <component
              :is="getComponentByNodeType(node.pickerNodeType)"
              :node="node"
              :selected="isNodeSelected(node.guid)"
              @select="onSelect"
              @open="navigator.navigate" />
          </template>
        </uc-navigator-view-grid>
      </uc-infinite-scroller>
    </template>
  </uc-navigator>
  <uc-navigator-empty-state
    v-else
    :title="$t('ucImagePicker.noMediaTitle')"
    :text="$t('ucImagePicker.noMediaText', { name: cms })" />
</template>

<script>
import { ArrowBack16Normal as ArrowBack } from '@/assets/icons'
import Constants from '@/constants.js'
import UcImagePickerGridImage from '@/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-image'
import UcImagePickerGridFolder from '@/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-folder'
import UcImagePickerGridFile from '@/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-file'
import UcBreadcrumbs from '@/components/widgets/uc-breadcrumbs/uc-breadcrumbs'
import { UcInfiniteScroller } from '@/components/generic/uc-infinite-scroller/'
import {
  UcNavigator,
  UcNavigatorViewGrid,
  UcNavigatorEmptyState,
} from '@/components/generic/uc-navigator'

const ComponentEvent = {
  SELECT_NODE: 'selectNode',
  OPEN_NODE: 'openNode',
}

const NodeTypeToComponentMap = {
  [Constants.NodeType.Image]: UcImagePickerGridImage,
  [Constants.NodeType.Folder]: UcImagePickerGridFolder,
  [Constants.NodeType.File]: UcImagePickerGridFile,
}

const PAGE_SIZE = 16

export default {
  name: 'UcImagePicker',
  components: {
    ArrowBack,
    UcNavigator,
    UcNavigatorViewGrid,
    UcNavigatorEmptyState,
    UcInfiniteScroller,
    UcBreadcrumbs,
  },
  props: {
    nodes: {
      type: Array,
      required: true,
    },
    isEmptyRootState: {
      type: Boolean,
      default: false,
    },
    selectedNodesGuids: {
      type: Array,
      default: () => [],
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    cms: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
      page: 1,
      path: [
        {
          name: 'Media',
        },
      ],
    }
  },
  computed: {
    nodesPaged () {
      return this.nodes.slice(0, PAGE_SIZE + this.page * PAGE_SIZE)
    },
  },
  methods: {
    isNodeSelected (guid) {
      return this.selectedNodesGuids.includes(guid)
    },
    getComponentByNodeType (nodeType) {
      return NodeTypeToComponentMap[nodeType.toLowerCase()]
    },
    openNode (node) {
      this.$emit(ComponentEvent.OPEN_NODE, node)
    },
    onSelect (node) {
      this.$emit(ComponentEvent.SELECT_NODE, node)
    },
    nextPage () {
      this.page++
    },
  },
}

</script>

<style scoped lang="stylus">
.uc-image-picker__empty-folder
  width 100%
  text-align center

.uc-image-picker__empty-folder__title
  primary-heading-2()

.uc-image-picker__empty-folder__back-label
  primary-details-text()
  margin-left 6px

.uc-image-picker__empty-folder__back
  display flex
  align-items center
  justify-content center
  cursor pointer

.uc-image-picker__view-switcher
  display flex
  justify-content flex-end
  padding-top 8px
  padding-bottom 8px

.uc-image-picker__actions
  display flex
  flex-direction row

.uc-image-picker__view-switcher__icon
  padding 4px
  cursor pointer
</style>
