// Import custom sections here
import ExampleSection from './example-section'
import FieldsSection from './fields-section'
import ContentEditorMultiSection from './content-editor-multi-section'
import ContentEditorSingleSection from './content-editor-single-section'

// Name of the editor as it comes from the API, and the section compponent it maps to
const editorToSectionMap = {
  Example: ExampleSection,
  ContentPickerMultiSelect: ContentEditorMultiSection,
  Content: ContentEditorSingleSection,
}

export function getSectionByEditorType (editor) {
  return editor in editorToSectionMap ? editorToSectionMap[editor] : FieldsSection
}
