<template>
  <form
    class="uc-promo-code-generator-unique"
    data-test="generator-unique"
    @submit.prevent="submit">
    <div class="uc-promo-code-generator-unique__infix-length">
      <editor-wrapper
        :errors="errorMessages.infixLength"
        required>
        <uc-numeric-input
          ref="infixLength"
          v-model.number="infixLength"
          :label="translations.infixLength"
          :has-errors="!isInfixLengthValid"
          data-test="infix-length" />
      </editor-wrapper>
    </div>

    <div class="uc-promo-code-generator-unique__count">
      <editor-wrapper
        :errors="errorMessages.count"
        required>
        <uc-numeric-input
          v-model.number="count"
          :label="translations.count"
          :has-errors="!isCountValid"
          data-test="count" />
      </editor-wrapper>
    </div>

    <!-- Advanced section -->
    <div class="uc-promo-code-generator-unique__advanced-toggle">
      <uc-button
        size="sm"
        theme="link"
        data-test="advanced-toggle"
        @click="toggleAdvanced">
        <span class="uc-promo-code-generator-unique__advanced-toggle-label">
          {{ translations.advanced }}
        </span>
      </uc-button>
    </div>

    <div
      v-show="isAdvanced"
      class="uc-promo-code-generator-unique__prefix">
      <editor-wrapper :errors="errorMessages.prefix">
        <uc-text-input
          ref="prefix"
          v-model="prefix"
          :label="translations.prefix"
          :has-errors="!isPrefixValid"
          data-test="prefix" />
      </editor-wrapper>
    </div>

    <div
      v-show="isAdvanced"
      class="uc-promo-code-generator-unique__suffix">
      <editor-wrapper :errors="errorMessages.suffix">
        <uc-text-input
          v-model="suffix"
          :label="translations.suffix"
          :has-errors="!isSuffixValid"
          data-test="suffix" />
      </editor-wrapper>
    </div>

    <div class="uc-promo-code-generator-unique__submit">
      <div class="uc-promo-code-generator-unique__example">
        {{ translations.example }}:
        <span data-test="example">
          {{ example }}
        </span>
      </div>

      <uc-button
        :theme="button.theme"
        :size="button.size"
        :type="button.type"
        :disabled="button.disabled"
        @click="submit">
        {{ translations.generate }}
        <template
          v-if="loading"
          #after>
          <loading-spinner />
        </template>
      </uc-button>
    </div>
  </form>
</template>

<script>
import EditorWrapper from '@/components/editors/editor-wrapper'
import { UcNumericInput } from '@/components/forms/uc-numeric-input/'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import { UcButton, UcButtonTheme, UcButtonSize, UcButtonType } from '@/components/forms/uc-button/'
import LoadingSpinner from '@/components/widgets/loading-spinner'

const ComponentEvent = {
  SUBMIT: 'submit',
}

const formDefault = {
  infixLength: 6,
  // We need to use undefined to distinguish between null from editor and us setting the value  count: undefined,
  count: undefined,
  prefix: '',
  suffix: '',
}

export default {
  name: 'UcPromoCodeGeneratorUnique',
  components: {
    EditorWrapper,
    UcNumericInput,
    UcTextInput,
    UcButton,
    LoadingSpinner,
  },
  props: {
    loading: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      infixLength: formDefault.infixLength,
      count: formDefault.count,
      prefix: formDefault.prefix,
      suffix: formDefault.suffix,
      isAdvanced: false,
      minLength: 6,
      maxLength: 20,
    }
  },
  computed: {
    button () {
      return {
        theme: this.loading ? UcButtonTheme.PRIMARY : UcButtonTheme.SECONDARY,
        size: UcButtonSize.SMALL,
        type: UcButtonType.SUBMIT,
        disabled: !(this.loading || this.isValidPayload),
      }
    },
    translations () {
      const { minLength, maxLength } = this
      return {
        infixLength: this.$t('ucPromoCodeGenerator.infixLength'),
        infixLengthError: this.$t('ucPromoCodeGenerator.infixLengthError', { minLength, maxLength }),
        count: this.$t('ucPromoCodeGenerator.count'),
        advanced: this.$t('ucPromoCodeGenerator.advanced'),
        prefix: this.$t('ucPromoCodeGenerator.prefix'),
        prefixError: this.$t('ucPromoCodeGenerator.prefixError', { maxLength }),
        suffix: this.$t('ucPromoCodeGenerator.suffix'),
        suffixError: this.$t('ucPromoCodeGenerator.suffixError', { maxLength }),
        example: this.$t('ucPromoCodeGenerator.example'),
        generate: this.$t('ucPromoCodeGenerator.generate'),
        required: this.$t('global.required'),
      }
    },
    isInfixLengthValid () {
      return this.infixLength >= this.minLength && this.infixLength <= this.maxLength
    },
    isCountValid () {
      return this.count === undefined || this.count > 0
    },
    isPrefixValid () {
      return this.prefix.length <= this.maxLength
    },
    isSuffixValid () {
      return this.suffix.length <= this.maxLength
    },
    errorMessages () {
      return {
        count: this.isCountValid ? [] : [this.translations.required],
        infixLength: this.isInfixLengthValid ? [] : [this.translations.infixLengthError],
        prefix: this.isPrefixValid ? [] : [this.translations.prefixError],
        suffix: this.isSuffixValid ? [] : [this.translations.suffixError],
      }
    },
    example () {
      const infix = this.isInfixLengthValid ? 'X'.repeat(this.infixLength) : ''
      const prefix = this.isPrefixValid && this.prefix ? `${this.prefix}-` : ''
      const suffix = this.isSuffixValid && this.suffix ? `-${this.suffix}` : ''
      return `${prefix}${infix}${suffix}`
    },
    isValidPayload () {
      return (this.infixLength !== null && this.isInfixLengthValid) &&
        (this.count !== undefined && this.isCountValid) &&
        this.isPrefixValid &&
        this.isSuffixValid
    },
  },
  mounted () {
    this.focusForm()
  },
  methods: {
    focusForm () {
      this.$refs.infixLength.focus()
    },
    toggleAdvanced () {
      this.isAdvanced = !this.isAdvanced
      this.$refs.prefix.focus()
    },
    submit () {
      if (!this.isValidPayload) {
        return
      }

      const payload = {
        length: this.infixLength,
        count: this.count,
        prefix: this.prefix,
        suffix: this.suffix,
      }

      this.$emit(ComponentEvent.SUBMIT, payload)
      this.clear()
    },
    clear () {
      this.length = formDefault.infixLength
      this.count = formDefault.count
      this.prefix = formDefault.prefix
      this.suffix = formDefault.suffix
      this.focusForm()
    },
  },
}
</script>

<style lang="stylus" scoped>
// grid container
.uc-promo-code-generator-unique
  display grid
  grid-template-areas 'field1 field2' '. advanced' 'field3 field4' 'submit submit'
  grid-template-columns 1fr 1fr
  column-gap $base-unit * 3
  row-gap $base-unit * 2

// grid row 1
.uc-promo-code-generator-unique__infix-length
  grid-area field1

.uc-promo-code-generator-unique__count
  grid-area field2

// grid row 2
.uc-promo-code-generator-unique__advanced-toggle
  grid-area advanced
  justify-self end
  cursor pointer

.uc-promo-code-generator-unique__advanced-toggle-label
  primary-details-text-bold()

// grid row 3
.uc-promo-code-generator-unique__prefix
  grid-area field3

.uc-promo-code-generator-unique__suffix
  grid-area field4

// grid row 4
.uc-promo-code-generator-unique__submit
  display flex
  grid-area submit
  align-items center
  justify-content space-between
  margin-top $base-unit * 2 // due to `row-gap` this can't be * 4
  padding-top $base-unit * 2
  border-top $base-border

.uc-promo-code-generator-unique__example
  primary-details-text()
</style>
