import { rest } from 'msw'

import {
  currencies,
} from '@/mocks/entities/currencies/'

const RESPONSE_DELAY = 200

export const rootCurrencyHandlers = [
  rest.get('/ucommerceapi/v2/currencies', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const totalCount = currencies.length
    const response = currencies.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', totalCount),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
]
