<template>
  <div
    class="uc-radio-group-editor"
    :class="cssClassObject">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="compactValue"
      :required="isRequired">
      <uc-fieldset :state="fieldsetState">
        <p
          class="uc-radio-group-editor__label"
          data-test="radio-group-label">
          {{ definition.label }}
        </p>

        <!-- No options -->
        <div
          v-if="!hasOptions"
          class="uc-radio-group-editor__empty-state__text"
          data-test="no-options-available">
          {{ $t('global.noOptionsFound') }}
        </div>

        <!-- Has options -->
        <div
          v-for="option in options"
          :key="option.guid"
          class="uc-radio-group-editor__option">
          <uc-radio
            :id="option.guid"
            :label="option.label"
            :value="option.guid"
            :checked="setChecked(option.guid)"
            :group="definition.guid"
            :nullable="!isRequired"
            :readonly="isReadonly"
            @change="onOptionChange" />
        </div>
      </uc-fieldset>
    </editor-wrapper>
  </div>
</template>

<script>
import { UcRadio } from '@/components/forms/uc-radio/'
import { UcFieldset, UcFieldsetStateProp } from '@/components/forms/uc-fieldset/'
import { isEmpty } from '@/utils/object.utils.js'
import editorMixin from '../editor-mixin'

export default {
  name: 'UcRadioGroupEditor',
  components: {
    UcRadio,
    UcFieldset,
  },
  mixins: [editorMixin],
  data () {
    return {
      selectedOption: {},
    }
  },
  computed: {
    cssClassObject () {
      return {
        'has-errors': this.hasErrors,
        'is-readonly': this.isReadonly,
      }
    },
    hasOptions () {
      return this.options.length > 0
    },
    compactValue () {
      return this.hasSelectedOption ? this.selectedOption.label : ''
    },
    selectedGuid () {
      return this.definition.selected ? this.definition.selected[0] : ''
    },
    hasSelectedOption () {
      return !isEmpty(this.selectedOption)
    },
    fieldsetState () {
      return this.hasErrors ? UcFieldsetStateProp.ERROR : UcFieldsetStateProp.NONE
    },
  },
  watch: {
    definition: {
      handler () {
        this.selectedOption = this.selectedGuid ? this.options.find(option => option.guid === this.selectedGuid) : {}
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    setChecked (optionGuid) {
      return this.hasSelectedOption && this.selectedOption.guid === optionGuid
    },
    onOptionChange (event) {
      const oldSelectedOption = this.selectedOption
      const emptyOption = !this.isRequired ? [] : undefined

      this.selectedOption = {
        guid: event.value,
        label: event.label,
      }

      this.onChange({
        label: this.label,
        newValue: this.hasSelectedOption ? [this.selectedOption.guid] : [],
        oldValue: !isEmpty(oldSelectedOption) ? [oldSelectedOption.guid] : emptyOption,
        valueFieldName: 'selected',
      })
    },
  },
}
</script>
<style lang="stylus" scoped>
.uc-radio-group-editor
  user-select none

.uc-radio-group-editor__label
  margin-top 0
  margin-bottom 3 * $base-unit

.uc-radio-group-editor__empty-state__text
  secondary-body-text()

.uc-radio-group-editor__option:not(:last-child)
  padding-bottom 3 * $base-unit

// state: has-errors
.uc-radio-group-editor.has-errors
  .uc-radio-group-editor__label
    color $base-color-error

// state: is-readonly
.uc-radio-group-editor.is-readonly
  .uc-radio-group-editor__label
    opacity $base-opacity-disabled

</style>
