import { EntityService, EntityType } from './_entity.service.js'

export class CurrencyService extends EntityService {
  constructor (httpClient) {
    super(EntityType.CURRENCY, httpClient)
  }

  // can't use super, cuz there's no definition to resolve `_decorateEntityWithDefinitionsAsync` call
  fetchEntitiesAsync ({ params }) {
    return this._httpClient.get(this._rootUrl, { params })
  }

  fetchEntityListAsync () {
    return this._httpClient.get(`${this._rootUrl}`)
  }
}
