import { LayoutType } from '@/layouts'
import Orders from './views/orders.vue'
import OrderEdit from './views/order-edit.vue'

export function OrdersApp ({ router }) {
  router.addRoute({
    path: '/orders/',
    name: 'orders',
    component: Orders,
    props: route => {
      const { statuses } = route.query

      return {
        query: {
          ...route.query,
          ...statuses && { statuses: (!statuses || Array.isArray(statuses)) ? statuses : [statuses] },
        },
      }
    },
    meta: {
      layout: LayoutType.STICKY_HEADER,
    },
  })

  router.addRoute({
    path: '/orders/:orderGuid',
    name: 'order-edit',
    component: OrderEdit,
    props: true,
    meta: {
      layout: LayoutType.BLANK,
    },
  })
}
