import faker from 'faker'
import { auditDefinitionsShallow } from './audit-definitions.mock.js'
import { getRandomFromArray } from '@/utils/array.utils.js'
import { orderStatuses } from '../orders/order.mock'

function mockGuid () {
  return `mock-${faker.datatype.uuid()}`
}

function auditFactory () {
  const entityGuid = mockGuid()
  const definition = auditDefinitionsShallow[0]
  const statusName = getRandomFromArray(orderStatuses).name

  return {
    meta: {
      definition,
      entityGuid,
      nodeType: 'audit',
    },
    definition: {
      value: definition.name,
      selected: [
        definition.guid,
      ],
      guid: mockGuid(),
    },
    status: {
      value: statusName,
      guid: mockGuid(),
    },
    date: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
    updatedBy: {
      value: `${faker.name.firstName()} ${faker.name.lastName()}`,
      guid: mockGuid(),
    },
    modifiedBy: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    message: {
      value: faker.lorem.paragraph(),
      guid: mockGuid(),
    },
    guid: {
      value: entityGuid,
      guid: mockGuid(),
    },
  }
}

export function generateAudits (count = 1) {
  let items = []

  for (let i = 0; i < count; i++) {
    items = [...items, auditFactory()]
  }

  return items
}

export const audits = generateAudits(1000)
