<template>
  <div
    class="uc-content-editor-single"
    :class="cssClassObject">
    <!-- Header area -->
    <div class="uc-content-editor-single__header">
      <div class="uc-content-editor-single__heading-area">
        <div
          v-truncate
          class="uc-content-editor-single__heading heading">
          <editor-wrapper
            v-bind="{ compact, errors, compactValue }"
            :required="isRequired">
            <h2
              v-truncate
              class="heading__header">
              {{ label }}
              <span
                v-if="isMultilingual"
                class="heading__language">({{ language }})
              </span>
            </h2>
          </editor-wrapper>
        </div>
        <div
          v-if="!compact"
          class="uc-content-editor-single__actions">
          <uc-button
            type="submit"
            :disabled="!isRequired && !isEmpty"
            size="sm"
            @click="openModal">
            {{ $t('global.add') }}
          </uc-button>
        </div>
      </div>
    </div>

    <!-- Selected content -->
    <div
      v-if="!compact"
      class="uc-content-editor-single__content">
      <!-- Error occured -->
      <p
        v-if="error"
        class="uc-content-editor-single__message">
        {{ $t('contentEditor.errorMessage') }}
      </p>

      <template v-if="!error">
        <!-- No content selected -->
        <p
          v-if="isEmpty"
          class="uc-content-editor-single__message">
          {{ $t('contentEditor.noContentSelected') }}
        </p>

        <template v-if="!isEmpty">
          <!-- Loading -->
          <div
            v-show="isLoading"
            class="uc-content-editor-single__loading-indicator">
            <loading-indicator is-loading />
          </div>

          <!-- Content overview-->
          <div
            v-show="!isLoading"
            class="uc-content-editor-single__item">
            <content-navigator-item :item="selectedItem">
              <template
                v-if="!isRequired"
                #actions>
                <uc-button-round
                  theme="ghost"
                  size="s"
                  @click="removeNode">
                  <close-icon />
                </uc-button-round>
              </template>
            </content-navigator-item>
          </div>
        </template>
      </template>
    </div>

    <!-- Content picker -->
    <uc-modal
      v-if="isModalOpen"
      :title="$t('ucContentPicker.header')"
      size="lg"
      @hide="closeModal">
      <div class="uc-content-editor-single__modal">
        <content-picker-single
          v-bind="{ contentType }"
          :selected="selected[0]"
          :cms="systemInfo.cms"
          @nodeSelected="onNodeSelected" />
      </div>
    </uc-modal>
  </div>
</template>

<script>
import editorMixin from '@/components/editors/editor-mixin'
import UcButton from '@/components/forms/uc-button/uc-button'
import UcModal from '@/components/widgets/uc-modal'
import { mapGetters } from 'vuex'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import ContentPickerSingle from './uc-content-picker-single'
import ContentNavigatorItem from './components/content-navigator-item'
import UcButtonRound from '@/components/forms/uc-button-round'
import { Close16Normal as CloseIcon } from '@/assets/icons'
import { getSelectedContentByGuids } from './components/content-navigator.http'

export default {
  name: 'UcContentEditorSingle',
  components: {
    UcButton,
    UcModal,
    LoadingIndicator,
    ContentPickerSingle,
    ContentNavigatorItem,
    UcButtonRound,
    CloseIcon,
  },
  mixins: [editorMixin],
  props: {
    contentType: {
      type: String,
      default: 'content',
    },
  },
  data () {
    return {
      isModalOpen: false,
      isLoading: false,
      selectedItem: {},
      error: false,
    }
  },
  computed: {
    ...mapGetters(['systemInfo']),
    label () {
      return this.definition.label
    },
    isEmpty () {
      return this.selected.length === 0
    },
    cssClassObject () {
      return {
        'is-empty': this.isEmpty,
        'is-compact': this.compact,
        'has-error': this.error,
      }
    },
    compactValue () {
      return this.isEmpty ? '' : this.definition.name
    },
  },
  watch: {
    selected: {
      async handler (value) {
        if (this.compact) {
          return
        }

        if (!value.length) {
          this.selectedItem = {}
          return
        }

        try {
          this.isLoading = true
          this.error = false
          const result = await getSelectedContentByGuids(this.contentType, value)
          this.selectedItem = result.data.length > 0 ? result.data[0] : {}
        } catch {
          this.error = true
        } finally {
          this.isLoading = false
        }
      },
      immediate: true,
    },
  },
  methods: {
    closeModal () {
      this.isModalOpen = false
    },
    openModal () {
      this.isModalOpen = true
    },
    onNodeSelected (items) {
      const newGuids = items.map(item => item.guid)
      const oldGuids = this.selected
      const selectionChanged = JSON.stringify(newGuids) !== JSON.stringify(oldGuids)

      if (selectionChanged) {
        this.onChange({
          changeValues: {
            selected: {
              newValue: newGuids,
              oldValue: oldGuids,
            },
            name: {
              newValue: items.length > 0 ? items[0].name : '',
              oldValue: this.definition.name,
            },
          },
        })
      }

      this.closeModal()
    },
    removeNode () {
      this.onNodeSelected([])
    },
  },
}
</script>

<style lang="stylus" scoped>
$header-height = $base-unit * 10
$list-item-height = $base-unit * 8

.uc-content-editor-single__content
  display flex
  flex-direction column
  height 2 * $list-item-height

.uc-content-editor-single
  &.is-empty
  &.has-error
    .uc-content-editor-single__content
      justify-content center

  &:not(.is-compact)
    .uc-content-editor-single__header
      position relative
      display flex
      align-items center
      width 100%
      height $header-height
      padding 0 ($base-unit * 3)
      border-bottom $base-border

    .uc-content-editor-single__heading-area
      display flex
      flex-direction row
      align-items center
      justify-content space-between
      width 100%

    .uc-content-editor-single__heading
      display flex
      flex-direction column

      >>> .feedback
        position absolute
        bottom 1.5 * $base-unit
        margin 0

    .heading__header
      primary-heading-2()
      margin 0
      user-select none

    .heading__language
      color $base-color-text-secondary
      font-weight normal

.uc-content-editor-single__loading-indicator
  display flex
  flex 1 1 auto
  align-items center
  justify-content center

.uc-content-editor-single__item
  padding $base-unit 3 * $base-unit
  padding-bottom 7px
  border-bottom $base-border
  user-select none

.uc-content-editor-single__message
  secondary-body-text()
  padding 0 12 * $base-unit
  text-align center

.uc-content-editor-single__modal
  position relative
  height 600px
</style>
