<template>
  <div class="billing-information">
    <fields-section paddingless>
      <template #heading>
        <div class="billing-information__header">
          <span class="billing-information__header-heading">
            {{ $tc('heading') }}
          </span>
        </div>
      </template>
      <template #actions>
        <uc-button
          v-if="editable"
          :theme="ucButtonTheme.TEXT"
          :size="buttonSizeSmall"
          @click="openModal">
          <template #before>
            <edit-icon />
          </template>
          {{ $t('global.edit') }}
        </uc-button>
      </template>
      <div class="billing-information__body">
        <uc-section-wrapper
          v-bind="{ state, errorMessage }"
          :empty-message="$t('emptyMessage')">
          <uc-entity-property-list
            v-for="address in billingAddresses"
            :key="address.meta.entityGuid"
            :entity="address"
            no-null-properties
            v-bind="{ ignoredProperties, contentLanguage }" />
        </uc-section-wrapper>
      </div>
    </fields-section>
    <!-- Update billing information modal -->
    <update-billing-information-modal
      v-if="isModalOpen"
      :content-language="contentLanguage"
      :parent-guid="parentGuid"
      :billing-information="billingAddresses[0]"
      @updated="billingUpdated"
      @cancel="closeModal" />
  </div>
</template>

<i18n>
{
  "en": {
    "emptyMessage": "No billing address is available.",
    "errorMessage": "Something went wrong — please try to reload the app.",
    "heading": "Billing information"
  }
}
</i18n>

<script>
import { billingService } from '@/services/'
import { UcSectionWrapper, UcSectionWrapperState } from '@/components/generic/uc-section-wrapper/'
import { UcEntityPropertyList } from '@/components/domain/uc-entity-property-list/'
import FieldsSection from '@/components/sections/fields-section/fields-section'
import { UcButton, UcButtonSize, UcButtonTheme } from '@/components/forms/uc-button/'
import { Edit16Normal as EditIcon } from '@/assets/icons/'
import { UpdateBillingInformationModal } from './update-billing-information-modal'

export default {
  name: 'BillingInformation',
  components: {
    FieldsSection,
    UcSectionWrapper,
    UcEntityPropertyList,
    UcButton,
    UpdateBillingInformationModal,
    EditIcon,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    parentGuid: {
      type: String,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  data () {
    return {
      billingAddresses: null,
      state: UcSectionWrapperState.LOADING,
      errorMessage: '',
      isModalOpen: false,
    }
  },
  computed: {
    ignoredProperties () {
      return ['definition', 'meta']
    },
    buttonSizeSmall: () => UcButtonSize.SMALL,
    ucButtonTheme: () => UcButtonTheme,
  },
  async created () {
    await this.fetchBillingInformation()
  },
  methods: {
    openModal () {
      this.isModalOpen = true
    },
    closeModal () {
      this.isModalOpen = false
    },
    async billingUpdated () {
      await this.fetchBillingInformation()
      this.closeModal()
    },
    async fetchBillingInformation () {
      try {
        this.state = UcSectionWrapperState.LOADING

        this.billingAddresses = (await billingService.fetchManyAsync({
          guid: this.parentGuid,
          cultureCode: this.contentLanguage,
          startAt: 0,
          limit: Number.MAX_SAFE_INTEGER,
        })).data

        this.state = this.billingAddresses.length > 0 ? UcSectionWrapperState.IDLE : UcSectionWrapperState.EMPTY
      } catch (error) {
        this.state = UcSectionWrapperState.ERROR
        this.errorMessage = error?.response?.data?.message || this.$t('errorMessage')
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.billing-information__body
  padding 4 * $base-unit 3 * $base-unit
</style>
