import { EntityService, EntityType } from '@/services/_entity.service'

export class CampaignService extends EntityService {
  constructor (httpClient) {
    super(EntityType.CAMPAIGN, httpClient)
  }

  updateEntityAsync ({ campaignGuid, cultureCode, payload }) {
    const url = `${this._rootUrl}/${campaignGuid}/${cultureCode}`
    return this._httpClient.patch(url, payload)
  }

  deleteEntitiesAsync (guids) {
    const payload = {
      data: guids,
    }
    return this._httpClient.delete(this._rootUrl, payload)
  }

  async fetchDefinitionsAsync (...args) {
    const definitionsResponse = await super.fetchDefinitionsAsync(...args)

    return {
      ...definitionsResponse,
      getStores: () => definitionsResponse.data.find(definition => 'stores' in definition).stores.options,
    }
  }

  createEntityAsync ({ name, definitionGuid, stores, startDate, endDate }) {
    return super.createEntityAsync({ name, definitionGuid, params: { stores, startDate, endDate } })
  }
}
