<template>
  <div class="products-navigate">
    <!-- Navigation -->
    <catalog-builder />

    <!-- Product listing  -->
    <div class="products-navigate__listing">
      <fields-section paddingless>
        <list
          :id="listId"
          :ref="listId"
          v-bind="{ nodeType, dataUrl, defaultColumnsConfiguration }"
          :header="$tc('list.products')"
          @sortBy="onSortBy"
          @row-clicked="openEntityEditor"
          @filter="onPriceFilter">
          <template #actionsHeader>
            <transition name="fade">
              <uc-button
                v-if="isProductCategory"
                size="sm"
                @click="toggleDialog">
                {{ $t('list.newButton') }}
              </uc-button>
            </transition>
          </template>
        </list>
      </fields-section>
    </div>
  </div>
</template>

<script>
import CatalogBuilder from '@/apps/catalogs/components/catalog-builder/catalog-builder.vue'
import List from '@/components/generic/generic-list/list'
import UcButton from '@/components/forms/uc-button'
import FieldsSection from '@/components/sections/fields-section'

import { nodeTypeApiMap, EntityType } from '@/constants.js'

export default {
  name: 'ProductsNavigate',
  components: {
    FieldsSection,
    CatalogBuilder,
    List,
    UcButton,
  },
  props: {
    listId: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
    displayedNode: {
      type: Object,
      required: true,
    },
  },
  data () {
    return {
      sortBy: '',
      priceGroups: [],
    }
  },
  computed: {
    isProductCategory () {
      return this.displayedNode.nodeType === EntityType.CATEGORY
    },
    nodeType () {
      return EntityType.PRODUCT
    },
    defaultColumnsConfiguration () {
      return {
        primaryImage: {
          width: '90px',
        },
        sku: {},
        name: {
          width: '250px',
        },
        definition: {},
        'eur 15 pct': {
          width: '360px',
        },
      }
    },
    dataUrl () {
      let url = this.displayedNode.nodeType ? `/${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}/products/${this.contentLanguage}` : ''
      const params = new URLSearchParams()
      params.set('sort', this.sortBy)

      if (this.priceGroups.length) {
        this.priceGroups.forEach(pg => params.append('priceGroups', pg))
      } else {
        params.set('priceGroups', '')
      }

      url = `${url}?${params.toString()}`
      return url
    },
  },
  methods: {
    onPriceFilter (priceColumns) {
      if (priceColumns) {
        this.priceGroups = priceColumns.map(priceColumn => priceColumn.guid)
      } else {
        this.priceGroups = []
      }
    },
    onSortBy (sortBy) {
      this.sortBy = sortBy
    },
    toggleDialog () {
      this.$emit('toggle-dialog')
    },
    openEntityEditor (row) {
      this.$emit('open-entity-editor', row)
    },
    closeEntityEditor () {
      this.$emit('close-entity-editor')
    },
    addItems (items) {
      this.$refs[this.listId].addItems(items)
    },
    removeItems (items) {
      this.$refs[this.listId].removeItems(items)
    },
  },
}
</script>

<style lang="stylus" scoped>
.products-navigate__listing
  margin-top 4 * $base-unit
</style>
