import * as types from './mutation-types'

const mutations = {
  [types.SET_SYSTEM_INFO] (state, info) {
    state.systemInfo = info
  },
  [types.SET_UI_LANGUAGE] (state, language) {
    state.uiLanguage = language
  },
  [types.ADD_UNDO] (state, command) {
    state.undos.push(command)
  },
  [types.CLEAR_UNDOS] (state) {
    state.undos = state.undos.filter(x => x.meta.survivesNodeChange)
  },
  [types.REMOVE_UNDO] (state, command) {
    state.undos = state.undos.filter(x => x !== command)
  },
  [types.SET_DEFINITIONS] (state, definitions) {
    state.definitions = definitions
  },
}

export default mutations
