import { EntityService, EntityType } from './_entity.service.js'

export class ShipmentService extends EntityService {
  constructor (httpClient) {
    super(EntityType.ORDER, httpClient)
  }

  get _definitionUrl () {
    return `${this._rootUrl}/shipments/definitions/full`
  }

  updateEntryAsync ({ orderGuid, cultureCode, payload }) {
    const url = `${this._rootUrl}/${orderGuid}/shipments/${cultureCode}`
    return this._httpClient.patch(url, payload)
  }

  fetchManyAsync ({ parentGuid, cultureCode, startAt, limit, searchTerm }) {
    const url = `${this._rootUrl}/${parentGuid}/shipments/${cultureCode}`
    return super.fetchEntitiesAsync({
      url,
      params: {
        startAt,
        limit,
        searchTerm,
      },
    })
  }
}
