<template>
  <transition name="fade">
    <div
      class="stores"
      :class="cssClassObject">
      <!-- Loading indicator -->
      <div
        v-if="isInitializing"
        class="stores__loading">
        <loading-indicator is-loading />
      </div>
      <template v-if="!isInitializing">
        <!-- Search bar -->
        <div
          v-if="shouldShowSearchBar"
          class="stores__search">
          <uc-search
            data-test="stores-search"
            :value="searchTerm"
            @input="search($event)" />
        </div>

        <!-- Stores overview -->
        <fields-section
          :heading="heading"
          paddingless>
          <template #actions>
            <router-link
              #default="{ navigate }"
              :to="{ name: 'store-create' }"
              custom>
              <uc-button
                size="sm"
                @click="navigate">
                {{ $t('global.new') }}
              </uc-button>
            </router-link>
          </template>
          <div class="stores__navigator">
            <store-navigator
              v-show="availableNodes.length > 0"
              v-bind="{ nodes: availableNodes, totalNodeCount, scrollTo }"
              :loading="isSearching"
              @select="editStore($event)"
              @requestNextPage="onRequestNextPage" />
            <p
              v-show="availableNodes.length === 0"
              class="stores__empty"
              data-test="stores-empty-text">
              {{ emptyText }}
            </p>
          </div>
        </fields-section>
        <transition name="fade">
          <!--
            HACK:
            ------------------------------------------------------------------------------------------------------------------------------------------------
            - The `<keep-alive>` and `:key` below is a hack implemented to keep the `store` component alive so that we can listen for its `resurrect` event
            ------------------------------------------------------------------------------------------------------------------------------------------------
          -->
          <keep-alive
            max="1"
            include="StoreEdit">
            <router-view
              :key="$route.params.guid"
              :content-language="selectedContentLanguage"
              @delete="deleteStore"
              @resurrect="resurrectStore"
              @create="createStore" />
          </keep-alive>
        </transition>
      </template>
    </div>
  </transition>
</template>

<script>
import StoreNavigator from '@/apps/stores/components/store-navigator'
import FieldsSection from '@/components/sections/fields-section'
import UcButton from '@/components/forms/uc-button'
import UcSearch from '@/components/forms/uc-search'
import LoadingIndicator from '@/components/widgets/loading-indicator'

import { storeService } from '@/services'
import { mapMutations, createNamespacedHelpers } from 'vuex'
import { uniqBy } from '@/utils/array.utils'

const builderStore = createNamespacedHelpers('builder')
const detailsStore = createNamespacedHelpers('details')
const persistedStore = createNamespacedHelpers('persisted')

const ComponentState = {
  INITIALIZING: 'initializing',
  SEARCHING: 'searching',
  IDLE: 'idle',
  ERROR: 'error',
}

export default {
  name: 'Stores',
  components: {
    UcSearch,
    FieldsSection,
    StoreNavigator,
    UcButton,
    LoadingIndicator,
  },
  data () {
    return {
      nodes: [],
      searchTerm: '',
      totalNodeCount: 0,
      initialTotalNodeCount: 0,
      scrollTo: [],
      state: ComponentState.INITIALIZING,
    }
  },
  computed: {
    ...builderStore.mapGetters(['stores']),
    ...detailsStore.mapGetters(['currentDetails']),
    ...persistedStore.mapGetters(['selectedContentLanguage']),
    availableNodes () {
      return this.nodes
    },
    heading () {
      return `${this.$t('stores.heading')} (${this.totalNodeCount})`
    },
    emptyText () {
      if (this.hasError) {
        return this.$t('stores.error')
      }

      return this.searchTerm ? this.$t('stores.noSearchResult') : this.$t('stores.empty')
    },
    cssClassObject () {
      return {
        'stores--size-sm': this.initialTotalNodeCount < 3,
        'stores--size-md': this.initialTotalNodeCount > 2 && this.initialTotalNodeCount < 5,
        'stores--size-lg': this.initialTotalNodeCount > 4,
      }
    },
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isSearching () {
      return this.state === ComponentState.SEARCHING
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
    shouldShowSearchBar () {
      return this.initialTotalNodeCount > 4
    },
  },
  async created () {
    try {
      this.state = ComponentState.INITIALIZING
      const response = await this.fetchStores()
      this.nodes = response.data
      this.totalNodeCount = this.getTotalCountFromResponse(response)
      this.initialTotalNodeCount = this.totalNodeCount
    } catch {
      this.nodes = []
      this.totalNodeCount = 0
      this.state = ComponentState.ERROR
    } finally {
      this.state = ComponentState.IDLE
    }
  },
  methods: {
    ...builderStore.mapMutations(['setStores']),
    ...mapMutations(['clearUndos']),
    fetchStores (params) {
      const limit = 12
      return storeService.fetchEntitiesAsync({
        cultureCode: this.selectedContentLanguage,
        params: {
          limit,
          ...params,
        },
      })
    },
    getTotalCountFromResponse (response) {
      return Number(response.headers['x-total-count'])
    },
    getResultCountFromResponse (response) {
      return Number(response.headers['x-result-count'])
    },
    async search (value) {
      try {
        this.searchTerm = value.trim()
        this.scrollTo = [0, 0]
        this.state = ComponentState.SEARCHING

        const response = await this.fetchStores({
          searchTerm: this.searchTerm,
        })

        this.nodes = response.data
        this.totalNodeCount = this.getResultCountFromResponse(response)
      } catch {
        this.nodes = []
        this.totalNodeCount = 0
        this.state = ComponentState.ERROR
      } finally {
        this.scrollTo = []
        this.state = ComponentState.IDLE
      }
    },
    editStore (store) {
      this.clearUndos()
      this.$router.push({
        name: 'store-edit',
        params: {
          guid: store.meta.entityGuid,
          entity: store,
        },
      })
    },
    addStore (store) {
      // Prevent duplicate nodes (when a store is resurrected through both recreation and undo)
      if (this.nodes.find(node => node.meta.entityGuid === store.meta.entityGuid)) {
        return
      }

      const newStore = {
        name: store.name.value,
        guid: store.meta.entityGuid,
      }

      const newStores = [...this.stores, newStore]
      this.setStores(newStores)

      this.nodes.unshift(store)
      this.totalNodeCount++
      this.scrollTo = []
    },
    createStore (store) {
      this.addStore(store)
      this.$router.replace({
        name: 'store-edit',
        params: {
          guid: store.meta.entityGuid,
          entity: store,
        },
      })
    },
    deleteStore (guid) {
      this.nodes = this.nodes.filter(node => node.meta.entityGuid !== guid)

      const newStores = this.stores.filter(store => store.guid !== guid)
      this.setStores(newStores)

      this.totalNodeCount--
      this.clearUndos()
    },
    async resurrectStore (guid) {
      const response = await storeService.fetchEntityAsync({ guid, cultureCode: this.selectedContentLanguage })
      this.addStore(response.data)
    },
    async onRequestNextPage () {
      const response = await this.fetchStores({
        startAt: this.availableNodes.length,
        searchTerm: this.searchTerm ? this.searchTerm : undefined,
      })

      const newNodes = response.data
      this.nodes = [...this.nodes, ...newNodes]
      this.count = this.getTotalCountFromResponse(response)

      // Filter out duplicates
      this.nodes = uniqBy([...this.nodes, ...newNodes], node => node.meta.entityGuid)
      this.totalNodeCount = this.searchTerm ? this.getResultCountFromResponse(response) : this.getTotalCountFromResponse(response)
    },
  },
}
</script>

<style lang="stylus" scoped>
$small-node-height = 140px
$large-node-height = 2 * $small-node-height
$grid-gap = 3 * $base-unit

.stores__loading
  position absolute
  top 0
  left 0
  display flex
  align-items center
  justify-content center
  width 100%
  height 100vh

.stores__search
  margin-bottom 3 * $base-unit

.stores__navigator
  display flex
  align-items center
  justify-content center
  min-height $large-node-height + (2 * $grid-gap)

.stores__empty
  secondary-body-text()

.stores--size-sm .stores__navigator
  height $large-node-height + (2 * $grid-gap)

.stores--size-md .stores__navigator
  height (2 * $large-node-height) + (3 * $grid-gap)

.stores--size-lg .stores__navigator
  height (3 * $small-node-height) + (4 * $grid-gap)
</style>
