<template>
  <form
    class="uc-promo-code-generator-manual"
    data-test="generator-manual"
    @submit.prevent="submit">
    <div class="uc-promo-code-generator-manual__code">
      <editor-wrapper
        :errors="errorMessages.code"
        required>
        <uc-text-input
          ref="code"
          v-model="code"
          :label="translations.code"
          :has-errors="!isCodeValid"
          data-test="code" />
      </editor-wrapper>
    </div>
    <div class="uc-promo-code-generator-manual__max-uses">
      <editor-wrapper
        :errors="errorMessages.maxUses"
        required>
        <uc-numeric-input
          v-model.number="maxUses"
          :label="translations.maxUses"
          :has-errors="!isMaxUsesValid"
          data-test="max-uses" />
      </editor-wrapper>
    </div>

    <div class="uc-promo-code-generator-manual__submit">
      <uc-button
        :theme="button.theme"
        :size="button.size"
        :type="button.type"
        :disabled="button.disabled"
        @click="submit">
        {{ translations.create }}
      </uc-button>
    </div>
  </form>
</template>

<script>
import EditorWrapper from '@/components/editors/editor-wrapper'
import { UcNumericInput } from '@/components/forms/uc-numeric-input/'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import { UcButton, UcButtonTheme, UcButtonSize, UcButtonType } from '@/components/forms/uc-button/'

const ComponentEvent = {
  SUBMIT: 'submit',
}

const formDefault = {
  code: undefined,
  maxUses: undefined,
}

export default {
  name: 'UcPromoCodeGeneratorManual',
  components: {
    EditorWrapper,
    UcNumericInput,
    UcTextInput,
    UcButton,
  },
  data () {
    return {
      code: formDefault.code,
      maxUses: formDefault.maxUses,
    }
  },
  computed: {
    button () {
      return {
        theme: UcButtonTheme.SECONDARY,
        size: UcButtonSize.SMALL,
        type: UcButtonType.SUBMIT,
        disabled: !this.isValidPayload,
      }
    },
    translations () {
      return {
        code: this.$t('ucPromoCodeGenerator.code'),
        maxUses: this.$t('ucPromoCodeGenerator.maxUses'),
        create: this.$t('global.create'),
        required: this.$t('global.required'),
      }
    },
    isCodeValid () {
      return this.code === undefined || this.code.length > 0
    },
    isMaxUsesValid () {
      return this.maxUses === undefined || this.maxUses > 0
    },
    errorMessages () {
      return {
        code: this.isCodeValid ? [] : [this.translations.required],
        maxUses: this.isMaxUsesValid ? [] : [this.translations.required],
      }
    },
    isValidPayload () {
      return this.isCodeValid && this.code !== undefined &&
        this.isMaxUsesValid && this.maxUses !== undefined
    },
  },
  mounted () {
    this.focusForm()
  },
  methods: {
    focusForm () {
      this.$refs.code.focus()
    },
    submit () {
      if (!this.isValidPayload) {
        return
      }

      const payload = {
        code: this.code,
        maxUses: this.maxUses,
      }

      this.$emit(ComponentEvent.SUBMIT, payload)
      this.clear()
    },
    clear () {
      this.code = formDefault.code
      this.maxUses = formDefault.maxUses
      this.focusForm()
    },
  },
}
</script>

<style lang="stylus" scoped>
// grid container
.uc-promo-code-generator-manual
  display grid
  grid-template-areas 'field1 field2' 'submit submit'
  grid-template-columns 1fr 1fr
  column-gap $base-unit * 3
  row-gap $base-unit * 2

// grid row 1
.uc-promo-code-generator-manual__code
  grid-area field1

.uc-promo-code-generator-manual__max-uses
  grid-area field2

// grid row 2
.uc-promo-code-generator-manual__submit
  display flex
  grid-area submit
  justify-content flex-end
  margin-top $base-unit * 2 // due to `row-gap` this can't be * 4
  padding-top $base-unit * 2
  border-top $base-border
</style>
