<template>
  <div class="api-settings">
    <portal to="header">
      <div class="api-settings__header">
        <store-selector @select="fetchClientKeysAsync" />
      </div>
    </portal>
    <generic-details no-header />
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
import GenericDetails from '@/components/generic/details-view/generic-details.vue'
import { clientKeysService } from '@/services/'
import StoreSelector from '@/components/widgets/store-selector.vue'

const persistedStore = createNamespacedHelpers('persisted')
const detailsStore = createNamespacedHelpers('details')

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR: 'error',
}

export default {
  name: 'ApiSettings',
  components: {
    GenericDetails,
    StoreSelector,
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      errorMessage: '',
    }
  },
  computed: {
    ...detailsStore.mapGetters(['currentDetails']),
    ...persistedStore.mapGetters(['selectedContentLanguage', 'selectedStoreGuid']),
  },
  async created () {
    await this.fetchClientKeysAsync()
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails', 'clearDetails']),
    async fetchClientKeysAsync () {
      try {
        const payload = {
          storeGuid: this.selectedStoreGuid,
          cultureCode: this.selectedContentLanguage,
        }
        const entity = (await clientKeysService.fetchOneAsync(payload)).data
        entity.meta.entityGuid = this.selectedStoreGuid
        entity.clientId.readonly = true
        this.setDetails({ details: entity })
        this.state = ComponentState.IDLE
        this.errorMessage = ''
      } catch (error) {
        this.errorMessage = error?.response?.data?.message || this.$t('global.errorMessage')
        this.state = ComponentState.ERROR
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.api-settings
  position relative
  width 100%

.api-settings__header
  display flex
  align-items center
  justify-content flex-end
  width 100%
</style>
