<template>
  <!-- Options -->
  <ul
    v-if="options.length"
    v-custom-scroll-bar
    class="uc-dropdown-list">
    <template v-for="option in options">
      <li
        :key="option.guid"
        class="uc-dropdown-list-item"
        data-test="uc-dropdown-item"
        @click="onSelect(option)">
        <slot v-bind="{ option, searchTerm }" />
      </li>
    </template>

    <!-- Loading spinner -->
    <uc-overlay
      v-if="isLoading"
      :style="cssStyleObject">
      <loading-indicator v-bind="{ isLoading }" />
    </uc-overlay>
  </ul>
  <!-- Empty state -->
  <div
    v-else
    class="uc-dropdown-list__empty-state">
    <p>{{ $t('global.searchNoResults') }}</p>
  </div>
</template>

<script>
import { UcOverlay } from '@/components/widgets/uc-overlay/'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'

const ComponentEvents = {
  SELECT: 'select',
}

export default {
  name: 'UcDropdownList',
  components: {
    UcOverlay,
    LoadingIndicator,
  },
  props: {
    options: {
      type: Array,
      required: true,
    },
    searchTerm: {
      type: String,
      default: '',
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    spinnerOffset: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    // necessary to offset the spinner, if there's anything above the list
    cssStyleObject () {
      const offset = `${this.spinnerOffset}px`
      return {
        top: offset,
        height: `calc(100% - ${offset})`,
      }
    },
  },
  methods: {
    onSelect (option) {
      this.$emit(ComponentEvents.SELECT, option)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-dropdown-list
  margin 0
  padding $base-unit * 2 0
  overflow-y scroll
  white-space wrap
  list-style none
  cursor pointer

.uc-dropdown-list-item
  display flex
  align-items center
  height 4 * $base-unit
  padding 0 $base-unit * 3

  &:not(:last-child)
    margin-bottom $base-unit

  &:hover
    background-color $base-color-gray-100

  &:active
    background-color $base-color-gray-300

.uc-dropdown-list__empty-state
  secondary-body-text()
  display flex
  align-items center
  justify-content center
  height $long-list-height
  color $base-border-color
  font-size 1.5em
  text-align center
</style>
