<template>
  <span
    class="uc-search"
    data-test="uc-search"
    :class="{ 'uc-search--dark': dark, 'uc-search--dense': dense }">
    <i
      class="uc-search__icon icon icon--search"
      @click="focusInput">
      <component :is="searchIcon" />
    </i>
    <input
      ref="searchInput"
      type="text"
      class="uc-search__input"
      spellcheck="false"
      :value="inputValue"
      :placeholder="placeholder"
      @input="onInput($event.target.value)">
    <i
      v-show="inputValue"
      class="uc-search__icon icon icon--clear"
      data-test-id="clear-search"
      @click="onClear">
      <component :is="closeIcon" />
    </i>
  </span>
</template>

<script>
import {
  Search16Normal as SearchIcon,
  Close16Normal as CloseIcon,
  Search24Normal as SearchIconLarge,
  Close24Normal as CloseIconLarge,
} from '@/assets/icons'
import debounce from '@/utils/debounce.js'
import { delay } from '@/utils/async.utils'

export default {
  name: 'UcSearch',
  components: {
    SearchIcon,
    CloseIcon,
    SearchIconLarge,
    CloseIconLarge,
  },
  props: {
    value: {
      type: String,
      default: '',
    },
    focus: {
      type: Boolean,
      default: false,
    },
    clear: {
      type: Boolean,
      default: false,
    },
    dark: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: function () {
        return this.$tc('global.search')
      },
    },
  },
  data () {
    return {
      inputValue: '',
    }
  },
  computed: {
    searchIcon () {
      return this.dense ? SearchIcon : SearchIconLarge
    },
    closeIcon () {
      return this.dense ? CloseIcon : CloseIconLarge
    },
  },
  watch: {
    value: {
      handler (newValue) {
        this.inputValue = newValue
      },
      immediate: true,
    },
    focus (newValue) {
      if (newValue) {
        this.focusInput()
      }
    },
    clear (newValue) {
      if (newValue) {
        this.onClear()
      }
    },
  },
  methods: {
    setValue (value) {
      this.inputValue = value
      this.$emit('input', this.inputValue)
    },
    async focusInput () {
    // Necessary to delay, otherwise focus and clear happens to soon
      await delay(100)
      this.$refs.searchInput.focus()
    },
    onInput: debounce(function (value) {
      this.setValue(value)
    }, 500),
    onClear () {
      this.setValue('')
      this.focusInput()
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-search
  display flex
  align-items center
  box-sizing border-box
  height 6 * $base-unit
  border 1px solid $base-border-color
  border-radius 3 * $base-unit
  background-color $base-color-white

  &.uc-search--dark
    background-color $base-background-color

  &.uc-search--dense
    height 4 * $base-unit

input.uc-search__input
  flex-shrink 1
  width 100%
  height 100%
  padding 12px 0
  border none
  background-color transparent
  outline none
  primary-body-text()

  .uc-search--dense &
    padding 6px 0
    primary-details-text()

  &::placeholder
    secondary-body-text()

    .uc-search--dense &
      secondary-details-text()

.icon
  display flex
  flex-shrink 0
  align-items center
  justify-content center

  &.icon--search
    padding (1.5 * $base-unit) $base-unit (1.5 * $base-unit) (1.5 * $base-unit)

  &.icon--clear
    padding (1.5 * $base-unit) (1.5 * $base-unit) (1.5 * $base-unit) $base-unit
    cursor pointer

  .uc-search--dense &
    padding $base-unit
</style>
