import { rest } from 'msw'
import { generateProducts } from '@/mocks/entities/products/'

const RESPONSE_DELAY = 200
let products = generateProducts(1, true)

export const freeGiftDiscountHandlers = [
  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/discounts/:discountGuid/freeGift/:cultureCode', (_req, res, ctx) => {
    const response = [...products]

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.delete('/ucommerceapi/v2/promotions/:promotionGuid/discounts/:discountGuid/freeGift', (req, res, ctx) => {
    products = products.filter(product => !req.body.includes(product.meta.entityGuid))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
]
