<template>
  <fieldset
    class="uc-fieldset"
    :class="cssClassObject">
    <legend
      v-if="legend"
      class="uc-fieldset__legend">
      {{ legend }}
    </legend>

    <div
      v-if="$slots.topRight"
      class="uc-fieldset__slot uc-fieldset__slot--top-right"
      data-test="top-right-slot">
      <div class="uc-fieldset__slot__content">
        <slot name="topRight" />
      </div>
    </div>

    <slot />
  </fieldset>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

const ComponentState = {
  NONE: 'none',
  ERROR: 'error',
}

export const StateProp = ComponentState

export default {
  name: 'UcFieldset',
  props: {
    legend: {
      type: String,
      default: '',
    },
    state: {
      type: String,
      validator: validateAgainstEnum(StateProp),
      default: StateProp.NONE,
    },
  },
  computed: {
    cssClassObject () {
      return {
        'has-error': this.state === ComponentState.ERROR,
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
$legend-height = $base-unit * 3

.uc-fieldset
  position relative
  min-width 0
  margin 0
  padding $base-unit * 3
  border $base-border
  border-radius $base-border-radius
  text-overflow ellipsis

// Truncate legend: https://stackoverflow.com/questions/7434756/overflow-and-text-overflow-within-fieldsets
.uc-fieldset__legend
  display inline-block
  min-width 0
  max-width 100%
  height $legend-height
  padding 0 ($base-unit / 4)
  overflow hidden
  white-space nowrap
  text-overflow ellipsis
  primary-body-text($base-font-weight-semibold)
  user-select none

.uc-fieldset__slot
  position absolute
  display flex

.uc-fieldset__slot--top-right
  top 0
  right 0

  & .uc-fieldset__slot__content
    transform translate(50%, -50%)

  .uc-fieldset__legend + &
    top -($legend-height / 2)

.uc-fieldset.has-error
  border-color $base-color-error

  .uc-fieldset__legend
    color $base-color-error
</style>
