<template>
  <div
    ref="section"
    :style="{width: sectionWidth + 'px', height: sectionHeight + 'px'}"
    class="cat-builder__section"
    @dragenter="sectionEnter"
    @mouseenter="mouseenterHandler"
    @mouseleave="mouseleaveHandler">
    <div
      ref="body"
      v-custom-scroll-bar
      class="section__body"
      @dragover="bodyDragover"
      @drop="bodyDrop"
      @scroll="checkIfMoreItemsNeeded">
      <template v-for="(cat, index) in section.categories">
        <drop-target
          :key="index"
          :target-index="index"
          :sort-order="cat.sortOrder"
          :visible-index="visibleTargetIndex"
          :section-guid="section.guid"
          @dropped="targetDrop" />
        <tree-node
          ref="node"
          :key="cat.guid"
          :cat="cat"
          :parent-guid="section.guid"
          :parent-name="section.name"
          :cat-index="index"
          @selected="$emit('nodeSelected', $event)"
          @nodeDropped="nodeDropped" />
      </template>
      <drop-target
        :target-index="99999"
        :sort-order="0"
        :visible-index="visibleTargetIndex"
        :section-guid="section.guid"
        @dropped="bodyDrop" />
      <div
        ref="remaining"
        :style="remainingItemsStyle"
        class="section__remaining-items-placeholder" />
    </div>
    <div
      :class="createClass"
      class="create-wrapper">
      <div
        v-show="shouldShowCreate || shouldShowCreateInEmptySection"
        :class="createClass"
        class="create-container"
        data-test="create-container"
        @click.prevent="$emit('requestShowInput', payload)">
        <add-icon />
      </div>
    </div>
    <div class="loading-wrapper">
      <loading-indicator
        :is-loading="isLoadingItems && !hasNoItems"
        :width="20" />
    </div>
  </div>
</template>

<script>
import * as nodeTypes from '@/constants.js'
import { createNamespacedHelpers } from 'vuex'
import TreeNode from './tree-node.vue'
import DropTarget from './drop-target'
import AddIcon from '@/assets/icons/legacy/add'
import LoadingIndicator from '@/components/widgets/loading-indicator'
const { mapGetters, mapMutations } = createNamespacedHelpers('builder')

export default {
  name: 'TreeSection',
  components: {
    TreeNode,
    DropTarget,
    AddIcon,
    LoadingIndicator,
  },
  props: {
    section: {
      type: Object,
      default: () => ({}),
    },
    creationTriggerVisible: {
      type: Boolean,
      default: false,
    },
    creationInputVisible: {
      type: Boolean,
      default: false,
    },
    isLoadingItems: {
      type: Boolean,
      required: true,
    },
  },
  data () {
    return {
      headerEnterTimer: null,
      visibleTargetIndex: -1,
    }
  },
  computed: {
    ...mapGetters(['sectionWidth', 'sectionHeight', 'dragInfo', 'selectedCatalog']),
    hasNoItems () {
      return !this.section.categories.length || !this.section.categoriesCount || this.section.endOfList
    },
    remainingItemsStyle () {
      const rowHeight = this.hasNoItems ? 0 : 24 // based on dimensions of tree-node
      const placeholderHeight = rowHeight * (this.section.categoriesCount - this.section.categories.length)
      return { height: `${placeholderHeight}px` }
    },
    canDrop () {
      if (!this.dragInfo.dragging) return false
      return this.dragInfo.node.guid !== this.section.guid
    },
    isSectionOfOrigin () {
      return this.dragInfo.parentGuid === this.section.guid
    },
    draggingCategoryIntoCatalogSection () {
      if (!this.dragInfo.dragging) return
      return (this.section.position === 0 && this.dragInfo.node.nodeType === nodeTypes.category)
    },
    sectionNodeType () {
      return this.section.position === 0 ? nodeTypes.catalog : nodeTypes.category
    },
    createPayload () {
      const payload = {}
      if (!this.selectedCatalog) return payload
      if (this.sectionNodeType !== nodeTypes.store) {
        payload.parentCategoryId = this.section.guid === this.selectedCatalog.guid ? null : this.section.guid
        payload.productCatalogId = this.selectedCatalog.guid
        payload.parentGuid = this.section.guid
        payload.parentName = this.section.name
      }
      return payload
    },
    payload () {
      return { nodeType: this.sectionNodeType, parentInfo: this.createPayload }
    },

    createClass () {
      return { idle: this.shouldShowCreateInEmptySection }
    },
    shouldShowCreate () {
      return this.creationTriggerVisible && !this.dragInfo.dragging && !this.creationInputVisible
    },
    shouldShowCreateInEmptySection () {
      return this.section.categories.length === 0 && !this.creationInputVisible
    },
  },
  methods: {
    ...mapMutations(['setDragSectionGuid']),

    mouseenterHandler () {
      this.$emit('beginHover', this.section)
    },

    mouseleaveHandler () {
      this.$emit('endHover', this.section)
    },

    sectionEnter () {
      this.setDragSectionGuid(this.section.guid)
    },

    bodyDragover (e) {
      const lastDropTargetIndex = 99999
      if (!this.canDrop) return
      e.preventDefault()
      e.dataTransfer.dropEffect = 'move'
      if (this.draggingCategoryIntoCatalogSection) {
        this.updateVisibleTargetIndex(-1)
        return
      }
      if (e.target.classList.contains('section__drop-target')) { // target is where it is supposed to be
        return
      }
      if (e.target.classList.contains('section__body')) {
        this.updateVisibleTargetIndex(lastDropTargetIndex) // body drop
        return
      }
      if (e.target.id === this.dragInfo.node.guid) {
        this.updateVisibleTargetIndex(-1)
        return
      }
      if (e.offsetY < 9 || e.offsetY > 26) { // show target above or below
        const offset = e.target.offsetTop + e.offsetY
        const individualNodeHeight = 24
        let index = Math.round(offset / individualNodeHeight)
        if (index === this.section.categories.length) { // last drop target has index of 99999 and since .section__body might be covered by other node we need to set the index specifically to the last drop target
          index = lastDropTargetIndex
        }
        this.updateVisibleTargetIndex(index)
      }
    },

    updateVisibleTargetIndex (newIndex) {
      if (newIndex !== this.visibleTargetIndex) {
        this.visibleTargetIndex = newIndex
      }
    },

    bodyDrop (e) {
      if (this.canDrop && !this.draggingCategoryIntoCatalogSection) {
        e && e.preventDefault()
        const newIndex = this.section.categories.length
        const sortOrder = newIndex ? this.section.categories[newIndex - 1].sortOrder + 1 : 0
        this.dropNode(sortOrder, newIndex)
      }
    },

    targetDrop (newSortOrder) {
      let newIndex = this.visibleTargetIndex
      if (this.isSectionOfOrigin && this.dragInfo.catIndex < this.visibleTargetIndex) {
        newIndex = this.visibleTargetIndex - 1
      }
      this.dropNode(newSortOrder, newIndex)
    },

    dropNode (sortOrder, newIndex) {
      const moveTo = {
        parentGuid: this.section.guid,
        sortOrder: sortOrder,
        newIndex: newIndex,
        name: this.section.name,
      }
      this.nodeDropped(moveTo)
    },

    nodeDropped (moveTo) {
      this.$emit('nodeDropped', moveTo)
      this.updateVisibleTargetIndex(-1)
    },

    resetScroll () {
      this.$refs.body.scrollTop = 0
    },

    getScrollPosition () {
      return this.$refs.body.scrollTop
    },

    setScrollPosition (scrollTop) {
      this.$refs.body.scrollTop = scrollTop
    },

    checkIfMoreItemsNeeded () {
      const remainingItemsPlaceholder = this.$refs.remaining
      const panel = this.$refs.body
      if (!remainingItemsPlaceholder || !panel) {
        return
      }
      const placeholderTop = remainingItemsPlaceholder.getBoundingClientRect().top
      const placeholderIsInViewOrNearby = placeholderTop < 2 * panel.offsetHeight
      if (placeholderIsInViewOrNearby) {
        this.$emit('needsMoreCats', this.section)
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.cat-builder__section
  position relative
  display inline-block
  min-width 200px
  padding 4px
  border-radius 14px
  vertical-align top
  background $base-button-background-color--disabled

  &:not(:last-child)
    margin-right 10px

  &.no-drop
    opacity 0.5

.section__body
  position relative
  width 100%
  height 100%
  overflow-x hidden
  overflow-y auto

.create-wrapper
  position absolute
  top 0
  display flex
  align-items flex-end
  justify-content center
  width 100%
  height 100%
  padding 16px
  pointer-events none

.create-container
  position relative
  z-index 10
  display flex
  align-items center
  justify-content center
  width 24px
  height 24px
  border-radius 100%
  background #fff
  box-shadow 0 2px 6px 0 rgba(0, 0, 0, 0.15)
  cursor pointer
  pointer-events all

.create-wrapper.idle
  align-items center

.create-container.idle
  width 36px
  height 36px
  border 1px solid $base-color-gray-300
  background $base-button-background-color--disabled
  box-shadow none

.loading-wrapper
  position absolute
  bottom 4px
  display flex
  align-items center
  justify-content center
  width 100%
</style>
