<template>
  <div
    class="uc-content-editor-multi"
    :class="cssClassObject">
    <!-- Content editor -->
    <div class="uc-content-editor-multi__content">
      <!-- Loading -->
      <div class="uc-content-editor-multi__loading-indicator">
        <loading-indicator is-loading />
      </div>
      <!-- Header area -->
      <div class="uc-content-editor-multi__header">
        <div class="uc-content-editor-multi__heading-area">
          <div
            v-truncate
            class="uc-content-editor-multi__heading heading">
            <editor-wrapper
              v-bind="{ compact, errors }"
              :required="isRequired">
              <template
                #compactValue>
                <div
                  v-if="!isEmpty"
                  class="compact">
                  <p
                    v-truncate
                    class="compact__name">
                    {{ compactValue.firstItem }}
                  </p>
                  <small
                    v-if="compactValue.itemsCount > 0"
                    class="compact__count">
                    (+ {{ compactValue.itemsCount }} {{ $t('global.more') }})
                  </small>
                </div>
              </template>
              <h2
                v-truncate
                class="heading__header">
                {{ label }} ({{ addedItems.length }})
                <span
                  v-if="isMultilingual"
                  class="heading__language">({{ language }})
                </span>
              </h2>
            </editor-wrapper>
          </div>
          <div
            v-if="!compact"
            class="uc-content-editor-multi__actions">
            <uc-button
              type="submit"
              size="sm"
              @click="openModal">
              {{ $t('global.add') }}
            </uc-button>
          </div>
        </div>
      </div>

      <!-- List of selected content -->
      <div
        v-if="!compact"
        v-custom-scroll-bar
        class="uc-content-editor-multi__list">
        <!-- Error occured -->
        <p
          v-if="error"
          class="uc-content-editor-multi__message">
          {{ $t('contentEditor.errorMessage') }}
        </p>

        <template v-if="!error">
          <!-- No content selected -->
          <p
            v-if="isEmpty"
            class="uc-content-editor-multi__message">
            {{ $t('contentEditor.noContentSelected') }}
          </p>

          <template v-if="!isEmpty">
            <!-- Content overview-->
            <content-navigator-selection
              v-model="selectedItems"
              :items="addedItems"
              @update-sort="onUpdateSort($event)">
              <template #actions>
                <uc-button
                  floating
                  :disabled="isRequired && lastItemLeft"
                  @click="removeNodes">
                  {{ $t('global.remove') }} ({{ selectedItems.length }})
                </uc-button>
              </template>
            </content-navigator-selection>
          </template>
        </template>
      </div>
    </div>

    <!-- Content picker -->
    <uc-modal
      v-if="isModalOpen"
      :title="$t('ucContentPicker.header')"
      size="lg"
      @hide="closeModal">
      <div class="uc-content-editor-multi__modal">
        <content-picker-multi
          v-bind="{ contentType }"
          :selected="addedItems"
          :required="isRequired"
          :cms="systemInfo.cms"
          @nodesSelected="onNodesSelected" />
      </div>
    </uc-modal>
  </div>
</template>

<script>
import editorMixin from '@/components/editors/editor-mixin'
import UcButton from '@/components/forms/uc-button/uc-button'
import UcModal from '@/components/widgets/uc-modal'
import ContentPickerMulti from './uc-content-picker-multi'
import ContentNavigatorSelection from './components/content-navigator-selection'
import { getSelectedContentByGuids } from './components/content-navigator.http'
import { mapGetters } from 'vuex'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { getGuidFromNode } from '@/components/generic/uc-image-picker/uc-image-picker.utils'

export default {
  name: 'UcContentEditorMulti',
  components: {
    UcButton,
    UcModal,
    ContentPickerMulti,
    ContentNavigatorSelection,
    LoadingIndicator,
  },
  mixins: [editorMixin],
  props: {
    contentType: {
      type: String,
      default: 'Content',
    },
  },
  data () {
    return {
      isModalOpen: false,
      addedItems: [],
      selectedItems: [],
      isLoading: false,
      error: false,
    }
  },
  computed: {
    ...mapGetters(['systemInfo']),
    label () {
      return this.definition.label
    },
    isEmpty () {
      return this.selected.length === 0
    },
    cssClassObject () {
      return {
        'is-empty': this.isEmpty,
        'is-compact': this.compact,
        'has-error': this.error,
        'is-max-height': this.addedItems.length > 7,
        'is-loading': this.isLoading,
      }
    },
    lastItemLeft () {
      return this.addedItems.length === 1 && this.selectedItems.length === 1
    },
    compactValue () {
      return {
        firstItem: this.definition.name,
        itemsCount: this.selected.length - 1,
      }
    },
    selectedItemsGuids () {
      return this.selectedItems.map(getGuidFromNode)
    },
  },
  watch: {
    selected: {
      async handler (value) {
        if (this.compact) {
          return
        }

        if (!value.length) {
          this.addedItems = []
          this.selectedItems = []
          return
        }

        let loadingIndicatorTimeoutId

        try {
          // Avoid spinner to show up, when dragging and dropping
          loadingIndicatorTimeoutId = setTimeout(() => {
            this.isLoading = true
          }, 500)
          this.error = false
          const result = await getSelectedContentByGuids(this.contentType, value)
          this.addedItems = result.data
          this.selectedItems = this.addedItems.filter(item => this.selectedItemsGuids.includes(getGuidFromNode(item)))
        } catch {
          this.error = true
        } finally {
          clearTimeout(loadingIndicatorTimeoutId)
          this.isLoading = false
        }
      },
      immediate: true,
    },
  },
  methods: {
    closeModal () {
      this.isModalOpen = false
    },
    openModal () {
      this.isModalOpen = true
    },
    saveNodes (items) {
      const newGuids = items.map(getGuidFromNode)
      const oldGuids = this.selected
      const selectionChanged = JSON.stringify(newGuids) !== JSON.stringify(oldGuids)

      if (selectionChanged) {
        this.onChange({
          changeValues: {
            selected: {
              newValue: newGuids,
              oldValue: oldGuids,
            },
            name: {
              newValue: items.length > 0 ? items[0].name : '',
              oldValue: this.definition.name,
            },
          },
        })
      }
    },
    onNodesSelected (items) {
      this.saveNodes(items)
      this.closeModal()
    },
    removeNodes () {
      const items = this.addedItems.filter((addedItem) => !this.selectedItems.includes(addedItem))
      this.saveNodes(items)
    },
    onUpdateSort (items) {
      this.saveNodes(items)
    },
  },
}
</script>

<style lang="stylus" scoped>
$header-height = $base-unit * 10
$list-item-height = $base-unit * 8

.uc-content-editor-multi__list
  position relative
  display flex
  flex-direction column
  min-height 4 * $list-item-height
  max-height 7.5 * $list-item-height // We want to indicate that list is scrollable with half item
  overflow-y auto
  transition opacity 200ms ease-in-out

.uc-content-editor-multi
  &.is-empty
  &.has-error
    .uc-content-editor-multi__list
      justify-content center

  &:not(.is-compact)
    .uc-content-editor-multi__content
      height 100%
      padding-top $header-height
      // Enable viewport for fixed header
      transform translateX(0)

    .uc-content-editor-multi__header
      position fixed
      top 0
      z-index 1
      display flex
      align-items center
      width 100%
      height $header-height
      padding 0 ($base-unit * 3)
      border-bottom $base-border
      background-color $base-color-white

    .uc-content-editor-multi__heading-area
      display flex
      flex-direction row
      align-items center
      justify-content space-between
      width 100%

    .uc-content-editor-multi__heading
      display flex
      flex-direction column

      >>> .feedback
        position absolute
        bottom 1.5 * $base-unit
        margin 0

    .heading__header
      primary-heading-2()
      margin 0
      user-select none

    .heading__language
      color $base-color-text-secondary
      font-weight normal

  // Prevent action button to expand list on smaller lists
  &:not(.is-max-height)
    >>> .uc-list-selectable__actions
      position fixed

.uc-content-editor-multi__loading-indicator
  position absolute
  top $header-height
  right 0
  bottom 0
  left 0
  z-index 1
  display flex
  flex 1 1 auto
  align-items center
  justify-content center
  background-color white
  visibility hidden
  opacity 0
  transition opacity 200ms ease-in-out

.uc-content-editor-multi__message
  secondary-body-text()
  padding 0 12 * $base-unit
  text-align center

.uc-content-editor-multi__modal
  position relative
  height 600px

.compact__name
  margin 0

.compact__count
  secondary-petit-text()

// state: .is-loading
.uc-content-editor-multi.is-loading
  .uc-content-editor-multi__list
    opacity 0

  .uc-content-editor-multi__loading-indicator
    visibility visible
    opacity 1
</style>
