<template>
  <span
    class="input-group"
    :class="cssClassObject"
    data-test="input-group">
    <component
      :is="tag"
      class="input-group__input"
      data-test="input-group-input">
      <slot />

      <span class="input-group__label">
        <span>{{ label }}</span>
      </span>

      <div
        v-if="$slots.after"
        class="input-group__after">
        <slot name="after" />
      </div>

    </component>
  </span>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

const allowedTags = {
  LABEL: 'label',
  DIV: 'div',
}

export default {
  name: 'InputGroup',
  props: {
    label: {
      type: String,
      default: '',
    },
    hasErrors: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    // When used with non-input elements, it might be neccesary to use another wrapper element
    tag: {
      type: String,
      default: 'label',
      validator: validateAgainstEnum(allowedTags),
    },
  },
  computed: {
    cssClassObject () {
      return {
        'input-group--error': this.hasErrors,
        'input-group--readonly': this.readonly,
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
/* stylelint-disable no-descending-specificity */
.input-group__label
  position absolute
  top -8px
  left 14px
  display block
  padding 0 2px
  isolation isolate
  transition all .2s
  user-select none
  pointer-events none
  secondary-petit-text()

  &::after
    position absolute
    bottom 0
    left 0
    z-index 1
    display block
    width 100%
    height calc(50% + 1px)
    background-color $base-color-white
    content ''

  span
    position relative;
    z-index 2

.input-group__input
  position relative
  display inline-flex
  box-sizing border-box
  width 100%
  min-height $base-unit * 6
  border 1px solid $base-color-gray-400
  border-radius ($base-unit / 2)
  background-color $base-color-white
  cursor text

  textarea
    resize none

  input
  textarea
    width 100%
    height 100%
    padding 13px 16px 15px
    border none
    background-color transparent
    outline none
    primary-body-text()

    &::-ms-clear
      display none

    &:focus
      color $base-color-brand

    &::placeholder
      opacity 0
      transition all .2s
      secondary-body-text()

    &:placeholder-shown:not(:focus)::placeholder,
    &:placeholder-shown:focus::placeholder
      opacity 0

    &:placeholder-shown:not(:focus) + .input-group__label
      secondary-body-text()
      top 12px

.input-group--error
  .input-group__input
    border 1px solid $base-color-error

  .input-group__label
    color $base-color-error

.input-group--readonly
  user-select none
  pointer-events none

  .input-group__label::after
    background-color $base-background-color

  .input-group__input
    color $base-color-gray-500
    background-color $base-background-color

    input
    textarea
      color $base-color-gray-500
      user-select none

.input-group__after
  display flex
  border-left $base-border
</style>
