<template>
  <div class="uc-content-picker-single">
    <use-navigator-data-provider :content-type="contentType">
      <template #default="{ data, getData, isLoading, isEmptyRootState }">
        <fragment v-if="!isEmptyRootState">
          <p class="uc-content-picker-single__subheader">
            {{ $t('ucContentPicker.subHeader') }}
          </p>
          <content-navigator
            :nodes="data"
            v-bind="{ isLoading, selectedNodesGuids }"
            @openNode="getData"
            @selectNode="onSelectNode" />
        </fragment>
        <uc-navigator-empty-state
          v-else
          :title="$t('ucContentPicker.noItemsTitle')"
          :text="$t('ucContentPicker.noItemsText', { name: cms })" />
      </template>
    </use-navigator-data-provider>
  </div>
</template>

<script>
import ContentNavigator from './components/content-navigator'
import { UseNavigatorDataProvider, UcNavigatorEmptyState } from '@/components/generic/uc-navigator'
import { validateAgainstEnum } from '@/utils/validate-against'
import { Fragment } from 'vue-fragment'

const ComponentEvent = {
  NODE_SELECTED: 'nodeSelected',
}

const ContentTypeProp = {
  IMAGE: 'image',
  CONTENT: 'content',
}

export default {
  name: 'UcContentPickerSingle',
  components: {
    UseNavigatorDataProvider,
    UcNavigatorEmptyState,
    ContentNavigator,
    Fragment,
  },
  props: {
    selected: {
      type: Object,
      default: () => ({}),
    },
    contentType: {
      type: String,
      default: ContentTypeProp.CONTENT,
      validator: validateAgainstEnum(ContentTypeProp),
    },
    cms: {
      type: String,
      default: '',
    },
  },
  computed: {
    selectedNodesGuids () {
      return [this.selected.guid || '']
    },
  },
  methods: {
    onSelectNode (node) {
      this.$emit(ComponentEvent.NODE_SELECTED, [node])
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-content-picker-single
  display flex
  flex-direction column
  height 100%

.uc-content-picker-single__subheader
  secondary-details-text()
  margin 0 auto 8 * $base-unit
  text-align center
</style>
