const getters = {
  selectedStoreGuid (state, _getters, _rootState, rootGetters) {
    const stores = rootGetters['builder/stores']

    if (stores.length === 0) {
      return undefined
    }

    const defaultStoreGuid = stores[0].guid

    // If no store was previously selected, we default to the first available store
    if (!state.selectedStoreGuid) {
      return defaultStoreGuid
    }

    // If the selected store was deleted, we return the first available store
    const hasSelectedStoreBeenDeleted = !stores.find(store => store.guid === state.selectedStoreGuid)
    return hasSelectedStoreBeenDeleted ? defaultStoreGuid : state.selectedStoreGuid
  },
  selectedStoreGuidName (_state, getters, _rootState, rootGetters) {
    const stores = rootGetters['builder/stores']

    if (stores.length === 0) {
      return undefined
    }

    return stores.find(store => store.guid === getters.selectedStoreGuid).name
  },
  selectedContentLanguage (state, _getters, _rootState, rootGetters) {
    if (rootGetters.languages.length === 0) {
      return undefined
    }

    const defaultLanguage = rootGetters.languages[0].value

    // If no language was previously selected, we default to the first available language
    if (!state.selectedContentLanguage) {
      return defaultLanguage
    }

    // If the selected language was deleted, we return the first available language
    const hasSelectedLanguageBeenDeleted = !rootGetters.languages.find(language => language.value === state.selectedContentLanguage)
    return hasSelectedLanguageBeenDeleted ? defaultLanguage : state.selectedContentLanguage
  },
  selectedContentLanguageName (_state, getters, _rootState, rootGetters) {
    if (rootGetters.languages.length === 0) {
      return undefined
    }

    return rootGetters.languages.find(language => language.value === getters.selectedContentLanguage).text
  },
}

export default getters
