<template>
  <article class="details-layout">
    <base-layout>
      <div class="details-layout__header">
        <uc-button
          v-if="showBackButton"
          data-test="back-button"
          :theme="buttonTheme"
          @click="onClick">
          <template #before>
            <arrow-back />
          </template>
          {{ $tc('global.back') }}
        </uc-button>
        <!-- TODO: Remove link slot when no legacy link anymore -->
        <div
          v-if="legacyUrl"
          class="details-layout__link">
          <legacy-link
            :href="legacyUrl" />
        </div>
      </div>
      <slot />
    </base-layout>
  </article>
</template>

<script>
import UcButton, { ThemeProp } from '@/components/forms/uc-button'
import { ArrowBack16Normal as ArrowBack } from '@/assets/icons'
import BaseLayout from './_base-layout.vue'
import { LegacyLink } from '@/components/widgets/legacy-link/'
import { createNamespacedHelpers, mapMutations } from 'vuex'

const detailsStore = createNamespacedHelpers('details')

const ComponentEvent = {
  BACK_CLICK: 'back-click',
}

export default {
  name: 'DetailsLayout',
  components: {
    BaseLayout,
    UcButton,
    ArrowBack,
    LegacyLink,
  },
  computed: {
    ...detailsStore.mapGetters(['currentDetails']),
    showBackButton () {
      return this.$listeners && this.$listeners[ComponentEvent.BACK_CLICK]
    },
    buttonTheme () {
      return ThemeProp.LINK
    },
    legacyUrl () {
      return this.currentDetails?.meta?.legacyUrl ?? ''
    },
  },
  methods: {
    ...mapMutations(['clearUndos']),
    onClick () {
      window.scroll(0, 0)
      this.clearUndos()
      this.$emit(ComponentEvent.BACK_CLICK)
    },
  },
}
</script>

<style lang="stylus" scoped>
.details-layout
  padding-bottom 4 * $base-unit

.details-layout__header
  position sticky
  top $base-unit
  z-index $base-z-index * 1000
  display flex
  align-items center
  justify-content space-between
  margin-top $base-unit * 2
  cursor pointer
  pointer-events all

.details-layout__link
  margin-left auto
</style>
