import { rest } from 'msw'
import {
  orders,
  orderStatuses,
  orderInitStatuses,
  orderAvailableStatuses,
  orderDefinitions,
  orderDefinitionsShallow,
  orderPriceGroups,
} from '@/mocks/entities/orders/'

import { ApiStatusCodes } from '@/constants.js'
import { getStatusCodeForMockGuid, getMockGuidForStatusCode } from '@/mocks/constants.js'


const RESPONSE_DELAY = 200

export const rootOrderHandlers = [
  rest.get('/ucommerceapi/v2/orders/definitions', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(orderDefinitionsShallow),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(orderDefinitions),
    )
  }),

  rest.patch('/ucommerceapi/v2/orders/:cultureCode', (req, res, ctx) => {
    const params = new URLSearchParams(req.url.search)
    const guids = params.getAll('guids')
    const bulkOperationResponse = guids.map(guid => {
      return {
        guid,
        message: 'success',
        statusCode: ApiStatusCodes.OK,
      }
    })

    const response = [...bulkOperationResponse]

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/:orderGuid/:cultureCode', (req, res, ctx) => {
    let response = {
      message: '',
    }

    let statusCode = 200
    const order = orders.find(order => order.meta.entityGuid === req.params.orderGuid)
    if (!order) {
      statusCode = 404
      response.message = 'The requested mock order does not exist.'
    } else {
      response = order
    }

    const mockStatusCode = getStatusCodeForMockGuid(req.params.orderGuid)
    if (mockStatusCode) {
      statusCode = mockStatusCode
      response.message = `This is a mock ${statusCode} message.`
    }

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(statusCode),
      ctx.json(response),
    )
  }),


  rest.get('/ucommerceapi/v2/orders/:orderGuid/statuses/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const totalCount = orderAvailableStatuses.length
    const response = orderAvailableStatuses.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', totalCount),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/statuses/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))
    const initial = Boolean(params.get('initial'))

    const statuses = initial
      ? orderInitStatuses
      : orderStatuses

    const totalCount = statuses.length
    const response = statuses.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', totalCount),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))
    const searchTerm = params.get('searchTerm')
    const storeGuid = params.get('store')

    const ordersInStore = orders.filter(order => order.store.guid === storeGuid)

    let searchResult
    let response = ordersInStore.slice(startAt, startAt + limit)
    let totalCount = ordersInStore.length

    if (searchTerm) {
      searchResult = ordersInStore.filter(order => order.customer.value.toLowerCase().includes(searchTerm.toLowerCase()))
      response = searchResult.slice(startAt, startAt + limit)
      totalCount = searchResult.length
    }

    return res(
      ctx.set('X-total-count', totalCount),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/:orderGuid/priceGroups/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))
    const searchTerm = params.get('searchTerm')

    let totalCount = 0
    let resultCount = 0
    let statusCode
    let response
    let searchResult

    switch (req.params.orderGuid) {
      case 'mock-guid-empty':
        statusCode = 200
        response = []
        break
      case getMockGuidForStatusCode(400):
        statusCode = 400
        response = { Message: 'Mock 400: Provided culture code is invalid.' }
        break
      case getMockGuidForStatusCode(401):
        statusCode = 401
        response = { Message: 'Mock 401: Missing permissions.' }
        break
      case getMockGuidForStatusCode(404):
        statusCode = 404
        response = { Message: 'Mock 404: Order not found.' }
        break
      default:
        statusCode = 200
        response = orderPriceGroups.slice(startAt, startAt + limit)
        totalCount = orderPriceGroups.length
        resultCount = orderPriceGroups.length

        if (searchTerm) {
          searchResult = orderPriceGroups.filter(order => order.name.toLowerCase().includes(searchTerm.toLowerCase()))
          response = searchResult.slice(startAt, startAt + limit)
          totalCount = searchResult.length
          resultCount = searchResult.length
        }
    }

    return res(
      ctx.set('x-total-count', totalCount),
      ctx.set('x-result-count', resultCount),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(statusCode),
      ctx.json(response),
    )
  }),
]
