<template>
  <div class="editor-wrapper">
    <template v-if="compact">
      <slot name="compactValue">
        {{ compactValue }}
      </slot>
    </template>
    <template v-else>
      <slot />
      <ul
        v-if="hasErrors"
        class="feedback feedback--error"
        data-test="error-message">
        <li
          v-for="(errorMessage, index) in errors"
          :key="index">
          {{ errorMessage }}
        </li>
      </ul>
      <span
        v-if="required && !hasErrors"
        class="feedback feedback--info"
        data-test="feedback-feedback-info">
        {{ $t('global.required') }}
      </span>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    compact: {
      type: Boolean,
      default: false,
    },
    compactValue: {
      type: String,
      default: '',
    },
    required: {
      type: Boolean,
      default: false,
    },
    errors: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    hasErrors () {
      return this.errors && this.errors.length > 0
    },
  },
}
</script>

<style lang="stylus" scoped>
.editor-wrapper
  display contents

.feedback
  margin 4px 16px 0
  padding 0
  list-style none
  user-select none
  primary-petit-text()

  &.feedback--info
    color $base-color-text-secondary

  &.feedback--error
    color $base-color-error
</style>
