<template>
  <form
    class="order-create"
    @submit.prevent="createOrder">
    <uc-modal
      :title="$t('title')"
      @hide="cancel">
      <p class="order-create-select__subheader">
        {{ $t('selectInfo') }}
      </p>

      <div
        v-if="isInitializing"
        class="order-create__loading">
        <loading-indicator is-loading />
      </div>

      <div
        v-show="isIdle || isPosting || hasError"
        class="order-create__form">
        <!-- Order number -->
        <div class="order-create__form-field">
          <uc-text-input
            v-model.trim="orderNumber"
            data-test="order-create-order-number"
            :label="$t('orderNumber')"
            :readonly="isPosting" />
        </div>

        <!-- Order status -->
        <div class="order-create__form-field">
          <uc-select
            data-test="order-create-status-dropdown"
            :options="statusOptions"
            :placeholder="$t('orderStatus')"
            :readonly="isPosting"
            @selected="setStatus">
            <template #pending>
              <loading-indicator is-loading />
            </template>
          </uc-select>
        </div>

        <!-- Store -->
        <div class="order-create__form-field">
          <uc-select
            data-test="order-create-store-dropdown"
            :options="storeOptions"
            :selected="store"
            :placeholder="$t('store')"
            :readonly="isPosting"
            @selected="setStore">
            <template #pending>
              <loading-indicator is-loading />
            </template>
          </uc-select>
        </div>

        <!-- Currency -->
        <div class="order-create__form-field">
          <uc-select
            data-test="order-create-currency-dropdown"
            :options="currencyOptions"
            :placeholder="$t('currency')"
            :readonly="isPosting"
            @selected="setCurrency">
            <template #pending>
              <loading-indicator is-loading />
            </template>
          </uc-select>
        </div>

        <!-- Error message -->
        <div
          v-show="hasError"
          class="order-create__error"
          data-test="order-create-error">
          {{ errorMessage }}
        </div>
      </div>

      <!-- Submit -->
      <template #footer>
        <uc-button
          data-test="order-create-add-button"
          :disabled="!isEnabled"
          type="submit"
          theme="primary"
          @click="createOrder">
          {{ $t('global.add') }}
          <template
            v-if="isPosting"
            #after>
            <loading-spinner is-loading />
          </template>
        </uc-button>
        <uc-button
          theme="text"
          @click="cancel">
          {{ $t('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<i18n>
{
  "en": {
    "title": "New order",
    "selectInfo": "Select information for the new order",
    "orderNumber": "Order number",
    "orderStatus": "Order status",
    "store": "Store",
    "currency": "Currency"
  },
  "da": {
  },
  "sv": {
  },
  "de": {
  }
}
</i18n>

<script>
import { createNamespacedHelpers } from 'vuex'
import { UcModal } from '@/components/widgets/uc-modal/'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import { UcSelect } from '@/components/forms/uc-select/'
import { UcButton } from '@/components/forms/uc-button/'
import LoadingSpinner from '@/components/widgets/loading-spinner'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { orderService, storeService, currencyService } from '@/services/'

const persistedStore = createNamespacedHelpers('persisted')

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

export const ComponentEvent = {
  CREATE: 'create',
  CANCEL: 'cancel',
}

export default {
  name: 'UcOrderCreateModal',
  components: {
    UcModal,
    UcTextInput,
    UcSelect,
    UcButton,
    LoadingSpinner,
    LoadingIndicator,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      statusOptions: [],
      storeOptions: [],
      currencyOptions: [],

      definition: null,
      orderNumber: '(auto)',
      orderStatus: '',
      store: '',
      currency: '',
      errorMessage: '',

      // TODO(ch-14269): When the story is finished, the pagination boiler code shouldn't exist on this level anymore.
      currentPage: 0,
      pageSize: Number.MAX_SAFE_INTEGER,
    }
  },
  computed: {
    ...persistedStore.mapGetters(['selectedStoreGuid']),
    isEnabled () {
      return this.orderNumber.length > 0 &&
        !!this.orderStatus &&
        !!this.store &&
        !!this.currency
    },
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
  },
  async created () {
    await this.fetchDataForModalFields()
    this.store = this.selectedStoreGuid
  },
  methods: {
    async fetchDataForModalFields () {
      const startAt = this.currentPage * this.pageSize
      const params = {
        startAt,
        limit: this.pageSize,
      }
      const cultureCode = this.contentLanguage

      try {
        this.definition = (await orderService.fetchDefinitionsAsync()).data[0]

        this.statusOptions = (await orderService.fetchStatusesAsync({ cultureCode, params: { ...params, initial: true } }))
          .data.map(this.mapToOption)

        this.storeOptions = (await storeService.fetchEntitiesAsync({ cultureCode, params }))
          // TODO(ch-14273): With `shallow` query string param, we will be able to use `data` directly.
          .getShallow().map(this.mapToOption)

        this.currencyOptions = (await currencyService.fetchEntitiesAsync({ params }))
          .data.map(this.mapToOption)

        this.state = ComponentState.IDLE
      } catch (e) {
        this.state = ComponentState.ERROR
        if (e.response.data.message) {
          this.errorMessage = e.response.data.message
        } else {
          throw e
        }
      }
    },
    mapToOption (item) {
      return {
        label: item.name,
        guid: item.guid,
      }
    },
    async createOrder () {
      if (this.isPosting) {
        return
      }

      const payload = {
        orderNumber: this.orderNumber,
        orderStatus: this.orderStatus,
        store: this.store,
        currency: this.currency,
      }

      try {
        this.state = ComponentState.POSTING
        const createOrderResponse = await orderService.createEntityAsync({
          ...payload,
          cultureCode: this.contentLanguage,
        })
        const orderGetResponse = await orderService.fetchEntityAsync({
          guid: createOrderResponse.data.guid,
          cultureCode: this.contentLanguage,
        })
        this.state = ComponentState.IDLE

        this.$emit(ComponentEvent.CREATE, orderGetResponse.data)
      } catch (e) {
        this.state = ComponentState.ERROR
        if (e.response.data.message) {
          this.errorMessage = e.response.data.message
        } else {
          throw e
        }
      }
    },
    cancel () {
      this.$emit(ComponentEvent.CANCEL)
    },
    setStatus (payload) {
      this.orderStatus = payload.guid
    },
    setStore (payload) {
      this.store = payload.guid
    },
    setCurrency (payload) {
      this.currency = payload.guid
    },
  },
}
</script>

<style lang="stylus" scoped>
.order-create__loading
  display flex
  align-items center
  justify-content center

.order-create__loading,
.order-create__form
  height $base-unit * 36

.order-create-select__subheader
  secondary-details-text()
  width 100%
  margin 0 auto 6 * $base-unit
  text-align center

.order-create__form-field:not(:last-of-type)
  margin-bottom $base-unit * 3

.order-create__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()
</style>
