<template>
  <transition name="fade">
    <div class="campaigns">
      <uc-campaign-list
        v-bind="{ campaigns, state, totalCount }"
        @next="requestNextPage"
        @drag-end="onDragEnd"
        @delete="deleteCampaign" />

      <!--
          HACK:
          ------------------------------------------------------------------------------------------------------------------------------------------------
          - The `<keep-alive>` and `:key` below is a hack implemented to keep the `campaign` component alive so that we can listen for its `resurrect` event
          ------------------------------------------------------------------------------------------------------------------------------------------------
        -->
      <keep-alive
        max="1"
        include="CampaignEdit">
        <router-view
          :key="$route.params.guid"
          :content-language="contentLanguage"
          @delete="deleteCampaign"
          @resurrect="resurrectCampaign"
          @create="createCampaign" />
      </keep-alive>

      <!-- State: Deleting -->
      <uc-warning-dialog
        v-if="isDeleting"
        ref="deleteConfirmationDialog"
        :title="$t('ucEntityList.deleteConfirmationDialog.title', { label: 'campaign' })"
        :text="$t('ucEntityList.deleteConfirmationDialog.text', { entity: `a campaign` })"
        :proceed-button-text="$t('ucEntityList.deleteConfirmationDialog.proceed')"
        :cancel-button-text="$t('ucEntityList.deleteConfirmationDialog.cancel')" />
    </div>
  </transition>
</template>


<script>
import { UcCampaignList } from '../components/uc-campaign-list/'
import { UcWarningDialog, UcWarningDialogEvent } from '@/components/widgets/uc-warning-dialog/'
import { campaignService } from '@/services'
import { expectComponentEventAsync } from '@/utils/vue.utils'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR_UNKNOWN: 'errorUnknown',
  DELETING: 'deleting',
  PATCHING: 'patching',
}

export default {
  name: 'Campaigns',
  components: {
    UcCampaignList,
    UcWarningDialog,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      campaigns: [],
      totalCount: 0,
      startAt: 0,
      currentPage: 1,
      pageSize: 20,
    }
  },
  computed: {
    isDeleting () {
      return this.state === ComponentState.DELETING
    },
  },
  async created () {
    await this.fetchCampaignsAsync()
  },
  methods: {
    async fetchCampaignsAsync (startAt = 0, currentPage = 1) {
      try {
        const response = await campaignService.fetchEntitiesAsync({ params: { startAt, limit: this.pageSize }, cultureCode: this.contentLanguage })
        const campaigns = response.data

        this.campaigns = [...this.campaigns, ...campaigns]
        this.totalCount = Number(response.headers['x-total-count'])
        this.currentPage = currentPage
        this.state = ComponentState.IDLE
      } catch (error) {
        if (error.response && error.response.status) {
          switch (error.response.status) {
            case 400:
              this.state = ComponentState.ERROR_UNKNOWN
              break
            default:
              this.state = ComponentState.ERROR_UNKNOWN
          }
        } else {
          throw error
        }
      }
    },

    addCampaign (entity) {
      // Prevent duplicate campaigns (when a campaign is resurrected through both recreation and undo)
      if (this.campaigns.find(campaign => campaign.meta.entityGuid === entity.meta.entityGuid)) {
        return
      }

      this.campaigns.unshift(entity)
      this.totalCount++
    },
    createCampaign (entity) {
      this.addCampaign(entity)
      this.$router.replace({
        name: 'campaign-edit',
        params: {
          campaignGuid: entity.meta.entityGuid,
          entity,
        },
      })
    },
    resurrectCampaign () {
      console.error('Not implemented.')
    },

    async deleteCampaignsAsync (campaignGuids) {
      try {
        await expectComponentEventAsync({
          component: this.$refs.deleteConfirmationDialog,
          resolveEvent: UcWarningDialogEvent.PROCEED,
          rejectEvent: UcWarningDialogEvent.CANCEL,
        })

        try {
          await campaignService.deleteEntitiesAsync(campaignGuids)
          this.campaigns = this.campaigns.filter(entity => !campaignGuids.includes(entity.meta.entityGuid))
          this.totalCount -= campaignGuids.length
          this.state = ComponentState.IDLE
        } catch (e) {
          // TODO(ch10038): We need a way to handle http 500 request errors
          console.error(e)
          this.totalCount += campaignGuids.length
          this.state = ComponentState.IDLE
        }
      } catch (e) {
        this.state = ComponentState.IDLE
      }
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1

      await this.fetchCampaignsAsync(startAt, page)
    },
    async deleteCampaign (selection) {
      this.state = ComponentState.DELETING
      const campaignGuids = selection.map(campaign => (
        campaign.guid
      ))
      await this.$nextTick()
      await this.deleteCampaignsAsync(campaignGuids)
    },
    async onDragEnd ({ guid, priority }) {
      try {
        this.state = ComponentState.PATCHING

        await campaignService.updateEntityAsync({
          campaignGuid: guid,
          cultureCode: this.contentLanguage,
          payload: {
            priority: {
              value: priority,
            },
          },
        })
        this.state = ComponentState.IDLE
      } catch (error) {
        // TODO(13671): Handle if drag and drop goes wrong on the backend
        this.state = ComponentState.ERROR
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.campaigns
  width 100%
  min-width $base-layout-width
</style>
