import { rest } from 'msw'
import { generateMockOrderLines } from '@/mocks/entities/discounts/order-line-discount.mock'

const orderLines = generateMockOrderLines()
let selected = []

const baseUrl = '/ucommerceapi/v2/promotions/:promotionGuid/discounts/:discountGuid'
const URLs = {
  GET_ORDER_LINES: `${baseUrl}/orderlines/:cultureCode`,
  PATCH_ORDER_LINES: `${baseUrl}/:cultureCode`,
  GET_SELECTED: `${baseUrl}/orderlines/selected/:cultureCode`,
}

const RESPONSE_DELAY = 200

export const orderLineDiscountHandlers = [
  rest.get(URLs.GET_ORDER_LINES, (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))
    const searchTerm = params.get('searchTerm')

    const response = searchTerm === ''
      ? orderLines.slice(startAt, startAt + limit)
      : orderLines
        .filter(orderLine => {
          const term = searchTerm.toLowerCase()
          const matchesSku = orderLine.sku.toLowerCase().includes(term)
          const matchesVariantSku = orderLine.variantSku.toLowerCase().includes(term)
          return matchesSku || matchesVariantSku
        })
        .slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', orderLines.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.patch(URLs.PATCH_ORDER_LINES, (req, res, ctx) => {
    selected = orderLines.find(orderLine => req.body.orderLine.selected.includes(orderLine.guid))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),

  rest.get(URLs.GET_SELECTED, (_req, res, ctx) => {
    const response = selected

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
]
