import { rest } from 'msw'
import { catalogs } from '@/mocks/entities/catalogs/'

const RESPONSE_DELAY = 200

export const rootCatalogHandlers = [
  rest.get('/ucommerceapi/v2/stores/:storeGuid/catalogs', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const response = catalogs.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', catalogs.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
]
