<template>
  <div class="orders">
    <portal to="header">
      <div class="orders__header">
        <store-selector @select="onStoreSelect" />
      </div>
    </portal>
    <div>
      <!-- Search bar and filters -->
      <div class="orders__search">
        <!-- Filters -->
        <uc-order-filter
          v-model="filters"
          v-bind="{ contentLanguage }" />

        <!-- Search bar -->
        <div class="orders__search-bar">
          <uc-search v-model.trim="searchTerm" />
        </div>
      </div>

      <!-- List of orders -->
      <uc-order-list
        v-model="selection"
        v-bind="{ contentLanguage, orders, state, totalCount: resultCount, emptyStateMessage }"
        @next="requestNextPage"
        @create="onCreate"
        @status-change="onStatusChange" />

      <!-- Notifications -->
      <div
        v-if="notifications.length > 0"
        class="orders__notifications">
        <div
          v-for="(notification, index) in notifications"
          :key="index"
          class="orders__notifications-item">
          <uc-snackbar
            v-bind="notification"
            @click="clearNotification(index)"
            @hide="clearFiniteNotifications" />
        </div>
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "en": {
    "noSearchResults": "No results found. Try adjusting your search or filters to find what you're looking for.",
    "emptyState": "No orders have been created yet. Click the button to get started.",
    "messages": {
      "success": "An order was successfully updated to '{newStatus}' status. | {count} orders were successfully updated to '{newStatus}' status."
    }
  }
}
</i18n>

<script>
import { orderService } from '@/services/'
import { UcSearch } from '@/components/forms/uc-search/'
import StoreSelector from '@/components/widgets/store-selector.vue'
import { UcOrderList } from '../components/uc-order-list/'
import { UcOrderFilter } from '../components/uc-order-filter/'
import { UcSnackbar, UcSnackbarTheme, UcSnackbarDuration } from '@/components/widgets/uc-snackbar/'
import { ApiStatusCodes } from '@/constants.js'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR_UNKNOWN: 'errorUnknown',
}
const apiStatusCodes = {
  ADDED: ApiStatusCodes.OK,
}

export default {
  name: 'Orders',
  components: {
    StoreSelector,
    UcSearch,
    UcOrderList,
    UcOrderFilter,
    UcSnackbar,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    selectedStore: {
      type: String,
      required: true,
    },
    query: {
      type: Object,
      default: () => ({}),
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      orders: [],
      selection: [],
      totalCount: 0,
      resultCount: 0,
      startAt: 0,
      currentPage: 1,
      pageSize: 20,
      selectedStoreGuid: this.selectedStore,
      notifications: [],
    }
  },
  computed: {
    emptyStateMessage () {
      return this.totalCount === 0 ? this.$t('emptyState') : this.$t('noSearchResults')
    },
    searchTerm: {
      get () {
        return this.query.q
      },
      set (searchTerm) {
        const q = searchTerm === '' ? undefined : searchTerm

        if (this.query.q !== q) {
          this.setQueryParameter({ q })
        }
      },
    },
    filters: {
      get () {
        const { statuses, from, to } = this.query

        return {
          statuses,
          from,
          to,
        }
      },
      set (filters) {
        this.selection = []
        this.setQueryParameter(filters)
      },
    },
  },
  watch: {
    query: {
      handler (query) {
        this.state = ComponentState.INITIALIZING
        this.orders = []
        this.fetchOrdersAsync(query)
      },
      immediate: true,
    },
    selectedStoreGuid () {
      this.state = ComponentState.INITIALIZING
      this.orders = []
      this.searchTerm = ''
    },
  },
  methods: {
    async fetchOrdersAsync ({ startAt = 0, currentPage = 1 } = {}) {
      try {
        const { statuses, to, from } = this.filters
        const response = await orderService.fetchEntitiesAsync({
          params: {
            startAt,
            limit: this.pageSize,
            searchTerm: this.searchTerm,
            store: this.selectedStoreGuid,
            statuses,
            to,
            from,
          },
          cultureCode: this.contentLanguage,
        })

        const orders = response.data

        this.orders = [...this.orders, ...orders]
        this.totalCount = Number(response.headers['x-total-count'])
        this.resultCount = Number(response.headers['x-result-count'])
        this.currentPage = currentPage
        this.state = ComponentState.IDLE
      } catch (error) {
        if (error.response && error.response.status) {
          switch (error.response.status) {
            case 400:
              this.state = ComponentState.ERROR_UNKNOWN
              break
            default:
              this.state = ComponentState.ERROR_UNKNOWN
          }
        } else {
          throw error
        }
      }
    },

    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const currentPage = this.currentPage + 1

      await this.fetchOrdersAsync({ startAt, currentPage })
    },

    onStoreSelect (store) {
      if (!store.guid) {
        this.state = ComponentState.ERROR_UNKNOWN
        return
      }

      this.selection = []
      this.orders = []
      this.searchTerm = ''
      this.selectedStoreGuid = store.guid
    },

    setQueryParameter (newQuery) {
      this.$router.replace({
        query: {
          ...this.query,
          ...newQuery,
        },
      })
    },

    onCreate (entity) {
      this.$router.push({
        name: 'order-edit',
        params: {
          orderGuid: entity.meta.entityGuid,
        },
      })
    },

    onStatusChange (args) {
      this.selection = []
      this.showItemsNotification(args)
      this.updateOrdersStatuses(args)
    },

    updateOrdersStatuses (args) {
      const matchesStatusCode = entity => entity.statusCode === ApiStatusCodes.OK
      const updatedEntitiesResponse = args.operationResponse.filter(matchesStatusCode)
      const updatedEntitiesGuids = updatedEntitiesResponse.map(entity => entity.guid)
      this.orders
        .filter(order => updatedEntitiesGuids.includes(order.meta.entityGuid))
        .forEach(order => { order.status.value = args.status })
    },

    // Methods related to showing notifications. TODO: Move to separate component
    createNotificationMessage (message, count, newStatus) {
      return this.$tc(message, count, { count, newStatus })
    },
    filterItemsByStatusCode (items, statusCode) {
      const matchesStatusCode = product => product.statusCode === statusCode
      return items.filter(matchesStatusCode)
    },
    showItemsNotification (args) {
      const items = args.operationResponse

      const numberOfItems = {
        [apiStatusCodes.ADDED]: this.filterItemsByStatusCode(items, apiStatusCodes.ADDED).length,
      }

      const message = {
        [apiStatusCodes.ADDED]: this.createNotificationMessage(
          'messages.success',
          numberOfItems[apiStatusCodes.ADDED],
          args.status,
        ),
      }

      const hasItems = numberOfItems[apiStatusCodes.ADDED]

      if (hasItems) {
        const notificationMessage = `${message[apiStatusCodes.ADDED]}`
        const theme = numberOfItems[apiStatusCodes.ADDED] === 0 ? UcSnackbarTheme.NEUTRAL : UcSnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showNotification ({ text, theme, duration = UcSnackbarDuration.SHORT }) {
      const newNotification = {
        text,
        dismissable: duration === UcSnackbarDuration.INFINITE,
        duration,
        theme,
      }

      this.notifications = [...this.notifications, newNotification]
    },
    showErrorNotification (text) {
      this.showNotification({ text, theme: UcSnackbarTheme.ERROR, duration: UcSnackbarDuration.INFINITE })
    },
    clearFiniteNotifications () {
      this.notifications = this.notifications.filter(notification => notification.duration === UcSnackbarDuration.INFINITE)
    },
    clearNotification (index) {
      this.notifications = this.notifications.filter((_notification, i) => i !== index)
    },
  },
}
</script>

<style lang="stylus" scoped>
.orders
  position relative
  width 100%
  min-width $base-layout-width

.orders__header
  display flex
  align-items center
  justify-content flex-end
  width 100%

.orders__search
  position relative
  display flex
  margin-bottom 3 * $base-unit

.orders__search-bar
  flex-grow 1
  margin-left $base-unit * 2

.orders__notifications
  position absolute
  top $base-unit * 25
  width 100%
  padding $base-unit * 2

.orders__notifications-item + .orders__notifications-item
  margin-top $base-unit
</style>
