import { rest } from 'msw'
import {
  orderLines,
  generateOrderLines,
  orderLineDefinitions,
  orderLineDefinitionsShallow,
  taxPercentage,
} from '@/mocks/entities/order-lines/'
import { getMockGuidForStatusCode } from '@/mocks/constants.js'
import { ApiStatusCodes } from '@/constants.js'

const RESPONSE_DELAY = 200
let lines = orderLines

export const rootOrderLineHandlers = [
  rest.get('/ucommerceapi/v2/orders/orderlines/definitions', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(orderLineDefinitionsShallow),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/orderlines/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(orderLineDefinitions),
    )
  }),

  rest.post('/ucommerceapi/v2/orders/:orderGuid/orderlines/:cultureCode', (req, res, ctx) => {
    const newProducts = generateOrderLines(req.body.length)

    lines = [...newProducts, ...lines]
    const resultCount = lines.length
    const totalCount = lines.length

    const response = req.body.map(guid => ({
      guid,
      message: 'success',
      statusCode: ApiStatusCodes.OK,
    }))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.set('x-total-count', totalCount),
      ctx.set('x-result-count', resultCount),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.delete('/ucommerceapi/v2/orders/:orderGuid/orderlines', (req, res, ctx) => {
    lines = lines.filter(orderLine => !req.body.includes(orderLine.meta.entityGuid))

    const response = req.body.map(guid => ({
      guid,
      message: 'success',
      statusCode: ApiStatusCodes.OK,
    }))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/:orderGuid/orderlines/:orderLineGuid/:cultureCode', (req, res, ctx) => {
    const { orderLineGuid } = req.params
    const orderLine = lines.find(line => line.meta.entityGuid === orderLineGuid)

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(orderLine),
    )
  }),

  rest.patch('/ucommerceapi/v2/orders/:orderGuid/orderlines/:orderLineGuid/:cultureCode', (req, res, ctx) => {
    const { orderLineGuid } = req.params
    const orderLine = lines.find(line => line.meta.entityGuid === orderLineGuid)

    if (req.body.quantity) {
      orderLine.quantity.value = req.body.quantity.value
    }

    if (req.body.listPrice) {
      orderLine.listPrice.value = req.body.listPrice.value
    }

    orderLine.tax.value = orderLine.listPrice.value * orderLine.quantity.value * taxPercentage
    orderLine.total.value = orderLine.listPrice.value * orderLine.quantity.value + orderLine.tax.value

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/:orderGuid/orderlines/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))
    let resultCount = 0
    let totalCount = 0
    let statusCode
    let response

    switch (req.params.orderGuid) {
      case 'mock-guid-empty':
        statusCode = 200
        response = []
        break
      case getMockGuidForStatusCode(400):
        statusCode = 400
        response = { Message: 'Mock 400: Provided culture code is invalid.' }
        break
      case getMockGuidForStatusCode(401):
        statusCode = 401
        response = { Message: 'Mock 401: Missing permissions.' }
        break
      case getMockGuidForStatusCode(404):
        statusCode = 404
        response = { Message: 'Mock 404: Order not found.' }
        break
      default:
        statusCode = 200
        resultCount = lines.length
        totalCount = lines.length
        response = lines.slice(startAt, startAt + limit)
    }

    return res(
      ctx.set('x-total-count', totalCount),
      ctx.set('x-result-count', resultCount),
      ctx.set('x-currency', 'DKK'),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(statusCode),
      ctx.json(response),
    )
  }),
]
