<template>
  <div class="uc-order-line-totals">
    <p class="uc-order-line-totals__summary">
      {{ totalLabel }}
      <component
        :is="getPropertyEditorComponent(order.total.editor)"
        no-currency-code
        compact
        :language="contentLanguage"
        :definition="order.total" />
    </p>

    <!-- Totals details -->
    <section class="uc-order-line-totals__details">
      <uc-dl>
        <template v-for="(property, propertyKey) in totals">
          <uc-dt :key="property.guid">
            <span class="uc-order-line-totals__details-label">
              {{ property.label }}
            </span>
          </uc-dt>
          <uc-dd :key="property.guid">
            <span class="uc-order-line-totals__details-value">
              <component
                :is="getPropertyEditorComponent(property.editor)"
                :name="propertyKey"
                no-currency-code
                :definition="property"
                :language="contentLanguage"
                compact />
            </span>
          </uc-dd>
        </template>
      </uc-dl>
    </section>
  </div>
</template>

<i18n>
{
  "en": {
    "heading": "GRAND TOTAL:"
  },
  "da": {
  },
  "sv": {
  },
  "de": {
  }
}
</i18n>

<script>
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcDl, UcDt, UcDd } from '@/components/generic/uc-dl/'

export default {
  name: 'UcOrderLineTotals',
  components: {
    UcDl,
    UcDt,
    UcDd,
  },
  props: {
    order: {
      type: Object,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  computed: {
    totals () {
      const { subTotal, taxTotal, shippingTotal, paymentTotal, discountTotal } = this.order

      return {
        subTotal,
        taxTotal,
        shippingTotal,
        paymentTotal,
        discountTotal,
      }
    },
    totalLabel () {
      return this.$t('heading')
    },
  },
  methods: {
    getPropertyEditorComponent,
  },
}
</script>
<style lang="stylus" scoped>
.uc-order-line-totals
  position relative

.uc-order-line-totals__summary
  display grid
  grid-template-columns min-content min-content
  color $base-color-brand
  // Avoid jumpy bold text on hover
  letter-spacing 0.125px
  white-space nowrap
  column-gap $base-unit
  cursor pointer
  user-select none

.uc-order-line-totals__details
  position absolute
  top 100%
  right 0
  padding 3 * $base-unit
  border $base-border
  border-radius $base-border-radius
  background-color $base-color-white
  box-shadow $base-box-shadow
  opacity 0
  transition all 250ms ease-in-out
  user-select none
  pointer-events none

.uc-order-line-totals__details-label
  white-space nowrap

.uc-order-line-totals__details-value
  text-align right

.uc-order-line-totals:hover .uc-order-line-totals__summary
  font-weight $base-font-weight-semibold
  letter-spacing 0

.uc-order-line-totals:hover .uc-order-line-totals__details
  opacity 1
  pointer-events auto
</style>
