<template>
  <section
    v-if="currentDetails && currentDetails.meta"
    :key="currentDetails.meta.entityGuid"
    class="details-view"
    data-test="generic-details"
    :class="cssClassObject">
    <!-- Header -->
    <sticky-header
      v-if="!noHeader"
      expand
      :flat="isCatalog || isCategory"
      :layer="isScrolled ? 150 : 100"
      @scroll="onScroll">
      <div
        id="GenericDetailsHeader"
        class="header">
        <div class="header__heading">
          <h1
            v-truncate
            class="header__title"
            data-test="details-title">
            {{ header }}
          </h1>
          <v-popover
            offset="18"
            container="#GenericDetailsHeader"
            class="header__button"
            :open.sync="isOpen">
            <uc-button-round
              size="m"
              theme="ghost"
              data-test="generic-details-popover"
              @keyup.esc.native="isOpen = false">
              <i class="header__icon">
                <truncate-icon />
              </i>
            </uc-button-round>
            <template #popover>
              <div>
                <div
                  v-if="isOrder"
                  v-close-popover
                  class="action"
                  :title="$t('details.changeStatus')"
                  @click="openDialog(currentDetails.status.guid)">
                  {{ $t('details.changeStatus') }}
                </div>
                <div
                  v-if="currentDetails.name"
                  v-close-popover
                  class="action"
                  :title="$t('details.rename', {label: ''})"
                  data-test="generic-details-popover-rename"
                  @click="openDialog(currentDetails.name.guid)">
                  {{ $t('details.rename', {label: ''}) }}
                </div>
                <div
                  v-if="nodeType !== 'productVariant' && !isCampaign && !isOrder"
                  v-close-popover
                  class="action"
                  :title="$t('details.changeDefinition')"
                  data-test="generic-details-popover-definition"
                  @click="openDialog('definition')">
                  {{ $t('details.changeDefinition') }}
                </div>
                <div
                  v-if="nodeType === 'product'"
                  v-close-popover
                  class="action"
                  :title="$t('details.changeSKU')"
                  data-test="generic-details-popover-sku"
                  @click="openDialog(currentDetails.sku.guid)">
                  {{ $t('details.changeSKU') }}
                </div>
                <div
                  v-if="nodeType === 'productVariant'"
                  v-close-popover
                  class="action"
                  :title="$t('details.changeVariantSKU')"
                  @click="openDialog(currentDetails.variantSku.guid)">
                  {{ $t('details.changeVariantSKU') }}
                </div>
                <div
                  v-if="!isOrder"
                  v-close-popover
                  :title="deleteLabel"
                  class="action delete"
                  data-test="generic-details-popover-delete"
                  @click="openDialog('confirmDelete')">
                  {{ deleteLabel }}
                </div>
              </div>
              <hr class="separator-line">
              <div class="audit-container">
                <div
                  v-if="isOrder"
                  class="audit-item">
                  <span class="audit-header">{{ $t('details.store') }}</span>
                  <br>
                  <span class="audit-text">{{ selectedStoreGuidName }}</span>
                </div>
                <div
                  v-if="!isOrder"
                  class="audit-item">
                  <span class="audit-header">{{ $t('details.created') }}</span>
                  <br>
                  <span class="audit-text">{{ currentDetails.meta.createdOn | format('DD. MMM YYYY', uiLanguage) }} {{ $t('details.by') }} {{ currentDetails.meta.createdBy }}</span>
                </div>
                <div
                  v-if="!isOrder"
                  class="audit-item">
                  <span class="audit-header">{{ $t('details.modified') }}</span>
                  <br>
                  <span class="audit-text">{{ currentDetails.meta.modifiedOn | format('DD. MMM YYYY', uiLanguage) }} {{ $t('details.by') }} {{ currentDetails.meta.modifiedBy }}</span>
                </div>
              </div>
            </template>
          </v-popover>
        </div>
        <div
          v-show="!isScrolled"
          class="header__subheading">
          <p
            v-if="!isCampaign && !isOrder"
            v-truncate
            class="header__subheader">
            Definition: {{ definitionName }}
          </p>
          <p
            v-if="isOrder"
            v-truncate
            class="header__subheader">
            {{ $t('orderEdit.status') }}: {{ currentDetails.status.value }}
          </p>
          <p
            v-if="nodeType === 'product'"
            v-truncate
            class="header__subheader">
            {{ currentDetails.sku.label }}: {{ currentDetails.sku.value }}
          </p>
          <p
            v-if="nodeType === 'productVariant'"
            v-truncate
            class="header__subheader">
            {{ currentDetails.variantSku.label }}: {{ currentDetails.variantSku.value }}
          </p>
        </div>
      </div>
      <template #actions>
        <template v-if="(isCatalog || isCategory) && isScrolled">
          <div class="header__selector">
            <store-selector />
          </div>
          <language-selector />
        </template>
        <language-selector v-if="!isCatalog && !isCategory && !isCampaign && !isOrder" />
      </template>
    </sticky-header>
    <div class="details-content">
      <ul
        v-if="currentDetails.media || currentDetails.primaryImage || dynamicProperties.length > 0 || builtInSections.length > 1"
        class="section-nav-ul">
        <li
          v-for="section in builtInSections"
          :key="section.key"
          v-truncate
          :class="{ active: isActiveNavField(section.key) }"
          class="section-nav"
          @click="scrollToElement(section.key, true)">
          {{ $tc(section.heading) }}
        </li>
        <li
          v-if="nodeType==='productCategory'"
          v-truncate
          class="section-nav"
          :class="{ active: isActiveNavField('products') }"
          @click="scrollToElement('products', true)">
          {{ $tc('details.products') }}
        </li>
        <li
          v-if="isProductFamily"
          v-truncate
          class="section-nav"
          :class="{ active: isActiveNavField('variants') }"
          @click="scrollToElement('variants', true)">
          {{ $tc('details.variants') }}
        </li>
        <li
          v-if="nodeType === 'product' || nodeType === 'productVariant'"
          v-truncate
          class="section-nav"
          :class="{ active: isActiveNavField('prices') }"
          @click="scrollToElement('prices', true)">
          {{ $tc('details.prices') }}
        </li>
        <li
          v-if="nodeType === 'product' || nodeType === 'productVariant'"
          v-truncate
          class="section-nav"
          :class="{ active: isActiveNavField('relations') }"
          @click="scrollToElement('relations', true)">
          {{ $tc('details.relations') }}
        </li>
        <li
          v-if="mediaType && nodeType !== 'productCategory'"
          v-truncate
          :class="{ active: isActiveNavField(mediaType) }"
          class="section-nav"
          data-test="section-nav-media"
          @click="scrollToElement(mediaType, true)">
          {{ $tc('details.media') }}
        </li>
        <li
          v-for="property in dynamicProperties"
          :key="currentDetails[property].guid"
          v-truncate
          :class="{ active: isActiveNavField(currentDetails[property].guid) }"
          class="section-nav"
          @click="scrollToElement(currentDetails[property].guid, true), setActiveNavField(currentDetails[property].guid)">
          {{ currentDetails[property].label }}
        </li>
      </ul>
      <div class="field-section-fields">
        <!-- Built in Sections -->
        <fields-section
          v-for="section in builtInSections"
          :key="section.key"
          :ref="section.key"
          :heading="$tc(section.heading)"
          :no-heading="section.noHeading"
          :paddingless="section.paddingless"
          :data-guid="section.key"
          :data-test="nodeType"
          data-field
          @click="setActiveNavField(section.key)">
          <!-- Built in Properties -->
          <div class="field-section-fields__property">
            <component
              :is="getEditorComponentByPropertyName(propertyName)"
              v-for="propertyName in section.properties"
              :key="propertyName"
              :definition="currentDetails[propertyName]"
              :language="selectedContentLanguage"
              :details="currentDetails"
              :name="propertyName"
              @change="saveChange" />
            <component
              :is="component"
              v-for="component in section.components"
              :key="component.name"
              :parent-guid="currentDetails.meta.entityGuid"
              :parent="currentDetails"
              :content-language="selectedContentLanguage"
              :store-guid="selectedStoreGuid"
              :editable="currentDetails.editable && currentDetails.editable.value"
              @change="onChange" />
          </div>
        </fields-section>
        <!--Products-->
        <fields-section
          v-if="nodeType === 'productCategory'"
          ref="products"
          data-field
          data-guid="products"
          paddingless
          @click="setActiveNavField('products')">
          <product-list
            :content-language="selectedContentLanguage"
            :category-guid="currentDetails.meta.entityGuid"
            :category-name="header"
            @initialLoadFinished="indexDomElements" />
        </fields-section>
        <!-- Products/variants -->
        <fields-section
          v-if="isProductFamily"
          ref="variants"
          data-field
          data-guid="variants"
          paddingless
          @click="setActiveNavField('variants')">
          <variant-list
            ref="VariantList"
            :content-language="selectedContentLanguage"
            :product-guid="currentDetails.meta.entityGuid"
            list-id="VariantList"
            @initialLoadFinished="indexDomElements"
            @openVariantList="openVariantList" />
        </fields-section>
        <fields-section
          v-if="(nodeType === 'product' || nodeType === 'productVariant') && priceEditors.length > 0"
          ref="prices"
          data-field
          data-guid="prices"
          paddingless
          :heading="$tc('details.prices')"
          @click="setActiveNavField('prices')">
          <div
            class="prices"
            :class="{ 'is-max-height': priceEditorsPaged.length > 4 }">
            <uc-infinite-scroller @next="priceEditorsPage++">
              <div class="prices__list">
                <component
                  :is="getPropertyEditorComponent('Price')"
                  v-for="(property) in priceEditorsPaged"
                  :key="currentDetails[property].guid"
                  :language="selectedContentLanguage"
                  :definition="currentDetails[property]"
                  :name="property"
                  @change="saveChange" />
              </div>
            </uc-infinite-scroller>
          </div>
        </fields-section>
        <fields-section
          v-if="nodeType === 'product' || nodeType === 'productVariant'"
          ref="relations"
          data-field
          data-guid="relations"
          paddingless
          @click="setActiveNavField('relations')">
          <relations-list
            id="RelationsList"
            :language="selectedContentLanguage"
            :product-guid="currentDetails.meta.entityGuid"
            @initialLoadFinished="indexDomElements" />
        </fields-section>
        <fields-section
          v-if="currentDetails.primaryImage"
          :ref="currentDetails.primaryImage.guid"
          data-field
          :data-guid="currentDetails.primaryImage.guid"
          :heading="$tc('details.media')">
          <div class="media-section">
            <component
              :is="getPropertyEditorComponent('Media')"
              :definition="currentDetails.primaryImage"
              name="primaryImage"
              content-type="media"
              @change="saveChange" />
            <component
              :is="getPropertyEditorComponent('Media')"
              :definition="currentDetails.thumbnailImage"
              name="thumbnailImage"
              content-type="media"
              @change="saveChange" />
          </div>
        </fields-section>
        <template v-if="dynamicProperties.length > 0">
          <component
            :is="getSectionByEditorType(currentDetails[property].editor)"
            v-for="property in dynamicProperties"
            :key="currentDetails[property].guid"
            :ref="currentDetails[property].guid"

            data-field
            :data-guid="currentDetails[property].guid"
            :heading="currentDetails[property].label"
            :definition="currentDetails[property]"
            :name="property"
            @change="saveChange"
            @click="setActiveNavField(currentDetails[property].guid)">
            <component
              :is="getEditorComponentByPropertyName(property)"
              v-if="currentDetails[property].editor"
              :language="selectedContentLanguage"
              :definition="currentDetails[property]"
              :name="property"
              @change="saveChange" />
          </component>
        </template>
      </div>
    </div>
    <property-editor-dialog
      v-if="currentDetails.name && dialogOpen === currentDetails.name.guid"
      :definition="currentDetails.name"
      :custom-header="nameEditorCustomHeader"
      name="name"
      @hideDialog="closeDialogs"
      @onInput="onInput" />
    <property-editor-dialog
      v-if="currentDetails.sku && dialogOpen === currentDetails.sku.guid"
      :definition="currentDetails.sku"
      name="sku"
      :custom-header="$t('details.changeSKU')"
      @hideDialog="closeDialogs"
      @onInput="onInput" />
    <property-editor-dialog
      v-if="dialogOpen === 'definition'"
      :definition="currentDetails.definition"
      name="definition"
      :custom-header="$t('details.changeDefinition')"
      @hideDialog="closeDialogs"
      @onInput="onInput" />
    <property-editor-dialog
      v-if="nodeType === 'productVariant' && dialogOpen === currentDetails.variantSku.guid"
      name="variantSku"
      :definition="currentDetails.variantSku"
      :custom-header="$t('details.changeVariantSKU')"
      @hideDialog="closeDialogs"
      @onInput="onInput" />
    <property-editor-dialog
      v-if="currentDetails.status && dialogOpen === currentDetails.status.guid"
      :definition="currentDetails.status"
      name="status"
      :entity-guid="currentDetails.meta.entityGuid"
      :custom-header="$t('details.changeStatus')"
      @hideDialog="closeDialogs"
      @onInput="onInput"
      @change="onChange" />
    <uc-warning-dialog
      v-if="dialogOpen === 'confirmDelete'"
      :title="deleteLabel"
      :text="deleteConfirmText"
      :proceed-button-text="$t('details.deleteConfirmProceed')"
      :cancel-button-text="$t('details.deleteConfirmCancel')"
      @proceed="deleteEntity"
      @cancel="closeDialogs" />
  </section>
</template>

<i18n>
{
  "en": {
    "apiSettings": {
      "clientKeysSectionHeading": "Client Keys",
      "uriWhitelistSectionHeading": "URI Whitelist"
    }
  }
}
</i18n>

<script>
/*
  *
  *  Displays details of and allows editing catalogs, categories, products, variants. Anything that complies with the shared data format
  *  It's main data source is the details store, and all editors rendered by generic-details will be bound to the properties of
  *  `this.currentDetails`.
  *
  */

import { getPropertyEditorComponent } from '@/components/editors/'
import FieldsSection from '@/components/sections/fields-section'
import UcButtonRound from '@/components/forms/uc-button-round'
import { Truncate16Thick as TruncateIcon } from '@/assets/icons'
import PropertyEditorDialog from './property-editor-dialog.vue'
import { UcWarningDialog } from '@/components/widgets/uc-warning-dialog'
import { UcInfiniteScroller } from '@/components/generic/uc-infinite-scroller/'
import VariantList from '@/apps/products/components/product-variant-list.vue'
import ProductList from '@/apps/products/components/product-list.vue'
import RelationsList from '@/apps/products/components/product-relations-list.vue'
import StickyHeader from '@/components/widgets/sticky-header'
import LanguageSelector from '@/components/widgets/language-selector.vue'
import StoreSelector from '@/components/widgets/store-selector.vue'
import { PromotionList } from '@/apps/promotions/components/promotion-list/'
import { PromotionCriteria } from '@/apps/promotions/components/promotion-criteria/'
import { PromotionDiscounts } from '@/apps/promotions/components/promotion-discounts/'
import { OrderLines } from '@/apps/orders/components/order-lines/'
import { OrderDiscounts } from '@/apps/orders/components/order-discounts/'
import { BillingInformation } from '@/apps/orders/components/billing-information/'
import { ShippingInformation } from '@/apps/orders/components/shipping-information/'
import { OrderAudits } from '@/apps/orders/components/order-audits/'
import { PaymentInformation } from '@/apps/orders/components/payment-information/'
import { UriWhitelist } from '@/apps/settings/security/api/components/uri-whitelist/'

import createSaveCommand from '@/utils/create-save-command'
import { getSectionByEditorType } from '@/components/sections'
import { EntityType, nodeTypeSingular } from '@/constants.js'
import { mapGetters, mapActions, createNamespacedHelpers } from 'vuex'

const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')
const persistedStore = createNamespacedHelpers('persisted')

const storeSectionsConfig = {
  sections: [
    {
      key: 'multiChannelConfiguration',
      heading: 'stores.multiChannelConfigurationHeading',
      properties: ['domain', 'currency', 'emailProfile', 'orderNumberSeries'],
    },
    {
      key: 'customers',
      heading: 'stores.customersHeading',
      properties: ['createCustomersAsMembers', 'memberGroup', 'memberType'],
    },
    {
      key: 'basicInformation',
      heading: 'details.basicInformation',
      properties: ['description'],
    },
  ],
}

const catalogSectionsConfig = {
  sections: [
    {
      key: 'basicInformation',
      heading: 'details.basicInformation',
      properties: ['displayOnWebsite', 'showPricesIncludingVAT', 'displayName', 'defaultPriceGroup', 'allowedPriceGroups'],
    },
  ],
}

const categorySectionsConfig = {
  sections: [
    {
      key: 'basicInformation',
      heading: 'details.basicInformation',
      properties: ['displayOnWebsite', 'displayName', 'description'],
    },
  ],
}

const productSectionsConfig = {
  sections: [
    {
      key: 'basicInformation',
      heading: 'details.basicInformation',
      properties: ['displayOnWebsite', 'displayName', 'shortDescription', 'longDescription'],
    },
  ],
}

const campaignSectionsConfig = {
  sections: [
    {
      key: 'basicInformation',
      heading: 'campaignEdit.campaignSectionHeading',
      properties: ['enabled', 'stores', 'startDate', 'endDate'],
    },
    {
      key: 'promotions',
      heading: 'campaignEdit.promotionsSectionHeading',
      components: [PromotionList],
      noHeading: true,
      paddingless: true,
    },
  ],
}

const promotionSectionsConfig = {
  sections: [
    {
      key: 'promotion',
      heading: 'promotionEdit.promotionSectionHeading',
      properties: ['enabled', 'allowNextCampaignItems'],
    },
    {
      key: 'criteria',
      heading: 'promotionEdit.criteriaSectionHeading',
      properties: ['anyTargetAppliesAwards'],
      components: [PromotionCriteria],
    },
    {
      key: 'discounts',
      heading: 'promotionEdit.discountsSectionHeading',
      components: [PromotionDiscounts],
    },
  ],
}

const orderSectionsConfig = {
  sections: [
    {
      key: 'orderEdit.sections.logbook',
      heading: 'orderEdit.sections.logbook',
      components: [OrderAudits],
      noHeading: true,
      paddingless: true,
    },
    {
      key: 'orderEdit.sections.orderLines',
      heading: 'orderEdit.sections.orderLines',
      components: [OrderLines],
      noHeading: true,
      paddingless: true,
    },
    {
      key: 'orderEdit.sections.billingInformation',
      heading: 'orderEdit.sections.billingInformation',
      components: [BillingInformation],
      noHeading: true,
      paddingless: true,
    },
    {
      key: 'orderEdit.sections.shippingInformation',
      heading: 'orderEdit.sections.shippingInformation',
      components: [ShippingInformation],
      noHeading: true,
      paddingless: true,
    },
    {
      key: 'orderEdit.sections.discounts',
      heading: 'orderEdit.sections.discounts',
      components: [OrderDiscounts],
      noHeading: true,
      paddingless: true,
    },
    {
      key: 'orderEdit.sections.paymentInformation',
      heading: 'orderEdit.sections.paymentInformation',
      components: [PaymentInformation],
    },
  ],
}
const apiSettingsSectionsConfig = {
  sections: [
    {
      key: 'clientKeys',
      heading: 'apiSettings.clientKeysSectionHeading',
      properties: ['clientId', 'secret'],
    },
    {
      key: 'uriWhitelist',
      heading: 'apiSettings.uriWhitelistSectionHeading',
      components: [UriWhitelist],
      noHeading: true,
      paddingless: true,
    },
  ],
}

const ComponentEvents = {
  CHANGE: 'change',
}

const SectionByNodeTypeMap = {
  [EntityType.STORE]: storeSectionsConfig,
  [EntityType.CATALOG]: catalogSectionsConfig,
  [EntityType.CATEGORY]: categorySectionsConfig,
  [EntityType.PRODUCT]: productSectionsConfig,
  [EntityType.PRODUCT_VARIANT]: productSectionsConfig,
  [EntityType.CAMPAIGN]: campaignSectionsConfig,
  [EntityType.PROMOTION]: promotionSectionsConfig,
  [EntityType.ORDER]: orderSectionsConfig,
  [EntityType.CLIENT_KEYS]: apiSettingsSectionsConfig,
}

export default {
  name: 'GenericDetails',
  components: {
    UcWarningDialog,
    UcInfiniteScroller,
    FieldsSection,
    UcButtonRound,
    TruncateIcon,
    PropertyEditorDialog,
    VariantList,
    ProductList,
    RelationsList,
    StickyHeader,
    StoreSelector,
    LanguageSelector,
  },
  props: {
    noHeader: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      activeItem: '',
      scrollPos: 0,
      domElements: [],
      // this offset is dynamically changing based on the scroll position -> it allows highlighting even bottom elements when user scrolls to the end of the page
      scrollOffset: 150,
      scrollFromMenu: { navScroll: false, sectionGuid: '' },
      isScrolling: null,
      // Amount of pixels from top of the scrolled element - This makes the top element to be 150px from the top line of the window
      initialScrollOffset: 150,
      initialHeaderTop: 0,
      isScrolled: false,
      intersectionObserver: null,
      isOpen: false,
      dialogOpen: {
        type: String,
        default: null,
      },
      saveCommand: undefined,
      priceEditorsPage: 1,
    }
  },
  computed: {
    ...detailsStore.mapGetters(['currentDetails']),
    ...persistedStore.mapGetters(['selectedContentLanguage', 'selectedStoreGuidName', 'selectedStoreGuid']),
    ...mapGetters(['uiLanguage', 'isProductFamily']),
    cssClassObject () {
      return {
        'is-scrolled': this.isScrolled,
      }
    },
    config () {
      return SectionByNodeTypeMap[this.nodeType]
    },
    builtInSections () {
      return this.config.sections || []
    },
    builtInVisibleProperties () {
      return this.builtInSections.flatMap(section => section.properties)
    },
    builtInHiddenProperties () {
      return this.config.hiddenProperties || []
    },
    deleteLabel () {
      return this.$t('details.delete', { label: nodeTypeSingular[this.currentDetails.meta.nodeType] })
    },
    deleteConfirmText () {
      if (this.isDeletePermanent) {
        return this.$t('details.deleteConfirmText_permanent', { entity: `a ${nodeTypeSingular[this.currentDetails.meta.nodeType]}` })
      } else {
        return this.$t('details.deleteConfirmText_undoable', { entity: `a ${nodeTypeSingular[this.currentDetails.meta.nodeType]}` })
      }
    },
    isDeletePermanent () {
      return ['product', 'productVariant', 'campaignItem', 'campaign'].includes(this.nodeType)
    },
    nameEditorCustomHeader () {
      return this.$t('details.rename', { label: nodeTypeSingular[this.currentDetails.meta.nodeType] })
    },
    meta () {
      return this.currentDetails.meta ? this.currentDetails.meta : {}
    },
    header () {
      if (this.isOrder) {
        return this.currentDetails.orderNumber?.value
      }
      return this.currentDetails.name ? this.currentDetails.name.value : ''
    },
    definitionName () {
      if (this.currentDetails.meta.definition) {
        return this.currentDetails.meta.definition.name
      } else {
        return this.$t('details.noDefinition')
      }
    },
    nodeType () {
      this.$nextTick(() => {
        this.indexDomElements()
        if (this.domElements.length === 1) {
          this.intersectionObserver.disconnect()
          this.detachScrollListeners()
        }
      }, 0)
      return this.currentDetails && this.currentDetails.meta ? this.currentDetails.meta.nodeType : null
    },
    dynamicProperties () {
      const byDynamicProperties = propertyName => !this.builtInVisibleProperties.includes(propertyName) && this.currentDetails[propertyName].editor !== 'Price'
      const byRenderableProperties = propertyName => this.currentDetails[propertyName].validation?.renderInEditor

      return Object.keys(this.currentDetails).filter(byDynamicProperties).filter(byRenderableProperties)
    },
    priceEditors () {
      return Object.keys(this.currentDetails).filter(propertyName => this.currentDetails[propertyName].editor === 'Price')
    },
    priceEditorsPaged () {
      const pageSize = 6
      return this.priceEditors.slice(0, pageSize + this.priceEditorsPage * pageSize)
    },
    mediaType () {
      if (this.currentDetails.primaryImage) {
        return this.currentDetails.primaryImage.guid
      }
      if (this.currentDetails.media) {
        return this.currentDetails.media.guid
      }
      return undefined
    },
    isCategory () {
      return this.currentDetails.meta.nodeType === EntityType.CATEGORY
    },
    isCatalog () {
      return this.currentDetails.meta.nodeType === EntityType.CATALOG
    },
    isCampaign () {
      return this.currentDetails.meta.nodeType === EntityType.CAMPAIGN
    },
    isOrder () {
      return this.currentDetails.meta.nodeType === EntityType.ORDER
    },
  },
  watch: {
    selectedContentLanguage: function (newValue) {
      if (this.currentDetails && this.currentDetails.meta) {
        this.changeSelectedContentLanguage(newValue)
      }
    },
    currentDetails () {
      if (!this.currentDetails) {
        return
      }
      this.removeNavigationListeners()
      this.attachNavigationListeners()
    },
  },
  mounted () {
    // Used to reset scroll position in order to prevent opening details with scroll from previous page
    window.scroll(0, 0)
  },
  created () {
    this.attachNavigationListeners()
  },
  destroyed () {
    this.removeNavigationListeners()
  },
  methods: {
    getPropertyEditorComponent,
    getSectionByEditorType,
    ...detailsStore.mapActions(['changeDefinition', 'changeSelectedContentLanguage']),
    ...mapActions(['save', 'delete']),
    ...builderStore.mapMutations(['setCurrentNodeName']),
    removeItems (items) {
      this.$refs.VariantList.removeItems(items)
    },
    onScroll (isScrolled) {
      this.isScrolled = isScrolled
    },
    onChange () {
      this.$emit(ComponentEvents.CHANGE)
    },
    async saveChange (command) {
      this.enrichCommand(command)
      await this.save(command)
    },
    async submitCommand () {
      await this.saveChange(this.saveCommand)
      if (this.currentDetails.name.errors) {
        return
      }
      this.closeDialogs()
      if (Object.prototype.hasOwnProperty.call(this.saveCommand.save, 'name')) {
        this.setCurrentNodeName(this.saveCommand.save.name.value)
      }
    },
    getEditorComponentByPropertyName (propertyName) {
      return this.getPropertyEditorComponent(this.currentDetails[propertyName].editor)
    },
    attachNavigationListeners () {
      this.$nextTick(() => {
        if (this.domElements.length > 0) {
          this.indexDomElements()
        }
      }, 0)
      this.attachScrollListeners()
    },
    removeNavigationListeners () {
      this.detachScrollListeners()
      if (this.intersectionObserver) {
        this.intersectionObserver.disconnect()
      }
    },
    onInput (saveCommand) {
      this.saveCommand = saveCommand
      this.$emit(ComponentEvents.CHANGE)
    },
    closeDialogs () {
      this.dialogOpen = null
    },
    openDialog (guid) {
      this.dialogOpen = guid
    },
    enrichCommand (command) {
      const { meta } = command
      meta.contentLanguage = this.selectedContentLanguage
      meta.nodeType = this.currentDetails.meta.nodeType
      meta.nodeGuid = this.currentDetails.meta.entityGuid
    },
    async deleteEntity () {
      this.closeDialogs()
      const command = createSaveCommand({
        definitionName: 'deleted',
        definitionGuid: 'cbaf8c70-639c-4716-a891-00efb0fb82bc',
        label: 'Is Deleted',
        newValue: true,
        oldValue: false,
      })
      const { meta } = command
      meta.contentLanguage = this.selectedContentLanguage
      meta.nodeType = this.currentDetails.meta.nodeType
      meta.nodeGuid = this.currentDetails.meta.entityGuid
      meta.survivesNodeChange = true
      if (this.isDeletePermanent) {
        command.undo = undefined
      } else {
        meta.undoMessage = `${this.currentDetails.name.value} ${this.$t('global.wasDeleted')}`
        meta.undoAction = (command) => {
          this.$emit('resurrected', command.meta.nodeGuid)
        }
      }
      await this.delete(command)
      this.$emit('deleted', { guid: meta.nodeGuid, isVariant: this.nodeType === 'productVariant' })
    },
    scrollToElement (refName, isFromMenu) {
      if (isFromMenu) {
        this.scrollFromMenu.navScroll = true
        this.scrollFromMenu.sectionGuid = refName
      }
      let domElement = this.$refs[refName]
      if (Array.isArray(domElement)) {
        domElement = domElement[0].$el
      } else {
        domElement = domElement.$el
      }
      const offset = domElement.getBoundingClientRect().top + window.scrollY - this.initialScrollOffset
      // Check if its possible to scroll the page that much, if not adjust the offset to scroll as much as possible to highlight the proper item -> limit is the maximum scroll position if the scroll position is unreachable, just set active class
      const limit = Math.max(document.body.scrollHeight, document.body.offsetHeight, document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight)
      if (limit - window.innerHeight < offset) {
        this.setActiveNavField(refName)
      }
      window.scrollTo({ top: offset, behavior: 'smooth' })
    },
    isActiveNavField (menuItem) {
      return this.activeItem === menuItem
    },
    setActiveNavField (menuItem) {
      if (menuItem !== '') {
        this.activeItem = menuItem
      }
    },
    // Method for highlighting the current active element in the navigation
    onScrollMenuHighlight () {
      const fieldSection = document.querySelectorAll('.details-content .field-section-fields')[0]

      if (fieldSection === undefined) return
      // Condition to check if there is need to modify offset for finding closest element
      if (fieldSection.getBoundingClientRect().height - window.scrollY < window.innerHeight &&
          !this.scrollFromMenu.navScroll) {
        const scrollBarHeight = window.innerHeight * (window.innerHeight / (fieldSection.getBoundingClientRect().height + fieldSection.offsetTop))
        this.scrollOffset = window.innerHeight - (fieldSection.getBoundingClientRect().height - window.scrollY - scrollBarHeight)
        if (window.scrollY < this.initialScrollOffset) {
          this.scrollOffset = this.initialScrollOffset
        }
        // Else reset the offset back to the initial value
      } else {
        this.scrollOffset = this.initialScrollOffset
      }

      this.scrollPos = window.scrollY + this.scrollOffset
      const visibleElements = this.domElements.filter(x => x.intersecting)
      const closestElIndex = this.findClosestDomElementIndex(this.scrollPos, visibleElements)
      const el = visibleElements[closestElIndex]

      if (!el) {
        return
      }

      if (!this.scrollFromMenu.navScroll) {
        this.setActiveNavField(el.guid)
      } else {
        this.setActiveNavField(this.scrollFromMenu.sectionGuid)
      }
    },
    // Method for finding the closest element based on the scroll position - used for loop because of the performance (reduce takes twice as much time)
    findClosestDomElementIndex (scrollPos, collection) {
      let closestValue = Infinity
      let closestIndex = -1
      for (let i = 0; i < collection.length; i++) {
        const diff = Math.abs(collection[i].topPos - scrollPos)
        if (diff < closestValue) {
          closestValue = diff
          closestIndex = i
        }
      }
      return closestIndex
    },
    // Method for saving all sections into data, therefore it is possible to work with the data afterwards (for example highlight current active nav item)
    indexDomElements () {
      this.$nextTick(() => {
        if (this.intersectionObserver) {
          this.intersectionObserver.disconnect()
        }
        const options = {
          root: null, // relative to document viewport
          rootMargin: '-50px', // margin around root. Values are similar to css property. Unitless values not allowed
          threshold: 0, // visible amount of item shown in relation to root
        }
        this.intersectionObserver = new IntersectionObserver(this.observeElements, options)
        let activeSection = this.builtInSections[0].key
        if (this.activeItem) {
          activeSection = this.activeItem
        }
        this.setActiveNavField(activeSection)
        this.domElements = []
        const collection = document.querySelectorAll('.details-content [data-field]')
        for (let i = 0; i < collection.length; i++) {
          collection[i].setAttribute('data-pos', collection[i].getBoundingClientRect().top.toString())
          const guid = collection[i].getAttribute('data-guid')
          const topPos = collection[i].getBoundingClientRect().y
          this.domElements.push({ guid: guid, topPos: topPos, intersecting: false })
          this.intersectionObserver.observe(collection[i])
        }
        // Needed to have at least one highlighted element
        this.domElements[0].intersecting = true
        this.attachScrollListeners()
      })
    },
    observeElements (entries) {
      for (let i = 0; i < entries.length; i++) {
        const domEL = this.domElements.find(x => x.guid === entries[i].target.getAttribute('data-guid'))
        if (domEL) {
          domEL.intersecting = entries[i].isIntersecting
        }
      }
    },
    attachScrollListeners () {
      // listener to auto-scroll the nav menu in case of many elements inside nav
      window.addEventListener('scroll', this.sectionNavAutoScroll)
      window.addEventListener('scroll', this.onScrollMenuHighlight)
    },
    detachScrollListeners () {
      window.removeEventListener('scroll', this.sectionNavAutoScroll)
      window.removeEventListener('scroll', this.onScrollMenuHighlight)
    },
    // Method for automatically scrolling the navbar in case there are that many properties that it requires vertical scrollbar
    sectionNavAutoScroll () {
      this.$nextTick(() => {
        window.clearTimeout(this.isScrolling)
        this.isScrolling = setTimeout(function () {
          this.scrollFromMenu.navScroll = false
          // Gets the active li and ul
          const activeElement = document.getElementsByClassName('active')[0]
          const sectionNavEl = document.querySelector('.section-nav-ul')

          if (activeElement && sectionNavEl) {
            // Gets pixel position of active item
            const activeElOffset = activeElement.offsetTop
            // Gets last visible pixel
            const scrollPosition = sectionNavEl.scrollTop + sectionNavEl.clientHeight
            // Adding 10 pixel tolerance in case of the last element in the list
            const totalHeight = parseInt(window.getComputedStyle(activeElement).getPropertyValue('margin-top')) +
              parseInt(window.getComputedStyle(activeElement).getPropertyValue('margin-bottom')) +
              activeElement.clientHeight
            if (activeElOffset - scrollPosition > -totalHeight) {
            // ensures that the scroll always scrolls the element into visible part of the navScrollBar
              sectionNavEl.scrollTop = activeElOffset - sectionNavEl.clientHeight + totalHeight
            }
            if (activeElOffset < sectionNavEl.scrollTop) {
              sectionNavEl.scrollTop = activeElOffset
            }
          }
        // binds are necessary to preserve reactivity in event listener (the code is inside timeout)
        }.bind(this), 100)
      }, 0)
    },
    openVariantList (row) {
      window.scroll(0, 0)
      this.$emit('openVariantList', row)
      this.$nextTick(() => {
        this.indexDomElements()
      })
    },
  },
}
</script>

<style lang="stylus" scoped>
.media-section
  display flex
  flex-direction row
  justify-content center

.media-section > div + div
  margin-left 20px

.media-section.align-left
  justify-content left

.details-view
  overflow-y initial

.details__static
  display grid
  grid-row-gap 36px
  grid-template-columns 1fr
  align-items center
  padding 35px 20px 20px 20px

.details-content
  display flex
  justify-content flex-end
  min-width $base-layout-width
  margin-top 3 * $base-unit

.section-nav-ul
  position sticky
  top 110px
  flex 1 0 auto
  width 20%
  max-height calc(100vh - 200px)
  margin 0
  padding 0
  padding-right 2 * $base-unit
  overflow-x hidden
  overflow-y auto
  list-style none
  user-select none
  scroll-behavior smooth
  scrollbar-width none

  &::-webkit-scrollbar
    width 0 !important

.section-nav
  position relative
  margin-bottom 15px
  padding-left 10px
  primary-body-text()
  color $base-color-text-secondary

  &:hover
    cursor pointer
    primary-body-text-bold()

  &.active
    primary-body-text-bold()

  &.active::before
    position absolute
    left 0
    width 10px
    height 100%
    margin-right 10px
    border-left 1px solid $base-color-navy
    content ' '

.field-section-fields
  flex 0 1 auto
  width 80%
  margin-bottom 8 * $base-unit // Neccesary for the scroll menu to work

.field-section-fields__property
  display grid
  grid-gap $base-unit * 4
  grid-template-rows 3

// Header styling
.header
  position relative
  min-width 50%
  margin-right 4 * $base-unit

.header__heading
  display flex
  align-items center

.header__title
  primary-heading-1()
  margin 0
  margin-right $base-unit

  .is-scrolled &
    primary-body-text(bold)

.header__button
  height 4 * $base-unit

.header__icon
  position absolute
  top 50%
  left 50%
  transform translate(-50%, -65%)

.header__subheading
  secondary-body-text()
  max-width 500px

.header__subheader
  margin 0

.action
  flex-grow 1
  height 24px
  primary-details-text()
  cursor pointer

  &::after
    display block
    height 0
    overflow hidden
    font-weight bold
    visibility hidden
    content attr(title)

  & + .action
    margin-top 20px

  &.delete
    color $base-color-red

    &:hover
      color $base-color-red

  &:hover
    primary-details-text-bold()

.audit-item + .audit-item
  margin-top 20px

.separator-line
  width calc(100% + 60px)
  height 1px
  margin 30px 0 30px -30px
  border none
  background-color $base-color-gray-200

.audit-header
  secondary-petit-text-bold()

.audit-text
  secondary-petit-text()

.is-hidden
  display none

.margin-bottom
  margin-bottom 24px

.header__selector
  margin-right 2 * $base-unit

.prices.is-max-height
  height 45 * $base-unit

.prices__list
  display grid
  row-gap 3 * $base-unit
  padding (4 * $base-unit) (3 * $base-unit)
</style>
