<template>
  <transition name="fade">
    <div class="promotion-list">
      <uc-promotion-list
        v-bind="{ state, totalCount }"
        :promotions="mappedPromotions"
        @drag-end="onDragEnd"
        @next="requestNextPage" />
    </div>
  </transition>
</template>

<script>
import { UcPromotionList } from '@/apps/promotions/components/uc-promotion-list/'
import { promotionService } from '@/services/'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR_UNKNOWN: 'errorUnknown',
  ERROR_NOT_FOUND: 'errorNotFound',
  PATCHING: 'patching',
}

export default {
  name: 'PromotionList',
  components: {
    UcPromotionList,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      promotions: [],
      totalCount: 0,
      startAt: 0,
      currentPage: 1,
      pageSize: 18,
    }
  },
  computed: {
    parentGuid () {
      return this.$route.params.campaignGuid
    },
    mappedPromotions () {
      return this.promotions.map(promotion => ({
        name: promotion.name,
        status: promotion.status,
        guid: promotion.meta?.entityGuid,
      }))
    },
  },
  async created () {
    await this.fetchPromotionsAsync()
  },
  methods: {
    async fetchPromotionsAsync (startAt = 0, currentPage = 1) {
      try {
        const response = await promotionService.fetchEntitiesAsync({ parentGuid: this.parentGuid, params: { startAt, limit: this.pageSize }, cultureCode: this.contentLanguage })
        const promotions = response.data

        this.promotions = [...this.promotions, ...promotions]
        this.totalCount = Number(response.headers['x-total-count'])
        this.currentPage = currentPage
        this.state = ComponentState.IDLE
      } catch (error) {
        if (error.response && error.response.status) {
          switch (error.response.status) {
            case 400:
              this.state = ComponentState.ERROR_UNKNOWN
              break
            case 404:
              this.state = ComponentState.ERROR_NOT_FOUND
              break
            default:
              this.state = ComponentState.ERROR_UNKNOWN
          }
        } else {
          throw error
        }
      }
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1

      await this.fetchPromotionsAsync(startAt, page)
    },
    async onDragEnd ({ guid, priority }) {
      try {
        this.state = ComponentState.PATCHING
        await promotionService.updateEntityAsync({
          promotionGuid: guid,
          cultureCode: this.contentLanguage,
          payload: {
            priority: {
              value: priority,
            },
          },
        })
        this.state = ComponentState.IDLE
      } catch (error) {
        // TODO(13671): Handle if drag and drop goes wrong on the backend
        this.state = ComponentState.ERROR_UNKNOWN
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.promotion-list
  width 100%
</style>
