<template>
  <transition name="fade">
    <div
      class="payment-methods"
      :class="cssClassObject">
      <!-- Loading indicator -->
      <div
        v-if="isInitializing"
        class="payment-methods__loading">
        <loading-indicator is-loading />
      </div>
      <template v-if="!isInitializing">
        <!-- Search bar -->
        <div
          v-if="shouldShowSearchBar"
          class="payment-methods__search">
          <uc-search
            data-test="payment-methods-search"
            :value="searchTerm"
            @input="search($event)" />
        </div>

        <!-- payment-methods overview -->
        <fields-section
          :heading="$t('heading')"
          paddingless>
          <div class="payment-methods__navigator">
            <payment-method-navigator
              v-show="availableNodes.length > 0"
              v-bind="{ nodes: availableNodes, totalNodeCount, scrollTo }"
              :loading="isSearching"
              @requestNextPage="onRequestNextPage" />
            <p
              v-show="availableNodes.length === 0"
              class="payment-methods__empty"
              data-test="payment-methods-empty-text">
              {{ emptyText }}
            </p>
          </div>
        </fields-section>
      </template>
    </div>
  </transition>
</template>

<i18n>
{
  "en": {
    "heading": "Payment Methods",
    "emptyError": "Something went wrong. Please try again.",
    "emptyText": "No payment method have been created yet. Click the button to get started.",
    "noSearchResult": "No results found. Try adjusting your search to find what you're looking for."
  },
  "da": {
  },
  "sv": {
  },
  "de": {
  }
}
</i18n>

<script>
import PaymentMethodNavigator from '@/apps/payment-methods/components/payment-method-navigator'
import FieldsSection from '@/components/sections/fields-section'
import UcSearch from '@/components/forms/uc-search'
import LoadingIndicator from '@/components/widgets/loading-indicator'

import { paymentMethodService } from '@/services'
import { mapMutations, createNamespacedHelpers } from 'vuex'
import { uniqBy } from '@/utils/array.utils'

const builderPaymentMethod = createNamespacedHelpers('builder')
const detailsPaymentMethod = createNamespacedHelpers('details')
const persistedPaymentMethod = createNamespacedHelpers('persisted')

const ComponentState = {
  INITIALIZING: 'initializing',
  SEARCHING: 'searching',
  IDLE: 'idle',
  ERROR: 'error',
}

export default {
  name: 'PaymentMethods',
  components: {
    UcSearch,
    FieldsSection,
    PaymentMethodNavigator,
    LoadingIndicator,
  },
  data () {
    return {
      nodes: [],
      searchTerm: '',
      totalNodeCount: 0,
      initialTotalNodeCount: 0,
      scrollTo: [],
      state: ComponentState.INITIALIZING,
    }
  },
  computed: {
    ...builderPaymentMethod.mapGetters(['paymentMethods']),
    ...detailsPaymentMethod.mapGetters(['currentDetails']),
    ...persistedPaymentMethod.mapGetters(['selectedContentLanguage']),
    availableNodes () {
      return this.nodes
    },
    emptyText () {
      if (this.hasError) {
        return this.$t('emptyError')
      }

      return this.searchTerm ? this.$t('noSearchResult') : this.$t('emptyText')
    },
    cssClassObject () {
      return {
        'payment-methods--size-sm': this.initialTotalNodeCount < 3,
        'payment-methods--size-md': this.initialTotalNodeCount > 2 && this.initialTotalNodeCount < 5,
        'payment-methods--size-lg': this.initialTotalNodeCount > 4,
      }
    },
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isSearching () {
      return this.state === ComponentState.SEARCHING
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
    shouldShowSearchBar () {
      return this.initialTotalNodeCount > 4
    },
  },
  async created () {
    try {
      this.state = ComponentState.INITIALIZING
      const response = await this.fetchPaymentMethods()
      this.nodes = response.data
    } catch {
      this.nodes = []
      this.totalNodeCount = 0
      this.state = ComponentState.ERROR
    } finally {
      this.state = ComponentState.IDLE
    }
  },
  methods: {
    ...builderPaymentMethod.mapMutations(['setPaymentMethods']),
    ...mapMutations(['clearUndos']),
    fetchPaymentMethods (params) {
      const limit = 12
      return paymentMethodService.fetchEntitiesAsync({
        cultureCode: this.selectedContentLanguage,
        params: {
          limit,
          ...params,
        },
      })
    },

    async search (value) {
      try {
        this.searchTerm = value.trim()
        this.scrollTo = [0, 0]
        this.state = ComponentState.SEARCHING

        const response = await this.fetchPaymentMethods({
          searchTerm: this.searchTerm,
        })

        this.nodes = response.data
        this.totalNodeCount = this.getResultCountFromResponse(response)
      } catch {
        this.nodes = []
        this.totalNodeCount = 0
        this.state = ComponentState.ERROR
      } finally {
        this.scrollTo = []
        this.state = ComponentState.IDLE
      }
    },

    async onRequestNextPage () {
      const response = await this.fetchPaymentMethods({
        startAt: this.availableNodes.length,
        searchTerm: this.searchTerm ? this.searchTerm : undefined,
      })

      const newNodes = response.data
      this.nodes = [...this.nodes, ...newNodes]
      this.count = this.getTotalCountFromResponse(response)

      // Filter out duplicates
      this.nodes = uniqBy([...this.nodes, ...newNodes], node => node.meta.entityGuid)
      this.totalNodeCount = this.searchTerm ? this.getResultCountFromResponse(response) : this.getTotalCountFromResponse(response)
    },
  },
}
</script>

<style lang="stylus" scoped>
$small-node-height = 140px
$large-node-height = 2 * $small-node-height
$grid-gap = 3 * $base-unit

.payment-methods__loading
  position absolute
  top 0
  left 0
  display flex
  align-items center
  justify-content center
  width 100%
  height 100vh

.payment-methods__search
  margin-bottom 3 * $base-unit

.payment-methods__navigator
  display flex
  align-items center
  justify-content center
  min-height $large-node-height + (2 * $grid-gap)

.payment-methods__empty
  secondary-body-text()

.payment-methods--size-sm .payment-methods__navigator
  height $large-node-height + (2 * $grid-gap)

.payment-methods--size-md .payment-methods__navigator
  height (2 * $large-node-height) + (3 * $grid-gap)

.payment-methods--size-lg .payment-methods__navigator
  height (3 * $small-node-height) + (4 * $grid-gap)
</style>
