<template>
  <div
    class="uc-order-discount-list"
    :class="cssClassObject">
    <!-- Table of discounts -->
    <uc-data-grid-selectable
      v-model="selection"
      v-bind="{ columns, rows }"
      row-key="guid"
      :disable-infinite-scroll="hasLoadedAllItems"
      :disable-selection-when="() => !editable"
      @next="onNext">
      <!-- Customise columns to contain editors -->
      <template
        v-for="column in columns"
        #[column.key]="{ cell }">
        <component
          :is="getPropertyEditorComponent(cell.editor)"
          :key="column.key"
          compact
          no-currency-code
          :name="cell.label"
          :language="contentLanguage"
          :definition="{ ...cell, readonly: true }" />
      </template>
    </uc-data-grid-selectable>
    <!-- Actions -->
    <transition name="fade-in-up">
      <div
        v-if="!isEmptySelection"
        class="uc-order-discount-list__actions-footer">
        <!-- Remove button -->
        <uc-button @click="selectDiscounts">
          {{ `${$t('global.remove')} (${selection.length})` }}
        </uc-button>
      </div>
    </transition>
  </div>
</template>

<i18n>
{
  "en": {
    "columns": {
      "campaignName": "Campaign",
      "campaignItemName": "Promotion",
      "description": "Details",
      "amountOffTotal": "Discount"
    }
  }
}
</i18n>

<script>
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcButton } from '@/components/forms/uc-button/'

const ComponentEvent = {
  NEXT: 'next',
  SELECT: 'select',
}

export default {
  name: 'UcOrderDiscountList',
  components: {
    UcDataGridSelectable,
    UcButton,
  },
  props: {
    discounts: {
      type: Array,
      required: true,
    },
    totalCount: {
      type: Number,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  data () {
    return {
      selection: [],
    }
  },
  computed: {
    isEmptySelection () {
      return this.selection.length === 0
    },
    columns () {
      return [
        {
          key: 'campaignName',
          value: this.$t('columns.campaignName'),
          cellStyle: {
            padding: '0 24px 0 0',
          },
        },
        {
          key: 'campaignItemName',
          value: this.$t('columns.campaignItemName'),
          cellStyle: {
            padding: '0 24px 0 0',
          },
        },
        {
          key: 'description',
          value: this.$t('columns.description'),
          cellStyle: {
            padding: '0 24px 0 0',
          },
        },
        {
          key: 'amountOffTotal',
          value: this.$t('columns.amountOffTotal'),
          cellStyle: {
            padding: '0 24px 0 0',
            textAlign: 'right',
          },
        },
      ]
    },
    rows () {
      return this.discounts.map(row => {
        const { campaignName, campaignItemName, description, amountOffTotal } = row

        return {
          campaignName,
          campaignItemName,
          description,
          amountOffTotal,
          guid: row.meta?.entityGuid,
        }
      })
    },
    cssClassObject () {
      return {
        'is-max-height': this.discounts.length > 9,
      }
    },
    hasLoadedAllItems () {
      return this.discounts.length === this.totalCount
    },
  },
  methods: {
    getPropertyEditorComponent,
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    selectDiscounts () {
      this.$emit(ComponentEvent.SELECT, this.selection)
      this.selection = []
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-order-discount-list
  position relative

  &.is-max-height
    height $base-unit * 61
    border-bottom $base-border

.uc-order-discount-list__actions-footer
  position absolute
  bottom $base-unit * 4
  z-index 1
  display flex
  align-items center
  justify-content center
  width 100%
  pointer-events none
</style>
