<template>
  <div class="uc-fieldset-button">
    <uc-button-round
      data-test="remove-button"
      :theme="buttonTheme"
      @click="onClick">
      <close-icon />
    </uc-button-round>
  </div>
</template>

<script>
import UcButtonRound, { themeProp } from '@/components/forms/uc-button-round'
import { Close16Thick as CloseIcon } from '@/assets/icons'

const ComponentEvent = {
  CLICK: 'click',
}

export default {
  name: 'UcFieldsetButton',
  components: {
    UcButtonRound,
    CloseIcon,
  },
  computed: {
    buttonTheme () {
      return themeProp.GHOST
    },
  },
  methods: {
    onClick () {
      this.$emit(ComponentEvent.CLICK)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-fieldset-button
  border-radius 50%
  background-color $base-color-white
</style>
