<template>
  <div class="uc-definition-type-picker">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="description"
      :required="isRequired">
      <template v-if="options && options.length > 0">
        <template v-if="options.length < 6">
          <div class="uc-input-wrapper">
            <uc-radio
              v-for="option in options"
              :key="option.guid"
              class="uc-definition-type-picker__radio"
              :readonly="isReadonly"
              :label="option.label"
              :nullable="!isRequired"
              :value="option.guid"
              :checked="setChecked(option.guid)"
              :group="fieldId"
              @change="change" />
          </div>
        </template>
        <template v-else>
          <uc-select
            :readonly="isReadonly"
            :options="options"
            :selected="selectedOption.value"
            :nullable="!isRequired"
            :placeholder="label"
            @selected="change" />
        </template>
      </template>
    </editor-wrapper>
  </div>
</template>

<script>
import UcRadio from '@/components/forms/uc-radio'
import UcSelect from '@/components/forms/uc-select'
import getId from '@/utils/id-generator.js'
import { isEmpty } from '@/utils/object.utils.js'
import editorMixin from '../editor-mixin.js'

export default {
  name: 'UcDefinitionTypePicker',
  components: {
    UcRadio,
    UcSelect,
  },
  mixins: [editorMixin],
  data () {
    return {
      selectedOption: {},
    }
  },
  computed: {
    fieldId () {
      return getId()
    },
    description () {
      if (this.value) {
        return this.value
      }

      if (!isEmpty(this.selectedOption)) {
        return this.selectedOption.label
      }

      return ''
    },
    selectedGuid () {
      return this.selected.length > 0 ? this.definition.selected[0] : ''
    },
  },
  watch: {
    definition: {
      handler () {
        const match = this.selectedGuid ? this.options.find(option => option.guid === this.selectedGuid) : {}
        this.selectedOption = !isEmpty(match) ? { label: match.label, value: match.guid } : match
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    setChecked (optionGuid) {
      return this.selectedGuid ? this.selectedGuid === optionGuid : false
    },
    change (item) {
      const oldSelectedOption = this.selectedOption
      const emptyOption = !this.isRequired ? [] : undefined
      this.selectedOption = item.value ? item : { label: item.label, value: item.guid }

      this.onChange({
        newValue: !isEmpty(this.selectedOption) ? [this.selectedOption.value] : [],
        oldValue: !isEmpty(oldSelectedOption) ? [oldSelectedOption.value] : emptyOption,
        valueFieldName: 'selected',
      })
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-definition-type-picker__radio
  margin-bottom 7px

.uc-input-wrapper
  margin-top 16px
</style>
