<template>
  <div class="uc-entity">
    <template v-for="(property, propertyName) in entity">
      <component
        :is="getPropertyEditorComponent(property.editor)"
        v-if="isEnumerableProperty(propertyName)"
        :key="propertyName"
        :name="propertyName"
        :definition="property"
        :language="contentLanguage"
        :promotion-guid="promotionGuid"
        :entity-guid="entity.meta.entityGuid"
        @change="updateProperty" />
    </template>
  </div>
</template>

<script>
import { getPropertyEditorComponent } from '@/components/editors/'

export const ComponentEvent = {
  CHANGE: 'change',
}

export default {
  name: 'UcEntity',
  props: {
    // TODO(ch12695): Should be removed once we have sub-resource links in place
    promotionGuid: {
      type: String,
      required: true,
    },
    entity: {
      type: Object,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  methods: {
    getPropertyEditorComponent,
    isEnumerableProperty (propertyName) {
      // TODO(ch11252): Use the 'Render In Editor' property once available
      const nonenumerableProperties = ['definition', 'meta', 'guid']
      return !nonenumerableProperties.includes(propertyName)
    },
    updateProperty (saveCommand) {
      this.$emit(ComponentEvent.CHANGE, saveCommand)
    },
  },
}
</script>
<style lang="stylus" scoped>
.uc-entity
  display grid
  row-gap 3 * $base-unit
</style>
