import { SET_SELECTED_STORE_GUID, SET_SELECTED_CONTENT_LANGUAGE } from './mutation-types'

const actions = {
  setSelectedStoreGuid ({ commit }, guid) {
    commit(SET_SELECTED_STORE_GUID, guid)
  },
  setSelectedContentLanguage ({ commit }, language) {
    commit(SET_SELECTED_CONTENT_LANGUAGE, language)
  },
}

export default actions
