<template>
  <div class="uc-image-picker-node">
    <uc-navigator-node
      v-bind="{ selected }"
      @click="onClick">
      <div class="uc-image-picker-node__container">
        <slot />
      </div>
      <div
        v-if="hasOpenEvent"
        class="uc-image-picker-node__action">
        <uc-button-round
          theme="ghost"
          size="s"
          @click.stop="onOpen">
          <chevron-forward />
        </uc-button-round>
      </div>
    </uc-navigator-node>
    <div
      v-truncate
      class="uc-image-picker-node__name">
      {{ node.name }}
    </div>
  </div>
</template>

<script>
import { UcNavigatorNode } from '@/components/generic/uc-navigator'
import UcButtonRound from '@/components/forms/uc-button-round'
import { ChevronForward16Thick as ChevronForward } from '@/assets/icons'

const ComponentEvent = {
  OPEN: 'open',
  CLICK: 'click',
}

export default {
  name: 'UcImagePickerNode',
  components: {
    UcNavigatorNode,
    UcButtonRound,
    ChevronForward,
  },
  props: {
    selected: {
      type: Boolean,
      default: false,
    },
    node: {
      type: Object,
      required: true,
    },
  },
  computed: {
    hasOpenEvent () {
      return !!this.$listeners[ComponentEvent.OPEN]
    },
  },
  methods: {
    onClick () {
      this.$emit(ComponentEvent.CLICK)
    },
    onOpen () {
      this.$emit(ComponentEvent.OPEN)
    },
  },
}
</script>
<style lang="stylus" scoped>
.uc-image-picker-node
  display flex
  flex-direction column

.uc-image-picker-node__container
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  height 150px
  border $base-border
  border-radius ($base-unit / 2)

.uc-image-picker-node__name
  secondary-petit-text()
  margin 0
  padding ($base-unit / 2) 0 0 0
  user-select none

.uc-image-picker-node:active
  .uc-image-picker-node__container
    transform scale(0.985)

.uc-image-picker-node__action
  position absolute
  right (2 * $base-unit)
  bottom (2 * $base-unit)
</style>
