export class ProductService {
  constructor (httpClient, promotionGuid, criterionGuid) {
    this._httpClient = httpClient
    this._promotionGuid = promotionGuid
    this._criterionGuid = criterionGuid
  }

  get _rootUrl () {
    return `/promotions/${this._promotionGuid}/criteria/${this._criterionGuid}/products`
  }

  fetchManyAsync ({ startAt, limit, cultureCode }) {
    const url = `${this._rootUrl}/${cultureCode}`
    const payload = {
      params: { startAt, limit },
    }
    return this._httpClient.get(url, payload)
  }

  createManyAsync ({ guids, cultureCode }) {
    const url = `${this._rootUrl}/${cultureCode}`
    return this._httpClient.post(url, guids)
  }

  deleteManyAsync ({ guids }) {
    return this._httpClient.delete(this._rootUrl, { data: guids })
  }
}
