<template>
  <div
    :class="{ vertical, horizontal }"
    class="resize-outer">
    <div
      :class="{ vertical, horizontal }"
      class="resize-inner"
      :style="{ cursor: cursor }"
      @mousedown="onMousedown" />
  </div>
</template>

<script>
export default {
  name: 'ResizeHandler',
  props: {
    size: {
      type: Number,
      required: true,
    },
    minSize: {
      type: Number,
      required: true,
    },
    isVertical: {
      type: Boolean,
      required: true,
    },
  },
  data () {
    return {
      startPos: null,
    }
  },
  computed: {
    cursor () {
      return (this.isVertical)
        ? 'n-resize'
        : 'w-resize'
    },
    vertical () {
      return this.isVertical
    },
    horizontal () {
      return !this.isVertical
    },
  },
  methods: {
    getPos (event) {
      return (this.isVertical)
        ? event.clientY
        : event.clientX
    },

    onMousedown (event) {
      event.preventDefault()
      const pos = this.getPos(event)
      this.startPos = pos
      window.document.body.style.cursor = this.cursor
      window.addEventListener('mousemove', this.onMousemove)
      window.addEventListener('mouseup', this.onMouseup)
      this.$emit('resizeMousedown')
    },

    onMousemove (event) {
      const pos = this.getPos(event)
      const delta = pos - this.startPos
      this.startPos = pos
      if (this.size < this.minSize && delta < 0) return
      this.$emit('emitResult', this.size + delta)
      this.$emit('emitDelta', delta)
    },

    onMouseup () {
      window.document.body.style.cursor = 'default'
      window.removeEventListener('mousemove', this.onMousemove)
      window.removeEventListener('mouseup', this.onMouseup)
      this.$emit('resizeMouseup')
    },
  },
}
</script>

<style lang="stylus" scoped>
$border = 1px solid $base-color-gray-600

.resize-outer
  display flex
  align-items center
  justify-content center
  width 100%
  height 100%

  &.vertical
    height 4px

  &.horizontal
    width 4px

.resize-inner
  width 100%
  height 100%

  &.vertical
    width 16px
    border-top $border
    border-bottom $border

  &.horizontal
    height 16px
    border-right $border
    border-left $border
</style>
