import { rest } from 'msw'
import {
  campaigns,
  campaignDefinitions,
  campaignDefinitionsShallow,
} from '@/mocks/entities/campaigns/'

const RESPONSE_DELAY = 200

export const rootCampaignHandlers = [
  rest.get('/ucommerceapi/v2/campaigns/definitions', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(campaignDefinitionsShallow),
    )
  }),

  rest.get('/ucommerceapi/v2/campaigns/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(campaignDefinitions),
    )
  }),

  rest.get('/ucommerceapi/v2/campaigns/:campaignGuid/:cultureCode', (req, res, ctx) => {
    const response = campaigns.find(campaign => campaign.meta.entityGuid === req.params.campaignGuid) || {}

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.patch('/ucommerceapi/v2/campaigns/:campaignGuid/:cultureCode', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),

  rest.delete('/ucommerceapi/v2/campaigns/:campaignGuid', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
]
