<template>
  <div
    class="uc-short-text"
    data-test="short-text-editor">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="value"
      :required="isRequired">
      <uc-text-input
        v-bind="{ value, label, errors, placeholder, hasErrors }"
        :readonly="isReadonly"
        @focus="$emit('focus', $event)"
        @blur="onBlur"
        @input="onInput" />
    </editor-wrapper>
  </div>
</template>

<script>
import { UcTextInput } from '@/components/forms/uc-text-input/'
import editorMixin from '../editor-mixin.js'

export default {
  name: 'UcShortText',
  components: {
    UcTextInput,
  },
  mixins: [editorMixin],
  methods: {
    onBlur (e) {
      const isSameValue = this.value === e.target.value || (!this.value && e.target.value === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue: e.target.value,
        oldValue: this.value,
      })
    },
    onInput (value) {
      if (this.value !== value && !this.hasErrors) {
        this.onChange({
          newValue: value,
          oldValue: this.value,
        }, 'onInput')
      }
    },
  },
}
</script>
