<template>
  <div class="uc-navigator">
    <!-- Header -->
    <div
      v-if="$scopedSlots.topLeft || $scopedSlots.topRight"
      data-test="navigator-header"
      class="uc-navigator__header">
      <slot
        name="topLeft"
        v-bind="{ navigate, navigateBack, path }" />
      <slot
        name="topRight"
        v-bind="{ navigate, navigateBack, path }" />
    </div>

    <!-- State: Initializing -->
    <div
      v-if="isInitializing"
      class="uc-navigator__pending"
      :class="cssClassObject"
      data-test="intializing-indicator">
      <loading-indicator :is-loading="isLoading" />
    </div>

    <!-- Needed only for media -->
    <slot
      v-if="nodes.length === 0"
      v-bind="{ navigate, navigateBack, path }"
      name="emptyFolder" />

    <!-- Content -->
    <div
      v-else
      class="uc-navigator__content">
      <uc-infinite-scroller
        v-bind="{ scrollTo }"
        :disabled="disableInfiniteScroll"
        @next="requestNextPage">
        <template #default>
          <slot v-bind="{ nodes, navigate, navigateBack }" />
        </template>
        <template #trigger>
          <slot name="trigger" />
        </template>
      </uc-infinite-scroller>

      <!-- State: Loading -->
      <div
        v-show="isLoading"
        class="uc-navigator__pending"
        :class="cssClassObject"
        data-test="loading-indicator">
        <loading-indicator :is-loading="isLoading" />
      </div>
    </div>
  </div>
</template>

<script>
import LoadingIndicator from '@/components/widgets/loading-indicator'
import UcInfiniteScroller from '@/components/generic/uc-infinite-scroller'

const ComponentEvent = {
  REQUEST_PAGE: 'requestPage',
  REQUEST_NEXT_PAGE: 'requestNextPage',
  NAVIGATE: 'navigate',
  UPDATE_PATH: 'update:path',
}

export default {
  name: 'UcNavigator',
  components: {
    LoadingIndicator,
    UcInfiniteScroller,
  },
  props: {
    nodes: {
      type: Array,
      required: true,
    },
    path: {
      type: Array,
      required: true,
    },
    totalNodeCount: {
      type: Number,
      default: undefined,
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    scrollTo: {
      type: Array,
      default: () => [],
      validator: (value) => value.length <= 2 && value.every(entry => Number.isInteger(entry)),
    },
  },
  data () {
    return {
      currentPage: 0,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'has-overlay': this.totalNodeCount > 0,
      }
    },
    currentNode () {
      return this.path[this.path.length - 1]
    },
    requestPagePayload () {
      return {
        node: this.currentNode,
        page: this.currentPage,
      }
    },
    disableInfiniteScroll () {
      return !this.totalNodeCount || this.nodes.length === this.totalNodeCount
    },
    isInitializing () {
      return this.isLoading && this.nodes.length === 0 && this.totalNodeCount === 0
    },
  },
  watch: {
    path: {
      immediate: true,
      handler () {
        this.currentPage = 0
        this.$emit(ComponentEvent.NAVIGATE, this.currentNode)
      },
    },
  },
  methods: {
    navigate (node) {
      if (this.currentNode && node === this.currentNode) {
        return
      }

      const existingNodeIndex = this.path.indexOf(node)
      let path

      if (existingNodeIndex > -1) {
        path = this.path.slice(0, existingNodeIndex + 1)
      } else {
        path = [...this.path, node]
      }

      this.$emit(ComponentEvent.UPDATE_PATH, path)
    },
    navigateBack () {
      const path = this.path.slice(0, this.path.length - 1)
      this.$emit(ComponentEvent.UPDATE_PATH, path)
    },
    requestPage () {
      this.currentPage++
      this.$emit(ComponentEvent.REQUEST_PAGE, this.requestPagePayload)
    },
    requestNextPage () {
      this.$emit(ComponentEvent.REQUEST_NEXT_PAGE)
      this.requestPage()
    },
  },
}
</script>

<style scoped lang="stylus">
.uc-navigator
  display flex
  flex 1
  flex-direction column
  height 100%

.uc-navigator__pending
  position absolute
  top 0
  right 0
  bottom 0
  left 0
  z-index 100
  display flex
  align-items center
  justify-content center

  &.has-overlay
    background-color $base-color-white--disabled

.uc-navigator__header
  display flex
  align-items center
  justify-content space-between
  height 6 * $base-unit
  padding-bottom 2 * $base-unit

.uc-navigator__content
  position relative
  flex 1 0 auto
  height 0
</style>
