<template>
  <uc-image-picker-node
    data-test="uc-image-picker-grid-image"
    v-bind="{ node, selected }"
    @click="select">
    <uc-image
      class="navigator-grid-image__image"
      :src="node.url"
      fit="scaleDown">
      <template #loading>
        <loading-indicator is-loading />
      </template>
    </uc-image>
  </uc-image-picker-node>
</template>

<script>
import UcImage from '@/components/generic/uc-image/uc-image'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import UcImagePickerNode from '../uc-image-picker-node'

const ComponentEvent = {
  SELECT: 'select',
}

export default {
  name: 'UcImagePickerGridImage',
  components: {
    UcImage,
    LoadingIndicator,
    UcImagePickerNode,
  },
  props: {
    node: {
      type: Object,
      required: true,
    },
    selected: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    select () {
      this.$emit(ComponentEvent.SELECT, this.node)
    },
  },
}
</script>
<style lang="stylus" scoped>
.navigator-grid-image__image
  width 100%
  max-height 100%
  border-radius 4px
</style>
