<template>
  <div class="payment-information">
    <uc-section-wrapper
      v-bind="{ state, errorMessage }"
      :empty-message="$t('emptyMessage')">
      <uc-entity-property-list
        v-for="payment in paymentInformation"
        :key="payment.meta.entityGuid"
        :entity="payment"
        no-null-properties
        v-bind="{ ignoredProperties, contentLanguage }" />
    </uc-section-wrapper>
  </div>
</template>

<i18n>
{
  "en": {
    "emptyMessage": "No payment information is available."
  }
}
</i18n>

<script>
import { orderPaymentService } from '@/services/'
import { UcSectionWrapper, UcSectionWrapperState } from '@/components/generic/uc-section-wrapper/'
import { UcEntityPropertyList } from '@/components/domain/uc-entity-property-list/'

export default {
  name: 'PaymentInformation',
  components: {
    UcSectionWrapper,
    UcEntityPropertyList,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    parentGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      paymentInformation: null,
      state: UcSectionWrapperState.LOADING,
      errorMessage: '',
    }
  },
  computed: {
    ignoredProperties () {
      return ['definition', 'meta', 'guid']
    },
  },
  async created () {
    try {
      this.state = UcSectionWrapperState.LOADING

      this.paymentInformation = (await orderPaymentService.fetchManyAsync({
        parentGuid: this.parentGuid,
        cultureCode: this.contentLanguage,
        startAt: 0,
        limit: Number.MAX_SAFE_INTEGER,
      })).data

      this.state = this.paymentInformation.length > 0 ? UcSectionWrapperState.IDLE : UcSectionWrapperState.EMPTY
    } catch (error) {
      this.state = UcSectionWrapperState.ERROR
      this.errorMessage = error?.response?.data?.message || this.$t('global.errorMessage')
    }
  },
}
</script>
