<template>
  <div class="uc-popup-calendar">
    <input-group
      v-bind="{ label, hasErrors, readonly }"
      tag="div">
      <date-picker
        v-model="inputValue"
        class="date-picker"
        :disabled="readonly"
        :format="formatter"
        :disabled-dates="disabledDates"
        full-month-name
        :placeholder="placeholder === null ? dateFormat : placeholder"
        monday-first
        :clear-button="clearable"
        calendar-class="calendar"
        wrapper-class="wrapper"
        input-class="datepicker-input"
        @selected="onSelected" />
    </input-group>
  </div>
</template>

<script>
import DatePicker from 'vuejs-datepicker'
import { Format as format, locales } from '@/utils/date-formatter'
import { InputGroup } from '../input-group'

const dateFormats = {
  default: 'MMMM D YYYY',
}

export default {
  name: 'UcPopupCalendar',
  components: {
    DatePicker,
    InputGroup,
  },
  props: {
    label: {
      type: String,
      default: '',
    },
    value: {
      type: [Date, String],
      default: null,
    },
    hasErrors: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    language: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: null,
    },
    disabledDates: {
      type: Object,
      // Must be undefined, as per documentation
      default: undefined,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      inputValue: '',
    }
  },
  computed: {
    dateFormat () {
      return dateFormats[this.language] || dateFormats.default
    },
  },
  watch: {
    value: {
      immediate: true,
      handler (newValue) {
        this.inputValue = newValue
      },
    },
  },
  methods: {
    formatter (date) {
      return format(date, this.dateFormat, locales[this.language])
    },
    onSelected (date) {
      this.$emit('selected', date)
    },
  },
}
</script>
<!-- eslint-disable vue-scoped-css/require-scoped -->
<style lang="stylus">
.date-picker
  width 100%

  & > div:first-child
    position relative;
    display flex
    align-items center

  .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).day:hover
  .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).month:hover
  .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).year:hover
    border 1px solid $base-color-brand-50

  .vdp-datepicker__calendar header .prev:not(.disabled):hover
  .vdp-datepicker__calendar header .next:not(.disabled):hover
  .vdp-datepicker__calendar header .up:not(.disabled):hover
    background $base-color-brand-50

  .vdp-datepicker__clear-button
    position absolute
    right $base-unit * 2
    display flex
    align-items center
    justify-content center
    width $base-unit * 3
    height $base-unit * 3
    border $base-border
    border-radius 50%
    background-color $base-color-white
    visibility hidden // Neccesary to avoid button quickly flashing when selecting date
    opacity 0
    transition opacity 250ms ease-out

    &::after
      display inline-block
      width $base-unit * 2
      height $base-unit * 2
      content url("data:image/svg+xml; utf8, <svg width='16' height='16' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'><path fill='currentColor' d='M11.96 4.182a.5.5 0 01.058.638l-.058.07-3.182 3.18 3.182 3.182a.5.5 0 01-.638.765l-.069-.058-3.182-3.182L4.89 11.96a.5.5 0 01-.765-.638l.058-.069 3.182-3.182-3.182-3.182a.5.5 0 01.638-.765l.07.058L8.07 7.364l3.182-3.182a.5.5 0 01.707 0z'/></svg>")

    span
      display none

  &:hover
    .vdp-datepicker__clear-button
      visibility visible
      opacity 1

  .cell.day.selected
  .cell.month.selected
  .cell.year.selected
  .vdp-datepicker__calendar .cell.selected:hover
    background $base-color-brand-50

  .calendar
    width 100%
    max-width 400px
    border-color $base-color-gray-400
    border-radius 4px

  .datepicker-input
    width 100%
    height 48px
    padding 13px 16px 15px
    border none
    border-radius 4px
    background-color transparent
    outline none
    cursor pointer
    primary-body-text()

    &::placeholder
      secondary-body-text()

    &:disabled
      color $base-color-gray-500
</style>
