<template>
  <div class="uc-content-picker-multi">
    <uc-stepper @beforeLeave="onBeforeLeaveStep">
      <!-- Back button -->
      <template #top-left="{ previous, step }">
        <transition name="fade">
          <div
            v-show="step > 1"
            class="uc-content-picker-multi__back">
            <uc-button
              theme="link"
              size="sm"
              data-test="previous"
              @click="previous">
              {{ $tc('global.back') }}
              <template #before>
                <arrow-back-icon />
              </template>
            </uc-button>
          </div>
        </transition>
      </template>

      <!-- Step 1: Navigator -->
      <template #step-1="{ next }">
        <div class="uc-content-picker-multi__picker">
          <use-navigator-data-provider
            :content-type="contentType"
            @emptyRootState="onEmptyRootState">
            <template #default="{ data, getData, isLoading, isEmptyRootState }">
              <fragment v-if="!isEmptyRootState">
                <p
                  class="uc-content-picker-multi__subheader">
                  {{ $t('ucContentPicker.subHeader') }}
                </p>
                <content-navigator
                  v-bind="{ isLoading, selectedNodesGuids }"
                  :nodes="data"
                  @openNode="getData"
                  @selectNode="selectNode">
                  <template #topRight>
                    <!-- Button: Select all -->
                    <div class="uc-content-picker-multi__action">
                      <uc-button
                        v-if="data.length > 0"
                        theme="ghost"
                        size="sm"
                        @click="selectAllClick(data)">
                        {{ isEverythingUnderCurrentBranchSelected(data) ? $t('global.deselect') : $t('global.select') }}
                        <template #after>
                          <div class="uc-content-picker-multi__icon">
                            <uc-button-round
                              size="s"
                              theme="ghost">
                              <component :is="isEverythingUnderCurrentBranchSelected(data) ? 'MinusIcon' : 'PlusIcon'" />
                            </uc-button-round>
                          </div>
                        </template>
                      </uc-button>
                    </div>

                    <!-- Button: Show selection -->
                    <div class="uc-content-picker-multi__action">
                      <uc-button
                        size="sm"
                        :disabled="selectedNodes.length === 0"
                        @click="next">
                        {{ selectedButtonLabel }}
                      </uc-button>
                    </div>
                  </template>
                </content-navigator>
              </fragment>
              <uc-navigator-empty-state
                v-else
                :title="$t('ucContentPicker.noItemsTitle')"
                :text="$t('ucContentPicker.noItemsText', { name: cms })" />
            </template>
          </use-navigator-data-provider>
        </div>
      </template>

      <!-- Step 2: Selection -->
      <template #step-2>
        <p class="uc-content-picker-multi__subheader">
          {{ $t('ucContentPicker.subHeaderOverview') }}
        </p>
        <div
          v-custom-scroll-bar
          class="uc-content-picker-multi__selection">
          <div class="uc-content-picker-multi__list list">
            <div class="list__header">
              {{ $t('ucContentPicker.selectedItems') }} • {{ selectedNodes.length }}
            </div>
            <div class="list__body">
              <content-navigator-selection
                v-model="selectedNodes"
                :items="listItems"
                @update-sort="onUpdateSort($event)" />
            </div>
          </div>
        </div>
      </template>
    </uc-stepper>

    <!-- Submit button -->
    <div
      v-if="!hasEmptyRootState"
      class="uc-content-picker-multi__submit">
      <uc-button
        :disabled="required && selectedNodes.length === 0"
        floating
        @click="submitSelection">
        {{ submitButtonLabel }}
      </uc-button>
    </div>
  </div>
</template>

<script>
import UcStepper from '@/components/generic/uc-stepper'
import UcButton from '@/components/forms/uc-button'
import UcButtonRound from '@/components/forms/uc-button-round'
import { ArrowBack16Normal as ArrowBackIcon, Dual16Normal as MinusIcon, Add16Normal as PlusIcon } from '@/assets/icons'
import ContentNavigator from './components/content-navigator'
import ContentNavigatorSelection from './components/content-navigator-selection'
import { UseNavigatorDataProvider, UcNavigatorEmptyState } from '@/components/generic/uc-navigator'
import { Fragment } from 'vue-fragment'

import intersection from 'lodash.intersection'
import { validateAgainstEnum } from '@/utils/validate-against'

import {
  toggleObjectInArray,
  removeObjectFromArray,
  addObjectToArray,
  getGuidFromNode,
} from '@/components/generic/uc-image-picker/uc-image-picker.utils'

const ComponentEvent = {
  NODES_SELECTED: 'nodesSelected',
}

const ContentTypeProp = {
  IMAGE: 'image',
  CONTENT: 'content',
}

export default {
  name: 'UcContentPickerMulti',
  components: {
    UcStepper,
    UcButton,
    UcButtonRound,
    UcNavigatorEmptyState,
    ArrowBackIcon,
    ContentNavigator,
    ContentNavigatorSelection,
    MinusIcon,
    PlusIcon,
    UseNavigatorDataProvider,
    Fragment,
  },
  props: {
    selected: {
      type: Array,
      default: () => [],
    },
    contentType: {
      type: String,
      default: ContentTypeProp.CONTENT,
      validator: validateAgainstEnum(ContentTypeProp),
    },
    required: {
      type: Boolean,
      default: false,
    },
    cms: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
      selectedNodes: [...this.selected],
      listItems: [],
      hasEmptyRootState: false,
    }
  },
  computed: {
    selectedNodesGuids () {
      return this.selectedNodes.map(getGuidFromNode)
    },
    submitButtonLabel () {
      return `${this.$t('global.done')} (${this.selectedNodes.length})`
    },
    selectedButtonLabel () {
      return `${this.$t('global.selected')} • ${this.selectedNodes.length}`
    },
  },
  methods: {
    onEmptyRootState (isEmptyRootState) {
      this.hasEmptyRootState = isEmptyRootState
    },
    isEverythingUnderCurrentBranchSelected (nodes) {
      const currentBranchAllGuids = nodes.map(getGuidFromNode)
      const currentBranchSelectedGuids = intersection(currentBranchAllGuids, this.selectedNodesGuids)
      return currentBranchSelectedGuids.length === currentBranchAllGuids.length
    },
    onBeforeLeaveStep (to) {
      if (to === 2) {
        this.listItems = [...this.selectedNodes]
      }
    },
    selectNode (node) {
      toggleObjectInArray(this.selectedNodes, node)
    },
    selectAllClick (nodes) {
      this.isEverythingUnderCurrentBranchSelected(nodes)
        ? this.removeAllItemsFromArray(nodes)
        : this.addAllMissingItemsToArray(nodes)
    },
    removeAllItemsFromArray (nodes) {
      nodes.forEach(node => removeObjectFromArray(this.selectedNodes, node))
    },
    addAllMissingItemsToArray (nodes) {
      nodes.forEach(node => addObjectToArray(this.selectedNodes, node))
    },
    submitSelection () {
      this.$emit(ComponentEvent.NODES_SELECTED, this.selectedNodes)
    },
    onUpdateSort (items) {
      this.selectedNodes = intersection(items, this.selectedNodes)
      this.listItems = items
    },
  },
}
</script>

<style lang="stylus" scoped>
$panel-horizontal-padding = $base-unit * 15
$list-header-height = 9 * $base-unit

.uc-content-picker-multi
  display flex
  flex-direction column
  height 100%

.uc-content-picker-multi__subheader
  secondary-details-text()
  width 100%
  margin 0 auto 8 * $base-unit
  text-align center

.uc-content-picker-multi__submit
  position absolute
  bottom 0
  left 50%
  z-index 1
  transform translateX(-50%)

.uc-content-picker-multi__picker
  display flex
  flex 1 0 auto
  flex-direction column
  width 100%
  height 0

.uc-content-picker-multi__action
  padding-right $base-unit
  padding-left $base-unit

.uc-content-picker-multi__selection
  width 100%
  height 100%
  overflow-y scroll

.uc-content-picker-multi__icon
  margin-left $base-unit
  pointer-events none

.list
  position relative
  width 100%

.list__header
  position fixed
  z-index 1
  width calc(100% - 6px) // 100% minus scrollbar width
  height $list-header-height
  padding $base-unit * 3
  border $base-border
  background-color $base-color-white
  primary-heading-2()

.list__body
  position absolute
  top $list-header-height
  left 0
  width 100%
  border $base-border
  border-top 0
  border-bottom 0
</style>
