<template>
  <uc-image-picker-node
    v-bind="{ node, selected }"
    @open="openFolder"
    @click="selectFolder">
    <folder-icon />
    <div class="uc-image-picker-node__meta">
      {{ childCountLabel }}
    </div>
  </uc-image-picker-node>
</template>

<script>
import FolderIcon from '@/assets/illustrations/folder-image'
import UcImagePickerNode from '../uc-image-picker-node'

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select',
}

export default {
  name: 'UcImagePickerGridFolder',
  components: {
    FolderIcon,
    UcImagePickerNode,
  },
  props: {
    node: {
      type: Object,
      required: true,
    },
    selected: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    childCountLabel () {
      return this.$tc('contentNavigator.items', this.node.childrenCount)
    },
  },
  methods: {
    openFolder () {
      this.$emit(ComponentEvent.OPEN, this.node)
    },
    selectFolder () {
      this.$emit(ComponentEvent.SELECT, this.node)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-image-picker-node__meta
  secondary-petit-text()
  margin 0
  padding ($base-unit / 2) 0 0 0
</style>
