import faker from 'faker'
import { orderDiscountDefinitionsShallow } from './order-discount-definitions.mock.js'

function mockGuid () {
  return `mock-${faker.datatype.uuid()}`
}

function orderDiscountFactory () {
  const entityGuid = mockGuid()
  const definition = orderDiscountDefinitionsShallow[0]
  const percentageOff = faker.datatype.number({ min: 1, max: 100 })
  const department = faker.commerce.department()

  return {
    meta: {
      definition,
      entityGuid,
      nodeType: 'discount',
    },
    definition: {
      value: definition.name,
      selected: [
        definition.guid,
      ],
      guid: mockGuid(),
    },
    createdBy: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    createdOn: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
    modifiedOn: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
    modifiedBy: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    description: {
      value: `${percentageOff}-OFF-${department.toUpperCase()}`,
      guid: mockGuid(),
    },
    campaignName: {
      value: `${department} Sale`,
      guid: mockGuid(),
    },
    campaignItemName: {
      value: `${percentageOff}% off ${department.toLowerCase()}`,
      guid: mockGuid(),
    },
    amountOffTotal: {
      value: faker.datatype.float({ min: 100, max: 100000 }).toFixed(2),
      guid: mockGuid(),
    },
    guid: {
      value: entityGuid,
      guid: mockGuid(),
    },
  }
}


export function generateOrderDiscounts (count = 1) {
  const sequence = [...Array(count).keys()]
  return sequence.map(orderDiscountFactory)
}

export const orderDiscounts = generateOrderDiscounts(200)
