<template>
  <div
    data-test="checkbox-group-editor"
    class="uc-checkbox-group-editor">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedValue"
      :required="isRequired">
      <p
        v-if="!noLabel"
        data-test="checkbox-group-editor-label"
        class="uc-checkbox-group-editor__label">
        {{ label }}
      </p>
      <p
        v-if="options.length === 0"
        data-test="checkbox-group-editor-no-options-text"
        class="uc-checkbox-group-editor__no-options">
        {{ $t('global.noOptionsFound') }}
      </p>
      <uc-checkbox-group
        v-else
        v-model="selectedOptions"
        :options="options"
        option-key="guid">
        <template #default="{ option, toggle, isSelected }">
          <uc-checkbox
            :label="option.label"
            :guid="option.guid"
            :readonly="isReadonlyOption(isSelected(option))"
            :checked="isSelected(option)"
            @change="toggle(option)" />
        </template>
      </uc-checkbox-group>
    </editor-wrapper>
  </div>
</template>

<script>
import { UcCheckbox } from '@/components/forms/uc-checkbox/'
import { UcCheckboxGroup } from '@/components/forms/uc-checkbox-group/'
import editorMixin from '@/components/editors/editor-mixin.js'
import EditorWrapper from '@/components/editors/editor-wrapper.vue'

export default {
  name: 'UcCheckboxGroupEditor',
  components: {
    EditorWrapper,
    UcCheckbox,
    UcCheckboxGroup,
  },
  mixins: [editorMixin],
  props: {
    noLabel: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    selectedOptions: {
      get () {
        return this.options.filter(option => this.selected.includes(option.guid))
      },
      set (event) {
        const emptyOption = !this.isRequired ? [] : undefined
        const oldValue = this.selected.length > 0 ? this.selected : emptyOption
        const newValue = event.map(item => item.guid)

        this.onChange({
          newValue,
          oldValue,
          valueFieldName: 'selected',
        })
      },
    },
    formattedValue () {
      const i18nParams = {
        firstOptionName: this.selectedOptions[0]?.label,
        remainingCount: this.selectedOptions.length - 1,
      }

      return this.$tc('global.selectFormattedValue', this.selectedOptions.length, i18nParams)
    },
  },
  methods: {
    isReadonlyOption (isSelectedOption) {
      return this.isReadonly || (this.isRequired && this.selectedOptions.length === 1 && isSelectedOption)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-checkbox-group-editor__label
  margin-top 0

.uc-checkbox-group-editor__no-options
  secondary-body-text()
</style>
