import faker from 'faker'
import { orderLineDefinitionsShallow } from './order-line-definitions.mock.js'
import { getRandomFromArray } from '@/utils/array.utils.js'

function mockGuid () {
  return `mock-${faker.datatype.uuid()}`
}
export const taxPercentage = 0.2
// Added a whole bunch of getters, due to the data not updating when using it to
// generate new orderLines in a post: https://stackoverflow.com/a/57779205

function orderLineFactory () {
  const createdBy = faker.internet.email()
  const createdOn = faker.datatype.datetime()
  const definition = getRandomFromArray(orderLineDefinitionsShallow)

  const listPrice = faker.datatype.float({ min: 10, max: 50000 })
  const quantity = faker.datatype.number({ min: 1, max: 1000 })
  const tax = listPrice * taxPercentage * quantity
  const total = listPrice * quantity + tax

  const currencyIsoCode = faker.finance.currencyCode()

  return {
    meta: {
      definition,
      entityGuid: mockGuid(),
      nodeType: 'orderLine',
      createdBy,
      createdOn,
    },
    definition: {
      value: definition.name,
      selected: [definition.guid],
      guid: mockGuid(),
    },
    name: {
      value: faker.commerce.productName(),
      guid: mockGuid(),
    },
    sku: {
      value: faker.commerce.productName(),
      guid: mockGuid(),
    },
    variantSku: {
      value: Math.random() < 0.5 ? faker.commerce.productName() : '',
      guid: mockGuid(),
    },
    quantity: {
      value: quantity.toString(),
      guid: mockGuid(),
    },
    listPrice: {
      value: listPrice,
      guid: mockGuid(),
      currencyIsoCode,
    },
    total: {
      value: total.toString(),
      guid: mockGuid(),
      currencyIsoCode,
    },
    tax: {
      value: tax.toString(),
      guid: mockGuid(),
      currencyIsoCode,
    },
    createdBy: {
      value: createdBy,
      guid: mockGuid(),
    },
    createdOn: {
      value: createdOn,
      guid: mockGuid(),
    },
  }
}

export function generateOrderLines (count = 1) {
  const sequence = [...Array(count).keys()]
  return sequence.map(orderLineFactory)
}

export const orderLines = generateOrderLines(200)
