<template>
  <transition name="fade">
    <overlay-modal>
      <div class="campaign-edit">
        <!-- State: Idle -->
        <details-layout
          v-if="isIdle"
          @back-click="closeCampaign">
          <generic-details
            @deleted="deleteCampaign"
            @resurrected="resurrectCampaign" />

          <router-view />
        </details-layout>

        <!-- State: Initializing -->
        <div
          v-else-if="isInitializing"
          class="campaign-edit__state">
          <loading-indicator is-loading />
        </div>

        <!-- State: Error -->
        <div
          v-else-if="hasError"
          class="campaign-edit__state">
          <p>{{ translations.entityNotFound }}</p>
          <uc-button
            theme="text"
            @click="closeCampaign">
            <template #before>
              <back-icon />
            </template>
            {{ translations.back }}
          </uc-button>
        </div>
      </div>
    </overlay-modal>
  </transition>
</template>


<script>
import { createNamespacedHelpers, mapMutations } from 'vuex'

import OverlayModal from '@/components/widgets/overlay-modal.vue'
import DetailsLayout from '@/layouts/details-layout.vue'
import GenericDetails from '@/components/generic/details-view/generic-details.vue'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import { UcButton } from '@/components/forms/uc-button/'
import { ArrowBack16Normal as BackIcon } from '@/assets/icons/'

import { campaignService } from '@/services/'

const detailsStore = createNamespacedHelpers('details')

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR: 'error',
}

const ComponentEvent = {
  DELETE: 'delete',
  RESURRECT: 'resurrect',
}

export default {
  name: 'CampaignEdit',
  components: {
    OverlayModal,
    DetailsLayout,
    GenericDetails,
    LoadingIndicator,
    UcButton,
    BackIcon,
  },
  props: {
    entity: {
      validator: prop => typeof prop === 'object' || prop === undefined,
      default: undefined,
    },
    campaignGuid: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
    }
  },
  computed: {
    ...detailsStore.mapGetters(['currentDetails']),
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
    translations () {
      return {
        entityNotFound: this.$t('campaignEdit.entityNotFound'),
        back: this.$t('global.back'),
      }
    },
  },
  async created () {
    try {
      const payload = {
        guid: this.campaignGuid,
        cultureCode: this.contentLanguage,
      }
      const entity = this.entity ? this.entity : (await campaignService.fetchEntityAsync(payload)).data
      this.setDetails({ details: entity })
      this.state = ComponentState.IDLE
    } catch {
      this.state = ComponentState.ERROR
    }
  },
  destroyed () {
    this.clearDetails()
  },
  methods: {
    ...mapMutations(['clearUndos']),
    ...detailsStore.mapMutations(['setDetails', 'clearDetails']),
    closeCampaign () {
      this.clearUndos()
      this.$router.back()
    },
    deleteCampaign (event) {
      this.$emit(ComponentEvent.DELETE, event.guid)
      this.$router.replace({ name: 'campaigns' })
    },
    resurrectCampaign (guid) {
      this.$emit(ComponentEvent.RESURRECT, guid)
    },
  },
}
</script>

<style lang="stylus" scoped>
.campaign-edit__state
  display flex
  flex-direction column
  align-items center
  justify-content center
  height 100vh
</style>
