<template>
  <div
    class="loading-indicator-container"
    :style="loadingIndicatorContainerStyle">
    <div
      v-if="isLoading"
      class="sk-flow">
      <div class="sk-flow-dot" />
      <div class="sk-flow-dot" />
      <div class="sk-flow-dot" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoadingIndicator',
  props: {
    isLoading: {
      type: Boolean,
      required: true,
    },
    width: {
      type: Number,
      default: 40,
    },
  },
  computed: {
    loadingIndicatorContainerStyle () {
      const width = `${this.width}px`
      const height = `${this.width / 4}px`
      return { width, height }
    },
  },
}
</script>

<style lang="stylus" scoped>
$sk_color = $base-color-gray-600

.sk-flow
  display flex
  justify-content space-between
  width 100%
  height 100%

.sk-flow-dot
  width 25%
  border-radius 50%
  background $sk_color
  animation sk-flow 1.4s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s infinite both

.sk-flow-dot:nth-child(1) { animation-delay: -0.30s }

.sk-flow-dot:nth-child(2) { animation-delay: -0.15s }

@keyframes sk-flow {
  0%, 80%, 100% {
    transform: scale(0.3) }

  40% {
    transform: scale(1) }
}
</style>
