export default {
  methods: {
    getSymbol (ISOCode) {
      const temp = 0
      return ISOCode
        ? temp
          .toLocaleString('en', {
            style: 'currency',
            currency: ISOCode,
            minimumFractionDigits: 2,
            maximumFractionDigits: 2,
          })
          .replace('0.00', '')
        : ''
    },
  },
}
