<template>
  <div class="uc-date-time-picker">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedDateTime"
      :required="isRequired">
      <div class="uc-date-time-picker__row">
        <!-- Date picker -->
        <uc-popup-calendar
          v-bind="{ label, value, hasErrors, language }"
          :readonly="isReadonly"
          @selected="onSelected" />

        <!-- Time picker -->
        <div class="uc-date-time-picker__column">
          <div class="uc-date-time-picker__time">
            <input-group
              v-bind="{ hasErrors }"
              :readonly="isReadonly">
              <vue-timepicker
                v-model="time"
                v-custom-scroll-bar
                class="time-picker"
                auto-scroll
                hide-clear-button
                :disabled="isReadonly" />
            </input-group>
          </div>

          <div
            v-if="!isRequired"
            class="uc-date-time-picker__clear"
            :class="{ 'is-hidden': !value }">
            <uc-button-round
              theme="ghost"
              size="s"
              @click="emitIfChanged(null)">
              <clear-icon />
            </uc-button-round>
          </div>
        </div>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import VueTimepicker from 'vue2-timepicker'
import UcPopupCalendar from '@/components/forms/uc-popup-calendar'
import { InputGroup } from '@/components/forms/input-group/'
import { UcButtonRound } from '@/components/forms/uc-button-round/'
import { Close16Normal as ClearIcon } from '@/assets/icons/'
import { Format as format, locales } from '@/utils/date-formatter'
import editorMixin from '../editor-mixin.js'

const dateFormats = {
  default: 'MMM D YYYY',
}

export default {
  name: 'UcDateTimePicker',
  components: {
    UcPopupCalendar,
    VueTimepicker,
    InputGroup,
    ClearIcon,
    UcButtonRound,
  },
  mixins: [editorMixin],
  computed: {
    time: {
      get: function () {
        const date = this.getDate()

        return {
          HH: date ? date.getHours().toString().padStart(2, '0') : null,
          mm: date ? date.getMinutes().toString().padStart(2, '0') : null,
        }
      },
      set: function (time) {
        this.changeTime(time)
      },
    },
    formattedDateTime () {
      return this.value ? `${this.dateFormatter(this.value)}, ${this.timeFormatter}` : ''
    },
    dateFormat () {
      return dateFormats[this.language] || dateFormats.default
    },
    timeFormatter () {
      return `${this.time.HH}:${this.time.mm}`
    },
  },
  methods: {
    getDate () {
      if (!this.value) {
        return null
      }

      const date = new Date(this.value)

      if (date instanceof Date && !isNaN(date)) {
        // Truncate date to make comparison possible
        return new Date(
          date.getFullYear(),
          date.getMonth(),
          date.getDate(),
          date.getHours(),
          date.getMinutes(),
        )
      } else {
        return null
      }
    },
    changeTime (time) {
      const date = this.getDate()

      if (date) {
        const dateTime = new Date(
          date.getFullYear(),
          date.getMonth(),
          date.getDate(),
          time.HH,
          time.mm,
        )

        this.emitIfChanged(dateTime)
      }
    },
    onSelected (newDate) {
      const date = this.getDate() || new Date()

      if (date) {
        const dateTime = new Date(
          newDate.getFullYear(),
          newDate.getMonth(),
          newDate.getDate(),
          date.getHours(),
          date.getMinutes(),
        )

        this.emitIfChanged(dateTime)
      }
    },
    emitIfChanged (dateTime) {
      const previousDate = this.getDate()

      if (previousDate === null && dateTime === null) {
        return
      }

      if ((previousDate === null && dateTime === null) | !previousDate || dateTime === null || dateTime.getTime() !== previousDate.getTime()) {
        this.onChange({
          newValue: dateTime,
          oldValue: previousDate,
        })
      }
    },
    dateFormatter (date) {
      return this.value ? format(date, this.dateFormat, locales[this.language]) : ''
    },
  },
}
</script>

<!-- eslint-disable vue-scoped-css/require-scoped -->
<style lang="stylus">
.uc-date-time-picker__row
  display grid
  grid-template-columns repeat(2, 1fr)
  gap $base-unit

.uc-date-time-picker__column
  display flex
  gap $base-unit
  align-items center
  justify-content center

.uc-date-time-picker__time
  width 100%

.uc-date-time-picker__clear
  visibility hidden
  opacity 0
  transition opacity 250ms ease-out

.uc-date-time-picker:hover .uc-date-time-picker__clear:not(.is-hidden)
  visibility visible
  opacity 1

.calendar
  width 100%
  min-width 37 * $base-unit
  max-width 50 * $base-unit
  margin-top $base-unit

.time-picker
  display flex
  align-items center
  width 100%
  primary-body-text()

  ul
    max-height $base-unit * 30
    margin 0
    padding 0
    overflow scroll
    list-style-type none

  li
    display flex
    align-items center
    justify-content center
    padding $base-unit $base-unit * 2
    line-height $base-unit * 3
    cursor pointer

  ul.hours > li:not(.hint):not(.disabled):not(.active)
  ul.minutes > li:not(.hint):not(.disabled):not(.active)
    border 1px solid transparent

  ul.hours > li:not(.hint):not(.disabled):not(.active):hover
  ul.minutes > li:not(.hint):not(.disabled):not(.active):hover
    border 1px solid $base-color-brand-50
    background-color transparent

  ul.hours > li:not(.hint):not(.disabled).active
  ul.minutes > li:not(.hint):not(.disabled).active
    color $base-color-text-primary
    background $base-color-brand-50

  .display-time
    width 100%
    height 100%
    padding 0 $base-unit * 2
    border none
    overflow hidden
    background-color transparent
    outline none
    cursor pointer
    primary-body-text()

    &::placeholder
      secondary-body-text()

    &:disabled
      color $base-color-gray-500

  .dropdown
    position absolute
    top 6 * $base-unit
    z-index 100
    width 100%
    margin-top $base-unit
    border $base-border
    border-radius $base-border-radius
    background-color $base-color-white
    box-shadow none

    .select-list
      display grid
      grid-template-columns repeat(2, 1fr)
      width 100%

    .hint
      display none
</style>
