<template>
  <div
    class="loading-spinner"
    :class="cssClassObject">
    <div /><div /><div /><div />
  </div>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

export const SizeProp = {
  SMALL: 'sm',
  MEDIUM: 'md',
  LARGE: 'lg',
}

export const ThemeProp = {
  LIGHT: 'light',
  DARK: 'dark',
}

export default {
  name: 'LoadingSpinner',
  props: {
    size: {
      type: String,
      default: SizeProp.SMALL,
      validate: validateAgainstEnum(SizeProp),
    },
    theme: {
      type: String,
      default: ThemeProp.LIGHT,
      validate: validateAgainstEnum(ThemeProp),
    },
  },
  computed: {
    cssClassObject () {
      return {
        'loading-spinner--theme-light': this.theme === ThemeProp.LIGHT,
        'loading-spinner--theme-dark': this.theme === ThemeProp.DARK,
        'loading-spinner--size-small': this.size === SizeProp.SMALL,
        'loading-spinner--size-medium': this.size === SizeProp.MEDIUM,
        'loading-spinner--size-large': this.size === SizeProp.LARGE,
      }
    },
  },
}
</script>

<style scoped lang="stylus">
$size-small = $base-unit * 2
$size-medium = $base-unit * 3
$size-large = $base-unit * 4

.loading-spinner
  position relative
  display inline-block

.loading-spinner div
  position absolute
  border-style $base-border-style
  border-radius 50%
  animation lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite

.loading-spinner--size-small
  width $size-small
  height $size-small

  div
    width $size-small
    height $size-small
    border-width 2px

.loading-spinner--size-medium
  width $size-medium
  height $size-medium

  div
    width $size-medium
    height $size-medium
    border-width 3px

.loading-spinner--size-large
  width $size-large
  height $size-large

  div
    width $size-large
    height $size-large
    border-width 4px

.loading-spinner--theme-light div
  border-color $base-color-white transparent transparent transparent

.loading-spinner--theme-dark div
  border-color $base-color-gray-600 transparent transparent transparent

.loading-spinner div:nth-child(1)
  animation-delay -0.45s

.loading-spinner div:nth-child(2)
  animation-delay -0.3s

.loading-spinner div:nth-child(3)
  animation-delay -0.15s

@keyframes lds-ring
  0%
    transform rotate(0deg)

  100%
    transform rotate(360deg)
</style>
