<template>
  <div class="uc-image-picker-editor--multi-select">
    <!-- Image editor -->
    <editor-wrapper
      v-bind="{ compact, errors }"
      :required="isRequired">
      <!-- Compact mode -->
      <template #compactValue>
        <div
          v-if="hasSelectedMedia"
          class="uc-image-picker-editor--multi-select__compact-image">
          <uc-image
            :readonly="isReadonly"
            :src="imageSrc"
            :alt="altText"
            fit="scaleDown" />
        </div>
      </template>
      <!-- Editor -->
      <div class="picker-container">
        <section
          class="stretch media-area"
          data-test="multiPickerEditor"
          @mouseover="isHovered = true"
          @mouseleave="isHovered = false"
          @click.prevent="openModal">
          <loading-spinner
            v-if="loading"
            size="sm"
            theme="dark" />
          <template v-if="!loading">
            <div
              v-if="!hasSelectedMedia && !isReadonly"
              class="stretch nothing-selected">
              <div class="stretch nothing-selected inner">
                <uc-add-button v-bind="{ label }" />
              </div>
            </div>
            <template v-if="hasSelectedMedia">
              <div class="stretch">
                <uc-image
                  v-if="isImage"
                  :src="selectedMedia.url"
                  :alt="selectedMedia.name"
                  fit="scaleDown" />
                <div
                  v-else
                  class="uc-image-picker-editor--multi-select__preview">
                  <file-icon v-if="isFile" />
                  <folder-icon v-else />
                  <p class="name">
                    {{ selectedMedia.name }}
                  </p>
                </div>
              </div>
              <div
                v-if="isHovered && !isRequired"
                class="action action--remove">
                <uc-button-round
                  theme="white"
                  size="s"
                  floating
                  @click.stop="reset()">
                  <remove-icon />
                </uc-button-round>
              </div>
              <div
                v-if="count > 1"
                class="meta">
                {{ $t('ucImagePicker.additionalSelectedMediaCount', { count: count - 1 }) }}
              </div>
            </template>
          </template>
        </section>
      </div>
    </editor-wrapper>

    <!-- Image picker -->
    <uc-modal
      v-if="isModalOpen"
      size="lg"
      :title="$t('ucImagePicker.selectMediaHeader')"
      sticky-footer
      @hide="closeModal">
      <div class="uc-image-picker-editor__modal">
        <use-navigator-data-provider :content-type="contentType">
          <template #default="{ data, getData, isLoading, isEmptyRootState }">
            <image-picker-multi-select
              v-bind="{ getData, isLoading, isEmptyRootState, selected, contentType }"
              :nodes="data"
              :required="isRequired"
              @nodesSelected="onNodesSelected" />
          </template>
        </use-navigator-data-provider>
      </div>
    </uc-modal>
  </div>
</template>

<script>
import UcImage from '@/components/generic/uc-image'
import UcModal from '@/components/widgets/uc-modal'
import UcAddButton from '@/components/forms/uc-button-add/uc-button-add'
import UcButtonRound from '@/components/forms/uc-button-round'
import { Close16Normal as RemoveIcon } from '@/assets/icons'
import FolderIcon from '@/assets/illustrations/folder-image'
import FileIcon from '@/assets/illustrations/file-image'
import editorMixin from '@/components/editors/editor-mixin'
import { UseNavigatorDataProvider } from '@/components/generic/uc-navigator'
import ImagePickerMultiSelect from '@/components/editors/uc-image-picker-editor/uc-image-picker/uc-image-picker--multi-select'
import { getSelectedMediaByGuids } from '@/components/editors/uc-image-picker-editor/uc-image-picker/uc-image-picker.http'
import Constants from '@/constants.js'
import { getGuidFromNode } from '@/components/generic/uc-image-picker/uc-image-picker.utils'
import LoadingSpinner from '@/components/widgets/loading-spinner'

export default {
  name: 'UcImagePickerEditorMultiSelect',
  components: {
    ImagePickerMultiSelect,
    UseNavigatorDataProvider,
    UcImage,
    UcModal,
    UcAddButton,
    UcButtonRound,
    RemoveIcon,
    FolderIcon,
    FileIcon,
    LoadingSpinner,
  },
  mixins: [editorMixin],
  props: {
    contentType: {
      type: String,
      default: 'media',
    },
  },
  data () {
    return {
      isModalOpen: false,
      isHovered: false,
      selectedMedia: {},
      loading: false,
    }
  },
  computed: {
    imageSrc () {
      return this.definition.url
    },
    altText () {
      return this.definition.name
    },
    count () {
      return this.selected.length
    },
    confirmButtonLabel () {
      return `${this.$t('global.done')} (${this.count})`
    },
    hostSystemName () {
      return this.systemInfo.cms
    },
    hasSelectedMedia () {
      return this.selected.length > 0
    },
    isFile () {
      if (!('nodeType' in this.selectedMedia)) {
        return
      }

      return this.selectedMedia.nodeType.toLowerCase() === Constants.NodeType.File
    },
    isImage () {
      if (!('nodeType' in this.selectedMedia)) {
        return
      }

      return this.selectedMedia.nodeType.toLowerCase() === Constants.NodeType.Image
    },
  },
  watch: {
    selected: {
      async handler (value) {
        if (this.compact) {
          return
        }

        this.loading = true
        const result = await getSelectedMediaByGuids(this.contentType, value)
        this.selectedMedia = result.data.length > 0 ? result.data[0] : {}
        this.loading = false
      },
      immediate: true,
    },
  },
  methods: {
    closeModal () {
      this.isModalOpen = false
    },
    openModal () {
      this.isModalOpen = true
    },
    getMediaUrl (node) {
      if ('nodeType' in node && node.nodeType.toLowerCase() === Constants.NodeType.Image) {
        return node.url
      }

      return `./icons/${node.icon}.svg`
    },
    onNodesSelected (nodes) {
      const newGuids = nodes.map(getGuidFromNode)
      const oldGuids = this.selected
      const selectionChanged = JSON.stringify(newGuids) !== JSON.stringify(oldGuids)
      const hasNodes = nodes.length > 0

      if (selectionChanged) {
        this.onChange({
          changeValues: {
            selected: {
              newValue: newGuids,
              oldValue: oldGuids,
            },
            name: {
              newValue: hasNodes ? nodes[0].name : '',
              oldValue: this.definition.name,
            },
            url: {
              newValue: hasNodes ? this.getMediaUrl(nodes[0]) : '',
              oldValue: this.definition.url,
            },
          },
        })
      }

      this.closeModal()
    },
    reset () {
      this.onNodesSelected([])
    },
  },
}
</script>

<style scoped lang="stylus">
.picker-container
  display flex
  flex-direction column
  width 240px
  height 200px

.uc-image-picker-editor--multi-select__compact-image
  width 50px
  height 50px

.media-area
  position relative
  display flex
  align-items center
  justify-content center
  border-radius ($base-unit / 2)
  background-image url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='4' ry='4' stroke='%23e8e8ed' stroke-width='2' stroke-dasharray='4%2c 5' stroke-dashoffset='22' stroke-linecap='round'/%3e%3c/svg%3e")

  .nothing-selected
    display flex
    align-items center
    justify-content center
    cursor pointer

    &.inner
      width 100%
      height 100%
      font-weight 800
      font-size 4em

  .folder
    display flex
    flex-direction column
    align-items center
    justify-content center
    height 100%

  .name
    margin 0
    padding-top $base-unit
    padding-bottom $base-unit
    secondary-petit-text()

  .action
    position absolute
    padding $base-unit

  .action--remove
    top 0
    right 0

  .action--add
    right 0
    bottom 0

  .meta
    position absolute
    bottom $base-unit
    left $base-unit
    padding ($base-unit / 2) $base-unit
    border-radius $base-unit * 1.5
    background-color white
    user-select none
    pointer-events none
    primary-petit-text()

.stretch
  width 100%
  height 100%

.uc-image-picker-editor__modal
  height 600px

.uc-image-picker-editor--multi-select
  cursor pointer

.uc-image-picker-editor--multi-select__preview
  display flex
  flex-direction column
  align-items center
  justify-content center
  height 100%

</style>
