export function generateMockPromoCodes (count = 100) {
  const codes = [...Array(count).keys()].map(i => {
    const index = i + 1
    return {
      guid: index,
      code: `${index}-%-off`,
      maxUses: 1,
      used: 0,
    }
  })

  return codes
}
