<template>
  <div class="relations-modal">
    <uc-modal
      v-if="open"
      size="lg"
      :title="$tc('relationsModal.title')"
      @hide="$emit('hide')">
      <div class="relations-modal__wizard">
        <uc-wizard
          :disable-next="!relationType"
          @change="onChangeStep($event)">
          <!-- Step 1-->
          <template #step-1>
            <div class="relations-modal__step">
              <p
                v-truncate
                class="relations-modal__sub-heading">
                {{ $tc('relationsModal.subHeading', productName, { productName: productName }) }}
              </p>
              <div class="relations-modal__content">
                <uc-select
                  :options="relationTypes"
                  :placeholder="$tc('relationsModal.placeholder')"
                  @selected="onRelationTypeSelected" />
                <p class="relations-modal__body-text">
                  {{ $tc('relationsModal.body') }}
                </p>
                <uc-toggle
                  :checked="isTwoWayRelation"
                  :label="$tc('relationsModal.label')"
                  @change="isTwoWayRelation = !isTwoWayRelation" />
              </div>
            </div>
          </template>

          <!-- Step 2 -->
          <template #step-2>
            <div class="relations-modal__step">
              <p class="relations-modal__sub-heading">
                {{ $tc('relationsModal.relationType') }} {{ relationType.label }}<br>
                <template v-if="isTwoWayRelation">
                  {{ $tc('relationsModal.twoWayRelationship') }}
                </template>
              </p>
              <div class="relations-modal__search">
                <product-search
                  :select-button-text="$tc('global.add')"
                  :content-language="language"
                  show-variants
                  show-families
                  :search-placeholder="$tc('relationsModal.searchPlaceholder')"
                  @selection="onSelection" />
              </div>
            </div>
          </template>
        </uc-wizard>

        <!-- Cancel -->
        <transition name="fade">
          <div
            v-show="isCancellable"
            class="relations-modal__actions">
            <uc-button
              theme="text"
              @click="$emit('hide')">
              {{ $tc('global.cancel') }}
            </uc-button>
          </div>
        </transition>
      </div>
    </uc-modal>
  </div>
</template>

<script>
import UcModal from '@/components/widgets/uc-modal'
import UcSelect from '@/components/forms/uc-select'
import { UcToggle } from '@/components/forms/uc-toggle/'
import UcWizard from '@/components/generic/uc-wizard'
import UcButton from '@/components/forms/uc-button'
import ProductSearch from './product-search.vue'

export default {
  name: 'RelationsModal',
  components: {
    UcModal,
    UcWizard,
    UcButton,
    UcSelect,
    UcToggle,
    ProductSearch,
  },
  props: {
    productName: {
      type: String,
      default: '',
    },
    open: {
      type: Boolean,
      default: false,
    },
    language: {
      type: String,
      default: 'en-US',
    },
    relationTypes: {
      type: Array, // Array of objects: [{ guid, label }]
      required: true,
    },
  },
  data () {
    return {
      relationType: '',
      isTwoWayRelation: true,
      isCancellable: true,
    }
  },
  watch: {
    open () {
      this.relationType = ''
      this.isTwoWayRelation = true
      this.isCancellable = true
    },
  },
  methods: {
    onRelationTypeSelected (relation) {
      this.relationType = relation
    },
    onChangeStep (step) {
      this.isCancellable = step === 1
    },
    onSelection (rows) {
      const payload = { rows: rows, relationType: this.relationType.guid, twoWay: this.isTwoWayRelation }
      this.$emit('selected', payload)
    },
  },
}
</script>

<style lang="stylus" scoped>
.relations-modal__wizard
  display flex
  flex-direction column
  flex-grow 1
  height calc(100vh - 528px)
  min-height 560px

.relations-modal__step
  display flex
  flex-direction column
  align-items center
  width 100%
  height 100%

.relations-modal__actions
  display flex
  flex-direction column
  align-items center
  justify-content center

.relations-modal__sub-heading
  secondary-details-text()
  max-width 100%
  margin 0
  text-align center

.relations-modal__body-text
  secondary-details-text()

.relations-modal__content
  width 500px
  margin-top 130px

.relations-modal__search
  width 100%
  height 100%
  margin-top 8 * $base-unit

</style>
