<template>
  <div
    class="uc-campaign-list"
    :class="cssClassObject">
    <fields-section
      paddingless
      :heading="translations.sectionHeader">
      <!-- Create button -->
      <template #actions>
        <router-link
          #default="{ navigate }"
          :to="{ name: 'campaign-create' }"
          custom>
          <uc-button
            size="sm"
            @click="navigate">
            {{ translations.create }}
          </uc-button>
        </router-link>
      </template>

      <div class="uc-campaign-list__container">
        <!-- State: Error -->
        <div
          v-if="hasError"
          class="uc-campaign-list__state">
          <p>{{ translations.errorMessage }}</p>
        </div>

        <!-- State: Empty -->
        <div
          v-else-if="isEmpty && isIdle"
          class="uc-campaign-list__state">
          <p class="uc-campaign-list__state-empty">
            {{ translations.emptyState }}
          </p>
        </div>

        <!-- State: Initializing -->
        <div
          v-else-if="isInitializing"
          class="uc-campaign-list__state">
          <loading-indicator is-loading />
        </div>

        <!-- State: Idle/Deleting/Patching -->
        <use-sorting
          v-if="!isEmpty && (isIdle || isDeleting || isPatching)"
          v-model="rows"
          selector="tbody"
          :options="{ handle: '.uc-campaign-list__drag-handle', disabled: isDraggingDisabled }"
          @end="onDragEnd">
          <!-- Table of campaigns -->
          <uc-data-grid-selectable
            v-model="selection"
            v-bind="{ columns, rows }"
            row-key="guid"
            :checkbox-position="1"
            :disable-infinite-scroll="hasLoadedAllItems"
            @next="onNext"
            @row-click="navigateToCampaign">
            <!-- Customise columns to contain editors -->
            <template
              v-for="column in columns"
              #[column.key]="{ cell }">
              <component
                :is="getPropertyEditorComponent(cell.editor)"
                :key="column.key"
                :name="cell.label"
                compact
                :definition="{ ...cell, readonly: true }" />
            </template>

            <!-- Drag icon column -->
            <template #drag>
              <div
                class="uc-campaign-list__drag-handle"
                @click.stop>
                <component :is="draggableIconComponent" />
              </div>
            </template>

            <!-- Timeline column -->
            <template #timeline="{ cell }">
              <div class="column-timeline">
                <!-- StartDate -->
                <div class="column-timeline-date">
                  <span class="column-timeline-date-label">
                    {{ translations.timelineStartDate }}
                  </span>
                  <component
                    :is="getPropertyEditorComponent(cell.startDate.editor)"
                    :name="cell.label"
                    compact
                    :definition="{ ...cell.startDate, readonly: true }" />
                </div>
                <!-- EndDate -->
                <div class="column-timeline-date">
                  <span class="column-timeline-date-label">
                    {{ translations.timelineEndDate }}
                  </span>
                  <component
                    :is="getPropertyEditorComponent(cell.endDate.editor)"
                    :name="cell.label"
                    compact
                    :definition="{ ...cell.endDate, readonly: true }" />
                </div>
              </div>
            </template>

            <!-- Stores column -->
            <template #stores="{ cell }">
              {{ formatStores(cell) }}
            </template>

            <!-- Status column -->
            <template #status="{ cell }">
              <div :class="setStatusColor(cell)">
                {{ formatStatus(cell) }}
              </div>
            </template>
          </uc-data-grid-selectable>
        </use-sorting>
      </div>

      <!-- Actions -->
      <transition name="fade-in-up">
        <div
          v-if="!isEmptySelection"
          class="uc-campaign-list__actions-footer">
          <!-- Remove button -->
          <uc-button
            :theme="ucButtonTheme.PRIMARY"
            @click="deleteCampaign">
            {{ translations.delete }}
          </uc-button>
        </div>
      </transition>
    </fields-section>
  </div>
</template>

<script>
import { FieldsSection } from '@/components/sections/fields-section/'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcButton, UcButtonTheme } from '@/components/forms/uc-button/'
import { validateAgainstEnum } from '@/utils/validate-against/'
import capitalize from '@/utils/capitalize.js'
import { UseSorting } from '@/components/wrappers/use-sorting/'
import { Reorder24Thick as ReorderIcon, Block24Thick as BlockIcon } from '@/assets/icons/'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  DELETING: 'deleting',
  PATCHING: 'patching',
  ERROR_UNKNOWN: 'errorUnknown',
}

const ComponentEvent = {
  NEXT: 'next',
  DELETE: 'delete',
  DRAG_END: 'drag-end',
}

const statusColorClass = {
  ACTIVE: 'is-active',
  PAUSED: 'is-paused',
  ENDED: 'is-ended',
  UPCOMING: 'is-upcoming',
}

export default {
  name: 'UcCampaignList',
  components: {
    LoadingIndicator,
    UcDataGridSelectable,
    FieldsSection,
    UcButton,
    UseSorting,
    ReorderIcon,
    BlockIcon,
  },
  props: {
    campaigns: {
      type: Array,
      required: true,
    },
    state: {
      type: String,
      required: true,
      validate: validateAgainstEnum(ComponentState),
    },
    totalCount: {
      type: Number,
      required: true,
    },
  },
  data () {
    return {
      selection: [],
      rows: [],
    }
  },
  computed: {
    ucButtonTheme: () => UcButtonTheme,
    draggableIconComponent () {
      return this.isDraggingDisabled ? 'block-icon' : 'reorder-icon'
    },
    isDraggingDisabled () {
      return this.isPatching ||
        this.campaigns.length === 1 ||
        this.selection.length > 0
    },
    columns () {
      return [
        {
          key: 'drag',
          width: '40px',
          cellStyle: {
            padding: '0',
          },
        },
        {
          key: 'name',
          value: this.translations.name,
          cellStyle: {
            padding: '0 24px 0 8px',
          },
        },
        {
          key: 'timeline',
          value: this.translations.timeline,
          width: '248',
        },
        {
          key: 'stores',
          value: this.translations.stores,
        },
        {
          key: 'childrenCount',
          value: this.translations.childrenCount,
          width: '128',
        },
        {
          key: 'status',
          value: this.translations.status,
          width: '128',
        },
      ]
    },
    cssClassObject () {
      return {
        'is-max-height': this.campaigns.length > 6,
        'has-dragging-disabled': this.isDraggingDisabled,
      }
    },
    hasLoadedAllItems () {
      return this.campaigns.length === this.totalCount
    },

    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    isDeleting () {
      return this.state === ComponentState.DELETING
    },
    isPatching () {
      return this.state === ComponentState.PATCHING
    },
    isErrorUnauthorized () {
      return this.state === ComponentState.ERROR_UNAUTHORIZED
    },
    isErrorUnknown () {
      return this.state === ComponentState.ERROR_UNKNOWN
    },

    isEmpty () {
      return this.campaigns.length === 0
    },
    isEmptySelection () {
      return this.selection.length === 0
    },
    translations () {
      const errorMessages = {
        [ComponentState.ERROR_UNKNOWN]: this.$t('campaigns.campaignsUnknownError'),
      }

      return {
        create: this.$t('global.new'),
        name: this.$t('campaigns.columns.name'),
        timeline: this.$t('campaigns.columns.timeline'),
        timelineStartDate: this.$t('campaigns.columns.timelineStartDate'),
        timelineEndDate: this.$t('campaigns.columns.timelineEndDate'),
        stores: this.$t('campaigns.columns.stores'),
        childrenCount: this.$t('campaigns.columns.childrenCount'),
        status: this.$t('campaigns.columns.status'),
        sectionHeader: `${this.$t('campaigns.heading')} (${this.totalCount})`,
        delete: `${this.$t('global.delete')} (${this.selection.length})`,
        emptyState: this.$t('campaigns.emptyState'),
        errorMessage: errorMessages[this.state] || '',
      }
    },
    hasError () {
      return this.isErrorUnauthorized || this.isErrorUnknown
    },
  },
  watch: {
    campaigns: {
      handler (value) {
        this.rows = value.map(campaign => ({
          priority: campaign.priority,
          name: campaign.name,
          timeline: {
            editor: 'ShortText',
            startDate: campaign.startDate,
            endDate: campaign.endDate,
          },
          stores: campaign.stores,
          childrenCount: campaign.childrenCount,
          status: campaign.status,
          guid: campaign.meta?.entityGuid,
        }))
      },
      immediate: true,
    },
  },
  methods: {
    getPropertyEditorComponent,
    setStatusColor (statusProperty) {
      return statusColorClass[statusProperty.value.toUpperCase()] || ''
    },
    formatStores (storesProperty) {
      const storeNames = storesProperty.value.split('|')
      const hasMultipleValues = storeNames.length > 1

      if (!hasMultipleValues) {
        return storesProperty.value
      }

      return this.$tc('campaigns.stores', storeNames.length, { firstStoreName: storeNames[0], remainingCount: storeNames.length - 1 })
    },
    formatStatus (statusProperty) {
      return capitalize(statusProperty.value)
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    navigateToCampaign (campaign) {
      this.$router.push({
        name: 'campaign-edit',
        params: {
          campaignGuid: campaign.guid,
          entity: this.findCampaignEntity(campaign.guid),
        },
      })
    },
    findCampaignEntity (guid) {
      return this.campaigns.find(campaign => campaign.meta.entityGuid === guid)
    },
    deleteCampaign () {
      this.$emit(ComponentEvent.DELETE, this.selection)
      this.selection = []
    },
    onDragEnd (event) {
      const element = this.rows[event.newIndex]
      this.$emit(ComponentEvent.DRAG_END, { guid: element.guid, priority: event.newIndex + 1 })
    },
  },
}
</script>

<style lang="stylus" scoped>
$list-min-height = $base-unit * 20

.uc-campaign-list
  position relative

.uc-campaign-list__container
  min-height $list-min-height

.uc-campaign-list__state
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  min-height $list-min-height

.uc-campaign-list__state-empty
  secondary-body-text()

.column-timeline
  display flex
  flex-direction column
  width 100%

.column-timeline-date
  display flex
  flex-direction row

.column-timeline-date-label
  width $base-unit * 6
  secondary-body-text()

.uc-campaign-list__actions-footer
  position absolute
  bottom $base-unit * 4
  z-index 1
  display flex
  align-items center
  justify-content center
  width 100%
  pointer-events none

.is-max-height .uc-campaign-list__container
  height $base-unit * 107
  border-bottom $base-border

.is-active
  color $base-color-green

.is-paused
  color $base-color-orange

.is-ended
  color $base-color-text-primary

.is-upcoming
  color $base-color-text-secondary

.uc-campaign-list__drag-handle
  display flex
  align-items center
  justify-content center
  width 100%
  min-width $base-unit * 7 // Prevent it from collapsing when dargging and dropping
  height 100%
  color $base-color-gray-400
  user-select none

.uc-campaign-list__drag-handle:hover
  cursor grab

.sortable-chosen .uc-campaign-list__drag-handle:hover
.sortable-chosen .uc-campaign-list__drag-handle:active
  cursor grabbing

.has-dragging-disabled .uc-campaign-list__drag-handle:hover
  cursor not-allowed
</style>
