<template>
  <div class="uc-definition-picker">
    <div
      v-if="isLoading"
      class="uc-definition-picker__loading-indicator">
      <loading-indicator :is-loading="isLoading" />
    </div>
    <template v-else>
      <template v-if="definitions.length === 0">
        <p class="uc-definition-picker__empty-state">
          {{ emptyStateText }}
        </p>
      </template>
      <template v-if="definitions.length === 1">
        <label
          v-if="!compact"
          class="uc-input-label">{{ currentDefinition.label }}
        </label>
        {{ description }}
      </template>
      <uc-single-select-editor
        v-if="definitions.length > 1"
        name="definition"
        :definition="{ ...currentDefinition, options: definitions }"
        @change="onChange" />
    </template>
  </div>
</template>

<script>
import { nodeTypeApiMap } from '@/constants.js'
import { mapGetters } from 'vuex'
import UcSingleSelectEditor from '@/components/editors/uc-single-select-editor'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import editorMixin from '../editor-mixin.js'

export default {
  name: 'UcDefinitionPicker',
  components: {
    LoadingIndicator,
    UcSingleSelectEditor,
  },
  mixins: [editorMixin],
  data () {
    return {
      availableDefinitions: [],
      isLoading: false,
    }
  },
  computed: {
    ...mapGetters({ currentDetails: 'details/currentDetails' }),
    description () {
      return this.currentDefinition.value
    },
    currentDefinition () {
      return this.currentDetails ? this.currentDetails.definition : this.definition
    },
    emptyStateText () {
      return this.$t('details.noDefinitionOptions', { nodeType: this.$t(`nodeType.${this.currentDetails.meta.nodeType}`) })
    },
    definitions () {
      // If the selected definition has been removed from the system, we add it to the options as disabled. This means it can be pre-selected, changed, but not selected again.
      const isCurrentDefinitionDeleted = !this.availableDefinitions.find(option => option.guid === this.currentDefinition.selected[0])

      if (isCurrentDefinitionDeleted) {
        const deletedDefinition = {
          guid: this.currentDefinition.selected[0],
          label: `${this.currentDefinition.value} (${this.$t('global.removed')})`,
          isDisabled: true,
        }

        return [deletedDefinition, ...this.availableDefinitions]
      }

      return this.availableDefinitions
    },
  },
  async mounted () {
    if (!this.compact) {
      this.isLoading = true
      this.availableDefinitions = await this.loadDefinitions()
      this.isLoading = false
    }
  },
  methods: {
    async loadDefinitions () {
      const definitions = await this.axios.get(`${nodeTypeApiMap[this.currentDetails.meta.nodeType]}/definitions`)
      return definitions.data.map(definition => { return { guid: definition.guid, label: definition.name } })
    },
    onChange (command) {
      this.$emit('change', command)
    },
  },
}
</script>

<style scoped lang="stylus">
.uc-definition-picker__empty-state
  text-align center

.uc-definition-picker__loading-indicator
  display flex
  justify-content center
</style>
