<template>
  <div class="uc-promo-code-editor">
    <!-- Promocode editor -->
    <uc-button
      :theme="ucButtonTheme"
      block
      @click="openModal">
      {{ translations.generate }}
    </uc-button>

    <template v-if="!isInitializing">
      <div class="uc-promo-code-editor__list">
        <!-- Summary -->
        <uc-promo-code-editor-summary
          :download-url="urls.download"
          :total-max-uses="totalMaxUses"
          :total-used="totalUsed" />

        <!-- Loading spinner -->
        <uc-overlay v-if="isLoading">
          <div class="uc-promo-code-editor__list__loading-indicator">
            <loading-indicator :is-loading="isLoading" />
          </div>
        </uc-overlay>
      </div>

      <div class="uc-promo-code-editor__list">
        <!-- Selection -->
        <uc-promo-code-editor-selection
          :rows="codes"
          :has-loaded-all-items="hasLoadedAllItems"
          @next="requestNextPage"
          @remove="deletePromoCodesAsync" />

        <!-- Loading spinner -->
        <uc-overlay v-if="isLoading">
          <div class="uc-promo-code-editor__list__loading-indicator">
            <loading-indicator :is-loading="isLoading" />
          </div>
        </uc-overlay>

        <!-- Notifications -->
        <template v-if="notifications.length > 0">
          <div
            v-for="(notification, index) in notifications"
            :key="index"
            class="uc-promo-code-editor__list__notifications">
            <uc-snackbar
              v-bind="notification"
              @click="clearNotification(index)"
              @hide="clearNotifications" />
          </div>
        </template>
      </div>
    </template>

    <!-- Promocode generator -->
    <uc-modal
      v-if="isModalOpen"
      :title="translations.generatorHeader"
      :size="ucModalSize"
      @hide="closeModal">
      <div class="uc-promo-code-editor__generator">
        <uc-promo-code-generator
          :generate-method="generatePromoCodesAsync"
          @save="createPromoCodesAsync" />
      </div>
    </uc-modal>
  </div>
</template>

<script>
import { UcButton, UcButtonTheme } from '@/components/forms/uc-button/'
import { UcModal, UcModalSize } from '@/components/widgets/uc-modal/'
import { UcPromoCodeGenerator } from './components/uc-promo-code-generator/'
import { UcPromoCodeEditorSummary } from './components/uc-promo-code-editor-summary/'
import { UcPromoCodeEditorSelection } from './components/uc-promo-code-editor-selection/'
import { UcSnackbar, UcSnackbarTheme, UcSnackbarDuration } from '@/components/widgets/uc-snackbar/'
import { ApiStatusCodes } from '@/constants.js'


import { UcOverlay } from '@/components/widgets/uc-overlay/'
import LoadingIndicator from '@/components/widgets/loading-indicator'

import http from '@/services/http.js'
import { PromoCodeService } from './promo-code.service.js'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  LOADING: 'loading',
}

const apiStatusCodes = {
  CREATED: ApiStatusCodes.CREATED,
  UNAUTHORIZED: ApiStatusCodes.NOT_PERMITTED, // we wouldn't get it per item when it comes to promoCodes
  NOT_FOUND: ApiStatusCodes.NOT_FOUND, // could happen if the `Voucher target` is deleted, should be displayed in global notification
  DUPLICATE: ApiStatusCodes.ALREADY_EXIST, // insert is `best effort`, strips out duplicates and returns 201
}

export default {
  name: 'UcPromoCodeEditor',
  components: {
    UcButton,
    UcModal,
    UcPromoCodeGenerator,
    UcPromoCodeEditorSummary,
    UcPromoCodeEditorSelection,
    UcSnackbar,
    UcOverlay,
    LoadingIndicator,
  },
  props: {
    promotionGuid: {
      type: String,
      required: true,
    },
    entityGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      isModalOpen: false,
      promoCodeService: null,
      state: ComponentState.INITIALIZING,
      codes: [],
      currentPage: 1,
      pageSize: 30,
      totalCount: 0,
      totalMaxUses: 0,
      totalUsed: 0,
      notifications: [],
    }
  },
  computed: {
    ucButtonTheme () {
      return UcButtonTheme.SECONDARY
    },
    ucModalSize () {
      return UcModalSize.LG
    },
    urls () {
      return this.promoCodeService.urls
    },
    translations () {
      return {
        generate: this.$tc('ucPromoCodeEditor.generate'),
        generatorHeader: this.$t('ucPromoCodeEditor.header'),
      }
    },
    isEmpty () {
      return this.codes.length === 0
    },
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isLoading () {
      return this.state === ComponentState.LOADING
    },
    hasLoadedAllItems () {
      return this.codes.length === this.totalCount
    },
    hasNoItemsToLoad () {
      return this.totalCount === 0
    },
  },
  async created () {
    this.promoCodeService = new PromoCodeService(http, this.promotionGuid, this.entityGuid)
    await this.fetchPromoCodesAsync()
  },
  methods: {
    closeModal () {
      this.isModalOpen = false
    },
    openModal () {
      this.isModalOpen = true
    },

    async fetchPromoCodesAsync (startAt = 0, limit = this.pageSize) {
      this.state = this.hasNoItemsToLoad
        ? ComponentState.INITIALIZING
        : ComponentState.LOADING

      try {
        const result = await this.promoCodeService.fetchManyAsync({ startAt, limit })
        const data = result.data.length > 0 ? result.data : []
        this.codes = [...this.codes, ...data]

        this.totalMaxUses = Number(result.headers['x-total-max-uses'])
        this.totalUsed = Number(result.headers['x-total-used'])
        this.totalCount = Number(result.headers['x-total-count'])
      } catch (error) {
        console.error(error)
        // TODO(ch10038): We need a way to handle http 500 request errors
      }

      this.state = this.hasNoItemsToLoad
        ? ComponentState.INITIALIZING
        : ComponentState.IDLE
    },

    generatePromoCodesAsync (payload) {
      return this.promoCodeService.generatePromoCodesAsync(payload)
    },

    async createPromoCodesAsync (promoCodes) {
      this.closeModal()
      this.state = ComponentState.LOADING

      const result = await this.promoCodeService.createManyAsync({ promoCodes })
      const data = result.data.length > 0 ? result.data : []

      if (!data.length) {
        return
      }

      this.reset()
      await this.fetchPromoCodesAsync()
      this.showPromoCodesNotification(data)
    },

    async deletePromoCodesAsync (codes) {
      this.state = ComponentState.LOADING

      const guids = codes.map(x => x.guid)
      await this.promoCodeService.deleteManyAsync({ guids })

      this.reset()
      this.clearNotifications()
      await this.fetchPromoCodesAsync()
    },

    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      this.currentPage++
      await this.fetchPromoCodesAsync(startAt)
    },
    reset () {
      this.codes = []
      this.currentPage = 1
    },

    // Methods related to showing notifications. TODO: Move to separate component
    countPromoCodes (promoCodes, statusCode) {
      const matchesStatusCode = promocode => promocode.statusCode === statusCode
      return promoCodes.filter(matchesStatusCode).length
    },
    showPromoCodesNotification (promoCodes) {
      const createdPromoCodesCount = this.countPromoCodes(promoCodes, apiStatusCodes.CREATED)
      const hasPromoCodes = createdPromoCodesCount > 0

      if (hasPromoCodes) {
        const success = this.$tc('ucPromoCodeEditor.messages.success', createdPromoCodesCount, { count: createdPromoCodesCount })
        const notificationMessage = success

        const theme = UcSnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showNotification ({ text, theme, duration = UcSnackbarDuration.SHORT }) {
      this.notifications.push({
        text,
        dismissable: duration === UcSnackbarDuration.INFINITE,
        duration,
        theme,
      })
    },
    clearNotification (index) {
      this.notifications = this.notifications.filter((_notification, i) => i !== index)
    },
    clearNotifications () {
      this.notifications = []
    },
  },
}

</script>

<style lang="stylus" scoped>
.uc-promo-code-editor__list
  position relative
  margin-top $base-unit * 4
  border $base-border

.uc-promo-code-editor__list__loading-indicator
  margin-top $base-unit * 6 // due to `data-grid` header misaligning the spinner

.uc-promo-code-editor__list__notifications
  position absolute
  top $base-unit * 5
  width 100%
  padding $base-unit * 2

.uc-promo-code-editor__generator
  height $base-unit * 75
</style>
