<template>
  <div
    class="uc-audit-list"
    :class="cssClassObject">
    <!-- Table of audits -->
    <uc-data-grid
      v-bind="{ columns, rows }"
      row-key="guid"
      :disable-infinite-scroll="hasLoadedAllItems"
      @next="onNext">
      <!-- Customise columns to contain editors -->
      <template
        v-for="column in columns"
        #[column.key]="{ cell }">
        <component
          :is="getPropertyEditorComponent(cell.editor)"
          :key="column.key"
          compact
          no-currency-code
          :name="cell.label"
          :language="contentLanguage"
          :definition="{ ...cell, readonly: true }" />
      </template>
      <!-- Updated info column -->
      <template #updatedInfo="{ cell }">
        <component
          :is="getPropertyEditorComponent(cell.date.editor)"
          v-truncate
          compact
          :name="cell.label"
          :language="contentLanguage"
          :definition="{ ...cell.date, readonly: true }" />
        <span class="uc-audit-list__updated-by">
          <component
            :is="getPropertyEditorComponent(cell.updatedBy.editor)"
            v-truncate
            compact
            :name="cell.label"
            :language="contentLanguage"
            :definition="{ ...cell.updatedBy, readonly: true }" />
        </span>
      </template>
    </uc-data-grid>
  </div>
</template>

<i18n>
{
  "en": {
    "columns": {
      "date": "Date",
      "updatedBy": "Updated by",
      "updatedInfo": "@:columns.date / @:columns.updatedBy",
      "message": "Message"
    }
  }
}
</i18n>

<script>
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcDataGrid } from '@/components/generic/uc-data-grid/'

const ComponentEvent = {
  NEXT: 'next',
}

export default {
  name: 'UcAuditList',
  components: {
    UcDataGrid,
  },
  props: {
    audits: {
      type: Array,
      required: true,
    },
    totalCount: {
      type: Number,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  computed: {
    columns () {
      return [
        {
          key: 'updatedInfo',
          value: this.$t('columns.updatedInfo'),
          width: 200,
          cellStyle: {
            verticalAlign: 'top',
          },
          headerCellStyle: {
            verticalAlign: 'inherit',
          },
        },
        {
          key: 'message',
          value: this.$t('columns.message'),
          cellStyle: {
            verticalAlign: 'top',
            whiteSpace: 'normal',
          },
          headerCellStyle: {
            verticalAlign: 'inherit',
          },
        },
      ]
    },
    rows () {
      return this.audits.map(row => {
        const { date, updatedBy, message } = row
        return {
          updatedInfo: { date, updatedBy },
          message,
          guid: row.meta?.entityGuid,
        }
      })
    },
    cssClassObject () {
      return {
        'is-max-height': this.audits.length > 4,
      }
    },
    hasLoadedAllItems () {
      return this.audits.length === this.totalCount
    },
  },
  methods: {
    getPropertyEditorComponent,
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
  },
}

</script>

<style lang="stylus" scoped>
.uc-audit-list
  position relative

  &.is-max-height
    height $base-unit * 42
    border-bottom $base-border

.uc-audit-list__updated-by
  secondary-details-text()
</style>
