<template>
  <div
    class="store-navigator-leaf-node"
    data-test="store-navigator-leaf-node"
    :class="cssClassObject"
    @click="onClick">
    <uc-navigator-node>
      <uc-card
        raised
        interactive
        stretch>
        <div class="store-navigator-leaf-node__content">
          <store-icon />
          <p class="store-navigator-leaf-node__name">
            {{ node.name.value }}
          </p>
        </div>
      </uc-card>
    </uc-navigator-node>
  </div>
</template>

<script>
import { UcNavigatorNode } from '@/components/generic/uc-navigator'
import UcCard from '@/components/generic/uc-card'
import { Store48Normal as StoreIcon } from '@/assets/icons'
import { validateAgainstEnum } from '@/utils/validate-against'

export const SizeProp = {
  SM: 'sm',
  MD: 'md',
  LG: 'lg',
}

const ComponentEvents = {
  CLICK: 'click',
}

export default {
  name: 'StoreNavigatorLeafNode',
  components: {
    UcNavigatorNode,
    UcCard,
    StoreIcon,
  },
  props: {
    node: {
      type: Object,
      required: true,
    },
    size: {
      type: String,
      default: SizeProp.MD,
      validator: validateAgainstEnum(SizeProp),
    },
  },
  computed: {
    cssClassObject () {
      return {
        'store-navigator-leaf-node--size-small': this.size === SizeProp.SM,
        'store-navigator-leaf-node--size-medium': this.size === SizeProp.MD,
        'store-navigator-leaf-node--size-large': this.size === SizeProp.LG,
      }
    },
  },
  methods: {
    onClick () {
      this.$emit(ComponentEvents.CLICK)
    },
  },
}
</script>

<style scoped lang="stylus">
$base-height = 70px

.store-navigator-leaf-node
  display flex

.store-navigator-leaf-node__content
  display flex
  align-items center
  height 100%
  padding 3 * $base-unit

.store-navigator-leaf-node__name
  primary-body-text-bold()
  margin-left $base-unit

.store-navigator-leaf-node--size-small
  height $base-height

.store-navigator-leaf-node--size-medium
  height 2 * $base-height

.store-navigator-leaf-node--size-large
  height 4 * $base-height
</style>
