<template>
  <div
    class="uc-promotion-list"
    :class="cssClassObject">
    <fields-section
      paddingless
      :heading="translations.sectionHeader">
      <!-- Add button -->
      <template #actions>
        <router-link
          #default="{ navigate }"
          :to="{ name: 'promotion-create' }"
          custom>
          <uc-button
            :theme="buttonTheme"
            :size="buttonSize"
            @click="navigate">
            {{ translations.create }}
          </uc-button>
        </router-link>
      </template>

      <div class="uc-promotion-list__container">
        <!-- State: Error -->
        <div
          v-if="hasError"
          class="uc-promotion-list__state">
          <p>{{ translations.errorMessage }}</p>
        </div>

        <!-- State: Empty -->
        <div
          v-else-if="states.EMPTY"
          class="uc-promotion-list__state">
          <p class="uc-promotion-list__state-empty">
            {{ translations.emptyState }}
          </p>
        </div>

        <!-- State: Initializing -->
        <div
          v-else-if="states.INITIALIZING"
          class="uc-promotion-list__state">
          <loading-indicator is-loading />
        </div>

        <!-- State: Idle/Patching -->
        <use-sorting
          v-else-if="states.IDLE || states.PATCHING"
          v-model="rows"
          selector="tbody"
          :options="{ handle: '.uc-promotion-list__drag-handle', disabled: disableDragging }"
          @end="onDragEnd">
          <uc-data-grid
            v-bind="{ columns, rows: promotions }"
            row-key="guid"
            :disable-infinite-scroll="hasLoadedAllItems"
            @next="requestNextPage"
            @row-click="navigateToPromotion">
            <template
              v-for="column in columns"
              #[column.key]="{ cell }">
              <component
                :is="getPropertyEditorComponent(cell.editor)"
                :key="column.key"
                :name="cell.label"
                compact
                :definition="{ ...cell, readonly: true }" />
            </template>

            <!-- Drag icon column -->
            <template #drag>
              <div
                class="uc-promotion-list__drag-handle"
                @click.stop>
                <component :is="draggableIconComponent" />
              </div>
            </template>

            <!-- Status column -->
            <template #status="{ cell }">
              <div
                :class="setStatusColor(cell)">
                {{ formatStatus(cell) }}
              </div>
            </template>
          </uc-data-grid>
        </use-sorting>
      </div>
    </fields-section>
  </div>
</template>

<script>
import { UcButton, UcButtonTheme, UcButtonSize } from '@/components/forms/uc-button/'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { FieldsSection } from '@/components/sections/fields-section/'
import { getPropertyEditorComponent } from '@/components/editors/'
import { validateAgainstEnum } from '@/utils/validate-against/'
import { UcDataGrid } from '@/components/generic/uc-data-grid/'
import { UseSorting } from '@/components/wrappers/use-sorting/'
import { Reorder24Thick as ReorderIcon, Block24Thick as BlockIcon } from '@/assets/icons/'
import { mapMutations } from 'vuex'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR_UNKNOWN: 'errorUnknown',
  ERROR_NOT_FOUND: 'errorNotFound',
  PATCHING: 'patching',
}

const ComponentEvent = {
  NEXT: 'next',
  DRAG_END: 'drag-end',
}

const StatusColorClass = {
  PUBLISHED: 'is-published',
  DRAFT: 'is-draft',
}

export default {
  name: 'UcPromotionList',
  components: {
    LoadingIndicator,
    UcDataGrid,
    FieldsSection,
    UcButton,
    UseSorting,
    ReorderIcon,
    BlockIcon,
  },
  props: {
    promotions: {
      type: Array,
      required: true,
    },
    state: {
      type: String,
      required: true,
      validate: validateAgainstEnum(ComponentState),
    },
    totalCount: {
      type: Number,
      required: true,
    },
  },
  data () {
    return {
      rows: [],
    }
  },
  computed: {
    buttonTheme: () => UcButtonTheme.PRIMARY,
    buttonSize: () => UcButtonSize.SMALL,
    disableDragging () {
      return this.states.PATCHING ||
      this.promotions.length === 1
    },
    draggableIconComponent () {
      return this.disableDragging ? 'block-icon' : 'reorder-icon'
    },
    columns () {
      return [
        {
          key: 'drag',
          width: 56,
          cellStyle: {
            padding: '0',
          },
        },
        {
          key: 'name',
          value: this.$t('promotions.columns.name'),
          cellStyle: {
            padding: '0 24px 0 8px',
          },
        },
        {
          key: 'status',
          value: this.$t('promotions.columns.status'),
          width: 200,
        },
      ]
    },
    cssClassObject () {
      return {
        'is-max-height': this.promotions.length > 6,
        'is-dragging-disabled': this.disableDragging,
      }
    },
    hasLoadedAllItems () {
      return this.promotions.length === this.totalCount
    },
    translations () {
      const errorMessages = {
        [ComponentState.ERROR_UNKNOWN]: this.$t('promotions.promotionsUnknownError'),
        [ComponentState.NOT_FOUND]: this.$t('promotions.promotionsNotFoundError'),
      }

      return {
        errorMessage: errorMessages[this.state] || '',
        sectionHeader: `${this.$t('promotions.heading')} (${this.totalCount})`,
        create: this.$t('promotionCreate.title'),
        emptyState: this.$t('promotions.emptyState'),
      }
    },
    states () {
      return {
        EMPTY: this.state === ComponentState.IDLE && this.promotions.length === 0,
        IDLE: this.state === ComponentState.IDLE,
        INITIALIZING: this.state === ComponentState.INITIALIZING,
        ERROR_UNAUTHORIZED: this.state === ComponentState.ERROR_UNAUTHORIZED,
        ERROR_UNKNOWN: this.state === ComponentState.ERROR_UNKNOWN,
        PATCHING: this.state === ComponentState.PATCHING,
      }
    },
    hasError () {
      return this.states.ERROR_UNAUTHORIZED || this.states.ERROR_UNKNOWN
    },
  },
  watch: {
    promotions: {
      handler (value) {
        this.rows = value
      },
      immediate: true,
    },
  },
  methods: {
    ...mapMutations(['clearUndos']),
    getPropertyEditorComponent,
    setStatusColor (statusProperty) {
      return StatusColorClass[statusProperty.value.toUpperCase()] || ''
    },
    formatStatus (statusProperty) {
      return this.$t(`promotions.status.${statusProperty.value}`)
    },
    requestNextPage () {
      this.$emit(ComponentEvent.NEXT)
    },
    navigateToPromotion (promotion) {
      this.clearUndos()
      this.$router.push({
        name: 'promotion-edit',
        params: {
          promotionGuid: promotion.guid,
        },
      })
    },
    onDragEnd (event) {
      const element = this.rows[event.newIndex]
      this.$emit(ComponentEvent.DRAG_END, { name: element.name.value, guid: element.guid, priority: event.newIndex + 1 })
    },
  },
}
</script>

<style lang="stylus" scoped>
$list-min-height = $base-unit * 20

.uc-promotion-list__container
  min-height $list-min-height

.uc-promotion-list__state
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  min-height $list-min-height

.uc-promotion-list__state-empty
  secondary-body-text()

.is-max-height .uc-promotion-list__container
  height $base-unit * 60
  border-bottom $base-border

.is-published
  color $base-color-green

.is-draft
  color $base-color-text-secondary

.uc-promotion-list__drag-handle
  display flex
  align-items center
  justify-content center
  width 100%
  min-width $base-unit * 7 // Prevent it from collapsing when dargging and dropping
  height 100%
  color $base-color-gray-400
  user-select none

.uc-promotion-list__drag-handle:hover
  cursor grab

.sortable-chosen .uc-promotion-list__drag-handle:hover
.sortable-chosen .uc-promotion-list__drag-handle:active
  cursor grabbing

.is-dragging-disabled .uc-promotion-list__drag-handle:hover
  cursor not-allowed

// Style ghost item, since forceFallback is set to true: https://github.com/SortableJS/Sortable#forcefallback-option
>>> .sortable-fallback
  display flex
  align-items center
  justify-content space-between
  width 800px // Width of table
  min-width 800px

  td
    display flex
    align-items center

  td:nth-child(2)
    flex 1 0 auto

  td:nth-child(3)
    flex 0 1 200px // Width of cell
</style>
