<template>
  <uc-infinite-scroller
    v-bind="{ scrollTo }"
    @next="next">
    <ul class="uc-list">
      <slot
        v-for="item in items"
        :item="item" />
    </ul>
  </uc-infinite-scroller>
</template>

<script>
import { UcInfiniteScroller } from '@/components/generic/uc-infinite-scroller/'

const ComponentEvent = {
  NEXT: 'next',
}

export default {
  name: 'UcList',
  components: {
    UcInfiniteScroller,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
    scrollTo: {
      type: Array,
      default: () => [],
      validator: (value) => value.length <= 2 && value.every(entry => Number.isInteger(entry)),
    },
  },
  methods: {
    next () {
      this.$emit(ComponentEvent.NEXT)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-list
  position relative
  margin 0
  padding-left 0
  list-style none
  user-select none
</style>
