<template functional>
  <component
    :is="props.tag"
    class="uc-card"
    data-test="card"
    :class="$options.methods.getCssClassObject(props)"
    v-on="listeners">
    <slot />
  </component>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

export const ThemeProp = {
  NEUTRAL: 'neutral',
}

export default {
  name: 'UcCard',
  props: {
    tag: {
      type: String,
      default: 'div',
    },
    theme: {
      type: String,
      default: ThemeProp.NEUTRAL,
      validator: validateAgainstEnum(ThemeProp),
    },
    raised: {
      type: Boolean,
      default: false,
    },
    interactive: {
      type: Boolean,
      default: false,
    },
    stretch: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    getCssClassObject (props) {
      return {
        'uc-card--theme-neutral': props.theme === ThemeProp.NEUTRAL,
        'uc-card--raised': props.raised,
        'uc-card--stretch': props.stretch,
        'is-interactive': props.interactive,
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-card
  border-radius $base-border-radius
  overflow hidden
  user-select none

// modifier: theme-neutral
.uc-card--theme-neutral
  background-color $base-color-gray-300

// modifier: raised
.uc-card--raised
  box-shadow $base-box-shadow-outset-2

  &.is-interactive:active
    box-shadow $base-box-shadow-outset-1

.uc-card--stretch
  width 100%
  height 100%

// state: .is-interactive
.uc-card.is-interactive
  position relative
  cursor pointer
  transition all 40ms ease-in-out

  &::before
    position absolute
    top 0
    right 0
    bottom 0
    left 0
    background-color white
    opacity 0
    transition opacity 40ms ease-in-out
    content ''
    pointer-events none

  &:hover::before
    opacity 0.12

  &:active::before
    opacity 0.155

  &:active
    transform scale($base-transform-scale--active)

</style>
