<template>
  <div class="uc-product-picker">
    <uc-stepper @beforeLeave="onBeforeLeaveStep">
      <!-- Back button -->
      <template #top-left="{ previous, step }">
        <transition name="fade">
          <uc-button
            v-show="step > 1"
            :theme="button.link"
            :size="button.small"
            @click="previous">
            {{ translations.back }}
            <template #before>
              <arrow-back-icon />
            </template>
          </uc-button>
        </transition>
      </template>

      <!-- Step 1-->
      <template #step-1="{ next }">
        <!-- Button: Show selection -->
        <div class="uc-product-picker__preview-button">
          <uc-button
            :size="button.small"
            :disabled="selection.length === 0"
            :theme="button.ghost"
            @click="next">
            {{ translations.previewButtonLabel }}
          </uc-button>
        </div>

        <!-- Product search -->
        <div class="uc-product-picker__step">
          <product-search
            v-model="selection"
            v-bind="{ id }"
            :select-button-text="translations.add"
            :columns="productSearchColumns"
            :content-language="language"
            show-variants
            show-families
            @selection="onSelection" />
        </div>
      </template>

      <!-- Step 2 -->
      <template #step-2>
        <div class="uc-product-picker__step">
          <p
            v-truncate
            class="uc-product-picker__sub-heading">
            {{ translations.subheading }}
          </p>
          <div class="uc-product-picker__data-grid">
            <h2 class="uc-product-picker__header">
              {{ translations.heading }}
            </h2>

            <!-- Selected products -->
            <uc-data-grid-selectable
              v-model="selectedItems"
              :columns="columns"
              :rows="rows"
              row-key="guid"
              disable-infinite-scroll>
              <template #image="{ cell }">
                <div class="uc-product-picker__image">
                  <uc-image
                    :src="cell"
                    :fit="imageFit" />
                </div>
              </template>
            </uc-data-grid-selectable>

            <!-- Add button -->
            <transition name="fade-in-up">
              <div
                v-show="selection.length > 0"
                class="uc-product-picker__add-button">
                <uc-button
                  floating
                  @click="onSelection(selectedItems)">
                  {{ translations.addButtonLabel }}
                </uc-button>
              </div>
            </transition>
          </div>
        </div>
      </template>
    </uc-stepper>
  </div>
</template>

<script>
import ProductSearch from '@/apps/products/components/product-search/'
import { UcButton, UcButtonTheme, UcButtonSize } from '@/components/forms/uc-button/'
import UcStepper from '@/components/generic/uc-stepper/'
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcImage, UcImageFitProp } from '@/components/generic/uc-image/'
import createId from '@/utils/id-generator.js'
import { ArrowBack16Normal as ArrowBackIcon } from '@/assets/icons/'

const ComponentEvent = {
  ADD: 'add',
}

export default {
  name: 'UcProductPicker',
  components: {
    UcStepper,
    UcButton,
    ProductSearch,
    ArrowBackIcon,
    UcDataGridSelectable,
    UcImage,
  },
  props: {
    language: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      rows: [],
      selectedItems: [],
      selection: [],
    }
  },
  computed: {
    imageFit: () => UcImageFitProp.CONTAIN,
    button: () => ({
      small: UcButtonSize.SMALL,
      ghost: UcButtonTheme.GHOST,
      link: UcButtonTheme.LINK,
    }),
    productSearchColumns: () => ({
      primaryImage: { width: '90px' },
      name: { width: '192px' },
      sku: { width: '150px' },
      variantSku: { width: '150px' },
    }),
    id: () => createId(),
    columns () {
      return [
        { key: 'image', value: this.$t('global.columns.image'), width: '90px' },
        { key: 'name', value: this.$t('global.columns.name') },
        { key: 'sku', value: this.$t('global.columns.sku') },
        { key: 'variantSku', value: this.$t('global.columns.variantSku') },
      ]
    },
    translations () {
      return {
        back: this.$t('global.back'),
        add: this.$t('global.add'),
        previewButtonLabel: `${this.$t('global.selected')} • ${this.selection.length}`,
        addButtonLabel: `${this.$t('global.add')} (${this.selectedItems.length})`,
        heading: `${this.$t('ucProductEditor.previewHeading')} • ${this.selectedItems.length}`,
        subheading: this.$t('ucProductEditor.subHeading'),
      }
    },
  },
  methods: {
    onSelection (rows) {
      this.$emit(ComponentEvent.ADD, rows)
    },
    onBeforeLeaveStep (to) {
      if (to === 1) {
        const findSelectedItemsByGuid = selected => this.selectedItems.find(selectedItem => selected.meta.entityGuid === selectedItem.guid)
        this.selection = this.selection.filter(findSelectedItemsByGuid)
      }

      if (to === 2) {
        this.rows = this.selection.map(item => ({
          image: item.primaryImage?.url,
          name: item.name?.value,
          sku: item.sku?.value,
          variantSku: item.variantSku?.value,
          guid: item.meta?.entityGuid,
        }))

        this.selectedItems = [...this.rows]
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-product-picker
  display flex
  flex-direction column
  flex-grow 1
  height 'calc(100vh - %s)' % ($base-unit * 66)
  min-height $base-unit * 70

.uc-product-picker__preview-button
  align-self flex-end
  margin-bottom $base-unit * 2

.uc-product-picker__step
  display flex
  flex-direction column
  align-items center
  width 100%
  height 100%

.uc-product-picker__sub-heading
  secondary-details-text()
  max-width 100%
  margin 0
  text-align center

.uc-product-picker__data-grid
  display flex
  flex-direction column
  align-items center
  height 100%
  margin-top $base-unit * 5
  border $base-border
  overflow hidden

.uc-product-picker__header
  align-self flex-start
  margin $base-unit * 3
  primary-heading-2()

.uc-product-picker__image
  display flex

.uc-product-picker__add-button
  position absolute
  bottom $base-unit * 5
  z-index 1
</style>
