import Stores from '@/apps/stores/views/stores.vue'
import StoreEdit from '@/apps/stores/views/store-edit.vue'
import StoreCreate from '@/apps/stores/views/store-create.vue'

export function StoresApp ({ router }) {
  router.addRoute({
    path: '/stores/',
    name: 'stores',
    component: Stores,
  })

  router.addRoute('stores', {
    path: 'new',
    name: 'store-create',
    component: StoreCreate,
  })

  router.addRoute('stores', {
    path: ':guid',
    name: 'store-edit',
    component: StoreEdit,
    props: true,
  })
}
