import { EntityService, EntityType } from './_entity.service.js'

export class BillingService extends EntityService {
  constructor (httpClient) {
    super(EntityType.ORDER, httpClient)
  }

  get _definitionUrl () {
    return '/addresses/definitions/full'
  }

  updateEntryAsync ({ orderGuid, cultureCode, payload }) {
    const url = `${this._rootUrl}/${orderGuid}/billing/${cultureCode}`
    return this._httpClient.patch(url, payload)
  }

  fetchManyAsync ({ guid, cultureCode, startAt, limit, searchTerm }) {
    const url = `${this._rootUrl}/${guid}/billing/${cultureCode}`
    return super.fetchEntitiesAsync({
      url,
      params: {
        startAt,
        limit,
        searchTerm,
      },
    })
  }
}
