import faker from 'faker'

const definition = {
  definition: {
    editor: 'DefinitionPicker',
    label: 'Definition',
    guid: 'mock-1933e238-f86d-4239-b120-84cbbf484572',
    placeholder: 'Select definition',
    multilingual: false,
    validation: {
      required: true,
      regex: null,
      errorMessage: null,
      renderInEditor: false,
    },
    options: null,
    description: null,
    isVariantProperty: null,
  },
  category: {
    editor: 'Category',
    label: 'Categories',
    guid: 'mock-26b06eef-f245-4e51-ab56-d00ca2f59b56',
    placeholder: null,
    multilingual: false,
    validation: {
      required: true,
      regex: null,
      errorMessage: null,
      renderInEditor: false,
    },
    options: [],
    description: '',
    isVariantProperty: null,
  },
}

const shallowDefinition = {
  name: 'Category',
  guid: definition.definition.guid,
}

const entityBase = {
  meta: {
    definition: {
      name: shallowDefinition.name,
      guid: shallowDefinition.guid,
    },
    entityGuid: 'mock-53e8ba8a-1710-4cf4-981a-867095a71ea3',
    nodeType: 'categoryCriterion',
  },
  definition: {
    value: shallowDefinition.name,
    selected: [
      shallowDefinition.guid,
    ],
    guid: 'mock-dabe9b37-1425-4c10-990f-720faf26e6aa',
  },
}

const entity = {
  ...entityBase,
}

export const categoryCriterion = {
  shallowDefinition,
  definition,
  entityBase,
  entity,
}

export function generateMockCategories (count = 100) {
  const sequence = [...Array(count).keys()]
  const categories = sequence.map(_ => {
    const guid = `mock-${faker.datatype.uuid()}`
    const name = faker.lorem.word()
    return {
      guid,
      name,
      url: `${faker.lorem.word()}.com/${faker.lorem.word()}/${name}`,
    }
  })
  return categories
}
