import {
  storeService,
  catalogService,
  categoryService,
} from '@/services/'

import { EntityType } from '@/constants'

export const services = {
  root: storeService,
  [EntityType.STORE]: catalogService,
  [EntityType.CATALOG]: catalogService,
  [EntityType.CATEGORY]: categoryService,
}

export function mapEntity (entity) {
  const nodeType = entity?.meta?.nodeType
  if (nodeType === EntityType.STORE) {
    return {
      name: entity.name.value,
      nodeType: entity.meta.nodeType,
      guid: entity.meta.entityGuid,
      childrenCount: entity.meta.childrenCount,
    }
  }
  return entity
}

export function mapEntityUrl (entity) {
  const urls = {
    [EntityType.STORE]: `stores/${entity.guid}/catalogs`,
    [EntityType.CATALOG]: `catalogs/${entity.guid}/categories`,
    [EntityType.CATEGORY]: `categories/${entity.guid}/categories`,
  }
  return urls[entity.nodeType] || ''
}

export function mapEntityForDisplay (entity) {
  return {
    category: entity,
    guid: entity.guid,
  }
}

const blacklistedNodeTypes = [
  EntityType.STORE,
  EntityType.CATALOG,
]

export const UcCategoryEditorService = {
  services,
  mapEntity,
  mapEntityUrl,
  mapEntityForDisplay,
  blacklistedNodeTypes,
}
