import { EntityService, EntityType } from './_entity.service.js'

export class AuditService extends EntityService {
  constructor (httpClient) {
    super(EntityType.AUDIT, httpClient)
  }

  fetchManyAsync ({ orderGuid, cultureCode, startAt, limit }) {
    const url = `orders/${orderGuid}/audits/${cultureCode}`
    return super.fetchEntitiesAsync({ url, params: { startAt, limit } })
  }

  createOneAsync ({ orderGuid, cultureCode, message }) {
    const url = `orders/${orderGuid}/audits/${cultureCode}`
    return this._httpClient.post(url, { message })
  }
}
