import { rest } from 'msw'
import { orderDiscounts, orderDiscountDefinitions, generateOrderDiscounts } from '@/mocks/entities/order-discounts/'
import { getMockGuidForStatusCode } from '@/mocks/constants.js'
import { ApiStatusCodes } from '@/constants.js'

const RESPONSE_DELAY = 200
let entries = orderDiscounts
export const rootOrderDiscountHandlers = [
  rest.get('/ucommerceapi/v2/orders/discounts/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(orderDiscountDefinitions),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/:orderGuid/discounts/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))
    let totalCount = 0
    let resultCount = 0
    let response
    let statusCode

    switch (req.params.orderGuid) {
      case 'mock-guid-empty':
        statusCode = 200
        response = []
        break
      case getMockGuidForStatusCode(400):
        statusCode = 400
        response = { Message: 'Mock 400: Provided culture code is invalid.' }
        break
      case getMockGuidForStatusCode(401):
        statusCode = 401
        response = { Message: 'Mock 401: Missing permissions.' }
        break
      case getMockGuidForStatusCode(404):
        statusCode = 404
        response = { Message: 'Mock 404: Order not found.' }
        break
      default:
        statusCode = 200
        totalCount = entries.length
        resultCount = entries.length
        response = entries.slice(startAt, startAt + limit)
    }

    return res(
      ctx.set('x-total-count', totalCount),
      ctx.set('x-result-count', resultCount),
      ctx.set('x-currency', 'DKK'),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(statusCode),
      ctx.json(response),
    )
  }),
  rest.post('/ucommerceapi/v2/orders/:orderGuid/discounts/:cultureCode', (req, res, ctx) => {
    const newEntry = generateOrderDiscounts()[0]
    newEntry.description.value = req.body?.details
    newEntry.amountOffTotal.value = req.body?.amount
    newEntry.campaignName.value = req.body?.campaign
    newEntry.campaignItemName.value = req.body?.promotion
    entries = [newEntry, ...entries]

    const response = [{
      guid: newEntry.meta.entityGuid,
      message: 'success',
      statusCode: ApiStatusCodes.OK,
    }]

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
  rest.delete('/ucommerceapi/v2/orders/:orderGuid/discounts', (req, res, ctx) => {
    entries = entries.filter(orderLine => !req.body.includes(orderLine.meta.entityGuid))
    const response = req.body.map(guid => ({
      guid,
      message: 'success',
      statusCode: ApiStatusCodes.OK,
    }))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

]
