<template>
  <transition name="fade">
    <overlay-modal>
      <div class="store">
        <!-- State: Idle -->
        <details-layout
          v-if="isIdle"
          @back-click="closeStore">
          <generic-details
            @deleted="deleteStore"
            @resurrected="resurrectStore" />
        </details-layout>

        <!-- State: Initializing -->
        <div
          v-else-if="isInitializing"
          class="store__state">
          <loading-indicator is-loading />
        </div>

        <!-- State: Error -->
        <div
          v-else-if="hasError"
          class="store__state">
          <p>{{ $t('stores.noStoreFound') }}</p>
          <uc-button
            theme="text"
            @click="closeStore">
            <template #before>
              <back-icon />
            </template>
            {{ $t('global.back') }}
          </uc-button>
        </div>
      </div>
    </overlay-modal>
  </transition>
</template>

<script>
import OverlayModal from '@/components/widgets/overlay-modal'
import DetailsLayout from '@/layouts/details-layout'
import GenericDetails from '@/components/generic/details-view/generic-details'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import UcButton from '@/components/forms/uc-button'
import { ArrowBack16Normal as BackIcon } from '@/assets/icons'

import { storeService } from '@/services'
import { createNamespacedHelpers } from 'vuex'

const detailsStore = createNamespacedHelpers('details')

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR: 'error',
}

const ComponentEvent = {
  DELETE: 'delete',
  RESURRECT: 'resurrect',
}

export default {
  name: 'StoreEdit',
  components: {
    OverlayModal,
    DetailsLayout,
    GenericDetails,
    LoadingIndicator,
    UcButton,
    BackIcon,
  },
  props: {
    guid: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
    entity: {
      type: Object,
      default: undefined,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
    }
  },
  computed: {
    ...detailsStore.mapGetters(['currentDetails']),
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
  },
  async created () {
    try {
      const entity = this.entity ? this.entity : (await storeService.fetchEntityAsync({ guid: this.guid, cultureCode: this.contentLanguage })).data
      this.setDetails({ details: entity })
      this.state = ComponentState.IDLE
    } catch {
      this.state = ComponentState.ERROR
    }
  },
  destroyed () {
    this.clearDetails()
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails', 'clearDetails']),
    closeStore () {
      this.$router.back()
    },
    deleteStore (event) {
      this.$emit(ComponentEvent.DELETE, event.guid)
      this.$router.replace({ name: 'stores' })
    },
    resurrectStore (guid) {
      this.$emit(ComponentEvent.RESURRECT, guid)
    },
  },
}
</script>

<style lang="stylus" scoped>
.store__state
  display flex
  flex-direction column
  align-items center
  justify-content center
  height 100vh
</style>
