export class PromoCodeService {
  constructor (httpClient, promotionGuid, criterionGuid) {
    this._httpClient = httpClient
    this._promotionGuid = promotionGuid
    this._criterionGuid = criterionGuid
  }

  get urls () {
    const resourceUrl = `/promotions/${this._promotionGuid}/criteria/${this._criterionGuid}/promocodes`
    return {
      promoCodes: resourceUrl,
      generate: `${resourceUrl}/generate`,
      download: `${this._httpClient.defaults.baseURL}/${resourceUrl}/download`,
    }
  }

  fetchManyAsync ({ startAt, limit }) {
    const payload = {
      params: { startAt, limit },
    }
    return this._httpClient.get(this.urls.promoCodes, payload)
  }

  async generatePromoCodesAsync ({ length, count, prefix, suffix }) {
    const payload = {
      params: { length, count, prefix, suffix },
    }

    const result = await this._httpClient.get(this.urls.generate, payload)
    const data = result.data.length > 0 ? result.data : []

    // transform into array of PromoCode
    const codes = data.map(code => {
      return {
        code,
        maxUses: 1,
      }
    })

    return codes
  }

  createManyAsync ({ promoCodes }) {
    return this._httpClient.post(this.urls.promoCodes, promoCodes)
  }

  deleteManyAsync ({ guids }) {
    const payload = {
      data: guids,
    }
    return this._httpClient.delete(this.urls.promoCodes, payload)
  }
}
