<template>
  <div class="products">
    <!-- Header -->
    <portal to="header">
      <fragment>
        <button-toggle-group
          v-model="navigationType"
          :values="navigation">
          <template #default="{ value }">
            <span
              :data-test="value"
              class="products__icon">
              <component :is="getIconType(value)" />
            </span>
            {{ value }}
          </template>
        </button-toggle-group>
        <div class="products__header">
          <transition name="fade">
            <div
              v-show="!isNavigationSearch"
              class="products__selector">
              <store-selector />
            </div>
          </transition>
          <language-selector />
        </div>
      </fragment>
    </portal>

    <transition
      appear
      name="fade"
      mode="out-in"
      @after-enter="toggleSearchFocus"
      @after-leave="toggleSearchFocus">
      <keep-alive>
        <products-search
          v-if="isNavigationSearch"
          :key="productSearchKey"
          :ref="searchListId"
          v-bind="{ isSearchFocused, contentLanguage }"
          :list-id="searchListId"
          @open-entity-editor="openEntityEditor"
          @close-entity-editor="closeEntityEditor"
          @toggle-dialog="toggleDialog" />

        <products-navigate
          v-else
          :ref="navigateListId"
          v-bind="{ contentLanguage, displayedNode }"
          :list-id="navigateListId"
          @open-entity-editor="openEntityEditor"
          @close-entity-editor="closeEntityEditor"
          @toggle-dialog="toggleDialog" />
      </keep-alive>
    </transition>

    <!-- Create product dialog -->
    <create-product
      v-if="isDialogOpen"
      v-bind="{ contentLanguage, categoryGuid }"
      @hideDialog="toggleDialog"
      @addProductToListAndOpenItsDetails="addProductToListAndOpenDetails" />

    <!-- Product details -->
    <overlay-modal v-if="hasDetails">
      <details-layout @back-click="onBackClick">
        <generic-details
          ref="GenericDetails"
          @deleted="deleteEntityAndCloseDetails"
          @openVariantList="openEntityEditor" />
      </details-layout>
    </overlay-modal>
  </div>
</template>

<script>
import { Fragment } from 'vue-fragment'
import LanguageSelector from '@/components/widgets/language-selector.vue'
import StoreSelector from '@/components/widgets/store-selector.vue'
import GenericDetails from '@/components/generic/details-view/generic-details'
import OverlayModal from '@/components/widgets/overlay-modal'
import DetailsLayout from '@/layouts/details-layout'
import ButtonToggleGroup from '@/components/generic/button-toggle-group'
import { Search24Normal as SearchIcon, Tree24Normal as TreeIcon } from '@/assets/icons'
import ProductsSearch from './products-search'
import ProductsNavigate from './products-navigate'
import CreateProduct from '../components/create-product.vue'

import { createNamespacedHelpers } from 'vuex'

const builderStore = createNamespacedHelpers('builder')
const detailsStore = createNamespacedHelpers('details')

const NavigationType = {
  SEARCH: 'Search',
  NAVIGATE: 'Navigate',
}

const IconType = {
  SEARCH: 'search-icon',
  NAVIGATE: 'tree-icon',
}

export default {
  name: 'Products',
  components: {
    LanguageSelector,
    StoreSelector,
    ProductsSearch,
    GenericDetails,
    OverlayModal,
    DetailsLayout,
    CreateProduct,
    ProductsNavigate,
    ButtonToggleGroup,
    SearchIcon,
    TreeIcon,
    Fragment,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      isDialogOpen: false,
      isSearchFocused: false,
      productSearchKey: 0,
      navigationType: NavigationType.SEARCH,
    }
  },
  computed: {
    ...builderStore.mapGetters(['displayedNode']),
    ...detailsStore.mapGetters(['currentDetails']),
    searchListId () {
      return NavigationType.SEARCH
    },
    categoryGuid () {
      return this.isNavigationSearch ? null : this.displayedNode.guid
    },
    navigateListId () {
      return NavigationType.NAVIGATE
    },
    currentListId () {
      return this.navigationType
    },
    hasDetails () {
      return !!this.currentDetails
    },
    isNavigationSearch () {
      return this.navigationType === NavigationType.SEARCH
    },
    navigation () {
      return Object.values(NavigationType)
    },
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails', 'clearDetails']),
    toggleDialog () {
      this.isDialogOpen = !this.isDialogOpen
    },
    async deleteEntityAndCloseDetails (payloadObject) {
      const componentId = payloadObject.isVariant ? 'GenericDetails' : this.currentListId
      this.closeEntityEditor()
      await this.$nextTick()
      this.$refs[componentId].removeItems([payloadObject.guid])
    },
    openEntityEditor (row) {
      this.setDetails({ details: row })
    },
    closeEntityEditor () {
      this.clearDetails()
    },
    addProductToListAndOpenDetails (row) {
      this.$refs[this.currentListId].addItems([row])
      // Force products-search to rerender to display the newly added product: https://michaelnthiessen.com/key-changing-technique/
      this.productSearchKey++
      this.openEntityEditor(row)
    },
    onBackClick () {
      this.closeEntityEditor()
    },
    toggleSearchFocus () {
      this.isSearchFocused = !this.isSearchFocused
    },
    getIconType (type) {
      const navigationKey = Object.keys(NavigationType).find(key => NavigationType[key] === type)
      return IconType[navigationKey]
    },
  },
}
</script>

<style lang="stylus" scoped>
.products
  min-width $base-layout-width

.products__header
  display flex
  align-items center
  justify-content flex-end
  width 100%

.products__selector
  margin-right 2 * $base-unit

.products__icon
  display inline-flex
  margin-right ($base-unit / 2)
</style>
