<template>
  <div class="catalogs">
    <portal to="header">
      <div class="catalogs__header">
        <div class="catalogs__selector">
          <store-selector />
        </div>
        <language-selector />
      </div>
    </portal>
    <section class="catalogs__builder">
      <catalog-builder ref="builder" />
    </section>
    <details-loader
      @deleted="selectParent"
      @resurrected="reload"
      @change="detailsChanged" />
  </div>
</template>

<script>
import CatalogBuilder from '../components/catalog-builder/catalog-builder.vue'
import DetailsLoader from '@/components/generic/details-view/details-loader.vue'
import LanguageSelector from '@/components/widgets/language-selector.vue'
import StoreSelector from '@/components/widgets/store-selector.vue'
import { createNamespacedHelpers } from 'vuex'
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'Catalogs',
  components: {
    CatalogBuilder,
    DetailsLoader,
    LanguageSelector,
    StoreSelector,
  },
  computed: {
    ...builderStore.mapGetters(['sectionHeight', 'displayedNode', 'treeSections']),
  },
  methods: {
    ...builderStore.mapMutations(['setSectionHeight', 'setCurrentNodeName']),

    async selectParent () {
      await this.$refs.builder.selectParent({ clearUndo: false })
    },

    detailsChanged (command) {
      if (command.save.name) {
        this.setCurrentNodeName(command.save.name.value)
      }
    },

    reload (nodeGuid) {
      const builder = this.$refs.builder
      builder.getCatalogs()
      builder.selectNodeWithGuid(nodeGuid)
    },
  },
}
</script>

<style lang="stylus" scoped>
.catalogs__header
  display flex
  align-items center
  justify-content flex-end
  width 100%

.catalogs__selector
  margin-right 2 * $base-unit

.catalogs__builder
  width 100%
  margin-bottom 4 * $base-unit
</style>
