export function makeDefinitionsByDefinitionGuid (definitions) {
  return definitions.reduce((acc, definition) => {
    return { ...acc, [definition.definition.guid]: definition }
  }, {})
}

export function mergeAllData (definitions, entities) {
  const propertyDefaults = {
    value: undefined,
    selected: [],
    url: undefined,
    name: undefined,
    errors: [],
  }

  return entities.map(entity => {
    const definition = definitions[entity.meta.definition.guid]
    const definitionFieldNames = Object.getOwnPropertyNames(definition)
    const mergedEntity = {}

    definitionFieldNames.forEach(definitionFieldName => {
      const definitionField = definition[definitionFieldName]

      // Do not include product variant fields for products
      if (definitionField.isVariantProperty === true && entity.meta.nodeType === 'product') {
        return
      }

      // Do not include product fields for product variants
      if (definitionField.isVariantProperty === false && entity.meta.nodeType === 'productVariant') {
        return
      }

      const property = entity[definitionFieldName]

      mergedEntity[definitionFieldName] = {
        ...definitionField,
        ...propertyDefaults,
        ...property,
      }
    })

    return {
      ...mergedEntity,
      ...{ meta: entity.meta },
    }
  })
}
