import { rest } from 'msw'
import { categories } from '@/mocks/entities/categories/'

const RESPONSE_DELAY = 200

export const rootCategoryHandlers = [
  rest.get('/ucommerceapi/v2/catalogs/:catalogGuid/categories', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const response = categories.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', categories.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get('/ucommerceapi/v2/categories/:categoryGuid/categories', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const response = categories.slice(startAt, startAt + limit)
    return res(
      ctx.set('x-total-count', categories.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
]
