<template>
  <div class="uc-date-picker">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formatter(value)"
      :required="isRequired">
      <uc-popup-calendar
        v-bind="{ label, value, hasErrors, language }"
        :readonly="isReadonly"
        :clearable="!isRequired"
        @selected="onSelected" />
    </editor-wrapper>
  </div>
</template>

<script>
import UcPopupCalendar from '@/components/forms/uc-popup-calendar'
import editorMixin from '../editor-mixin'
import { Format as format, locales } from '@/utils/date-formatter'

const dateFormats = {
  default: 'MMMM D YYYY',
}

export default {
  name: 'UcDatePicker',
  components: {
    UcPopupCalendar,
  },
  mixins: [editorMixin],
  computed: {
    dateFormat () {
      return dateFormats[this.language] || dateFormats.default
    },
  },
  methods: {
    formatter (date) {
      return this.value ? format(date, this.dateFormat, locales[this.language]) : ''
    },
    onSelected (newValue) {
      const oldValue = this.value
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null) && newValue === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue,
      })
    },
  },
}
</script>
