import * as types from './mutation-types'
import Vue from 'vue'

const mutations = {
  [types.SET_DETAILS] (state, { details, shouldUpdateLastElement }) {
    if (shouldUpdateLastElement) {
      state.details.pop()
      state.details.push(details)
    } else {
      if (details === null) {
        state.details.pop()
      } else {
        state.details.push(details)
      }
    }
  },
  [types.CLEAR_DETAILS] (state) {
    state.details.pop()
  },
  [types.RESET_DETAILS] (state) {
    state.details = []
  },
  [types.SET_PRICE_GROUPS] (state, priceGroups) {
    state.priceGroups = priceGroups
  },
  [types.UPDATE_DETAILS] (state, save) {
    for (const propertyName in save) {
      const property = state.details[state.details.length - 1][propertyName]
      if (property) {
        property.value = save[propertyName].value
        property.selected = save[propertyName].selected
        property.url = save[propertyName].url
        property.name = save[propertyName].name
        // TODO: Figure out how to reflect any media changes updates in the list
      }
    }
  },
  [types.SET_VALIDATION_ERRORS] (state, args) {
    const { errors, save } = args
    state.validationErrors = errors
    const currentDetails = state.details[state.details.length - 1]

    for (const propertyName in currentDetails) {
      if (save[propertyName]) {
        const property = currentDetails[propertyName]
        if (property === Object(property)) {
          Vue.set(property, 'errors', null)
        }
      }
    }
    for (const errorProperty in errors) {
      const propertyInError = currentDetails[errorProperty]
      if (propertyInError === Object(propertyInError)) {
        Vue.set(propertyInError, 'errors', errors[errorProperty])
      }
    }
  },
  [types.CLEAR_VALIDATION_ERRORS] (state) {
    state.validationErrors = {}
  },
}

export default mutations
