<template>
  <div
    v-custom-scroll-bar
    class="uc-infinite-scroller"
    data-test="infinite-scroller">
    <!-- Body -->
    <slot />

    <!-- Trigger -->
    <use-intersection-observer
      v-if="isScrollable && !disabled"
      :root="$el"
      :root-margin="rootMargin"
      :threshold="threshold"
      @enter="next">
      <slot name="trigger">
        <div />
      </slot>
    </use-intersection-observer>
  </div>
</template>

<script>
import UseIntersectionObserver from '@/components/wrappers/use-intersection-observer'

const ComponentEvent = {
  NEXT: 'next',
}

export default {
  name: 'UcInfiniteScroller',
  components: {
    UseIntersectionObserver,
  },
  props: {
    threshold: {
      type: Array,
      required: false,
      default: () => [0],
    },
    rootMargin: {
      type: String,
      required: false,
      default: '0px 0px 100% 0px',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    scrollTo: {
      type: Array,
      default: () => [],
      validator: (value) => value.length <= 2 && value.every(entry => Number.isInteger(entry)),
    },
  },
  data () {
    return {
      isScrollable: false,
    }
  },
  watch: {
    scrollTo ([x, y]) {
      this.$el.scrollTo(x, y)
    },
  },
  mounted () {
    this.initResizeObserver()
  },
  methods: {
    next () {
      this.$emit(ComponentEvent.NEXT)
    },
    initResizeObserver () {
      const resizeObserver = new ResizeObserver(entries => {
        this.isScrollable = entries[0].target.scrollHeight > entries[0].target.clientHeight
      })

      resizeObserver.observe(this.$el)
      this.$on('hook:destroyed', () => resizeObserver.disconnect())
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-infinite-scroller
  height 100%
  overflow-y auto
</style>
