import { EntityService, EntityType } from './_entity.service.js'

export class UriWhitelistService extends EntityService {
  constructor (httpClient) {
    super(EntityType.URI_WHITELIST, httpClient)
  }

  fetchManyAsync ({ storeGuid, cultureCode, startAt, limit }) {
    const url = `/settings/api-access/redirectUrls/${storeGuid}/${cultureCode}`
    return super.fetchEntitiesAsync({ url, params: { startAt, limit } })
  }

  createOneAsync ({ storeGuid, cultureCode, uri }) {
    const url = `/settings/api-access/redirectUrl/${storeGuid}/${cultureCode}`
    return this._httpClient.post(url, { uri })
  }

  deleteManyAsync ({ guids }) {
    const url = 'settings/api-access/redirectUrls'
    const payload = {
      data: guids,
    }
    return this._httpClient.delete(url, payload)
  }
}
