<template>
  <div
    class="product-search">
    <!-- Search input -->
    <div class="product-search__input">
      <uc-search
        v-model.trim="searchTerm"
        :placeholder="searchPlaceholder" />
    </div>

    <!-- Search results -->
    <div class="product-search__results">
      <list
        :id="listId"
        v-bind="{
          dataUrl,
          defaultColumnsConfiguration,
          selectableRows
        }"
        :header="$tc('list.products')"
        node-type="product"
        show-variants
        display-all-columns
        :value="selection"
        @sortBy="onSortBy"
        @input="onInput"
        @change="onChange">
        <template #actionsFooter="{ selected }">
          <uc-button
            v-if="selectableRows"
            :disabled="disableButton || selected.length === 0"
            floating
            @click="selectProducts(selected)">
            {{ `${selectButtonText} (${ selected.length })` }}
          </uc-button>
        </template>
      </list>
    </div>
  </div>
</template>

<script>
import { UcButton } from '@/components/forms/uc-button/'
import { UcSearch } from '@/components/forms/uc-search/'
import List from '@/components/generic/generic-list/list.vue'
import { isEmpty } from '@/utils/object.utils.js'
import setUrlParam from '@/utils/set-url-param/'

const ComponentEvent = {
  INPUT: 'input',
  SELECTION: 'selection',
  CHANGE: 'change',
}

export default {
  name: 'ProductSearch',
  components: {
    UcSearch,
    UcButton,
    List,
  },
  props: {
    id: {
      type: String,
      default: '',
    },
    columns: {
      type: Object,
      default: () => ({}),
    },
    contentLanguage: {
      type: String,
      default: '',
    },
    selectButtonText: {
      type: String,
      required: true,
    },
    searchPlaceholder: {
      type: String,
      default: function () {
        return this.$tc('global.search')
      },
    },
    selectableRows: {
      type: Boolean,
      default: true, // must default to `true` to not break existing UI flows
    },
    showVariants: {
      type: Boolean,
      default: false,
    },
    showFamilies: {
      type: Boolean,
      default: false,
    },
    value: {
      type: Array,
      default: () => [],
    },
    disableButton: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      searchTerm: '',
      sortBy: '',
      selection: [],
    }
  },
  computed: {
    dataUrl () {
      let url = `/products/${this.contentLanguage}`

      url = setUrlParam(url, 'sort', this.sortBy)
      url = setUrlParam(url, 'searchTerm', this.searchTerm)

      // false must be a string, otherwise it is removed by the `setUrlParam` util.
      url = setUrlParam(url, 'includeVariants', `${this.showVariants}`)
      url = setUrlParam(url, 'includeFamilies', `${this.showFamilies}`)

      return url
    },
    defaultColumnsConfiguration () {
      if (!isEmpty(this.columns)) {
        return this.columns
      }

      if (this.showVariants) {
        return {
          primaryImage: { width: '90px' },
          sku: { width: '150px' },
          variantSku: { width: '150px' },
          name: { width: '308px' },
        }
      }

      return {
        primaryImage: { width: '90px' },
        sku: { width: '100px' },
        name: { width: '508px' },
      }
    },
    listId () {
      if (this.id) {
        return this.id
      }

      return this.showVariants ? 'ProductAndVariantSearch' : 'ProductSearch'
    },
  },
  watch: {
    value: {
      handler (val) {
        this.selection = val
      },
      immediate: true,
    },
  },
  methods: {
    selectProducts (products) {
      this.$emit(ComponentEvent.SELECTION, products)
    },
    onSortBy (sortBy) {
      this.sortBy = sortBy
    },
    onInput (event) {
      this.$emit(ComponentEvent.INPUT, event)
    },
    onChange (event) {
      this.$emit(ComponentEvent.CHANGE, event)
    },
  },
}
</script>
<style scoped lang="stylus">
.product-search
  display flex
  flex-direction column
  width 100%
  height 100%

.product-search__results
  flex 1 0 auto
  height 0

  >>> .list-table-container
    border 1px solid $base-color-gray-50
    border-top none
</style>
