<template>
  <div
    v-on-clickaway="closeDropdown"
    :class="cssClassObject"
    class="uc-dropdown-paged">
    <uc-dropdown-input
      ref="dropdownInput"
      v-bind="{
        value,
        disabled,
        enabled,
        selected,
      }"
      :direction="dropDirection"
      @toggle="onToggle"
      @open="openList" />
    <transition :name="dropDirection">
      <div
        v-show="shouldShowList"
        ref="dropdownList"
        class="uc-dropdown-paged__container">
        <!-- Search -->
        <div class="uc-dropdown-paged__search">
          <uc-search
            dark
            dense
            :value="searchTerm"
            :focus="shouldShowList"
            :clear="shouldShowList"
            @input="onSearchChanged" />
        </div>

        <uc-infinite-scroller
          :style="cssStyleObject"
          :disabled="hasLoadedAllItems"
          :scroll-to="scrollTo"
          @next="onNext">
          <!-- Options -->
          <uc-dropdown-list v-bind="{ options, isLoading, spinnerOffset }">
            <template #default="{ option }">
              <slot v-bind="{ option, searchTerm }" />
            </template>
          </uc-dropdown-list>
        </uc-infinite-scroller>
      </div>
    </transition>
  </div>
</template>

<script>
import { directive as onClickaway } from 'vue-clickaway'
import { UcSearch } from '@/components/forms/uc-search/'
import { UcInfiniteScroller } from '@/components/generic/uc-infinite-scroller/'
import { UcDropdownInput, UcDropdownInputDirection } from '../uc-dropdown-input/'
import { UcDropdownList } from '../uc-dropdown-list/'
import { validateAgainstEnum } from '@/utils/validate-against'

export const DirectionProp = {
  UP: UcDropdownInputDirection.UP,
  DOWN: UcDropdownInputDirection.DOWN,
}

const MAX_LIST_HEIGHT = 230

const ComponentEvent = {
  NEXT: 'next',
  SEARCH: 'search',
  TOGGLE: 'toggle',
}

export default {
  name: 'UcDropdownPaged',
  components: {
    UcSearch,
    UcDropdownInput,
    UcDropdownList,
    UcInfiniteScroller,
  },
  directives: {
    onClickaway,
  },
  props: {
    options: {
      type: Array,
      default: () => ([]),
    },
    collapse: {
      type: Boolean,
      required: true,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      required: true,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    hasLoadedAllItems: {
      type: Boolean,
      default: true,
    },
    direction: {
      type: String,
      default: null,
      validator: validateAgainstEnum(DirectionProp) || null,
    },
  },
  data () {
    return {
      shouldShowList: false,
      searchTerm: '',
      scrollTo: [],
      spinnerOffset: 48, // total height of the search
      dropDirection: this.direction ?? DirectionProp.DOWN,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-dropup': this.dropDirection === DirectionProp.UP,
        'is-dropdown': this.dropDirection === DirectionProp.DOWN,
      }
    },
    cssStyleObject () {
      // computed prop can't be used, since it won't trigger
      const hasLongList = this.options.length > 6
      return {
        height: hasLongList ? `${MAX_LIST_HEIGHT}px` : 'auto',
      }
    },
    value () {
      return this.placeholder
    },
    disabled () {
      return this.readonly
    },
    // toggles between label styles
    enabled () {
      return this.shouldShowList
    },
  },
  watch: {
    collapse (value) {
      this.shouldShowList = !value
    },
    shouldShowList (value) {
      this.scrollTo = value ? [] : [0]
    },
  },
  methods: {
    openList () {
      // If direction prop is explicitly set, no automatic flip
      if (this.direction) {
        return
      }

      // Get height of dropdownList and quickly hide it again
      this.$refs.dropdownList.style.display = ''
      const domRect = this.$refs.dropdownList.getBoundingClientRect()
      this.$refs.dropdownList.style.display = 'none'

      const domRectInput = this.$refs.dropdownInput.$el.getBoundingClientRect()

      const isOverflowing = domRectInput.bottom + domRect.height > document.documentElement.clientHeight
      this.dropDirection = isOverflowing ? DirectionProp.UP : DirectionProp.DOWN
    },
    onToggle (isEnabled) {
      this.shouldShowList = isEnabled
      this.$emit(ComponentEvent.TOGGLE, this.shouldShowList)
    },
    onSearchChanged (searchTerm) {
      // reset scroll
      this.scrollTo = [0]

      this.searchTerm = searchTerm
      this.$emit(ComponentEvent.SEARCH, searchTerm)
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    closeDropdown () {
      this.shouldShowList = false
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-dropdown-paged
  position relative
  width 100%
  user-select none

.uc-dropdown-paged__container
  position absolute
  z-index 3
  width 100%
  border 1px solid $base-border-color
  background-color $base-color-white

  .is-dropdown &
    border-top none
    border-radius 0 0 ($base-unit / 2) ($base-unit / 2)

  .is-dropup &
    bottom $base-unit * 6
    border-bottom none
    border-radius ($base-unit / 2) ($base-unit / 2) 0 0

.uc-dropdown-paged__search
  padding (2 * $base-unit) (2 * $base-unit) 0
</style>
