<template>
  <div
    class="uc-percent-editor"
    :class="cssClassObject">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="compactValue"
      :required="isRequired">
      <div class="uc-percent-editor__input">
        <uc-numeric-input
          v-bind="{ label, placeholder, hasErrors }"
          :readonly="isReadonly"
          :value="numericValue"
          @blur="onBlur">
          <template #after>
            <input-group-cap>
              %
            </input-group-cap>
          </template>
        </uc-numeric-input>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import editorMixin from '../editor-mixin'
import { UcNumericInput } from '@/components/forms/uc-numeric-input/'
import { InputGroupCap } from '@/components/forms/input-group/'

export default {
  name: 'UcPercentEditor',
  components: {
    UcNumericInput,
    InputGroupCap,
  },
  mixins: [editorMixin],
  computed: {
    cssClassObject () {
      return {
        'uc-percent-editor--readonly': this.isReadonly,
      }
    },
    compactValue () {
      return `${this.value} %`
    },
    numericValue () {
      return this.value !== null ? Number(this.value) : null
    },
  },
  methods: {
    onBlur (e) {
      const newValue = e.target.value
      const oldValue = this.value
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null) && newValue === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue,
      })
    },
  },
}
</script>
