<template>
  <div
    class="uc-order-list"
    :class="cssClassObject">
    <fields-section
      paddingless
      :heading="$tc('heading', totalCount)">
      <!-- Create button -->
      <template #actions>
        <uc-button
          data-test="order-create-new-button"
          size="sm"
          @click="openCreateModal">
          {{ $t('global.new') }}
        </uc-button>
      </template>

      <div class="uc-order-list__container">
        <!-- State: Error -->
        <div
          v-if="hasError"
          class="uc-order-list__state">
          <p>{{ errorMessage }}</p>
        </div>

        <!-- State: Empty -->
        <div
          v-else-if="isEmpty && isIdle"
          class="uc-order-list__state">
          <p
            class="uc-order-list__state-empty"
            data-test="uc-order-list__state-empty">
            {{ emptyStateMessage }}
          </p>
        </div>

        <!-- State: Initializing -->
        <div
          v-else-if="isInitializing || isSearching"
          class="uc-order-list__state">
          <loading-indicator is-loading />
        </div>

        <!-- State: Idle -->
        <!-- Table of orders -->
        <uc-data-grid-selectable
          v-if="!isEmpty && isIdle"
          v-model="selection"
          v-bind="{ columns, rows, disableSelectionWhen }"
          row-key="guid"
          :disable-infinite-scroll="hasLoadedAllItems"
          @next="onNext"
          @row-click="navigateToOrder">
          <!-- Customise columns to contain editors -->
          <template
            v-for="column in columns"
            #[column.key]="{ cell }">
            <component
              :is="getPropertyEditorComponent(cell.editor)"
              :key="column.key"
              :name="cell.label"
              compact
              :definition="{ ...cell, readonly: true }" />
          </template>

          <!-- Customer column -->
          <template #customer="{ cell }">
            <div class="column-customer">
              <!-- Customer name -->
              <div class="column-customer__name">
                <component
                  :is="getPropertyEditorComponent(cell.customer.editor)"
                  v-truncate
                  :name="cell.label"
                  compact
                  :definition="{ ...cell.customer, readonly: true }" />
              </div>
              <!-- Customer email -->
              <div
                v-if="cell.email.value"
                class="column-customer__email">
                <component
                  :is="getPropertyEditorComponent(cell.email.editor)"
                  v-truncate
                  :name="cell.label"
                  compact
                  :definition="{ ...cell.email, readonly: true }" />
              </div>
            </div>
          </template>

          <!-- Status column -->
          <template #status="{ cell }">
            <div>
              {{ formatStatus(cell) }}
            </div>
          </template>
        </uc-data-grid-selectable>
      </div>

      <!-- Actions -->
      <transition name="fade-in-up">
        <div
          v-if="!isEmptySelection"
          class="uc-order-list__actions-footer">
          <!-- Remove button -->
          <uc-button
            :theme="ucButtonTheme.PRIMARY"
            @click="openChangeStatusModal">
            {{ $t('changeStatus') }}
          </uc-button>
        </div>
      </transition>

      <!-- Change status modal -->
      <uc-orders-change-status
        v-if="shouldShowChangeStatusModal"
        v-bind="{ contentLanguage, currentStatusName, selectedOrders }"
        @cancel="closeChangeStatusModal"
        @status-change="onStatusChange" />

      <!-- Create modal -->
      <uc-order-create-modal
        v-if="shouldShowCreateModal"
        v-bind="{ contentLanguage }"
        @cancel="closeCreateModal"
        @create="onCreate" />
    </fields-section>
  </div>
</template>

<i18n>
{
  "en": {
    "heading": "Orders | Orders ({count}) | Orders ({count})",
    "unknownError": "Something went wrong — please try again.",
    "noSearchResults": "No results found. Try adjusting your search to find what you're looking for.",
    "emptyState": "No orders have been created yet. Click the button to get started.",
    "columns": {
      "orderNumber": "Order #",
      "status": "Status",
      "customer": "Customer",
      "orderDate": "Order date",
      "total": "Total"
    },
    "changeStatus": "Change status"
  }
}
</i18n>

<script>
import { FieldsSection } from '@/components/sections/fields-section/'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcButton, UcButtonTheme } from '@/components/forms/uc-button/'
import { validateAgainstEnum } from '@/utils/validate-against/'
import capitalize from '@/utils/capitalize.js'
import { UcOrdersChangeStatus } from './components/uc-orders-change-status/'
import { UcOrderCreateModal } from './components/uc-order-create-modal/'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR_UNKNOWN: 'errorUnknown',
  SEARCHING: 'searching',
}

const ComponentEvent = {
  NEXT: 'next',
  INPUT: 'input',
  STATUS_CHANGE: 'status-change',
  CREATE: 'create',
}

export default {
  name: 'UcOrderList',
  components: {
    LoadingIndicator,
    UcDataGridSelectable,
    FieldsSection,
    UcButton,
    UcOrdersChangeStatus,
    UcOrderCreateModal,
  },
  props: {
    orders: {
      type: Array,
      required: true,
    },
    value: {
      type: Array,
      required: true,
    },
    state: {
      type: String,
      required: true,
      validate: validateAgainstEnum(ComponentState),
    },
    totalCount: {
      type: Number,
      required: true,
    },
    emptyStateMessage: {
      type: String,
      default () {
        return this.$t('orders.emptyState')
      },
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      rows: [],
      shouldShowChangeStatusModal: false,
      shouldShowCreateModal: false,
    }
  },
  computed: {
    ucButtonTheme: () => UcButtonTheme,
    columns () {
      return [
        {
          key: 'orderNumber',
          value: this.$t('columns.orderNumber'),
          width: '35%',
          cellStyle: {
            padding: '0 24px 0 0',
          },
        },
        {
          key: 'status',
          value: this.$t('columns.status'),
          width: '190px',
          cellStyle: {
            padding: '0 24px 0 0',
          },
        },
        {
          key: 'customer',
          value: this.$t('columns.customer'),
          width: '35%',
          cellStyle: {
            padding: '0 24px 0 0',
          },
        },
        {
          key: 'orderDate',
          value: this.$t('columns.orderDate'),
          width: '190px',
          cellStyle: {
            padding: '0 24px 0 0',
          },
        },
        {
          key: 'total',
          value: this.$t('columns.total'),
          width: '150px',
          cellStyle: {
            padding: '0 24px 0 0',
            'text-align': 'right',
          },
        },
      ]
    },
    cssClassObject () {
      return {
        'is-max-height': this.orders.length > 9,
      }
    },
    selection: {
      get () {
        return this.value
      },
      set (newValue) {
        this.$emit(ComponentEvent.INPUT, newValue)
      },
    },
    hasLoadedAllItems () {
      return this.orders.length === this.totalCount
    },

    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    isDeleting () {
      return this.state === ComponentState.DELETING
    },
    isPatching () {
      return this.state === ComponentState.PATCHING
    },
    isErrorUnauthorized () {
      return this.state === ComponentState.ERROR_UNAUTHORIZED
    },
    isErrorUnknown () {
      return this.state === ComponentState.ERROR_UNKNOWN
    },
    isSearching () {
      return this.state === ComponentState.SEARCHING
    },

    isEmpty () {
      return this.orders.length === 0
    },
    isEmptySelection () {
      return this.selection.length === 0
    },
    currentStatusName () {
      return this.isEmptySelection ? '' : this.selection[0].status.value
    },
    selectedOrders () {
      return this.selection
    },
    errorMessage () {
      const errorMessages = {
        [ComponentState.ERROR_UNKNOWN]: this.$t('unknownError'),
      }

      return errorMessages[this.state] || ''
    },
    hasError () {
      return this.isErrorUnauthorized || this.isErrorUnknown
    },
  },
  watch: {
    orders: {
      handler (value) {
        this.rows = value.map(order => ({
          orderNumber: order.orderNumber,
          status: order.status,
          customer: {
            editor: 'ShortText',
            customer: order.customer,
            email: order.email,
          },
          orderDate: order.orderDate,
          total: order.total,
          guid: order.meta?.entityGuid,
        }))
      },
      immediate: true,
    },
  },
  methods: {
    closeChangeStatusModal () {
      this.shouldShowChangeStatusModal = false
    },
    openChangeStatusModal () {
      this.shouldShowChangeStatusModal = true
    },
    closeCreateModal () {
      this.shouldShowCreateModal = false
    },
    openCreateModal () {
      this.shouldShowCreateModal = true
    },
    disableSelectionWhen (row, rows, selectedRowGuids) {
      if (selectedRowGuids.length === 0) {
        return false
      }

      const firstSelectedRow = rows.find(x => x.guid === selectedRowGuids[0])
      return row.status?.value !== firstSelectedRow?.status?.value
    },
    getPropertyEditorComponent,
    formatStatus (statusProperty) {
      return capitalize(statusProperty.value)
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    navigateToOrder (order) {
      this.$router.push({
        name: 'order-edit',
        params: {
          orderGuid: order.guid,
        },
      })
    },
    onStatusChange (args) {
      this.closeChangeStatusModal()
      this.$emit(ComponentEvent.STATUS_CHANGE, args)
    },
    onCreate (args) {
      this.$emit(ComponentEvent.CREATE, args)
    },
  },
}
</script>

<style lang="stylus" scoped>
$list-min-height = $base-unit * 24

.uc-order-list
  position relative

.uc-order-list__container
  min-height $list-min-height

.uc-order-list__state
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  min-height $list-min-height

.uc-order-list__state-empty
  secondary-body-text()

.is-max-height .uc-order-list__container
  height $base-unit * 92
  border-bottom $base-border

.column-customer__email
  margin-bottom $base-unit * -2
  secondary-details-text()

.uc-order-list__actions-footer
  position absolute
  bottom $base-unit * 4
  z-index 1
  display flex
  align-items center
  justify-content center
  width 100%
  pointer-events none
</style>
