import { isEmpty } from '@/utils/object.utils.js'

export default ({
  definitionName,
  definitionGuid,
  label,
  newValue,
  oldValue,
  valueFieldName = 'value',
  changeValues = {}, // Contains objects: valueFieldName: { newValue, oldValue }
  isNullable,
  ...extra
}) => {
  const meta = {
    meta: {
      label,
      contentLanguage: undefined,
      timeStamp: new Date(),
      nodeType: '',
      nodeGuid: '',
      undoMessage: undefined,
      undoAction: undefined,
    },
  }

  const saveObject = {}
  const undoObject = {}

  if (!isEmpty(changeValues)) {
    for (const [key, value] of Object.entries(changeValues)) {
      saveObject[key] = value.newValue

      const oldValue = value.oldValue
      if (oldValue !== null && oldValue !== undefined && !(Array.isArray(oldValue) && oldValue.length === 0)) {
        undoObject[key] = value.oldValue
      }
    }
  }

  const save = {
    save: {
      [definitionName]: {
        guid: definitionGuid,
        [valueFieldName]: newValue,
        ...saveObject,
        ...extra,
      },
    },
  }

  const undo = {
    undo: {
      [definitionName]: {
        guid: definitionGuid,
        [valueFieldName]: oldValue,
        ...undoObject,
        ...extra,
      },
    },
  }

  const hasOldValue = (oldValue !== null && oldValue !== undefined && !(Array.isArray(oldValue) && oldValue.length === 0)) || !isEmpty(undoObject)
  const isUndoable = isNullable || hasOldValue

  if (isUndoable) {
    return { key: definitionGuid, ...save, ...undo, ...meta }
  } else {
    return { key: definitionGuid, ...save, ...meta }
  }
}
