import { EntityService, EntityType } from './_entity.service.js'

export class OrderDiscountService extends EntityService {
  constructor (httpClient) {
    super(EntityType.ORDER_DISCOUNT, httpClient)
  }

  fetchManyAsync ({ orderGuid, cultureCode, startAt, limit }) {
    const url = `orders/${orderGuid}/discounts/${cultureCode}`
    return super.fetchEntitiesAsync({ url, params: { startAt, limit } })
  }

  deleteManyAsync ({ orderGuid, guids }) {
    const url = `orders/${orderGuid}/discounts`
    const payload = {
      data: guids,
    }
    return this._httpClient.delete(url, payload)
  }

  createOneAsync ({ orderGuid, cultureCode, campaign, promotion, amount, details }) {
    const url = `orders/${orderGuid}/discounts/${cultureCode}`
    return this._httpClient.post(url, { campaign, promotion, amount, details })
  }
}
