<template>
  <div class="content-navigator-selection">
    <use-sorting
      :value="items"
      :options="{
        ghostClass: 'content-navigator-selection__ghost',
      }"
      selector=".uc-list"
      @input="$emit('update-sort', $event)">
      <uc-list-selectable
        item-key="guid"
        :value="value"
        :items="items"
        @input="$emit('input', $event)">
        <template #default="{ item }">
          <div class="content-navigator-selection__item">
            <content-navigator-item :item="item" />
          </div>
        </template>
        <template
          v-if="$scopedSlots.actions"
          #actions>
          <slot name="actions" />
        </template>
      </uc-list-selectable>
    </use-sorting>
  </div>
</template>

<script>
import UseSorting from '@/components/wrappers/use-sorting'
import UcListSelectable from '@/components/generic/uc-list-selectable'
import ContentNavigatorItem from '../content-navigator-item'

export default {
  components: {
    UcListSelectable,
    ContentNavigatorItem,
    UseSorting,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
    value: {
      type: Array,
      default: () => [],
    },
  },
}
</script>
<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
.content-navigator-selection__ghost
  opacity 0.1 !important
</style>

<style lang="stylus" scoped>
.content-navigator-selection
  display flex
  flex 1 1 auto
  flex-direction column
  height 100%

.content-navigator-selection__item
  cursor move
</style>
