<template>
  <div class="uc-boolean">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="translatedValue">
      <uc-toggle
        v-bind="{ label, guid, compact }"
        :readonly="isReadonly"
        :checked="value"
        @change="onChange({ newValue: $event.value, oldValue: value })" />
    </editor-wrapper>
  </div>
</template>

<script>
import { UcToggle } from '@/components/forms/uc-toggle/'
import editorMixin from '../editor-mixin'

export default {
  name: 'UcBoolean',
  components: {
    UcToggle,
  },
  mixins: [editorMixin],
  computed: {
    translatedValue () {
      const answer = this.value ? 'yes' : 'no'
      return this.$t(`global.${answer}`)
    },
  },
}
</script>
