import { rest } from 'msw'
import { discountTemplates, discounts, discountDefinitions, discountShallowDefinitions } from '@/mocks/entities/discounts/'
import mockGuid from '@/utils/mock-guid.js'

const RESPONSE_DELAY = 200
export const rootDiscountHandlers = [
  rest.get('/ucommerceapi/v2/promotions/discounts/definitions', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(discountShallowDefinitions),
    )
  }),
  rest.get('/ucommerceapi/v2/promotions/discounts/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(discountDefinitions),
    )
  }),
  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/discounts/:discountGuid/:cultureCode', (req, res, ctx) => {
    const discount = JSON.parse(JSON.stringify(discountTemplates.find(newDiscount => newDiscount.meta.entityGuid === req.params.discountGuid)))
    discount.meta.entityGuid = mockGuid()

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(discount),
    )
  }),
  rest.patch('/ucommerceapi/v2/promotions/:promotionGuid/discounts/:discountGuid/:cultureCode', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
  rest.delete('/ucommerceapi/v2/promotions/:promotionGuid/discounts/:discountGuid', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/discounts/:cultureCode', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(discounts),
    )
  }),
  rest.post('/ucommerceapi/v2/promotions/:promotionGuid/discounts', (req, res, ctx) => {
    const guid = discountTemplates.find(criterionTemplate => criterionTemplate.meta.definition.guid === req.body.definitionGuid).meta.entityGuid

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json({ guid }),
    )
  }),
]
