<template>
  <uc-dropdown-paged
    v-bind="{
      options,
      collapse,
      selected,
      isLoading,
      hasLoadedAllItems,
    }"
    :placeholder="label"
    @toggle="onToggle"
    @next="requestNextPage"
    @search="search">
    <template #default="{ option }">
      <div class="uc-price-group-select__option">
        <uc-radio
          :label="option.label"
          :value="option.guid"
          :group="fieldId"
          @change="onChange">
          <!-- eslint-disable-next-line vue/no-v-html -->
          <span v-html="highlight(option.label, searchTerm)" />
          <template #icon>
            <span class="uc-price-group-select__icon">
              <check-icon />
            </span>
          </template>
        </uc-radio>
      </div>
    </template>
  </uc-dropdown-paged>
</template>

<i18n>
{
  "en": {
      "placeholder": "Select a price group"
    }
}
</i18n>

<script>
import { orderService } from '@/services'
import { UcDropdownPaged } from '@/components/forms/uc-dropdown/'
import { UcRadio } from '@/components/forms/uc-radio/'
import { Check24Normal as CheckIcon } from '@/assets/icons'
import highlight from '@/utils/word-highlight/'
import getId from '@/utils/id-generator.js'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  LOADING: 'loading',
  ERROR: 'error',
}

const ComponentEvent = {
  INPUT: 'input',
}

export default {
  name: 'UcPriceGroupSelect',
  components: {
    UcDropdownPaged,
    UcRadio,
    CheckIcon,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    orderGuid: {
      type: String,
      required: true,
    },
    value: {
      type: Object,
      default: null,
    },
  },
  data () {
    return {
      collapse: false,
      state: ComponentState.INITIALIZING,
      currentPage: 1,
      pageSize: 18,
      totalCount: 0,
      resultCount: 0,
      searchTerm: '',
      options: [],
    }
  },
  computed: {
    fieldId () {
      return getId()
    },
    label () {
      return this.value?.label ?? this.$t('placeholder')
    },
    selected () {
      return !!this.value
    },
    isLoading () {
      return this.state === ComponentState.LOADING
    },
    hasLoadedAllItems () {
      return this.options.length === this.resultCount
    },
  },
  async created () {
    await this.fetchManyAsync()
  },
  methods: {
    highlight,
    setChecked (optionGuid) {
      // TODO: make work on scroll
      return this.value?.guid === optionGuid
    },
    onChange (payload) {
      this.collapse = true
      this.$emit(ComponentEvent.INPUT, payload)
    },
    onToggle (collapse) {
      this.collapse = !collapse
    },
    async fetchManyAsync (startAt = 0, currentPage = 1, reset = false) {
      this.state = ComponentState.LOADING

      try {
        const response = await orderService.fetchPriceGroupsAsync({
          startAt,
          orderGuid: this.orderGuid,
          limit: this.pageSize,
          cultureCode: this.contentLanguage,
          searchTerm: this.searchTerm,
        })

        const mappedResponseData = response.data.map(({ name, guid }) => ({
          guid,
          label: name,
        }))

        if (reset) {
          this.options = []
        }

        this.options = [...this.options, ...mappedResponseData]

        this.totalCount = Number(response.headers['x-total-count'])
        this.resultCount = Number(response.headers['x-result-count'])
        this.currentPage = currentPage

        this.state = ComponentState.IDLE
      } catch (error) {
        // TODO(ch10038): We need a way to handle request errors
        console.error(error)
        this.state = ComponentState.ERROR
      }
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1
      await this.fetchManyAsync(startAt, page)
    },
    search (searchTerm) {
      this.searchTerm = searchTerm
      this.fetchManyAsync(0, 1, true)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-price-group-select__option
  width 100%
  height 100%

>>> .highlight
  font-weight bold

.uc-price-group-select__icon
  margin-top $base-unit * 0.5
</style>
