import { rest } from 'msw'
import { orderPaymentDefinitions, orderPayments } from '@/mocks/entities/order-payments/'
import { getMockGuidForStatusCode } from '@/mocks/constants.js'

const RESPONSE_DELAY = 200

export const orderPaymentHandlers = [
  rest.get('/ucommerceapi/v2/orders/payments/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(orderPaymentDefinitions),
    )
  }),

  rest.get('/ucommerceapi/v2/orders/:orderGuid/payments/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt')) || 0
    const limit = Number(params.get('limit')) || Number.MAX_SAFE_INTEGER
    let totalCount = 0
    let response
    let statusCode

    switch (req.params.orderGuid) {
      case 'mock-guid-empty':
        statusCode = 200
        response = []
        break
      case getMockGuidForStatusCode(400):
        statusCode = 400
        response = { Message: 'Mock 400: Provided culture code is invalid.' }
        break
      case getMockGuidForStatusCode(401):
        statusCode = 401
        response = { Message: 'Mock 401: Missing permissions.' }
        break
      case getMockGuidForStatusCode(404):
        statusCode = 404
        response = { Message: 'Mock 404: Order not found.' }
        break
      // No error message
      case getMockGuidForStatusCode(418):
        statusCode = 418
        break
      default:
        statusCode = 200
        totalCount = orderPayments.length
        response = orderPayments.slice(startAt, startAt + limit)
    }

    return res(
      ctx.set('x-total-count', totalCount),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(statusCode),
      ctx.json(response),
    )
  }),
]
