import DefaultLayout from './default-layout.vue'
import StickyHeaderLayout from './sticky-header-layout.vue'
import DetailsLayout from './details-layout.vue'
import BlankLayout from './blank-layout.vue'

export const LayoutType = {
  DEFAULT: 'default',
  STICKY_HEADER: 'sticky-header',
  DETAILS: 'details',
  BLANK: 'blank',
}

export const LayoutComponentMap = {
  [LayoutType.DEFAULT]: DefaultLayout,
  [LayoutType.STICKY_HEADER]: StickyHeaderLayout,
  [LayoutType.DETAILS]: DetailsLayout,
  [LayoutType.BLANK]: BlankLayout,
}

export function getLayoutComponentFromRoute (route) {
  // Find closest route/parent-route that has defined a layout
  const closestRouteWithLayout = [...route.matched].reverse().find(route => route.meta.layout)
  return getLayoutComponent(closestRouteWithLayout && closestRouteWithLayout.meta.layout)
}

export function getLayoutComponent (layout = LayoutType.DEFAULT) {
  if (!(Object.values(LayoutType).includes(layout))) {
    // eslint-disable-next-line
    console.error(`[layout] The requested layout did not match any of the available layouts. Available layouts are: ${Object.values(LayoutType)}`)
  }

  if (!(layout in LayoutComponentMap)) {
    // eslint-disable-next-line
    console.error(`[layout] Could not find a layout component matching the requested layout type. Did you remember to update the LayoutComponentMap?`)
  }

  return LayoutComponentMap[layout]
}
