<template>
  <form
    class="store-create"
    @submit.prevent="createStore">
    <uc-modal
      :title="$t('storeCreate.title')"
      @hide="cancel">
      <!-- Definition -->
      <div class="store-create__field">
        <uc-select
          :options="availableDefinitions"
          :selected="selectedDefinitionGuid"
          :placeholder="definitionPlaceholderText"
          :readonly="isPosting"
          @selected="selectedDefinitionGuid = $event.guid">
          <template #pending>
            <loading-indicator is-loading />
          </template>
        </uc-select>
      </div>

      <!-- Domain -->
      <div class="store-create__field">
        <uc-select
          :options="availableDomains"
          :selected="selectedDomainGuid"
          :placeholder="domainPlaceholderText"
          :readonly="isPosting"
          @selected="selectedDomainGuid = $event.guid">
          <template #pending>
            <loading-indicator is-loading />
          </template>
        </uc-select>
      </div>

      <!-- Name -->
      <div class="store-create__field">
        <uc-text-input
          ref="input"
          v-model.trim="newName"
          :label="nameLabelText"
          :readonly="isPosting" />
        <div
          v-show="errorMessage.length"
          class="store-create__error"
          data-test="store-create-error">
          {{ errorMessage }}
        </div>
      </div>

      <!-- Submit -->
      <template #footer>
        <uc-button
          :disabled="!isEnabled"
          type="submit"
          theme="primary"
          @click="createStore">
          {{ $t('storeCreate.createStore') }}
          <template
            v-if="isPosting"
            #after>
            <loading-spinner is-loading />
          </template>
        </uc-button>
        <uc-button
          theme="text"
          @click="cancel">
          {{ $tc('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<script>
import UcSelect from '@/components/forms/uc-select'
import UcModal from '@/components/widgets/uc-modal'
import UcButton from '@/components/forms/uc-button/uc-button'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import capitalize from '@/utils/capitalize'
import LoadingSpinner from '@/components/widgets/loading-spinner'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { storeService } from '@/services'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

const ComponentEvent = {
  CREATE: 'create',
}

export default {
  name: 'StoreCreate',
  components: {
    LoadingIndicator,
    UcTextInput,
    UcSelect,
    UcModal,
    UcButton,
    LoadingSpinner,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      availableDefinitions: undefined,
      availableDomains: undefined,
      selectedDefinitionGuid: '',
      selectedDomainGuid: '',
      newName: '',
      errorMessage: '',
      state: ComponentState.INITIALIZING,
    }
  },
  computed: {
    definitionPlaceholderText () {
      return `${capitalize(this.$t('global.select'))} ${this.$t('global.definition')}`
    },
    domainPlaceholderText () {
      return `${capitalize(this.$t('global.select'))} ${this.$t('global.domain')}`
    },
    nameLabelText () {
      return `${capitalize(this.$t('storeCreate.storeName'))}`
    },
    isEnabled () {
      return this.newName.length > 0 && this.selectedDefinitionGuid.length > 0
    },
    isPosting () {
      return this.state === ComponentState.POSTING
    },
  },
  async created () {
    this.availableDefinitions = (await storeService.fetchDefinitionsListAsync()).data.map(definition => ({ guid: definition.guid, label: definition.name }))
    this.availableDomains = (await storeService.fetchDefinitionsAsync()).getDomains()
    this.state = ComponentState.IDLE
  },
  methods: {
    async createStore () {
      if (this.isPosting) {
        return
      }

      const payload = {
        name: this.newName,
        definitionGuid: this.selectedDefinitionGuid,
        domainId: this.selectedDomainGuid,
      }

      try {
        this.state = ComponentState.POSTING
        const createStoreResponse = await storeService.createEntityAsync(payload)
        const getStoreResponse = await storeService.fetchEntityAsync({
          guid: createStoreResponse.data.guid,
          cultureCode: this.contentLanguage,
        })
        this.state = ComponentState.IDLE

        this.$emit(ComponentEvent.CREATE, getStoreResponse.data)
      } catch (e) {
        this.state = ComponentState.ERROR
        if (e.response.data.message) {
          this.errorMessage = e.response.data.message
        } else {
          throw e
        }
      }
    },
    cancel () {
      this.$router.replace({ name: 'stores' })
    },
  },

}
</script>

<style lang="stylus" scoped>
.store-create__field:not(:last-of-type)
  margin-bottom $base-unit * 3

.store-create__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()
</style>
