<template>
  <div v-on-clickaway="closeFilterDropdown">
    <uc-button
      :theme="buttonThemeOutline"
      @click="toggleFilterDropdown">
      <template #before>
        <filter-icon />
      </template>
      {{ $t('buttonFilterText') }} • {{ filtersCount }}
    </uc-button>
    <transition name="dropdown">
      <div
        v-show="showFilters"
        class="orders__filter">
        <uc-filter
          :sections="filterSections"
          @change="setFilters"
          @clear="clearFilters"
          @filter="applyFilters" />
      </div>
    </transition>
  </div>
</template>

<i18n>
{
  "en": {
    "buttonFilterText": "Filter",
    "status": "Status",
    "timeFrame": "Time frame"
  }
}
</i18n>

<script>
import { directive as onClickaway } from 'vue-clickaway'
import { isEqual } from '@/utils/object.utils.js'
import { orderService } from '@/services/'
import { UcFilter } from '@/components/generic/uc-filter/'
import { UcButton, UcButtonTheme } from '@/components/forms/uc-button/'
import { Filter24Thick as FilterIcon } from '@/assets/icons/'

/**
 * @typedef Filters
 * @type {object}
 * @property {string[]} statuses — Array of GUIDs representing the selected statuses
 * @property {string} from - Date ISO string
 * @property {string} to - Date ISO string
 */

const ComponentEvent = {
  INPUT: 'input',
}

export default {
  name: 'UcOrderFilter',
  components: {
    UcFilter,
    UcButton,
    FilterIcon,
  },
  directives: {
    onClickaway,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    value: {
      /** @type {import('vue').PropType<Filters>} */
      type: Object,
      required: true,
    },
  },
  /** @type {{ filters: Filters }} */
  data () {
    return {
      orderStatuses: [],
      statusesErrorMessage: null,
      showFilters: false,
      filters: {
        statuses: undefined,
        from: undefined,
        to: undefined,
      },
    }
  },
  computed: {
    isDirty () {
      return !isEqual(this.filters, this.value)
    },
    buttonThemeOutline: () => UcButtonTheme.OUTLINE,
    filtersCount () {
      const statusesCount = this.value.statuses?.length || 0
      const timeFrameCount = Number(!!this.value.to || !!this.value.from)
      return statusesCount + timeFrameCount
    },
    filterSections () {
      return {
        timeFrame: {
          description: '',
          editor: 'TimeFrame',
          enumerable: true,
          guid: 'f3cf5a7f-324c-4a71-ac18-1808993d41a5',
          isVariantProperty: null,
          label: this.$t('timeFrame'),
          multilingual: false,
          options: null,
          placeholder: '',
          value: {
            ...this.filters.from && { from: this.filters.from },
            ...this.filters.to && { to: this.filters.to },
          },
          validation: { required: false, regex: null, errorMessage: null },
        },
        statuses: {
          description: '',
          editor: 'EnumCheckboxSelect',
          enumerable: true,
          guid: 'c2dac463-7e87-4f55-992a-882545b85698',
          isVariantProperty: null,
          label: this.$t('status'),
          multilingual: false,
          options: this.orderStatuses,
          placeholder: '',
          selected: this.filters.statuses,
          validation: { required: false, regex: null, errorMessage: this.statusesErrorMessage },
        },
      }
    },
  },
  watch: {
    value: {
      handler (value) {
        const { statuses, from, to } = value

        this.filters = {
          statuses,
          from,
          to,
        }
      },
      immediate: true,
    },
  },
  async created () {
    await this.fetchStatusesAsync()
  },
  methods: {
    async fetchStatusesAsync () {
      try {
        const response = await orderService.fetchStatusesAsync({
          params: {
            startAt: 0,
            limit: Number.MAX_SAFE_INTEGER,
          },
          cultureCode: this.contentLanguage,
        })

        this.orderStatuses = response.data?.map(({ name, guid }) => ({
          label: name,
          guid,
        }))
      } catch (error) {
        this.statusesErrorMessage = this.$t('global.errorMessage')
      }
    },
    setFilters ({ save }) {
      for (const [key, value] of Object.entries(save)) {
        if ('selected' in value) {
          this.$set(this.filters, key, value.selected)
        } else if ('value' in value) {
          if (typeof value.value === 'object') {
            for (const [innerKey, innerValue] of Object.entries(value.value)) {
              this.$set(this.filters, innerKey, innerValue)
            }
          } else {
            this.$set(this.filters, key, value.value[key])
          }
        }
      }
    },
    toggleFilterDropdown () {
      this.showFilters = !this.showFilters
    },
    closeFilterDropdown () {
      this.showFilters = false
      this.filters = { ...this.value }
    },
    clearFilters () {
      this.filters = {
        statuses: undefined,
        from: undefined,
        to: undefined,
      }

      if (this.isDirty) {
        this.$emit(ComponentEvent.INPUT, this.filters)
      }

      this.closeFilterDropdown()
    },
    applyFilters () {
      if (this.isDirty) {
        this.$emit(ComponentEvent.INPUT, this.filters)
      }

      this.closeFilterDropdown()
    },
  },
}
</script>
<style lang="stylus" scoped>
.orders__filter
  position absolute
  top $base-unit * 9
  z-index $base-z-index + 1
</style>
