import { difference, differenceBy, intersectionBy, intersection } from '@/utils/array.utils'

const ComponentEvent = {
  INPUT: 'input',
  SELECT: 'select',
  DESELECT: 'deselect',
}

export default {
  name: 'UseSelection',
  props: {
    items: {
      type: Array,
      required: true,
    },
    value: {
      type: Array,
      required: false,
    },
    sync: {
      type: Boolean,
      default: true,
    },
    itemKey: {
      type: String | Number,
      required: false,
    },
  },
  data: () => {
    return {
      internalValue: [],
    }
  },
  watch: {
    items () {
      if (this.sync) {
        this._syncSelection()
      }
    },
  },
  computed: {
    selection: {
      get () {
        return this.value ? this.value : this.internalValue
      },
      set (newValue) {
        if (this.value) {
          this.$emit(ComponentEvent.INPUT, newValue)
        } else {
          this.internalValue = newValue
        }
      },
    },
    areAllSelected () {
      return this.items.length > 0 && !this.items.find(item => !this.isSelected(item))
    },
  },
  methods: {
    toggle (items) {
      items = Array.isArray(items) ? items : [items]
      const itemsToSelect = []
      const itemsToDeselect = []

      items.forEach(item => {
        this.isSelected(item) ? itemsToDeselect.push(item) : itemsToSelect.push(item)
      })

      if (itemsToDeselect.length > 0) {
        this.deselect(itemsToDeselect)
      }

      if (itemsToSelect.length > 0) {
        this.select(itemsToSelect)
      }
    },

    toggleAll () {
      this.toggle(this.items)
    },

    select (items) {
      items = Array.isArray(items) ? items : [items]
      const itemsToSelect = items.filter(item => !this.isSelected(item))

      if (itemsToSelect.length === 0) {
        return
      }

      this.selection = [...this.selection, ...itemsToSelect]
      this.$emit(ComponentEvent.SELECT, itemsToSelect)
    },

    selectAll () {
      this.select(this.items)
    },

    deselect (items) {
      items = Array.isArray(items) ? items : [items]
      let deselectedItems
      if (this.itemKey) {
        deselectedItems = intersectionBy(items, this.selection, this.itemKey)
      } else {
        deselectedItems = intersection(items, this.selection)
      }

      if (deselectedItems.length === 0) {
        return
      }
      this.$emit(ComponentEvent.DESELECT, deselectedItems)
      if (this.itemKey) {
        this.selection = differenceBy(this.selection, deselectedItems, this.itemKey)
      } else {
        this.selection = difference(this.selection, deselectedItems)
      }
    },

    deselectAll () {
      if (this.selection.length === 0) {
        return
      }

      this.deselect(this.items)
    },

    reset () {
      if (this.selection.length === 0) {
        return
      }

      this.$emit(ComponentEvent.DESELECT, this.selection)
      this.selection = []
    },

    isSelected (item) {
      if (this.itemKey) {
        return this.selection.some(x => x[this.itemKey] === item[this.itemKey])
      } else {
        return this.selection.indexOf(item) !== -1
      }
    },

    _syncSelection () {
      const removedItems = this.selection.filter(item => this.items.indexOf(item) === -1)

      if (removedItems.length > 0) {
        this.deselect(removedItems)
      }
    },
  },

  render () {
    const props = {
      isSelected: this.isSelected,
      areAllSelected: this.areAllSelected,
      selection: this.selection,
      toggle: this.toggle,
      toggleAll: this.toggleAll,
      select: this.select,
      selectAll: this.selectAll,
      deselect: this.deselect,
      deselectAll: this.deselectAll,
      reset: this.reset,
    }

    if (!this.$scopedSlots.default) {
      // eslint-disable-next-line
      return console.error(`[use-selection] No root node found. Please ensure that the component is wrapping a node.`)
    }

    const slotContent = this.$scopedSlots.default(props)

    if (slotContent && slotContent.length > 1) {
      // eslint-disable-next-line
      return console.error(`[use-selection] Multiple root nodes found. Please ensure that the component is only wrapping a single root node.`)
    }

    return slotContent
  },
}
