<template>
  <div
    :id="id"
    class="uc-selector">
    <v-popover
      :offset="12"
      :container="`#${id}`"
      popover-inner-class="tooltip-inner popover-inner custom-scroll-bar"
      :open.sync="isOpen">
      <!-- Trigger button -->
      <button
        data-test="selector-button"
        class="uc-selector__button"
        @keyup.esc="isOpen = false">
        <span v-truncate>
          {{ buttonText }}
        </span>
        <span class="uc-selector__icon">
          <arrow-down />
        </span>
      </button>

      <!-- The actual popover -->
      <template #popover>
        <template v-if="isOverflowing">
          <!-- Search input -->
          <div class="uc-selector__search">
            <use-fuzzy-search
              :keys="searchKeys"
              :list="items"
              @search="onSearch">
              <template #default="searchProps">
                <uc-search
                  data-test="selector-search"
                  dark
                  dense
                  :value="searchProps.value"
                  :focus="isOpen"
                  :clear="!isOpen"
                  @input="searchProps.setValue($event, value)" />
              </template>
            </use-fuzzy-search>
          </div>

          <!-- Empty search -->
          <p
            v-if="!hasSearchResults"
            class="uc-selector__no-results">
            {{ $t('global.searchNoResults') }}
          </p>
        </template>

        <!-- Search results -->
        <ul
          v-if="hasSearchResults"
          class="uc-selector__list">
          <li
            v-for="(item, index) in searchResults"
            :key="itemKey ? item[itemKey] : index"
            v-truncate
            v-close-popover
            class="uc-selector__item"
            data-test="selector-option"
            :class="isSelected(item) ? 'is-selected' : ''"
            @click="onClick(item)">
            <slot :item="item">
              {{ item }}
            </slot>
          </li>
        </ul>
      </template>
    </v-popover>
  </div>
</template>

<script>
import Vue from 'vue'
import createId from '@/utils/id-generator.js'
import UcSearch from '@/components/forms/uc-search'
import UseFuzzySearch from '@/components/wrappers/use-fuzzy-search'
import { ChevronDown16Thick as ArrowDown } from '@/assets/icons'
import { VPopover, VClosePopover } from 'v-tooltip'

Vue.directive('close-popover', VClosePopover)

const ComponentEvent = {
  INPUT: 'input',
}

export default {
  name: 'UcSelector',
  components: {
    ArrowDown,
    VPopover,
    UcSearch,
    UseFuzzySearch,
  },
  props: {
    value: {
      type: String,
      required: true,
    },
    items: {
      type: Array,
      required: true,
    },
    emptyText: {
      type: String,
      default: function () {
        return this.$t('global.selectPlaceholder')
      },
    },
    itemKey: {
      type: [String, Number],
      default: '',
    },
    searchKeys: {
      type: Array,
      default: () => [],
    },
  },
  data () {
    return {
      isOpen: false,
      searchResults: this.items,
    }
  },
  computed: {
    buttonText () {
      return this.value || this.emptyText
    },
    id () {
      return createId()
    },
    isOverflowing () {
      return this.items.length > 6
    },
    hasSearchResults () {
      return this.searchResults.length > 0
    },
  },
  methods: {
    onClick (item) {
      this.$emit(ComponentEvent.INPUT, item)
    },
    onSearch (results) {
      this.searchResults = results
    },
    isSelected (item) {
      return item === this.value || Object.values(item).includes(this.value)
    },
  },
}
</script>
<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
$popover-width = $base-unit * 32

.uc-selector
  .tooltip
    width $popover-width

  .tooltip.popover .popover-inner
    padding 2 * $base-unit
</style>

<style lang="stylus" scoped>
.uc-selector
  user-select none

.uc-selector__button
  display flex
  align-items center
  height 4 * $base-unit
  padding 0 2 * $base-unit
  border none
  border-radius 2 * $base-unit
  background-color $base-color-brand-50
  outline none
  cursor pointer
  primary-details-text(bold)

  &:hover
  &:focus-visible
    background-color $base-color-brand-50--hover

  &:active
    background-color $base-color-brand-50--active

.uc-selector__icon
  display inline-flex
  margin-left ($base-unit / 2)

.uc-selector__list
  margin 0
  padding 0
  list-style-type none

.uc-selector__item
  padding $base-unit
  cursor pointer
  primary-details-text()

  &:hover
  &.is-selected
    primary-details-text(bold)

.uc-selector__search
  margin $base-unit 0

.uc-selector__no-results
  margin $base-unit
</style>
