import { rest } from 'msw'
import { generateProducts, productDefinitions } from '@/mocks/entities/products/'

const RESPONSE_DELAY = 200
let products = []

export const rootProductHandlers = [
  rest.get('/ucommerceapi/v2/products/:cultureCode', (req, res, ctx) => {
    const searchTerm = req.url.searchParams.get('searchTerm')
    const includeVariants = req.url.searchParams.get('includeVariants')
    products = generateProducts(40, includeVariants)

    if (searchTerm) {
      products = products.filter(product => product.name.value.toLowerCase().includes(searchTerm.toLowerCase()))
    }

    return res(
      ctx.set('x-total-count', products.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(products),
    )
  }),

  rest.get('/ucommerceapi/v2/products/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(productDefinitions),
    )
  }),
]

export { products }
