<template>
  <div class="uc-multi-select-editor">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedValue"
      :required="isRequired">
      <uc-dropdown
        :options="options"
        :readonly="isReadonly"
        :placeholder="placeholder"
        :name="label"
        :selected="hasSelectedOptions">
        <template #default="{ option, searchTerm }">
          <uc-checkbox
            :guid="option.guid"
            :value="option.guid"
            :label="option.label"
            :checked="setChecked(option.guid)"
            :readonly="setReadonly(option.guid)"
            @change="onOptionChange">
            <!-- eslint-disable-next-line vue/no-v-html -->
            <span v-html="highlight(option.label, searchTerm)" />
          </uc-checkbox>
        </template>
      </uc-dropdown>
    </editor-wrapper>
  </div>
</template>

<script>
import UcCheckbox from '@/components/forms/uc-checkbox'
import UcDropdown from '@/components//forms/uc-dropdown'
import highlight from '@/utils/word-highlight'
import editorMixin from '../editor-mixin'

const VALUE_DELIMITER = '|'

export default {
  name: 'UcMultiSelectEditor',
  components: {
    UcCheckbox,
    UcDropdown,
  },
  mixins: [editorMixin],
  computed: {
    formattedValue () {
      if (this.options?.length > 0 && this.definition.selected?.length > 0) {
        return this.$tc('value', this.selectedOptions.length, { firstValue: this.selectedOptions[0].label, remainingCount: this.selectedOptions.length - 1 })
      }

      if (!this.value) {
        return ''
      }

      const values = this.value.split(VALUE_DELIMITER)

      return this.$tc('value', values.length, { firstValue: values[0], remainingCount: values.length - 1 })
    },
    placeholder () {
      if (this.selectedOptions.length === 0) {
        return this.label || this.$t('global.selectPlaceholder')
      }

      return this.formattedValue
    },
    selectedOptions () {
      if (!this.definition.selected) {
        return []
      }

      return this.options.filter(option => this.definition.selected.includes(option.guid))
    },
    hasSelectedOptions () {
      return this.selectedOptions.length > 0
    },
  },
  methods: {
    highlight,
    setChecked (optionGuid) {
      return this.selectedOptions ? this.selectedOptions.map(selectedOption => selectedOption.guid).includes(optionGuid) : false
    },
    setReadonly (optionGuid) {
      return this.isRequired && this.selectedOptions.length === 1 && this.setChecked(optionGuid)
    },
    setSelectedOptions (event) {
      if (event.value) {
        return [...this.selectedOptions, event]
      }

      return this.selectedOptions.filter((option) => option.guid !== event.guid)
    },
    onOptionChange (optionGuid) {
      const emptyOption = !this.isRequired ? [] : undefined
      this.onChange({
        label: this.placeholder,
        newValue: this.setSelectedOptions(optionGuid).map(item => item.guid),
        oldValue: this.selected.length > 0 ? this.selected : emptyOption,
        valueFieldName: 'selected',
      })
    },
  },
}
</script>
<i18n>
{
  "en": {
    "value": "{firstValue} | {firstValue} | {firstValue} and {remainingCount} more"
  }
}
</i18n>
<style lang="stylus" scoped>
>>> .highlight
  font-weight bold
</style>
