<template>
  <div
    class="uc-uri-list"
    :class="cssClassObject">
    <!-- Table of Uris -->
    <uc-data-grid-selectable
      v-model="selection"
      v-bind="{ columns, rows }"
      row-key="guid"
      :disable-infinite-scroll="hasLoadedAllItems"
      @next="onNext">
      <template
        v-for="column in columns"
        #[column.key]="{ cell }">
        <component
          :is="getPropertyEditorComponent(cell.editor)"
          :key="column.key"
          compact
          :name="cell.label"
          :language="contentLanguage"
          :definition="cell" />
      </template>
    </uc-data-grid-selectable>
    <!-- Actions -->
    <transition name="fade-in-up">
      <div
        v-if="selection.length > 0"
        class="uc-uri-list__actions-footer">
        <!-- Remove button -->
        <uc-button @click="deleteUris">
          {{ `${$t('global.remove')} (${selection.length})` }}
        </uc-button>
      </div>
    </transition>
  </div>
</template>
<script>
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcButton } from '@/components/forms/uc-button/'

const ComponentEvent = {
  NEXT: 'next',
  DELETE: 'delete',
}
export default {
  name: 'UcUriList',
  components: {
    UcDataGridSelectable,
    UcButton,
  },
  props: {
    uris: {
      type: Array,
      required: true,
    },
    totalCount: {
      type: Number,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      selection: [],
    }
  },
  computed: {
    columns () {
      return [
        {
          key: 'redirectUrl',
          value: 'Redirect URI',
        },
      ]
    },
    rows () {
      return this.uris.map(row => {
        const { redirectUrl, guid } = row
        return {
          guid: guid.value,
          redirectUrl,
        }
      })
    },
    cssClassObject () {
      return {
        'is-max-height': this.uris.length > 5,
      }
    },
    hasLoadedAllItems () {
      return this.uris.length === this.totalCount
    },
  },
  methods: {
    getPropertyEditorComponent,
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    deleteUris () {
      this.$emit(ComponentEvent.DELETE, this.selection)
      this.selection = []
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-uri-list
  position relative

  &.is-max-height
    height $base-unit * 46
    border-bottom $base-border

.uc-uri-list__actions-footer
  position absolute
  bottom $base-unit * 4
  z-index 1
  display flex
  align-items center
  justify-content center
  width 100%
  pointer-events none
</style>
