import { LayoutType } from '@/layouts'
import Products from './views/products.vue'

export function ProductsApp ({ router, store }) {
  router.addRoute({
    path: '/products/',
    name: 'products',
    component: Products,
    beforeEnter: (_to, _from, next) => {
      store.commit('details/resetDetails')
      next()
    },
    meta: {
      layout: LayoutType.STICKY_HEADER,
    },
  })
}
