import { UcBoolean } from './uc-boolean/'
import { UcDatePicker } from './uc-date-picker/'
import { UcDateTimePicker } from './uc-date-time-picker/'
import { UcDefinitionPicker } from './uc-definition-picker/'
import { UcDefinitionTypePicker } from './uc-definition-type-picker/'
import { UcEmailContent } from './uc-email-content/'
import { UcLongText } from './uc-long-text/'
import { UcMultiSelectEditor } from './uc-multi-select-editor/'
import { UcNumber } from './uc-number/'
import { UcRichText } from './uc-rich-text/'
import { UcShortText } from './uc-short-text/'
import { UcSingleSelectEditor } from './uc-single-select-editor/'
import { UcPriceEditor } from './uc-price-editor/'
import { UcImagePickerEditorSingleSelect, UcImagePickerEditorMultiSelect } from './uc-image-picker-editor/'
import { UcContentEditorSingle, UcContentEditorMulti } from './uc-content-picker-editor/'
import { UcRadioGroupEditor } from './uc-radio-group-editor/'
import { UcCheckboxGroupEditor } from './uc-checkbox-group-editor/'
import { UcPercentEditor } from './uc-percent-editor/'
import { UcPromoCodeEditor } from './uc-promo-code-editor/'
import { UcProductEditor } from './uc-product-editor/'
import { UcFreeGiftEditor } from './uc-free-gift-editor/'
import { UcCategoryEditor } from './uc-category-editor/'
import { UcCatalogEditor } from './uc-catalog-editor/'
import { UcOrderLineEditor } from './uc-order-line-editor/'
import { UcTimeFrameEditor } from './uc-time-frame-editor'
import { UcOrderStatusEditor } from './uc-order-status-editor'

export const PropertyEditors = {
  Boolean: UcBoolean,
  Date: UcDatePicker,
  DatePicker: UcDatePicker,
  DateTime: UcDateTimePicker,
  DefinitionPicker: UcDefinitionPicker,
  DefinitionTypePicker: UcDefinitionTypePicker,
  EmailContent: UcEmailContent,
  LongText: UcLongText,
  EnumMultiSelect: UcMultiSelectEditor,
  Number: UcNumber,
  RichText: UcRichText,
  ShortText: UcShortText,
  Enum: UcSingleSelectEditor,
  EnumRadioSelect: UcRadioGroupEditor,
  EnumCheckboxSelect: UcCheckboxGroupEditor,
  Price: UcPriceEditor,
  Media: UcImagePickerEditorSingleSelect,
  ImagePickerMultiSelect: UcImagePickerEditorMultiSelect,
  Content: UcContentEditorSingle,
  ContentPickerMultiSelect: UcContentEditorMulti,
  Percent: UcPercentEditor,
  PromoCode: UcPromoCodeEditor,
  Product: UcProductEditor,
  FreeGift: UcFreeGiftEditor,
  Category: UcCategoryEditor,
  Catalog: UcCatalogEditor,
  OrderLine: UcOrderLineEditor,
  TimeFrame: UcTimeFrameEditor,
  OrderStatus: UcOrderStatusEditor,
}

export function getPropertyEditorComponent (editor) {
  if (!(editor in PropertyEditors)) {
    return console.warn(`[editors] The requested property editor '${editor}' could not be found.\nAvailable property editors are:\n- ${Object.keys(PropertyEditors).join('\n- ')}`)
  }

  return PropertyEditors[editor]
}

export default PropertyEditors
