import faker from 'faker'
import { productDefinitions, definitionGuids } from './product-definitions.mock.js'
import { generateRandomString } from '@/utils/generate-random-string.js'
import { getRandomFromArray } from '@/utils/array.utils.js'

const definitionNames = ['Shirt', 'Shoe', 'Accessory']

function generateSku () {
  return generateRandomString().toUpperCase()
}

function mockGuid () {
  return `mock-${faker.datatype.uuid()}`
}

function productFactory (isVariant) {
  const productName = faker.commerce.productName()
  const definitionName = getRandomFromArray(definitionNames)
  const entityGuid = mockGuid()
  const defintionGuid = getRandomFromArray(definitionGuids)
  const imageGuid = mockGuid()

  return {
    meta: {
      definition: {
        name: definitionName,
        guid: defintionGuid,
      },
      entityGuid: entityGuid,
      createdBy: faker.internet.email(),
      createdOn: faker.datatype.datetime(),
      modifiedBy: faker.internet.email(),
      modifiedOn: faker.datatype.datetime(),
      nodeType: isVariant ? 'productVariant' : 'product',
      legacyUrl: faker.internet.url(),
    },
    sku: {
      value: generateSku(),
      guid: mockGuid(),
    },
    variantSku: {
      value: isVariant ? generateSku() : null,
      guid: mockGuid(),
    },
    name: {
      value: productName,
      guid: mockGuid(),
    },
    displayName: {
      value: productName,
      guid: mockGuid(),
    },
    createdBy: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    createdOn: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
    modifiedBy: {
      value: faker.internet.email(),
      guid: mockGuid(),
    },
    modifiedOn: {
      value: faker.datatype.datetime(),
      guid: mockGuid(),
    },
    guid: {
      value: entityGuid,
      guid: mockGuid(),
    },
    displayOnWebsite: {
      value: faker.datatype.boolean(),
      guid: mockGuid(),
    },
    sortOrder: {
      value: faker.datatype.number(),
      guid: mockGuid(),
    },
    longDescription: {
      value: `<p>${faker.lorem.paragraph()}</p>`,
      guid: mockGuid(),
    },
    shortDescription: {
      value: `<p>${faker.lorem.paragraph()}</p>`,
      guid: mockGuid(),
    },
    primaryImage: {
      selected: isVariant ? [] : [imageGuid],
      name: isVariant ? '' : productName,
      url: isVariant ? '' : faker.random.image(),
      guid: mockGuid(),
    },
    thumbnailImage: {
      selected: isVariant ? [] : [imageGuid],
      name: isVariant ? '' : productName,
      url: isVariant ? '' : faker.random.image(),
      guid: mockGuid(),
    },
    definition: {
      value: definitionName,
      selected: [
        defintionGuid,
      ],
      guid: mockGuid(),
    },
    showOnHomepage: {
      value: faker.datatype.boolean(),
      guid: mockGuid(),
    },
    'eur 15 pct': {
      value: faker.commerce.price(),
      guid: mockGuid(),
      minimumQuantity: faker.datatype.number(),
    },
  }
}

export function generateProducts (number = 1, includeVariants = false) {
  faker.seed(123)

  let productArray = []

  for (let i = 0; i < number; i++) {
    productArray = [...productArray, productFactory(includeVariants ? faker.datatype.boolean() : includeVariants)]
  }

  return productArray
}

export { productDefinitions }
