<template>
  <div
    ref="dropdownInput"
    class="uc-dropdown-input"
    :class="cssClassObject"
    data-test="uc-dropdown-input"
    @click="toggle">
    <transition name="slide">
      <label
        v-if="name"
        v-show="(selected || disabled)"
        v-truncate
        class="uc-dropdown-input__label">
        {{ name }}
      </label>
    </transition>
    <input
      v-truncate
      class="uc-dropdown-input__field"
      data-test="uc-dropdown-input__field"
      :class="{ 'is-selected': selected }"
      :disabled="disabled"
      type="text"
      :value="value"
      readonly>
    <span class="uc-dropdown-input__toggle">
      <i
        class="uc-dropdown-input__icon"
        :class="{ 'is-rotated': isEnabled }">
        <chevron-down />
      </i>
    </span>
  </div>
</template>

<script>
import { ChevronDown16Thick as ChevronDown } from '@/assets/icons'
import { validateAgainstEnum } from '@/utils/validate-against'

const ComponentEvent = {
  TOGGLE: 'toggle',
  OPEN: 'open',
  CLOSE: 'close',
}

export const DirectionProp = {
  UP: 'dropup',
  DOWN: 'dropdown',
}

export default {
  name: 'UcDropdownInput',
  components: {
    ChevronDown,
  },
  props: {
    value: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    enabled: {
      type: Boolean,
      default: false,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
      default: function () {
        return this.value
      },
    },
    direction: {
      type: String,
      default: DirectionProp.DOWN,
      validator: validateAgainstEnum(DirectionProp),
    },
  },
  data () {
    return {
      isEnabled: false,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-dropup': this.direction === DirectionProp.UP,
        'is-dropdown': this.direction === DirectionProp.DOWN,
        'is-disabled': this.disabled,
        'is-enabled': this.isEnabled && !this.disabled,
      }
    },
  },
  watch: {
    enabled: {
      handler (value) {
        this.isEnabled = value
      },
      immediate: true,
    },
  },
  methods: {
    toggle () {
      if (this.disabled) {
        return
      }

      this.isEnabled = !this.isEnabled
      this.$emit(this.isEnabled ? ComponentEvent.OPEN : ComponentEvent.CLOSE)
      this.$emit(ComponentEvent.TOGGLE, this.isEnabled)
    },
  },
}
</script>

<style lang="stylus" scoped>
$input-height = $base-unit * 6
$border = 1px solid $base-border-color

.uc-dropdown-input__label
  secondary-petit-text()
  position absolute
  top -1 * $base-unit
  left (2 * $base-unit) - ($base-unit / 4)
  z-index 1
  max-width 'calc(100% - %s)' % (4 * $base-unit)
  padding 0 ($base-unit / 4)

  &::before
    position absolute
    top 0
    left 0
    z-index -1
    display block
    width 100%
    height 50%
    background-color $base-color-white
    content ''

input.uc-dropdown-input__field
  position relative
  flex-shrink 1
  width 100%
  height 100%
  padding 13px $base-unit * 2 15px
  border none
  background-color transparent
  outline none
  cursor pointer
  pointer-events none
  secondary-body-text()

  &.is-selected
    primary-body-text()

.uc-dropdown-input__toggle
  display inline-block
  display flex
  flex-shrink 0
  align-items center
  justify-content center
  width $input-height
  height $input-height
  border-left $border

.uc-dropdown-input__icon
  width 2 * $base-unit
  height 2 * $base-unit
  transition transform 500ms

  &.is-rotated
    transform rotate(180deg)

.uc-dropdown-input
  position relative
  display flex
  align-items center
  justify-content space-between
  width 100%
  height $input-height
  border $border
  border-radius ($base-unit / 2)
  background-color $base-color-white
  cursor pointer
  user-select none

  &.is-enabled
    &.is-dropdown
      border-bottom-right-radius 0
      border-bottom-left-radius 0

    &.is-dropup
      border-top-left-radius 0
      border-top-right-radius 0

    .uc-dropdown-input__field
      primary-body-text-bold()

  &.is-disabled
    background-color $base-background-color
    cursor default
    user-select none

    .uc-dropdown-input__label
      background transparent

    .uc-dropdown-input__icon
      color $base-color-gray-500

    .uc-dropdown-input__field
      color $base-color-gray-500
      cursor default

.slide-enter-active
  transition transform 200ms ease-out

.slide-enter
  transform translate(0, 100%)
  opacity 1

.slide-leave-active
  transition opacity 250ms

.slide-leave-to
  opacity 0
</style>
