<template>
  <div class="uc-suneditor">
    <textarea ref="editor" />
  </div>
</template>

<script>
import Suneditor from 'suneditor'
import { formatBlock, blockquote, list, table, link } from 'suneditor/src/plugins'

const ComponentEvents = {
  INPUT: 'input',
  BLUR: 'blur',
  FOCUS: 'focus',
}

export default {
  name: 'UcSunEditor',
  props: {
    value: {
      type: String,
      default: '',
    },
  },
  watch: {
    value (value) {
      this.editor.setContents(value === null ? '' : value)
    },
  },
  mounted () {
    this.editor = Suneditor.create(this.$refs.editor, {
      value: this.value,
      width: '100%',
      height: 'auto',
      plugins: [formatBlock, blockquote, list, table, link],
      resizingBar: false,
      minHeight: '96',
      formats: [
        'p',
        'h1',
        'h2',
        'h3',
      ],
      buttonList: [
        [
          'formatBlock',
        ],
        [
          'bold',
          'underline',
          'italic',
        ],
        [
          'link',
          'list',
          'table',
        ],
        [
          'codeView',
          'fullScreen',
        ],
      ],
    })

    this.editor.onInput = (_e, core) => this.$emit(ComponentEvents.INPUT, core.getContents())
    this.editor.onChange = (contents) => this.$emit(ComponentEvents.INPUT, contents)
    this.editor.onFocus = () => this.$emit(ComponentEvents.FOCUS)
    this.editor.onBlur = (e, _core) => {
      // Prevent `blur` event from being fired when focus is still within an editor element, e.g. an editor dialog
      if (this.$el.contains(e.relatedTarget)) {
        return
      }
      this.$emit(ComponentEvents.BLUR)
    }
  },
  destroyed () {
    this.editor.destroy()
  },
}
</script>

<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
@import '~suneditor/dist/css/suneditor.min.css'
/* stylelint-disable no-descending-specificity */
$suneditor-padding = $base-unit * 2

.sun-editor-editable
  primary-body-text()
  padding $suneditor-padding
  overflow-wrap anywhere
  background-color unset

  a,
  a.on
    color $base-color-brand
    text-decoration underline
    background-color unset

  &:focus
    color $base-color-brand

.sun-editor
  border unset
  border-radius $base-border-radius

  textarea
    border unset
    border-radius unset

    &:focus
      border unset

  .se-wrapper
    width auto
    margin-right  -$suneditor-padding
    margin-left -$suneditor-padding

    .se-wrapper-wysiwyg
      background-color unset

  .se-wrapper-innner
    padding-right $suneditor-padding
    padding-left $suneditor-padding

  .se-wrapper-inner.se-wrapper-code
    padding $suneditor-padding
    overflow auto !important
    font-size inherit
    line-height inherit
    background-color $legacy-color-dark-gray-300

  .se-toolbar
    width unset
    padding 10px 0 5px 0
    border-bottom $base-border
    background-color unset
    outline unset

  .se-btn-module
    padding-right $base-unit
    padding-left $base-unit
    border-radius unset

    &:last-child
      border-right none

  .se-btn-tray
    padding unset

  // Buttons
  .se-btn,
  .se-btn-list
    color $base-color-gray-700

    &:enabled:hover
      color $legacy-color-dark-gray-300

    &:enabled.active
      color $base-color-brand

    &:enabled:hover,
    &:enabled:focus
      background-color unset

    &:enabled:active,
    &:enabled:active:hover,
    &:enabled.active:hover
      background-color unset
      box-shadow unset

    &:enabled.on,
    &:enabled.on:hover
      background-color unset

    &:enabled.on:active
      box-shadow unset

  .se-list-format .se-btn-list
    &,
    &:enabled.active
      color unset

  .se-btn-module-border
    border unset
    border-right $base-border

  .se-resizing-bar
    min-height $base-unit * 4
    margin 0 -$suneditor-padding
    padding $base-unit $suneditor-padding
    border-top $base-border
    background-color unset

  .se-list-layer
    border $base-border
    box-shadow $base-box-shadow
</style>
