import VueI18n from 'vue-i18n'
import globalMessages from '@/translations/'
import dashboardMessages from '@/apps/dashboard/translations/'
import storeMessages from '@/apps/stores/translations/'
import productMessages from '@/apps/products/translations/'
import promotionMessages from '@/apps/promotions/translations/'
import orderMessages from '@/apps/orders/translations/'
import Vue from 'vue'

function createMessage (countryCode) {
  return {
    ...storeMessages[countryCode],
    ...globalMessages[countryCode],
    ...dashboardMessages[countryCode],
    ...productMessages[countryCode],
    ...promotionMessages[countryCode],
    ...orderMessages[countryCode],
    global: {
      ...globalMessages[countryCode].global,
      ...dashboardMessages[countryCode].global,
    },
  }
}

Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'en',
  fallbackLocale: 'en',
  silentFallbackWarn: true,
  silentTranslationWarn: true,
  messages: {
    en: createMessage('en'),
    da: createMessage('da'),
    sv: createMessage('sv'),
    de: createMessage('de'),
  },
})

export default i18n
