<template>
  <div
    class="uc-navigator-grid-leaf-node"
    data-test="grid-leaf-node"
    :class="cssClassObject"
    @click="onClick">
    <div class="uc-navigator-grid-leaf-node__container">
      <slot :node="node" />
    </div>
    <div
      v-truncate
      class="uc-navigator-grid-leaf-node__name">
      {{ node.name }}
    </div>
  </div>
</template>

<script>

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select',
}

export default {
  name: 'UcNavigatorGridLeafNode',
  props: {
    selected: {
      type: Boolean,
      default: false,
    },
    node: {
      type: Object,
      required: true,
    },
  },
  computed: {
    cssClassObject () {
      return {
        'is-selected': this.selected,
      }
    },
  },
  methods: {
    onClick () {
      this.$emit(ComponentEvent.SELECT, this.node)
    },
  },
}
</script>
<style lang="stylus" scoped>
.uc-navigator-grid-leaf-node__container
  position relative
  display flex
  align-items center
  justify-content center
  width 100%
  height 150px
  cursor pointer

  &::before, &::after
    position absolute
    top 0
    right 0
    bottom 0
    left 0
    z-index 1
    border-radius ($base-unit / 2)
    transition all ease-in-out 100ms
    content ''

  &::before
    border $base-border

  &::after
    border 2px solid $base-color-brand
    opacity 0

.uc-navigator-grid-leaf-node__name
  secondary-petit-text()
  margin 0
  padding $base-unit 0 0 0

// :hover
.uc-navigator-grid-leaf-node:hover
  .uc-navigator-grid-leaf-node__container::before
    border-color $base-color-brand

// .is-selected
.uc-navigator-grid-leaf-node.is-selected
  .uc-navigator-grid-leaf-node__container::before
    opacity 0

  .uc-navigator-grid-leaf-node__container::after
    transform none
    opacity 1

// :active
.uc-navigator-grid-leaf-node:active
  .uc-navigator-grid-leaf-node__container
    transform scale(0.985)

</style>
