<template>
  <div class="uc-single-select-editor">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedValue"
      :required="isRequired">
      <uc-select
        v-bind="{ options, placeholder }"
        :selected="selectedGuid"
        :readonly="isReadonly"
        :nullable="!isRequired"
        @selected="onSelected" />
    </editor-wrapper>
  </div>
</template>

<script>
import UcSelect from '@/components/forms/uc-select'
import { isEmpty } from '@/utils/object.utils.js'
import editorMixin from '../editor-mixin'

export default {
  name: 'UcSingleSelectEditor',
  components: {
    UcSelect,
  },
  mixins: [editorMixin],
  computed: {
    formattedValue () {
      return this.definition.value
    },
    placeholder () {
      return this.label || this.$t('global.selectPlaceholder')
    },
    selectedGuid () {
      return this.definition.selected === undefined ? '' : this.definition.selected[0]
    },
    selectedOption () {
      return this.options.find(option => option.guid === this.selectedGuid)
    },
  },
  methods: {
    onSelected (event) {
      const emptyOption = !this.isRequired ? [] : undefined

      this.onChange({
        label: this.placeholder,
        newValue: !isEmpty(event) ? [event.guid] : [],
        oldValue: !isEmpty(this.selectedOption) ? [this.selectedOption.guid] : emptyOption,
        valueFieldName: 'selected',
      })
    },
  },
}
</script>
