<template>
  <div class="promotion-discounts">
    <uc-entity-list v-bind="{ promotionGuid: parentGuid, contentLanguage, entityType }" />
  </div>
</template>

<script>
import { discountService, EntityType } from '@/services/'
import { UcEntityList } from '../uc-entity-list/'

export default {
  name: 'PromotionDiscounts',
  components: {
    UcEntityList,
  },
  provide: {
    entityService: discountService,
  },
  props: {
    parentGuid: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  computed: {
    entityType () {
      return EntityType.DISCOUNT
    },
  },
}
</script>
