import { mergeAllData, makeDefinitionsByDefinitionGuid } from '@/services/utils/merge-definitions-and-data.js'
import { nodeTypeApiMap } from '@/constants.js'
export { EntityType } from '@/constants'

const _definitionsLastModified = {}
const _definitionsResponseCache = {}
export class EntityService {
  constructor (entityType, httpClient) {
    this._httpClient = httpClient
    this._entityType = entityType
  }

  get _rootUrl () {
    return nodeTypeApiMap[this._entityType]
  }

  get _definitionUrl () {
    return `${this._rootUrl}/definitions/full`
  }

  get _definitionListUrl () {
    return `${this._rootUrl}/definitions`
  }

  fetchDefinitionsAsync ({ cancelToken } = {}) {
    return this._fetchDefinitionsAsync({ url: this._definitionUrl, cancelToken })
  }

  fetchDefinitionsListAsync ({ cancelToken } = {}) {
    return this._fetchDefinitionsAsync({ url: this._definitionListUrl, cancelToken })
  }

  async fetchEntitiesAsync ({ cultureCode, params, url }) {
    url = url || `${this._rootUrl}/${cultureCode}`
    const response = await this._httpClient.get(url, { params })

    return {
      ...response,
      data: await this._decorateEntityWithDefinitionsAsync(response.data),
      getShallow: () => response.data.map(store => {
        return {
          name: store.name.value,
          guid: store.meta.entityGuid,
        }
      }),
    }
  }

  async fetchEntityAsync ({ guid, cultureCode, url }) {
    url = url || `${this._rootUrl}/${guid}/${cultureCode}`
    const response = await this._httpClient.get(url)

    return {
      ...response,
      data: (await this._decorateEntityWithDefinitionsAsync([response.data]))[0],
    }
  }

  fetchEntityListAsync () {
    return this._httpClient.get(`${this._rootUrl}/shallow`)
  }

  createEntityAsync ({ name, definitionGuid, cultureCode, params }) {
    const url = cultureCode
      ? `${this._rootUrl}/${cultureCode}`
      : this._rootUrl
    return this._httpClient.post(url, { name, definitionGuid, ...params })
  }

  async _fetchDefinitionsAsync ({ url, cancelToken }) {
    const headers = {}

    if (_definitionsLastModified[url]) {
      headers['If-Modified-Since'] = _definitionsLastModified[url]
    }

    try {
      const response = await this._httpClient.get(url, { headers, cancelToken })

      _definitionsLastModified[url] = response.headers['last-modified']
      _definitionsResponseCache[url] = response
      return _definitionsResponseCache[url]
    } catch (e) {
      switch (e.response?.status) {
        case 304:
          return _definitionsResponseCache[url]
        default:
          throw new Error(e)
      }
    }
  }

  async _decorateEntityWithDefinitionsAsync (entities) {
    const response = await this.fetchDefinitionsAsync()
    const definitions = makeDefinitionsByDefinitionGuid(response.data)
    return mergeAllData(definitions, entities)
  }
}
