<template>
  <generic-details
    @resurrected="resurrected"
    @deleted="entityDeleted" />
</template>

<script>
/*
*
* Listens to whatever is selected in the catalog builder, and
* loads the data from the right details API, and passes it on to
* the GenericDetails component for editing.
*
*/

import { nodeTypeApiMap } from '@/constants.js'
import GenericDetails from './generic-details.vue'
import { createNamespacedHelpers } from 'vuex'
import { getCancellationToken, isRequestCanceled } from '@/services/http'

const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')
const persistedStore = createNamespacedHelpers('persisted')

export default {
  name: 'DetailsSection',
  components: {
    GenericDetails,
  },
  data () {
    return {
      bufferedDetailsGuid: null,
      currentDetailsGuid: null,
      typeNameMap: {
        productCategory: 'Category',
        productCatalog: 'Catalog',
      },
    }
  },
  computed: {
    ...builderStore.mapGetters(['displayedNode', 'dragInfo']),
    ...persistedStore.mapGetters(['selectedContentLanguage']),
  },
  watch: {
    async displayedNode () {
      if (this.dragInfo.dragging) {
        return
      }
      if (!this.displayedNode.nodeType) {
        this.setDetails({ details: null })
        return
      }
      this.bufferedDetailsGuid = this.displayedNode.guid
      await this.getDetails()
    },
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails']),
    async getDetailsBase () {
      try {
        const result = await this.axios.get(`${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}/${this.selectedContentLanguage}`, {
          cancelToken: this.cancelToken.token,
        })
        return result
      } catch (e) {
        if (isRequestCanceled(e)) {
          return
        }
        throw e
      }
    },
    async getDetails () {
      if (this.cancelToken) {
        this.cancelToken.cancel()
      }
      this.currentDetailsGuid = this.displayedNode.guid
      this.cancelToken = getCancellationToken()
      this.populateDetails(await this.getDetailsBase())
    },
    populateDetails (details) {
      if (!details) {
        return
      }
      if (this.currentDetailsGuid !== this.bufferedDetailsGuid) {
        this.getDetails()
        return
      }
      this.setDetails({ details: details.data, shouldUpdateLastElement: true })
    },
    entityDeleted () {
      this.setDetails({ details: null })
      this.$emit('deleted')
    },
    resurrected (nodeGuid) {
      this.$emit('resurrected', nodeGuid)
    },
  },
}
</script>
