<template>
  <form
    class="order-audits-create-modal"
    @submit.prevent="addEntry">
    <uc-modal
      :title="$t('title')"
      @hide="cancel">
      <uc-text-area
        v-model="message"
        :label="$t('placeholder')" />
      <template #footer>
        <!-- Add button-->
        <uc-button
          :disabled="isDisabled"
          :type="buttonTypeSubmit">
          {{ $t('global.add') }}
          <template #after>
            <loading-spinner v-show="isPosting" />
          </template>
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<i18n>
{
  "en": {
    "title": "Add logbook entry",
    "placeholder": "Log message"
  }
}
</i18n>

<script>
import { UcModal } from '@/components/widgets/uc-modal/'
import { UcButton, UcButtonType } from '@/components/forms/uc-button/'
import { LoadingSpinner } from '@/components/widgets/loading-spinner/'
import { UcTextArea } from '@/components/forms/uc-text-area/'

const ComponentEvent = {
  ADD: 'add',
  CANCEL: 'cancel',
}

const ComponentState = {
  IDLE: 'idle',
  POSTING: 'posting',
}

export default {
  name: 'OrderAuditCreateModal',
  components: {
    UcModal,
    UcButton,
    UcTextArea,
    LoadingSpinner,
  },
  data () {
    return {
      message: '',
      state: ComponentState.IDLE,
    }
  },
  computed: {
    buttonTypeSubmit: () => UcButtonType.SUBMIT,
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    isDisabled () {
      return this.message.length === 0
    },
  },
  methods: {
    addEntry () {
      this.$emit(ComponentEvent.ADD, this.message)
      this.state = ComponentState.POSTING
    },
    cancel () {
      this.$emit(ComponentEvent.CANCEL)
    },
  },
}
</script>
