import { rest } from 'msw'
import { categories } from '@/mocks/entities/categories/'
import { difference, intersection } from '@/utils/array.utils.js'
import { ApiStatusCodes } from '@/constants.js'

let selected = []

const baseUrl = '/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/categories'
const URLs = {
  POST_CATEGORIES: `${baseUrl}/:cultureCode`,
  GET_CATEGORIES: `${baseUrl}/:cultureCode`,
  DELETE_CATEGORIES: baseUrl,
}

const RESPONSE_DELAY = 200

export const categoryCriterionHandlers = [
  rest.post(URLs.POST_CATEGORIES, (req, res, ctx) => {
    const selectedCategoriesGuids = selected.map(category => category.guid)

    const duplicateGuids = intersection(req.body, selectedCategoriesGuids)
    const newGuids = difference(req.body, selectedCategoriesGuids)
    const newCategories = categories.filter(category => newGuids.includes(category.guid))

    selected = [...selected, ...newCategories]

    const responseDuplicates = duplicateGuids.map(guid => ({ guid, message: 'duplicate', statusCode: ApiStatusCodes.ALREADY_EXIST }))
    const responseNewProducts = newGuids.map(guid => ({ guid, message: 'success', statusCode: ApiStatusCodes.OK }))
    const response = [...responseDuplicates, ...responseNewProducts]

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get(URLs.GET_CATEGORIES, (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const response = selected.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', selected.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.delete(URLs.DELETE_CATEGORIES, (req, res, ctx) => {
    selected = selected.filter(category => !req.body.includes(category.guid))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
]
