<template>
  <form
    class="uc-orders-change-status"
    @submit.prevent="changeOrderStatus">
    <uc-modal
      :title="$t('title')"
      @hide="cancel">
      <uc-order-status-editor
        :language="contentLanguage"
        :entity-guid="entityGuid"
        :definition="{
          value: currentStatusName,
        }"
        @change="setSelectedOrderStatus" />

      <!-- Submit -->
      <template #footer>
        <div class="uc-orders-change-status__submit">
          <uc-button
            :disabled="!isEnabled"
            type="submit"
            theme="primary"
            @click="changeOrderStatus">
            {{ $t('global.save') }}
            <template
              v-if="isPosting"
              #after>
              <loading-spinner is-loading />
            </template>
          </uc-button>
        </div>
        <uc-button
          theme="text"
          @click="cancel">
          {{ $t('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<i18n>
{
  "en": {
    "title": "Change status"
  }
}
</i18n>

<script>
import { UcModal } from '@/components/widgets/uc-modal/'
import { UcButton } from '@/components/forms/uc-button/'
import { LoadingSpinner } from '@/components/widgets/loading-spinner/'
import { UcOrderStatusEditor } from '@/components/editors/uc-order-status-editor'
import { orderService } from '@/services/'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

const ComponentEvent = {
  CANCEL: 'cancel',
  STATUS_CHANGE: 'status-change',
}

export default {
  name: 'UcOrdersChangeStatus',
  components: {
    UcModal,
    UcButton,
    LoadingSpinner,
    UcOrderStatusEditor,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    currentStatusName: {
      type: String,
      required: true,
    },
    selectedOrders: {
      type: Array,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      selectedOrderStatus: null,
      errorMessage: '',

      // TODO(ch-14269): When the story is finished, the pagination boiler code shouldn't exist on this level anymore.
      currentPage: 0,
      pageSize: Number.MAX_SAFE_INTEGER,
    }
  },
  computed: {
    isEnabled () {
      return !!this.selectedOrderStatus?.guid
    },
    entityGuid () {
      return this.selectedOrdersGuids[0]
    },
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
    selectedOrdersGuids () {
      return this.selectedOrders.map(order => order.guid)
    },
  },
  methods: {
    setSelectedOrderStatus (payload) {
      this.selectedOrderStatus = 'save' in payload ? Object.values(payload.save)[0] : null
    },
    async changeOrderStatus () {
      if (this.isPosting) {
        return
      }

      const cultureCode = this.contentLanguage
      const guids = [...this.selectedOrdersGuids]
      const statusFieldGuid = this.selectedOrdersGuids[0]

      const payload = {
        status: {
          guid: statusFieldGuid,
          value: this.selectedOrderStatus?.guid,
        },
      }

      try {
        this.state = ComponentState.POSTING

        const updateStatusOfOrdersResponse = await orderService.updateStatusOfOrders({ cultureCode, guids, payload })
        const args = {
          operationResponse: updateStatusOfOrdersResponse.data,
          status: this.selectedOrderStatus?.value,
        }
        this.$emit(ComponentEvent.STATUS_CHANGE, args)

        this.state = ComponentState.IDLE
      } catch (e) {
        this.state = ComponentState.ERROR
        if (e.response.data.message) {
          this.errorMessage = e.response.data.message
        } else {
          throw e
        }
      }
    },
    cancel () {
      this.$emit(ComponentEvent.CANCEL)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-orders-change-status__submit
  height $base-unit * 6
</style>
