<template>
  <uc-dl :one-column="!renderInCompactMode">
    <template v-for="(property, propertyKey) in entity">
      <template v-if="shouldRenderProperty(property, propertyKey)">
        <uc-dt
          v-if="renderInCompactMode"
          :key="property.guid"
          data-test="property-label">
          {{ property.label }}
        </uc-dt>
        <uc-dd
          :key="property.guid"
          data-test="property-value">
          <component
            :is="getPropertyEditorComponent(property.editor)"
            :name="propertyKey"
            no-currency-code
            :definition="property"
            :language="contentLanguage"
            :compact="renderInCompactMode"
            @change="onChange" />
        </uc-dd>
      </template>
    </template>
  </uc-dl>
</template>

<script>
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcDl, UcDt, UcDd } from '@/components/generic/uc-dl/'

const ComponentEvent = {
  CHANGE: 'change',
}


export default {
  name: 'UcEntityPropertyList',
  components: {
    UcDl,
    UcDt,
    UcDd,
  },
  props: {
    entity: {
      type: Object,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
    renderInCompactMode: {
      type: Boolean,
      default: true,
    },

    /**
     * Properties that should not be rendered
     * Example: ['meta', 'definition']
     */
    ignoredProperties: {
      type: Array,
      default: () => [],
    },

    /**
     * Hide properties whose value are null or undefined
     */
    noNullProperties: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    getPropertyEditorComponent,
    shouldRenderProperty (property, propertyKey) {
      const isIgnoredProperty = propertyKey => this.ignoredProperties.some(ignoredPropertyKey => ignoredPropertyKey === propertyKey)
      const isNullProperty = property => !property.value || property.value === null || property.value === undefined

      return !isIgnoredProperty(propertyKey) && !(this.noNullProperties && isNullProperty(property))
    },
    onChange (payload) {
      this.$emit(ComponentEvent.CHANGE, payload)
    },
  },
}
</script>
