<template>
  <div class="uc-promo-code-generator-toggle">
    <button-toggle-group
      v-model="promoCodeType"
      :values="promoCodeTypes"
      data-test="promocode-generator-toggle"
      @input="onToggle">
      <template #default="{ value }">
        <span>{{ value }}</span>
      </template>
    </button-toggle-group>
  </div>
</template>

<script>
import ButtonToggleGroup from '@/components/generic/button-toggle-group/'

const ComponentEvent = {
  TOGGLE: 'toggle',
}

export const PromoCodeType = {
  MANUAL: 'Manual',
  UNIQUE: 'Unique',
}

export default {
  name: 'UcPromoCodeGeneratorToggle',
  components: {
    ButtonToggleGroup,
  },
  data () {
    return {
      promoCodeType: PromoCodeType.MANUAL,
    }
  },
  computed: {
    promoCodeTypes () {
      return Object.values(PromoCodeType)
    },
  },
  methods: {
    onToggle (value) {
      this.$emit(ComponentEvent.TOGGLE, value)
    },
  },
}
</script>
