<template>
  <div class="button-toggle-group">
    <span class="button-toggle-group__track">
      <label
        v-for="(item, itemIndex) in values"
        ref="button-toggle"
        :key="itemIndex"
        class="button-toggle"
        data-test="button-toggle">
        <input
          v-model="checkedValue"
          type="radio"
          :name="group"
          :value="item"
          class="button-toggle__radio"
          data-test="button-toggle-radio">
        <span class="button-toggle__content">
          <slot
            :value="item"
            :index="itemIndex" />
        </span>
      </label>
      <span
        data-test="button-toggle-slider"
        class="button-toggle-group__slider"
        :style="cssStyleObject" />
    </span>
  </div>
</template>

<script>
import createId from '@/utils/id-generator'

const ComponentEvent = {
  INPUT: 'input',
}

export default {
  name: 'ButtonToggleGroup',
  props: {
    value: {
      type: String,
      default: '',
    },
    values: {
      type: Array,
      required: true,
    },
    group: {
      type: String,
      default: () => createId(),
    },
  },
  data () {
    return {
      checkedValue: this.value || this.values[0],
      buttonToggleWidth: 0,
    }
  },
  computed: {
    index () {
      return this.values.indexOf(this.checkedValue)
    },
    cssStyleObject () {
      return {
        width: `${this.buttonToggleWidth}px`,
        transform: `translateX(${(100 * this.index)}%)`,
      }
    },
  },
  watch: {
    checkedValue: {
      handler (value) {
        this.$emit(ComponentEvent.INPUT, value)
      },
      immediate: true,
    },
  },
  mounted () {
    function toLargestOffsetWidth (previous, current) {
      return previous.offsetWidth > current.offsetWidth ? previous.offsetWidth : current.offsetWidth
    }

    // Find buttonToggle with the largest width - this will determine the width of it's siblings
    const buttonToggles = this.$refs['button-toggle']
    this.buttonToggleWidth = buttonToggles.reduce(toLargestOffsetWidth)

    // Use the largest width as width of all buttonToggles
    buttonToggles.map(buttonToggle => { buttonToggle.style.width = `${this.buttonToggleWidth}px` })
  },
}
</script>

<style lang="stylus" scoped>
$height = $base-unit * 4
$border-radius = $base-unit * 4
$background-color-track = $base-color-gray-200
$background-color-slider = $base-color-white
$box-shadow-slider = 0 0 4px 0 rgba(162, 162, 162, 0.15)

.button-toggle-group
  position relative
  display flex
  align-items center
  user-select none

.button-toggle-group__track
  position relative
  display flex
  align-items center
  justify-content space-between
  height $height
  border-radius $border-radius
  background-color $background-color-track
  cursor pointer

.button-toggle-group__slider
  position absolute
  bottom 0
  left 0
  height $height
  border-radius $border-radius
  background-color $background-color-slider
  box-shadow $box-shadow-slider
  transition all 0.4s ease-out

.button-toggle
  position relative
  z-index 1
  display flex
  align-items center
  justify-content center
  max-width $base-unit * 32
  height 100%
  white-space nowrap
  cursor pointer

.button-toggle__radio
  display none

.button-toggle__content
  display flex
  align-items center
  justify-content center
  padding 0 $base-unit * 2
  primary-details-text(600)
</style>
