import {
  CUSTOM,
  LAST_THREE_MONTHS,
  THIS_MONTH,
  THIS_WEEK,
  THIS_YEAR,
  TODAY,
} from './date.utils.options.js'
import { differenceInDays, isToday } from 'date-fns'

export function translateDateToTimeFrame (from, to) {
  if (!from || !to) {
    return CUSTOM
  }
  if (validateCombination(to, from, 0)) {
    return TODAY
  }
  if (validateCombination(to, from, 7)) {
    return THIS_WEEK
  }
  if (validateCombination(to, from, 30)) {
    return THIS_MONTH
  }
  if (validateCombination(to, from, 90)) {
    return LAST_THREE_MONTHS
  }
  if (validateCombination(to, from, 365)) {
    return THIS_YEAR
  } else {
    return CUSTOM
  }
}
export function validateCombination (from, to, expectedValue) {
  return differenceInDays(to, from) === expectedValue && isToday(to)
}
