<template>
  <div class="uc-select">
    <uc-dropdown
      v-bind="{ options, collapse }"
      :readonly="readonly"
      :placeholder="label"
      :name="placeholder"
      :selected="hasSelectedOption"
      @toggle="onToggle">
      <template #pending>
        <slot name="pending" />
      </template>
      <template #default="{ option, searchTerm }">
        <uc-radio
          :label="option.label"
          :value="option.guid"
          :checked="setChecked(option.guid)"
          :group="fieldId"
          :nullable="nullable"
          :readonly="option.isDisabled"
          @change="onOptionChange">
          <!-- eslint-disable-next-line vue/no-v-html -->
          <span v-html="highlight(option.label, searchTerm)" />
          <template #icon>
            <span class="uc-select__icon">
              <check-icon />
            </span>
          </template>
        </uc-radio>
      </template>
    </uc-dropdown>
  </div>
</template>

<script>
import UcRadio from '@/components/forms/uc-radio'
import UcDropdown from '@/components/forms/uc-dropdown'
import { Check24Normal as CheckIcon } from '@/assets/icons'
import highlight from '@/utils/word-highlight'
import getId from '@/utils/id-generator.js'
import { isEmpty } from '@/utils/object.utils.js'

export default {
  name: 'UcSelect',
  components: {
    UcDropdown,
    UcRadio,
    CheckIcon,
  },
  props: {
    options: {
      validator: prop => Array.isArray(prop) || prop === undefined,
      required: true,
    },
    selected: {
      validator: prop => typeof prop === 'string' || prop === undefined,
      default: undefined,
    },
    placeholder: {
      type: String,
      default: function () {
        return this.$t('global.selectPlaceholder')
      },
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    nullable: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      collapse: false,
      selectedOption: undefined, // radio event object { value: String, label: String }
    }
  },
  computed: {
    fieldId () {
      return getId()
    },
    label () {
      return (this.selectedOption && this.selectedOption.label) || this.placeholder
    },
    hasSelectedOption () {
      return !isEmpty(this.selectedOption)
    },
  },
  watch: {
    selected: {
      handler (value) {
        if (value === undefined || this.options === undefined) {
          this.selectedOption = undefined
          return
        }

        const match = this.options.find(option => option.guid === value)
        this.selectedOption = !isEmpty(match) ? { label: match.label, value: match.guid } : undefined
      },
      immediate: true,
    },
  },
  methods: {
    highlight,
    setChecked (optionGuid) {
      return this.hasSelectedOption && this.selectedOption.value === optionGuid
    },
    onToggle (event) {
      this.collapse = !event
    },
    onOptionChange (event) {
      this.collapse = true
      this.selectedOption = event.checked ? event : {}
      const payload = this.hasSelectedOption ? { label: event.label, guid: event.value } : {}

      this.$emit('selected', payload)
    },
  },
}
</script>

<style lang="stylus" scoped>
>>> .highlight
  font-weight bold

.uc-select__icon
  margin-top 4px
</style>
