import ApiSettings from './security/api/views/api-settings.vue'
import { LayoutType } from '@/layouts/'

export function SettingsApp ({ router }) {
  router.addRoute({
    path: '/settings/security/api',
    name: 'apiSettings',
    component: ApiSettings,
    meta: {
      layout: LayoutType.STICKY_HEADER,
    },
  })
}
