<template>
  <use-selection
    v-model="selection"
    :items="rows"
    :sync="false"
    @select="onSelect"
    @deselect="onDeselect">
    <template #default="{ toggle, isSelected }">
      <uc-data-grid
        v-bind="{ rows, disableInfiniteScroll, rowKey, headingless }"
        :columns="computedColumns"
        @next="onNext"
        @[rowClickEvent]="onRowClick($event)">
        <template #select="{ row }">
          <div class="uc-data-grid-selectable__checkbox">
            <uc-checkbox
              :id="row[rowKey]"
              data-test="data-grid-checkbox"
              :readonly="disableSelectionWhen(row, rows, selection)"
              :checked="isSelected(row[rowKey])"
              @change="toggle(row[rowKey])" />
          </div>
        </template>
        <template
          v-for="(index, name) in $scopedSlots"
          #[name]="props">
          <slot
            :name="name"
            v-bind="props">
            {{ props.cell }}
          </slot>
        </template>
      </uc-data-grid>
    </template>
  </use-selection>
</template>

<script>
import { UcDataGrid } from '../uc-data-grid'
import UcCheckbox from '@/components/forms/uc-checkbox'
import UseSelection from '@/components/wrappers/use-selection'

const ComponentEvent = {
  INPUT: 'input',
  SELECT: 'select',
  DESELECT: 'deselect',
  NEXT: 'next',
  ROW_CLICK: 'row-click',
}

export default {
  name: 'UcDataGridSelectable',
  components: {
    UseSelection,
    UcDataGrid,
    UcCheckbox,
  },
  props: {
    columns: {
      type: Array,
      required: true,
    },
    rows: {
      type: Array,
      required: true,
    },
    rowKey: {
      type: [Number, String],
      required: true,
    },
    value: {
      type: Array,
      default: null,
    },
    checkboxPosition: {
      type: Number,
      default: 0,
    },
    disableInfiniteScroll: {
      type: Boolean,
      default: false,
    },
    headingless: {
      type: Boolean,
      default: false,
    },
    disableSelectionWhen: {
      type: Function,
      default: () => false,
    },
  },
  data () {
    return {
      internalValue: [],
    }
  },
  computed: {
    computedColumns () {
      const columnsCopy = [...this.columns]
      const checkboxCell = {
        key: 'select',
        width: '64px',
        cellStyle: {
          padding: '0',
        },
      }
      columnsCopy.splice(this.checkboxPosition, 0, checkboxCell)

      return columnsCopy
    },
    rowClickEvent () {
      return this.$listeners && this.$listeners[ComponentEvent.ROW_CLICK] ? ComponentEvent.ROW_CLICK : null
    },
    selection: {
      get () {
        return this.value ? this.value.map(val => val[this.rowKey]) : this.internalValue
      },
      set (newValue) {
        this.internalValue = newValue

        if (this.value) {
          const payload = this.rows.filter(row => newValue.includes(row[this.rowKey]))
          this.$emit(ComponentEvent.INPUT, payload)
        }
      },
    },
  },
  methods: {
    onDeselect (deselectedRows) {
      const rows = this.rows.filter(row => deselectedRows.includes(row[this.rowKey]))
      this.$emit(ComponentEvent.DESELECT, rows[0])
    },
    onSelect (selectedRows) {
      const rows = this.rows.filter(row => selectedRows.includes(row[this.rowKey]))
      this.$emit(ComponentEvent.SELECT, rows[0])
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    onRowClick (row) {
      this.$emit(ComponentEvent.ROW_CLICK, row)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-data-grid-selectable__checkbox
  width 100%
  min-width $base-unit * 8
  height 100%
</style>
