<template>
  <div class="list-wrapper">
    <div class="list-container">
      <!-- List header -->
      <header class="list-header-area">
        <div class="list-heading-wrapper">
          <h2 class="list-heading">
            {{ header }}
            <span
              v-if="listCount > 0"
              class="item-count">
              ({{ listCount }})
            </span>
          </h2>
        </div>
        <div class="list-heading-wrapper">
          <slot name="actionsHeader" />
          <div class="list__settings">
            <settings
              v-model="selection"
              :items="currentList.columns"
              @select="toggleColumn"
              @deselect="toggleColumn" />
          </div>
        </div>
      </header>

      <!-- List notifications -->
      <div
        v-if="hasNotificationSlot"
        class="list-container__notifications">
        <slot
          name="notifications"
          :selected="selected"
          :clearSelected="clearSelected" />
      </div>

      <!-- The actual table -->
      <list-table
        :ref="id"
        :list="currentList"
        :total-count="listCount"
        :content-language="selectedContentLanguage"
        v-bind="{ defaultColumnsConfiguration, selected, selectableRows, isLoadingItems }"
        @columnsDragged="columnsDragged"
        @needsMoreData="loadMoreData"
        @rowClick="onRowClick"
        @sortBy="sortBy"
        @toggleRow="toggleSelected">
        <template
          v-if="isLoadingCategory"
          #categoryLoader>
          <div class="list-table__loading-indicator">
            <loading-indicator
              :is-loading="isLoadingItems" />
          </div>
        </template>
      </list-table>

      <!-- Footer slot -->
      <div
        v-if="hasFooterSlot"
        class="list-container__actions-footer">
        <slot
          name="actionsFooter"
          :selected="selected"
          :clearSelected="clearSelected"
          :removeItems="removeItems" />
      </div>
    </div>
  </div>
</template>

<script>
import Settings from './settings'
import LoadingIndicator from '@/components/widgets/loading-indicator'

import { mergeAllData } from '@/services/utils/merge-definitions-and-data.js'
import getId from '@/utils/id-generator.js'
import { getCancellationToken, isRequestCanceled } from '@/services/http'
import { getEntityService } from '@/services'
import { distinctByEntityGuid } from '@/utils/distinct'
import Helpers from '@/utils/generic-list-helpers'

import { createNamespacedHelpers, mapGetters, mapMutations } from 'vuex'
const detailsStore = createNamespacedHelpers('details')
const persistedStore = createNamespacedHelpers('persisted')

function mapMetadata (arr, suffix) {
  let index = 0
  return arr.map(x => {
    x.index = `${index++}-${suffix}`
    x.show = false
    return x
  })
}

const ComponentEvent = {
  SORT: 'sortBy',
  INITIAL_LOAD_STARTED: 'initialLoadStarted',
  INITIAL_LOAD_FINISHED: 'initialLoadFinished',
  ROW_CLICK: 'row-clicked',
  // Temporary fix until empty state is handled from within list-table
  ROWS_ADDED: 'rows-added',
  ROWS_REMOVED: 'rows-removed',
  INPUT: 'input',
  CHANGE: 'change',
  FILTER: 'filter',
}

export default {
  name: 'List',
  components: {
    Settings,
    // Fix for when loading an editor containing a list.
    // Otherwise, this will cause a recursion error.
    ListTable: () => import(/* webpackMode: "eager" */ './list-table.vue'),
    LoadingIndicator,
  },
  props: {
    id: {
      type: String,
      required: true,
    },
    nodeType: {
      type: String,
      required: true,
    },
    dataUrl: {
      type: String,
      default: '',
    },
    header: {
      type: String,
      required: true,
    },
    selectableRows: {
      type: Boolean,
      default: false,
    },
    defaultColumnsConfiguration: {
      type: Object,
      default: () => ({}),
    },
    value: {
      type: Array,
      default: () => [],
    },
    showVariants: {
      type: Boolean,
      default: false,
    },
    displayAllColumns: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      isLoadingItems: false,
      isLoadingCategory: false,
      selected: [],
      selectedColumns: null,
      listCount: -1,
      currentList: {
        localStorageKey: `uc-list-setting-${this.id}`,
        columns: [],
        rows: [],
      },
      hasLoadedDefinitions: false,
    }
  },
  computed: {
    ...mapGetters(['definitionsByGuid']),
    ...detailsStore.mapGetters(['currentDetails']),
    ...persistedStore.mapGetters(['selectedContentLanguage']),
    hasFooterSlot () {
      return !!this.$scopedSlots.actionsFooter
    },
    hasNotificationSlot () {
      return !!this.$scopedSlots.notifications
    },
    shownPriceColumns () {
      return this.currentList.columns.filter(column => column.show && column.editor === 'Price')
    },
    selection: {
      get () {
        return this.selectedColumns ?? this.currentList.columns.filter(column => column.show)
      },
      set (value) {
        this.selectedColumns = value
      },
    },
  },
  watch: {
    dataUrl () {
      this.load()
    },
    value: {
      handler (val) {
        this.selected = val
      },
      immediate: true,
    },
  },
  created () {
    this.entityService = getEntityService(this.nodeType)
    this.load()
  },
  methods: {
    ...mapMutations(['setDefinitions']),
    /**
     * List manipulation
     **/
    applyColumnOrder () {
      const columnConfiguration = Helpers.getLocalStorageData(this.currentList)
      let columns = Helpers.mergeColumns(this.currentList, columnConfiguration.defaultOrder)
      let columnNames = columnConfiguration.defaultOrder

      if (columnConfiguration.currentOrder.length !== 0) {
        columns = Helpers.mergeColumns(this.currentList, columnConfiguration.currentOrder)
        columnNames = columnConfiguration.currentOrder
      }

      this.setListColumns(columns)
      this.toggleListColumns(columnNames)
    },
    toggleListColumn (index) {
      const selectedColumn = this.currentList.columns.find(column => column.index === index)

      if (!selectedColumn) {
        return
      }

      selectedColumn.show = !selectedColumn.show
      Helpers.saveColumnOrder(this.currentList)

      // Only want to emit if a column has been toggled ON. When toggling off we don't want to re-request data.
      if (selectedColumn.show && selectedColumn.editor === 'Price') {
        this.$emit(ComponentEvent.FILTER, this.shownPriceColumns)
      }
    },
    toggleListColumns (columnNames) {
      this.currentList.columns.filter(column => columnNames.includes(column.key)).forEach(column => { column.show = true })
      this.currentList.columns.filter(column => !columnNames.includes(column.key)).forEach(column => { column.show = false })

      Helpers.saveColumnOrder(this.currentList)
    },
    addListRows (rows) {
      this.currentList.rows = distinctByEntityGuid(this.currentList.rows.concat(rows))
    },
    setListColumns (columns, defaultColumnNames = [], initDefaultColumns = false) {
      this.currentList.columns = columns
      Helpers.saveColumnOrder(this.currentList)

      if (initDefaultColumns) {
        Helpers.setDefaultColumnOrder(this.currentList, defaultColumnNames)
      }
    },
    removeItems (itemGuids) {
      const filteredList = this.currentList.rows.filter(row => !itemGuids.includes(row.meta.entityGuid))
      this.currentList.rows = filteredList
      this.listCount -= itemGuids.length
      this.$emit(ComponentEvent.ROWS_REMOVED, this.listCount)
    },
    addItems (newItems) {
      this.currentList.rows = [...newItems, ...this.currentList.rows]
      this.listCount += newItems.length
      this.$emit(ComponentEvent.ROWS_ADDED, this.listCount)
    },
    toggleSelected (row) {
      const index = this.selected.findIndex(selected => selected.meta.entityGuid === row.meta.entityGuid)

      if (index === -1) {
        this.selected.push(row)
      } else {
        this.selected.splice(index, 1)
      }

      this.$emit(ComponentEvent.INPUT, this.selected)
    },
    sortBy (columnKey) {
      this.$emit(ComponentEvent.SORT, columnKey)
    },
    columnsDragged (columns) {
      this.setListColumns(columns)
    },
    clearSelected () {
      this.selected = []
    },
    async onRowClick (row) {
      // HACK: Enable fake 2-way-binding
      const rowIndex = this.currentList.rows.indexOf(row)
      const guid = row.meta.entityGuid

      const productUrl = `/products/${guid}/${this.selectedContentLanguage}`

      const entityData = await this.axios.get(productUrl, {
        cancelToken: this.cancelToken.token,
      })

      this.currentList.rows[rowIndex] = mergeAllData(this.definitionsByGuid, [entityData.data])[0]

      this.$emit(ComponentEvent.ROW_CLICK, this.currentList.rows[rowIndex])
      // workaround to allow single-select
      this.$emit(ComponentEvent.CHANGE, [row])
    },
    toggleColumn ({ index }) {
      this.toggleListColumn(index)
    },
    /**
     * Data fetching: TODO: Move out to data provider
     **/
    async load () {
      this.isLoadingCategory = true
      this.$emit(ComponentEvent.INITIAL_LOAD_STARTED)

      if (this.cancelToken) {
        this.cancelToken.cancel()
      }
      this.cancelToken = getCancellationToken()

      if (!this.hasLoadedDefinitions) {
        await this.loadMetadata()

        this.$emit(ComponentEvent.FILTER, this.shownPriceColumns)
      }
      if (!this.dataUrl) {
        return
      }

      await this.loadData()
      this.isLoadingCategory = false
    },
    async loadMetadata () {
      this.isLoadingItems = true
      try {
        const definitions = (await this.entityService.fetchDefinitionsAsync({ cancelToken: this.cancelToken.token })).data
        this.setDefinitions(definitions)
        const uniqueColumns = this.groupDefinitionFields(definitions)
        const mappedColumns = this.filterByListType(mapMetadata(uniqueColumns, getId()))
        const listColumns = this.showVariants && !this.displayAllColumns ? this.includeOnlyCurrentDefinitionFields(mappedColumns) : mappedColumns
        this.setListColumns(listColumns, Object.keys(this.defaultColumnsConfiguration), true)
        this.applyColumnOrder()
        this.hasLoadedDefinitions = true
        return definitions
      } catch (error) {
        if (isRequestCanceled(error)) {
          return
        }
        if (!error.response || error.response.status !== 304) {
          throw error
        }
      }
      this.isLoadingItems = false
    },
    async loadData () {
      this.isLoadingItems = true

      try {
        const result = await this.axios.get(this.dataUrl, {
          cancelToken: this.cancelToken.token,
        })
        this.listCount = Number(result.headers['x-total-count'])
        this.currentList.rows = result.data
        this.$emit(ComponentEvent.INITIAL_LOAD_FINISHED, this.listCount)
        this.$nextTick(() => {
          if (!this.$refs[this.id]) {
            return
          }

          this.$refs[this.id].initColumnsConfiguration(this.defaultColumnsConfiguration, 'width')
          this.$refs[this.id].resetScrollPosition()
        })
      } catch (error) {
        if (isRequestCanceled(error)) {
          return
        }
        throw error
      } finally {
        this.isLoadingItems = false
      }
    },
    async loadMoreData () {
      if (this.isLoadingItems || this.currentList.rows.length >= this.listCount) {
        return
      }

      this.isLoadingItems = true

      try {
        let url = this.dataUrl

        const params = new URLSearchParams()
        params.set('limit', 30)
        params.set('startAt', this.currentList.rows.length)

        url = `${url}&${params.toString()}`

        const result = await this.axios.get(url, {
          cancelToken: this.cancelToken.token,
        })
        this.addListRows(result.data)
      } catch (error) {
        if (isRequestCanceled(error)) {
          return
        }
        throw error
      } finally {
        this.isLoadingItems = false
      }
    },
    /**
     * Data fetching helper methods
     */
    filterByListType (columns) {
      return columns.filter(column => column.isVariantProperty === this.showVariants || column.isVariantProperty === null)
    },
    includeOnlyCurrentDefinitionFields (columns) {
      const currentDefinitionGuid = this.currentDetails.meta.definition.guid
      const definitionProperties = this.definitionsByGuid[currentDefinitionGuid]
      const usedPropertiesGuids = Object.keys(definitionProperties).map(key => definitionProperties[key].guid)
      const byPropertyGuid = ({ guid }) => usedPropertiesGuids.includes(guid)
      return columns.filter(byPropertyGuid)
    },
    groupDefinitionFields (definitions) {
      const map = new Map()
      definitions.forEach(definition => {
        Object.keys(definition).forEach(fieldKey => {
          const currentField = { ...definition[fieldKey] }
          currentField.key = fieldKey
          map.set(fieldKey, currentField)
        })
      })
      return [...map.values()]
    },
  },
}
</script>

<style lang="stylus" scoped>
.list-wrapper
  height 100%

.list-container
  position relative
  display flex
  flex-direction column
  height 100%
  margin 0 auto
  padding 0
  user-select none

.list-header-area
  position relative
  z-index 105
  display flex
  align-items center
  justify-content space-between
  padding $base-unit * 3

.list-heading-wrapper
  display flex
  flex-direction row

.list-heading
  primary-heading-2()
  margin 0

.item-count
  font-weight normal

.list-table__loading-indicator
  position absolute
  z-index 105
  display flex
  align-items center
  justify-content center
  width 100%
  height 100%
  background $base-color-white

.list-container__actions-footer
  position absolute
  right 0
  bottom 0
  left 0
  z-index 104 // Necessary to overflow notification and list header
  display flex
  align-items center
  justify-content center
  padding-bottom $base-unit * 5
  overflow hidden
  pointer-events none

.list-container__notifications
  position absolute
  top $base-unit * 16
  right 0
  left 0
  z-index 102
  padding $base-unit * 2

  &:empty
    display none

.list__settings
  margin-left 2 * $base-unit
</style>
