<template>
  <div class="order-edit">
    <!-- State: Idle -->
    <details-layout
      v-if="isIdle"
      @back-click="closeOrder">
      <generic-details
        @change="onChange" />
    </details-layout>

    <!-- State: Initializing -->
    <div
      v-else-if="isInitializing"
      class="order-edit__state">
      <loading-indicator is-loading />
    </div>

    <!-- State: Error -->
    <div
      v-else-if="hasError"
      class="order-edit__state">
      <p>{{ errorMessage }}</p>
      <uc-button
        theme="text"
        @click="closeOrder">
        <template #before>
          <back-icon />
        </template>
        {{ $t('global.back') }}
      </uc-button>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers, mapMutations } from 'vuex'
import DetailsLayout from '@/layouts/details-layout.vue'
import GenericDetails from '@/components/generic/details-view/generic-details.vue'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import { UcButton } from '@/components/forms/uc-button/'
import { ArrowBack16Normal as BackIcon } from '@/assets/icons/'
import { orderService } from '@/services/'

const detailsStore = createNamespacedHelpers('details')

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR: 'error',
}

export default {
  name: 'OrderEdit',
  components: {
    DetailsLayout,
    GenericDetails,
    LoadingIndicator,
    UcButton,
    BackIcon,
  },
  props: {
    orderGuid: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      errorMessage: '',
    }
  },
  computed: {
    ...detailsStore.mapGetters(['currentDetails']),
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
  },
  async created () {
    await this.loadOrder()
  },
  destroyed () {
    this.clearDetails()
  },
  methods: {
    ...mapMutations(['clearUndos']),
    ...detailsStore.mapMutations(['setDetails', 'clearDetails']),
    closeOrder () {
      this.clearUndos()
      this.$router.back()
    },
    async onChange () {
      await this.loadOrder()
    },
    async loadOrder () {
      try {
        const payload = {
          guid: this.orderGuid,
          cultureCode: this.contentLanguage,
        }
        const entity = (await orderService.fetchEntityAsync(payload)).data
        this.setDetails({ details: entity })
        this.state = ComponentState.IDLE
        this.errorMessage = ''
      } catch (error) {
        this.state = ComponentState.ERROR
        this.errorMessage = error.response.data.message
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.order-edit__state
  display flex
  flex-direction column
  align-items center
  justify-content center
  height 100vh
</style>
