<template>
  <div class="uc-order-status-editor">
    <!-- State: Loading -->
    <div
      v-if="isInitializing"
      class="uc-order-status-editor__loading">
      <loading-indicator is-loading />
    </div>

    <!-- State: Empty -->
    <div
      v-if="!isInitializing && !hasAvailableStatuses"
      class="uc-order-status-editor__state">
      <p
        class="uc-order-status-editor__state-empty">
        {{ $tc('noAvailableStatuses') }}
      </p>
    </div>

    <div
      v-show="hasAvailableStatuses && (isIdle || isPosting || hasError)"
      class="uc-order-status-editor__form">
      <!-- Current status -->
      <div class="uc-order-status-editor__form-field">
        <uc-text-input
          :label="$t('currentStatus')"
          :value="currentStatusName"
          readonly />
      </div>

      <!-- New status -->
      <div class="uc-order-status-editor__form-field">
        <uc-select
          :options="statusOptions"
          :placeholder="$t('changeStatusTo')"
          :readonly="isPosting"
          @selected="changeOrderStatus">
          <template #pending>
            <loading-indicator is-loading />
          </template>
        </uc-select>
      </div>

      <!-- Error message -->
      <div
        v-show="hasError"
        class="uc-order-status-editor__error">
        {{ errorMessage }}
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "en": {
    "currentStatus": "Current status",
    "changeStatusTo": "Change status to...",
    "noAvailableStatuses": "There are no more available status options."
  }
}
</i18n>

<script>
import { orderService } from '@/services/'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import { UcSelect } from '@/components/forms/uc-select/'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import editorMixin from '@/components/editors/editor-mixin'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

export default {
  name: 'UcOrderStatusEditor',
  components: {
    UcTextInput,
    UcSelect,
    LoadingIndicator,
  },
  mixins: [editorMixin],
  props: {
    entityGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      statusOptions: [],
      errorMessage: '',

      // TODO(ch-14269): When the story is finished, the pagination boiler code shouldn't exist on this level anymore.
      currentPage: 0,
      pageSize: Number.MAX_SAFE_INTEGER,
    }
  },
  computed: {
    currentStatusName () {
      return this.definition.value
    },
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
    hasAvailableStatuses () {
      return this.statusOptions.length > 0
    },
  },
  async created () {
    await this.fetchDataForModalFields()
  },
  methods: {
    async fetchDataForModalFields () {
      try {
        const mapToOption = item => ({
          label: item.name,
          guid: item.guid,
        })

        this.statusOptions = (await orderService.fetchAvailableStatusesForOrderAsync({
          orderGuid: this.entityGuid,
          cultureCode: this.language,
          params: {
            startAt: this.currentPage * this.pageSize,
            limit: this.pageSize,
          },
        }))
          .data.map(mapToOption)

        this.state = ComponentState.IDLE
      } catch (error) {
        this.state = ComponentState.ERROR
        if (error.response.data.message) {
          this.errorMessage = error.response.data.message
        } else {
          throw error
        }
      }
    },
    changeOrderStatus (payload) {
      this.onChange({
        definitionName: 'status',
        definitionGuid: payload.guid,
        newValue: payload.label,
        isNullable: false, // IMPORTANT! Status should not be undoable
      })
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-order-status-editor__state
  display flex
  flex-direction column
  align-items center
  justify-content center

.uc-order-status-editor__state-empty
  secondary-body-text()

.uc-order-status-editor__loading
  display flex
  align-items center
  justify-content center

.uc-order-status-editor__loading,
.uc-order-status-editor__form,
.uc-order-status-editor__state
  height $base-unit * 15

.uc-order-status-editor-select__subheader
  secondary-details-text()
  width 100%
  margin 0 auto 6 * $base-unit
  text-align center

.uc-order-status-editor__form-field:not(:last-of-type)
  margin-bottom $base-unit * 3

.uc-order-status-editor__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()
</style>
