<template>
  <div class="uc-promo-code-generator">
    <uc-stepper
      :render-steps-after="0"
      @beforeLeave="onBeforeLeaveStep">
      <!-- Back button -->
      <template #top-left="{ previous, step }">
        <transition name="fade">
          <div
            v-show="step > 1"
            class="uc-promo-code-generator__back">
            <uc-button
              :theme="ucButtonTheme.LINK"
              :size="ucButtonSize.SMALL"
              data-test="previous"
              @click="previous">
              {{ translations.back }}
              <template #before>
                <arrow-back-icon />
              </template>
            </uc-button>
          </div>
        </transition>
      </template>

      <!-- Step 1: Generator selection  -->
      <template #step-1="{ next }">
        <div class="uc-promo-code-generator__step">
          <div class="uc-promo-code-generator__subheader">
            {{ translations.subheaderGenerate }}
          </div>

          <div class="uc-promo-code-generator__actions">
            <!-- Generator toggle -->
            <uc-promo-code-generator-toggle @toggle="toggleGeneratorForm" />

            <!-- Button: Show promo codes -->
            <div class="uc-promo-code-generator__action">
              <uc-button
                :size="ucButtonSize.SMALL"
                :disabled="codes.length === 0"
                @click="next">
                {{ translations.selectedButtonLabel }}
              </uc-button>
            </div>
          </div>

          <!-- Generator form -->
          <div class="uc-promo-code-generator__form">
            <uc-promo-code-generator-manual
              v-if="useManualGenerator"
              @submit="generateManual" />

            <uc-promo-code-generator-unique
              v-else
              :loading="isGenerating"
              @submit="generateUnique" />
          </div>
        </div>
      </template>

      <!-- Step 2: Selection -->
      <template #step-2>
        <div class="uc-promo-code-generator__subheader">
          {{ translations.subheaderManage }}
        </div>

        <uc-promo-code-generator-selection
          :header="translations.selectedCodesSummary"
          :rows="currentCodes"
          :has-loaded-all-items="hasLoadedAllItems"
          @next="requestNextPage"
          @change="onChange" />
      </template>
    </uc-stepper>

    <!-- Submit button -->
    <div class="uc-promo-code-generator__submit">
      <uc-button
        :disabled="codes.length === 0"
        floating
        @click="submitSelection">
        {{ translations.submitButtonLabel }}
      </uc-button>
    </div>
  </div>
</template>

<script>
import { UcStepper } from '@/components/generic/uc-stepper/'
import { ArrowBack16Normal as ArrowBackIcon } from '@/assets/icons/'
import { UcButton, UcButtonTheme, UcButtonSize } from '@/components/forms/uc-button/'
import { UcPromoCodeGeneratorToggle, PromoCodeType } from '../uc-promo-code-generator-toggle/'
import { UcPromoCodeGeneratorManual } from '../uc-promo-code-generator-manual/'
import { UcPromoCodeGeneratorUnique } from '../uc-promo-code-generator-unique/'
import { UcPromoCodeGeneratorSelection } from '../uc-promo-code-generator-selection/'
import { difference } from '@/utils/array.utils'

const ComponentState = {
  IDLE: 'idle',
  GENERATING: 'generating',
}

const ComponentEvent = {
  SAVE: 'save',
}

export default {
  name: 'UcPromoCodeGenerator',
  components: {
    UcStepper,
    ArrowBackIcon,
    UcButton,
    UcPromoCodeGeneratorToggle,
    UcPromoCodeGeneratorManual,
    UcPromoCodeGeneratorUnique,
    UcPromoCodeGeneratorSelection,
  },
  props: {
    generateMethod: {
      type: Function,
      required: true,
    },
  },
  data () {
    return {
      promoCodeType: PromoCodeType.MANUAL,
      state: ComponentState.IDLE,
      selection: [],
      codes: [],
      currentPage: 1,
      pageSize: 30,

    }
  },
  computed: {
    ucButtonTheme () {
      return UcButtonTheme
    },
    ucButtonSize () {
      return UcButtonSize
    },
    useManualGenerator () {
      return this.promoCodeType === PromoCodeType.MANUAL
    },
    translations () {
      return {
        back: this.$tc('global.back'),
        subheaderGenerate: this.$t('ucPromoCodeGenerator.subheaderGenerate'),
        subheaderManage: this.$t('ucPromoCodeGenerator.subheaderManage'),
        selectedButtonLabel: `${this.$t('ucPromoCodeGenerator.promoCodes')} • ${this.codes.length}`,
        selectedCodesSummary: `${this.$t('ucPromoCodeGenerator.generatedCodes')} • ${this.selection.length}`,
        submitButtonLabel: `${this.$t('global.done')} (${this.selection.length})`,
      }
    },
    isGenerating () {
      return this.state === ComponentState.GENERATING
    },
    currentCodes () {
      // This will eventually get heavier and heavier
      return this.codes.slice(0, this.currentPage * this.pageSize)
    },
    hasLoadedAllItems () {
      return this.currentCodes.length === this.codes.length
    },
  },
  methods: {
    requestNextPage () {
      this.currentPage++
    },
    onChange (event) {
      this.selection = difference(this.codes, event)
    },
    toggleGeneratorForm (promoCodeType) {
      this.promoCodeType = promoCodeType
    },
    onBeforeLeaveStep (to) {
      if (to === 2) {
        this.selection = [...this.codes]
      }

      if (to === 1) {
        this.codes = [...this.selection]
        this.currentPage = 1
      }
    },
    generateManual (payload) {
      this.appendPromocodes([payload])
    },
    async generateUnique (payload) {
      this.state = ComponentState.GENERATING

      try {
        const codes = await this.generateMethod({ ...payload })
        this.appendPromocodes(codes)
      } catch (error) {
        console.error(error)
        // TODO(ch10038): We need a way to handle http 500 request errors
      }

      this.state = ComponentState.IDLE
    },
    appendPromocodes (newCodes) {
      if (!newCodes || newCodes.length === 0) {
        return
      }
      this.codes = this.selection = [...this.codes, ...newCodes]
    },
    submitSelection () {
      this.codes = this.selection
      this.$emit(ComponentEvent.SAVE, this.codes)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-promo-code-generator
  position relative
  display flex
  flex-direction column
  height 100%

.uc-promo-code-generator__step
  width 100%

.uc-promo-code-generator__subheader
  margin-bottom $base-unit * 8
  text-align center
  secondary-details-text()

.uc-promo-code-generator__actions
  display flex
  justify-content space-between
  width 100%

.uc-promo-code-generator__form
  padding-top $base-unit * 4
  padding-bottom $base-unit * 4

.uc-promo-code-generator__submit
  position absolute
  bottom 0
  left 50%
  z-index 1
  transform translateX(-50%)
</style>
