<template>
  <form
    v-custom-scroll-bar
    class="order-billing-update__form"
    @submit.prevent="updateBillingInformationAsync">
    <uc-modal
      :title="$t('title')"
      :size="modalSize"
      :sticky-footer="true"
      @hide="cancel">
      <div
        v-custom-scroll-bar
        class="order-billing-update__form-fields">
        <uc-entity-property-list
          :entity="billingInformation"
          v-bind="{ ignoredProperties, contentLanguage }"
          :render-in-compact-mode="false"
          @change="enrichPayload" />
      </div>
      <!-- Error message -->
      <div
        v-show="isError"
        class="billing-update__error"
        data-test="billing-update__error">
        {{ errorMessage }}
      </div>
      <template #footer>
        <!-- Save button-->
        <uc-button
          :type="buttonTypeSubmit">
          {{ $t('global.save') }}
          <template #after>
            <loading-spinner v-show="isPosting" />
          </template>
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<i18n>
{
  "en": {
    "title": "Edit billing information"
  }
}
</i18n>

<script>
import { UcModal, UcModalSize } from '@/components/widgets/uc-modal/'
import { UcButton, UcButtonType } from '@/components/forms/uc-button/'
import { LoadingSpinner } from '@/components/widgets/loading-spinner/'
import { billingService } from '@/services/'
import { UcEntityPropertyList } from '@/components/domain/uc-entity-property-list/'


const ComponentEvent = {
  ADD: 'add',
  CANCEL: 'cancel',
  UPDATED: 'updated',
}

const ComponentState = {
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

export default {
  name: 'UpdateBillingInformationModal',
  components: {
    UcModal,
    UcButton,
    LoadingSpinner,
    UcEntityPropertyList,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    parentGuid: {
      type: String,
      default: '',
    },
    billingInformation: {
      type: Object,
      required: true,
    },
  },
  data () {
    return {
      payload: {},
      errorMessage: '',
      state: ComponentState.IDLE,
    }
  },
  computed: {
    buttonTypeSubmit: () => UcButtonType.SUBMIT,
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    isError () {
      return this.state === ComponentState.ERROR
    },
    modalSize: () => UcModalSize.LG,
    ignoredProperties () {
      return ['definition', 'meta', 'createdOn', 'createdBy']
    },
  },
  methods: {
    cancel () {
      this.state = ComponentState.IDLE
      this.$emit(ComponentEvent.CANCEL)
    },
    async updateBillingInformationAsync () {
      // Force `change` event on focused editor when submitting form with ENTER key
      document.activeElement.blur()

      try {
        const payload = this.payload
        this.state = ComponentState.POSTING
        await billingService.updateEntryAsync({
          orderGuid: this.parentGuid,
          cultureCode: this.contentLanguage,
          payload,
        })
        this.$emit(ComponentEvent.UPDATED)
      } catch (error) {
        this.state = ComponentState.ERROR
        if (error?.response?.data?.message) {
          this.errorMessage = error.response.data.message
        } else {
          throw error
        }
      }
    },
    enrichPayload (data) {
      for (const key of Object.keys(data.save)) {
        this.payload[key] = data.save[key]
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.order-billing-update__form-fields
  display grid
  row-gap 3 * $base-unit
  padding-top 3 * $base-unit

.billing-update__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()
</style>
