import { rest } from 'msw'
import { differenceWith } from '@/utils/array.utils.js'
import { generateRandomString } from '@/utils/generate-random-string.js'
import mockGuid from '@/utils/mock-guid.js'

const RESPONSE_DELAY = 250
let codes = []

export const promoCodeCriterionHandlers = [
  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/promocodes/generate', (req, res, ctx) => {
    const params = req.url.searchParams
    const count = Number(params.get('count'))
    const length = Number(params.get('length'))
    const prefix = params.get('prefix') ? `${params.get('prefix')}-` : ''
    const suffix = params.get('suffix') ? `-${params.get('suffix')}` : ''
    const response = []

    for (let i = 0; i < count; i++) {
      response.push(`${prefix}${generateRandomString(length)}${suffix}`)
    }

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/promocodes', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const response = codes.slice(startAt, startAt + limit)

    return res(
      ctx.set('X-total-count', codes.length),
      ctx.set('X-total-max-uses', codes.reduce((acc, code) => acc + code.maxUses, 0)),
      ctx.set('X-total-used', 0),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
  rest.post('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/promocodes', (req, res, ctx) => {
    codes = req.body.map(code => ({ code: code.code, maxUses: code.maxUses, used: 0, guid: mockGuid() }))
    const response = codes.map(code => ({ guid: code.guid, message: 'success', statusCode: 201 }))

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),
  rest.delete('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/promocodes', (req, res, ctx) => {
    codes = differenceWith(codes, req.body, (code, guid) => code.guid === guid)

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
]
