<template>
  <uc-navigator-grid-node
    v-bind="{ selected, childrenCount }"
    @select="onSelect"
    @open="onOpen">
    <template #branchIcon>
      <content-branch-icon />
    </template>
    <template #leafIcon>
      <content-leaf-icon />
    </template>
    <template #default>
      {{ node.name }}
    </template>
  </uc-navigator-grid-node>
</template>

<script>
import { UcNavigatorGridNode } from '@/components/generic/uc-navigator/'
import { ContentBranch24Thick as ContentBranchIcon, ContentLeaf24Thick as ContentLeafIcon } from '@/assets/icons'

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select',
}

export default {
  name: 'ContentNavigatorGridNode',
  components: {
    UcNavigatorGridNode,
    ContentBranchIcon,
    ContentLeafIcon,
  },
  props: {
    node: {
      type: Object,
      required: true,
    },
    selected: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    childrenCount () {
      return this.node.childrenCount
    },
  },
  methods: {
    onOpen () {
      this.$emit(ComponentEvent.OPEN, this.node)
    },
    onSelect () {
      this.$emit(ComponentEvent.SELECT, this.node)
    },
  },
}
</script>
