<template>
  <div class="uc-number">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="value"
      :required="isRequired">
      <uc-numeric-input
        v-bind="{ label, placeholder, hasErrors }"
        :readonly="isReadonly"
        :value="numericValue"
        @blur="onBlur" />
    </editor-wrapper>
  </div>
</template>

<script>
import { UcNumericInput } from '@/components/forms/uc-numeric-input/'
import editorMixin from '../editor-mixin.js'

export default {
  name: 'UcNumber',
  components: {
    UcNumericInput,
  },
  mixins: [editorMixin],
  computed: {
    numericValue () {
      return this.value !== null && this.value?.length > 0 ? Number(this.value) : null
    },
  },
  methods: {
    onBlur (e) {
      const newValue = e.target.value
      const oldValue = this.value
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null) && newValue === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue,
      })
    },
  },
}
</script>
