<template>
  <div class="uc-free-gift-picker">
    <!-- Product search -->
    <product-search
      id="FreeGiftPicker"
      v-model="selection"
      :selectable-rows="false"
      :select-button-text="translations.add"
      :columns="productSearchColumns"
      :content-language="language"
      show-variants
      @change="onSelection" />
  </div>
</template>

<script>
import ProductSearch from '@/apps/products/components/product-search/'

const ComponentEvent = {
  ADD: 'add',
}

export default {
  name: 'UcFreeGiftPicker',
  components: {
    ProductSearch,
  },
  props: {
    language: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      rows: [],
      selection: [],
    }
  },
  computed: {
    productSearchColumns: () => ({
      primaryImage: { width: '90px' },
      name: { width: '192px' },
      sku: { width: '150px' },
      variantSku: { width: '150px' },
    }),
    translations () {
      return {
        add: this.$t('global.add'),
      }
    },
  },
  methods: {
    onSelection (rows) {
      this.$emit(ComponentEvent.ADD, rows)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-free-gift-picker
  height 'calc(100vh - %s)' % ($base-unit * 66)
  min-height $base-unit * 70
</style>
