<template>
  <div class="product-list">
    <div
      v-show="!loading && !hasRows"
      class="empty-state"
      data-test="product-empty-state">
      <h1 class="empty-state__header">
        {{ $tc('list.products') }}
      </h1>
      <p class="empty-state__text">
        {{ $tc('list.productEmptyState') }}
      </p>
      <uc-button
        size="sm"
        @click="openProductSearchModal">
        {{ $t('productList.addProduct') }}
      </uc-button>
    </div>
    <div
      v-if="loading"
      class="loading-container">
      <loading-indicator :is-loading="loading" />
    </div>
    <list
      v-show="!loading && hasRows"
      :id="listId"
      :ref="listId"
      :header="$tc('list.products')"
      node-type="product"
      :data-url="dataUrl"
      :default-columns-configuration="{
        'primaryImage': { 'width': '90px' },
        'sku': {'width': '200px'},
        'displayName': { 'width': '200px' },
        'eur 15 pct': {'width': '234px' }
      }"
      selectable-rows
      @initialLoadStarted="loading === true"
      @initialLoadFinished="onInitialLoadFinished"
      @rows-added="setHasRows"
      @rows-removed="setHasRows"
      @sortBy="sortBy">
      <!-- Add button -->
      <template #actionsHeader>
        <uc-button
          size="sm"
          @click="openProductSearchModal">
          {{ $t('global.add') }}
        </uc-button>
      </template>

      <!-- Notifications -->
      <template #notifications>
        <uc-snackbar
          v-if="notification"
          dismissable
          anchor="top"
          v-bind="notification"
          @hide="clearNotification" />
      </template>

      <!-- Remove button -->
      <template
        #actionsFooter="{ selected, clearSelected, removeItems }">
        <transition name="fade-in-up">
          <uc-button
            v-if="selected.length"
            floating
            @click="removeProducts(selected, clearSelected, removeItems)">
            {{ removeButtonLabel }}
          </uc-button>
        </transition>
      </template>
    </list>
    <uc-modal
      v-if="isProductSearchModalOpen"
      size="lg"
      :title="$t('productList.addProductTitle', { category: categoryName })"
      @hide="closeProductSearchModal">
      <div class="product-search-modal__content">
        <div class="product-search-modal__subtitle">
          {{ $t('productList.addProductSubtitle') }}
        </div>
        <product-search
          :content-language="contentLanguage"
          :select-button-text="$t('global.add')"
          show-families
          @selection="addProductsAsync" />
      </div>
    </uc-modal>
  </div>
</template>
<script>
import ProductSearch from './product-search.vue'
import setUrlParam from '@/utils/set-url-param'
import List from '@/components/generic/generic-list/list.vue'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import UcModal from '@/components/widgets/uc-modal'
import UcSnackbar, { ThemeProp as SnackbarTheme } from '@/components/widgets/uc-snackbar'
import UcButton from '@/components/forms/uc-button/'

const apiStatusCodes = {
  PRODUCT_ADDED: 200,
  PRODUCT_DUPLICATE: 304,
  PRODUCT_IS_VARIANT: 400,
  PRODUCT_NOT_FOUND: 404,
}

export default {
  name: 'ProductList',
  components: {
    UcButton,
    UcModal,
    UcSnackbar,
    LoadingIndicator,
    ProductSearch,
    List,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    categoryGuid: {
      type: String,
      required: true,
    },
    categoryName: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      dataUrl: null,
      loading: true,
      listId: 'CategoryProducts',
      isProductSearchModalOpen: false,
      notification: null,
      hasRows: false,
    }
  },
  computed: {
    metadataUrl () {
      return '/products/definitions/full'
    },
    removeButtonLabel () {
      return this.$t('global.remove')
    },
  },
  watch: {
    categoryGuid () {
      this.setUrl()
    },
  },
  mounted () {
    this.setUrl()
  },
  methods: {
    getUrl (columnKey) {
      const baseUrl = `/categories/${this.categoryGuid}/products/${this.contentLanguage}?sort=${columnKey}`
      return setUrlParam(baseUrl, 'sort', columnKey)
    },
    sortBy (columnKey) {
      this.dataUrl = this.getUrl(columnKey)
    },
    setUrl () {
      this.dataUrl = this.getUrl()
    },
    async removeProducts (products, clearSelected, removeItems) {
      const productGuids = products.map(product => product.meta.entityGuid)
      await this.axios.delete(`/categories/${this.categoryGuid}/products`, { data: { productGuids } })
      removeItems(productGuids)
      clearSelected()
    },
    async addProductsAsync (products) {
      this.closeProductSearchModal()

      const productsGuids = products.map(product => product.meta.entityGuid)
      const result = await this.axios.post(`/categories/${this.categoryGuid}/products`, { productsGuids })

      const addedProductGuids = []
      const duplicateProductGuids = []

      result.data.productToCategoryStatuses.forEach(status => {
        switch (status.statusCode) {
          case apiStatusCodes.PRODUCT_ADDED:
            addedProductGuids.push(status.productGuid)
            break
          case apiStatusCodes.PRODUCT_DUPLICATE:
            duplicateProductGuids.push(status.productGuid)
            break
        }
      })

      const isAddedProduct = product => addedProductGuids.indexOf(product.meta.entityGuid) !== -1
      const addedProducts = products.filter(isAddedProduct)

      this.$refs[this.listId].addItems(addedProducts)
      this.showAddedProductsNotification({ addedProductGuids, duplicateProductGuids })
    },
    openProductSearchModal () {
      this.isProductSearchModalOpen = true
    },
    closeProductSearchModal () {
      this.isProductSearchModalOpen = false
    },
    onInitialLoadFinished (totalItems) {
      this.loading = false
      this.setHasRows(totalItems)
      this.$emit('initialLoadFinished')
    },
    showAddedProductsNotification ({ addedProductGuids, duplicateProductGuids }) {
      const hasAddedProducts = addedProductGuids.length > 0
      const hasDuplicatedProducts = duplicateProductGuids.length > 0
      const notifications = []

      if (hasAddedProducts) {
        notifications.push(this.$tc('productList.productsAddedToCategory', addedProductGuids.length, { count: addedProductGuids.length }))
      }

      if (hasDuplicatedProducts) {
        notifications.push(this.$tc('productList.productsAlreadyInCategory', duplicateProductGuids.length, { count: duplicateProductGuids.length }))
      }
      const serializedNotifications = notifications.join('. ')

      if (hasDuplicatedProducts && !hasAddedProducts) {
        this.showInfoNotification(serializedNotifications)
      } else {
        this.showSuccessNotification(serializedNotifications)
      }
    },
    showInfoNotification (text) {
      this.notification = {
        text,
        theme: SnackbarTheme.NEUTRAL,
      }
    },
    showSuccessNotification (text) {
      this.notification = {
        text,
        theme: SnackbarTheme.SUCCESS,
      }
    },
    clearNotification () {
      this.notification = null
    },
    setHasRows (rows) {
      this.hasRows = rows > 0
    },
  },
}
</script>

<style lang="stylus" scoped>
.empty-state
  display flex
  flex-direction column
  align-items center
  justify-content center
  min-height 346px
  padding 48px 100px 48px 100px

.empty-state__header
  primary-heading-2()
  margin 0

.empty-state__text
  secondary-body-text()
  text-align center

.loading-container
  display flex
  align-items center
  justify-content center
  height 346px

.product-list
  width 100%

.product-search-modal__subtitle
  padding-bottom 40px
  text-align center
  secondary-details-text()

.product-search-modal__content
  display flex
  flex-direction column
  flex-grow 1
  height calc(100vh - 528px)
  min-height 560px
</style>
