import { EntityService, EntityType } from './_entity.service.js'

export class OrderLineService extends EntityService {
  constructor (httpClient) {
    super(EntityType.ORDER_LINE, httpClient)
  }

  fetchManyAsync ({ orderGuid, cultureCode, startAt, limit }) {
    const url = `orders/${orderGuid}/orderlines/${cultureCode}`
    return super.fetchEntitiesAsync({
      url,
      params: {
        startAt,
        limit,
      },
    })
  }

  fetchOneAsync ({ orderGuid, orderLineGuid, cultureCode }) {
    const url = `orders/${orderGuid}/orderlines/${orderLineGuid}/${cultureCode}`
    return super.fetchEntityAsync({ url })
  }

  deleteManyAsync ({ orderGuid, guids }) {
    const url = `orders/${orderGuid}/orderlines`
    const payload = {
      data: guids,
    }
    return this._httpClient.delete(url, payload)
  }

  createManyAsync ({ guids, orderGuid, cultureCode, priceGroupGuid }) {
    const url = `orders/${orderGuid}/orderlines/${cultureCode}`
    return this._httpClient.post(url, {
      productGuids: guids,
      priceGroupGuid: priceGroupGuid,
    })
  }

  updateOneAsync ({ orderGuid, orderLineGuid, cultureCode, payload }) {
    const url = `orders/${orderGuid}/orderlines/${orderLineGuid}/${cultureCode}`

    return this._httpClient.patch(url, payload)
  }
}
