import Vue from 'vue'

const warn = (msg) => {
  if (!Vue.config.silent) {
    /* eslint-disable-next-line */
    console.warn(msg)
  }
}
export default {
  name: 'UseIntersectionObserver',
  abstract: true,
  props: {
    threshold: {
      type: Array,
      required: false,
      default: () => [0, 0.2],
    },
    root: {
      type: HTMLElement,
      required: false,
      default: () => null,
    },
    rootMargin: {
      type: String,
      required: false,
      default: () => '0px 0px 0px 0px',
    },
  },
  created () {
    this.observer = new IntersectionObserver((entries) => {
      if (!entries[0].isIntersecting) {
        this.$emit('leave', [entries[0]])
      } else {
        this.$emit('enter', [entries[0]])
      }

      this.$emit('change', [entries[0]])
    }, {
      threshold: this.threshold,
      root: this.root,
      rootMargin: this.rootMargin,
    })
  },
  mounted () {
    this.$nextTick(() => {
      if (this.$slots.default && this.$slots.default.length > 1) {
        warn('[use-intersection-observer] Multiple root nodes found. Please ensure that the component is only wrapping a single root node.')
      } else if (!this.$slots.default || this.$slots.default.length < 1) {
        warn('[use-intersection-observer] No root node found. Please ensure that the component is wrapping a node.')
        return
      }

      this.observer.observe(this.$el)
    })
  },
  destroyed () {
    this.observer.disconnect()
  },
  render () {
    return this.$slots.default ? this.$slots.default[0] : null
  },
}
