<template>
  <transition name="fade">
    <section
      v-custom-scroll-bar
      tabindex="0"
      class="uc-modal"
      :class="cssClassObject"
      @keyup.esc="onHide">
      <div
        class="uc-modal__overlay"
        @click.prevent="onHide" />
      <div
        v-scroll="onScroll"
        class="uc-modal__main"
        data-test="uc-modal-main">
        <header
          v-if="stickyHeader && isScrolled"
          class="uc-modal__header">
          <h1
            v-if="title"
            class="uc-modal__heading">
            {{ title }}
          </h1>
          <span
            v-if="!persistent"
            class="uc-modal__close">
            <uc-button-round
              data-test="uc-modal-close"
              theme="ghost"
              size="s"
              @click="onHide">
              <close-icon />
            </uc-button-round>
          </span>
          <slot name="header" />
        </header>
        <div
          ref="content"
          class="uc-modal__content"
          data-test="uc-modal-content">
          <h1
            v-if="title"
            ref="heading"
            class="uc-modal__heading">
            {{ title }}
          </h1>
          <span
            v-if="!persistent"
            class="uc-modal__close">
            <uc-button-round
              theme="ghost"
              size="s"
              data-test="uc-modal-close"
              @click="onHide">
              <close-icon />
            </uc-button-round>
          </span>
          <slot />
        </div>
        <footer
          v-if="hasFooter"
          class="uc-modal__footer">
          <slot name="footer" />
        </footer>
      </div>
    </section>
  </transition>
</template>

<script>
import Vue from 'vue'
import UcButtonRound from '@/components/forms/uc-button-round'
import { Close16Normal as CloseIcon } from '@/assets/icons'
import { validateAgainstEnum } from '@/utils/validate-against'
import vuescroll from 'vue-scroll'
import customScrollBar from '@/utils/custom-scroll-bar'

Vue.use(vuescroll)

export const SizeProp = {
  SM: 'sm',
  LG: 'lg',
}

export default {
  name: 'UcModal',
  directives: {
    customScrollBar,
  },
  components: {
    CloseIcon,
    UcButtonRound,
  },
  props: {
    size: {
      type: String,
      default: 'sm',
      validator: validateAgainstEnum(SizeProp),
    },
    title: {
      type: String,
      default: '',
    },
    persistent: {
      type: Boolean,
      default: false,
    },
    stickyFooter: {
      type: Boolean,
      default: false,
    },
    stickyHeader: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      isScrolled: false,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-modal--size-sm': this.size === SizeProp.SM,
        'uc-modal--size-lg': this.size === SizeProp.LG,
        'is-persistent': this.persistent,
        'uc-modal--sticky-footer': this.stickyFooter,
        'uc-modal--sticky-header': this.stickyHeader,
      }
    },
    hasFooter () {
      return !!this.$slots.footer
    },
  },
  mounted () {
    // https://stackoverflow.com/questions/49042667/vuejs-keyup-esc-on-div-element-is-not-working
    this.$el.focus()
    document.body.classList.add('is-locked')
  },
  destroyed () {
    document.body.classList.remove('is-locked')
  },
  methods: {
    getStyleValue (element, styleProperty) {
      return parseInt(window.getComputedStyle(element).getPropertyValue(styleProperty), 10)
    },
    onScroll (_event, position) {
      if (!this.stickyHeader) {
        return
      }

      const contentPaddingTop = this.getStyleValue(this.$refs.content, 'padding-top')
      const headingHeight = this.$refs.heading.offsetHeight + this.getStyleValue(this.$refs.heading, 'margin-bottom')
      const scrollHeight = contentPaddingTop + headingHeight

      if (this.isScrolled) {
        this.isScrolled = position.scrollBottom < scrollHeight
      }

      this.isScrolled = position.scrollTop > scrollHeight
    },
    onHide () {
      if (this.persistent) {
        return
      }
      this.$emit('hide')
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-modal
  position fixed
  top 0
  left 0
  z-index 10000
  width 100%
  height 100%
  overflow-y auto
  user-select none

.uc-modal__overlay
  position fixed
  width 100%
  height 100%
  background #000
  cursor pointer
  opacity 0.4

.uc-modal__main
  position relative
  top 50%
  left 50%
  display flex
  flex-direction column
  align-items center
  background-color #fff
  box-shadow 0 4px 30px 0 rgba(0, 0, 0, 0.15)
  transform translate(-50%, -50%)

.uc-modal__heading
  primary-heading-1()
  position relative
  width 100%
  max-height 10 * $base-unit
  margin 0 auto 3 * $base-unit auto
  overflow hidden
  text-align center
  // https://css-tricks.com/multi-line-truncation-with-pure-css/
  // Multiline truncation
  &::before
    position absolute
    right 0
    bottom 0
    content '...'
  // Hide ellipsis if text is short
  &::after
    position absolute
    right 0
    width 2rem
    height 2.5rem
    background $base-color-white
    content ''

.uc-modal__close
  position absolute
  top 3 * $base-unit
  right 3 * $base-unit

.uc-modal__header
  position sticky
  top 0
  left 0
  z-index 1
  display flex
  align-items center
  justify-content center
  width 100%
  padding (3 * $base-unit) (14 * $base-unit)
  background-color $base-color-white
  box-shadow 0 2px 15px 0 rgba(0, 0, 0, 0.1)

  .uc-modal__heading
    primary-body-text-bold()
    min-width 100%
    margin 0
    overflow hidden
    white-space nowrap
    text-overflow ellipsis
    // Clear multiline ellipsis
    &::before,
    &::after
      width 0
      height 0
      content ''

  .uc-modal__close
    top 50%
    transform translateY(-50%)

.uc-modal__content
  display flex
  flex-direction column
  flex-grow 1
  width 100%
  padding (8 * $base-unit) (14 * $base-unit)

.uc-modal__footer
  display flex
  flex-direction column
  flex-shrink 0
  align-items center
  width 100%
  margin-bottom 6 * $base-unit

.uc-modal--size-sm
  .uc-modal__main
    width 600px

  .uc-modal__heading
    max-width 370px

.uc-modal--size-lg
  .uc-modal__main
    width 1000px
    min-height 762px
    max-height 'calc(100% - %s)' % (6 * $base-unit)
    overflow-y auto

  .uc-modal__heading
    max-width 500px

  &.has-title
    .uc-modal__content
      margin-top 0

.uc-modal--sticky-footer
  .uc-modal__footer
    position sticky
    bottom 5 * $base-unit
    left 0

.uc-modal.is-persistent
  .uc-modal__overlay
    cursor default
</style>
