<template>
  <div
    class="uc-category-editor-selection"
    :class="cssClassObject">
    <uc-data-grid-selectable
      v-model="selection"
      v-bind="{ columns, rows }"
      row-key="guid"
      :disable-infinite-scroll="hasLoadedAllItems"
      @next="onNext"
      @input="onChange">
      <template #category="{ cell }">
        <!-- Category item -->
        <uc-node-row>
          <uc-node-title>
            {{ cell.name }}
          </uc-node-title>
          <uc-node-subtitle>
            {{ cell.path }}
          </uc-node-subtitle>
        </uc-node-row>
      </template>
    </uc-data-grid-selectable>

    <!-- Actions -->
    <transition name="fade-in-up">
      <div
        v-if="!isEmptySelection"
        class="uc-category-editor-selection__actions-footer">
        <!-- Remove -->
        <uc-button
          :theme="ucButtonTheme.PRIMARY"
          @click="onRemove">
          {{ $tc('global.remove') }} ({{ selection.length }})
        </uc-button>
      </div>
    </transition>
  </div>
</template>

<script>
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcButton, UcButtonTheme } from '@/components/forms/uc-button/'
import {
  UcNodeRow,
  UcNodeTitle,
  UcNodeSubtitle,
} from '@/components/generic/uc-node-row/'

const ComponentEvent = {
  REMOVE: 'remove',
  NEXT: 'next',
}

export default {
  name: 'UcCategoryEditorSelection',
  components: {
    UcDataGridSelectable,
    UcButton,
    UcNodeRow,
    UcNodeTitle,
    UcNodeSubtitle,
  },
  props: {
    rows: {
      type: Array,
      required: true,
    },
    hasLoadedAllItems: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      selection: [],
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-max-height': this.rows.length > 6,
      }
    },
    ucButtonTheme () {
      return UcButtonTheme
    },
    columns () {
      return [
        {
          key: 'category',
          value: this.$t('ucCategoryEditor.category'),
        },
      ]
    },
    isEmptySelection () {
      return this.selection.length === 0
    },
  },
  methods: {
    onChange (value) {
      this.selection = value
    },
    onRemove () {
      this.$emit(ComponentEvent.REMOVE, this.selection)
      this.selection = []
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-category-editor-selection
  position relative
  background $base-color-white

  &.is-max-height
    height $base-unit * 60
    border-bottom $base-border

.uc-category-editor-selection__actions-footer
  position absolute
  bottom $base-unit * 4
  z-index 1
  display flex
  align-items center
  justify-content center
  width 100%
  pointer-events none

</style>
