<template>
  <use-selection
    v-if="options && options.length > 0"
    v-model="selection"
    :items="options">
    <template #default="{ toggle, isSelected }">
      <div
        class="uc-checkbox-group"
        data-test="checkbox-group">
        <div
          v-for="option in options"
          :key="option[optionKey]"
          class="uc-checkbox-group__option"
          data-test="checkbox-group-option">
          <slot v-bind="{ option, toggle, isSelected }" />
        </div>
      </div>
    </template>
  </use-selection>
</template>

<script>
import UseSelection from '@/components/wrappers/use-selection/'

const ComponentEvent = {
  INPUT: 'input',
}

export default {
  name: 'UcCheckboxGroup',
  components: {
    UseSelection,
  },
  props: {
    options: {
      type: Array,
      required: true,
    },
    optionKey: {
      type: String,
      required: true,
    },
    value: {
      type: Array,
      required: true,
    },
  },
  computed: {
    selection: {
      get () {
        return this.value
      },
      set (payload) {
        this.$emit(ComponentEvent.INPUT, payload)
      },
    },
  },
}
</script>
<style lang="stylus" scoped>
.uc-checkbox-group__option
  height 4 * $base-unit
</style>
