<template>
  <form
    v-if="definition"
    class="create-campaign"
    @submit.prevent="campaignCreate">
    <uc-modal
      class="create-modal"
      :title="translations.title"
      @hide="cancel">
      <div
        v-show="isIdle || hasError || isPosting">
        <!-- Campaign Name -->
        <div class="create-campaign__field">
          <uc-text-input
            v-model.trim="name"
            :label="translations.name"
            :readonly="isPosting" />
        </div>

        <!-- Stores -->
        <div class="create-campaign__field">
          <uc-multi-select-editor
            name="stores"
            :definition="definition.stores"
            :readonly="isPosting"
            @change="setStore">
            <template #pending>
              <loading-indicator is-loading />
            </template>
          </uc-multi-select-editor>
        </div>

        <!-- Start date -->
        <div class="create-campaign__field">
          <uc-date-time-picker
            name="startDate"
            :definition="fieldDefinitions.startDate"
            :readonly="isPosting"
            @change="setStartDate" />
        </div>

        <!-- End date -->
        <div class="create-campaign__field">
          <uc-date-time-picker
            name="endDate"
            :definition="fieldDefinitions.endDate"
            :readonly="isPosting"
            @change="setEndDate" />
        </div>

        <!-- Error message -->
        <div
          v-show="hasError"
          class="create-campaign__error"
          data-test="create-campaign-error">
          {{ errorMessage }}
        </div>
      </div>

      <!-- Submit -->
      <template #footer>
        <uc-button
          :disabled="!isEnabled"
          type="submit"
          theme="primary"
          @click="campaignCreate">
          {{ translations.title }}
          <template
            v-if="isPosting"
            #after>
            <loading-spinner is-loading />
          </template>
        </uc-button>
        <uc-button
          theme="text"
          @click="cancel">
          {{ translations.cancel }}
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<script>

import { UcModal } from '@/components/widgets/uc-modal/'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import { UcMultiSelectEditor } from '@/components/editors/uc-multi-select-editor/'
import { UcDateTimePicker } from '@/components/editors/uc-date-time-picker/'
import { UcButton } from '@/components/forms/uc-button/'
import LoadingSpinner from '@/components/widgets/loading-spinner'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import capitalize from '@/utils/capitalize.js'
import { campaignService } from '@/services/'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

const ComponentEvent = {
  CREATE: 'create',
}

export default {
  name: 'CampaignCreate',
  components: {
    UcModal,
    UcTextInput,
    UcMultiSelectEditor,
    UcDateTimePicker,
    UcButton,
    LoadingIndicator,
    LoadingSpinner,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      definition: null,
      name: '',
      startDate: '',
      endDate: '',
      errorMessage: '',
    }
  },
  computed: {
    translations () {
      return {
        title: this.$t('campaignCreate.title'),
        name: `${capitalize(this.$t('campaignCreate.name'))}`,
        store: this.$t('campaignCreate.store'),
        cancel: this.$tc('global.cancel'),
      }
    },
    isEnabled () {
      return this.name.length > 0 &&
        this.stores.length > 0 &&
        !!this.startDate &&
        !!this.endDate
    },
    isPosting () {
      return this.state === ComponentState.POSTING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
    fieldDefinitions () {
      return {
        startDate: {
          ...this.definition.startDate,
          value: this.startDate,
        },
        endDate: {
          ...this.definition.endDate,
          value: this.endDate,
        },
      }
    },
    stores () {
      return this.definition.stores.selected
    },
  },
  async created () {
    const definition = (await campaignService.fetchDefinitionsAsync()).data[0]
    definition.stores.selected = []
    definition.stores.value = undefined
    this.definition = definition

    this.state = ComponentState.IDLE
  },
  methods: {
    async campaignCreate () {
      if (this.isPosting) {
        return
      }

      const payload = {
        name: this.name,
        stores: this.stores,
        startDate: this.startDate,
        endDate: this.endDate,
      }

      try {
        this.state = ComponentState.POSTING
        const campaignCreateResponse = await campaignService.createEntityAsync(payload)
        const campaignGetResponse = await campaignService.fetchEntityAsync({
          guid: campaignCreateResponse.data.guid,
          cultureCode: this.contentLanguage,
        })
        this.state = ComponentState.IDLE

        this.$emit(ComponentEvent.CREATE, campaignGetResponse.data)
      } catch (e) {
        this.state = ComponentState.ERROR
        if (e.response.data.message) {
          this.errorMessage = e.response.data.message
        } else {
          throw e
        }
      }
    },
    cancel () {
      this.$router.replace({ name: 'campaigns' })
    },
    setStore (payload) {
      this.definition.stores.selected = payload.save.stores.selected
    },
    setStartDate (payload) {
      this.startDate = payload.save.startDate.value
    },
    setEndDate (payload) {
      this.endDate = payload.save.endDate.value
    },
  },

}
</script>

<style lang="stylus" scoped>
.create-campaign__field:not(:last-of-type)
  margin-bottom $base-unit * 3

.create-campaign__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()
</style>
