import * as types from './mutation-types'
import { processNewChunk } from '../js/feed-handler'

const mutations = {
  [types.SET_METRICS]: (state, metrics) => {
    state.metrics = metrics
  },
  [types.SET_USER]: (state, user) => {
    state.user = user
  },
  [types.SET_SHOW_ADVANCED]: (state, show) => {
    state.showAdvancedMetrics = show
  },
  [types.SET_LOCALE]: (state, locale) => {
    state.locale = locale
  },
  [types.SET_CULTURE]: (state, culture) => {
    state.culture = culture
  },
  [types.SHOW_MENU]: (state, show) => {
    state.showMenu = show
  },
  [types.SET_FEED]: (state, rawFeed) => {
    const newArray = processNewChunk(rawFeed).concat(state.feed)
    const sorted = newArray.sort((a, b) => {
      return b.created - a.created
    })
    state.feed = sorted.slice(0, 200) // limit feed to 200 newest
  },
  [types.SHOW_BUNDLE]: (state, { itemId, show }) => {
    state.feed.find(x => x.id === itemId).showBundle = show
  },
  [types.SET_INTERVAL]: (state, days) => {
    state.daysDisplayed = days
  },
  [types.SET_FEED_TYPES]: (state, feedTypes) => {
    state.feedTypes = feedTypes
  },
  [types.TOGGLE_FEED_TYPE]: (state, type) => {
    state.feedTypes[type].muted = !state.feedTypes[type].muted
  },
  [types.SET_LICENSE_ERROR]: (state, error) => {
    state.licenseError = error
  },
}

export default mutations
