<template>
  <transition-group
    name="list-left"
    tag="div"
    class="uc-undo-list">
    <div
      v-for="(undo, key, index) in groups"
      :key="key"
      class="uc-undo"
      :class="{ latest: isLatest(groups, index) }">
      <!-- eslint-disable-next-line vue/no-v-html -->
      <span v-html="caption(undo)" />
      <button
        v-if="isUndoable(undo)"
        class="uc-undo__btn"
        data-test="btn-uc-undo-btn"
        @click="undoLast(undo)">
        {{ buttonCaption(undo) }}
      </button>
    </div>
  </transition-group>
</template>

<script>
import customUndo from '@/translations/components/custom-undo.translation.js'

const groupBy = function (xs, key) {
  return xs.reduce(function (rv, x) {
    (rv[x[key]] = rv[x[key]] || []).push(x)
    return rv
  }, {})
}

export default {
  name: 'Undo',
  props: {
    undos: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    groups () {
      return groupBy(this.undos, 'key')
    },
  },
  methods: {
    caption (undos) {
      const message = undos[undos.length - 1].meta.undoMessage
      if (message) {
        return message
      }
      const label = undos[undos.length - 1].meta.label
      const labelPretty = label[0].toUpperCase() + label.slice(1)
      const nodeType = undos[undos.length - 1].meta.nodeType
      const nodeTypePretty = nodeType.replace(/([A-Z])/g, ' $1')
      const locale = this.$i18n.locale
      const key = label.replace(/\s/g, '').toLowerCase()

      if (customUndo[locale][key] !== undefined) {
        return this.$tc(`customUndo.${key}`, undos.length, { label: labelPretty, count: undos.length, nodeType: nodeTypePretty })
      } else {
        return this.$tc('global.undoCaption', undos.length, { label: labelPretty, count: undos.length, nodeType: nodeTypePretty })
      }
    },
    isLatest (groups, index) {
      return index === Object.getOwnPropertyNames(groups).length - 1
    },
    buttonCaption (undos) {
      return this.$tc('global.undo', undos.length)
    },
    undoLast (undos) {
      this.$emit('undo', undos[undos.length - 1])
    },
    isUndoable (undos) {
      return undos[undos.length - 1].undo
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-undo-list
  position fixed
  right 20px
  bottom 60px
  z-index 9999
  display flex
  flex-direction column
  align-items flex-end
  user-select none

.uc-undo
  display flex
  align-items center
  justify-content center
  height 40px
  margin-bottom 4px
  padding 6px 6px 6px 12px
  border-radius 3px
  color #fff
  font-size 0.875rem
  background-color $legacy-color-dark-gray-300
  box-shadow 2px 2px 7px rgba(0,0,0,0.3)
  opacity 0.75

  &.latest
    opacity 1.0

.uc-undo__btn
  height 2em
  margin-left 30px
  border none
  border-radius 2px
  color $legacy-color-dark-gray-300
  font-weight 600
  white-space nowrap
  background-color #fff
  outline none
  cursor pointer
</style>
