<template>
  <div class="create-product">
    <uc-modal
      :title="dialogTitle"
      @hide="hideDialog">
      <div class="create-product__definition-picker">
        <uc-select
          :options="availableDefinitions"
          :selected="selectedDefinitionGuid"
          :placeholder="definitionPlaceholder"
          :readonly="isLoading"
          @selected="selectedDefinitionGuid = $event.guid">
          <template #pending>
            <loading-indicator :is-loading="true" />
          </template>
        </uc-select>
      </div>
      <div class="create-product__input">
        <uc-text-input
          ref="input"
          :label="nameLabel"
          :value="newName"
          :readonly="isLoading"
          @input="updateName" />
      </div>
      <div class="create-product__input">
        <uc-text-input
          ref="input"
          :label="skuLabel"
          :value="newSku"
          :readonly="isLoading"
          @input="updateSku" />
        <div
          v-show="errorMsg.length"
          class="create-product__error"
          data-test="create-product-error">
          <span>{{ errorMsg }}</span>
        </div>
      </div>
      <template #footer>
        <uc-button
          type="submit"
          :disabled="!enabled"
          @click="createNewProduct">
          {{ createProductLabel }}
          <template
            v-if="isLoading"
            #after>
            <loading-spinner />
          </template>
        </uc-button>
        <uc-button
          theme="text"
          @click="hideDialog">
          {{ $tc('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </div>
</template>

<script>
import UcSelect from '@/components/forms/uc-select'
import UcModal from '@/components/widgets/uc-modal'
import UcButton from '@/components/forms/uc-button/uc-button'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import capitalize from '@/utils/capitalize'
import { mergeAllData } from '@/services/utils/merge-definitions-and-data.js'
import { createNamespacedHelpers, mapGetters } from 'vuex'
import LoadingSpinner from '@/components/widgets/loading-spinner'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import { productService } from '@/services'

const detailsStore = createNamespacedHelpers('details')

export default {
  name: 'CreateProduct',
  components: {
    LoadingIndicator,
    UcTextInput,
    UcSelect,
    UcModal,
    UcButton,
    LoadingSpinner,
  },
  props: {
    categoryGuid: {
      type: String,
      default: '',
    },
    contentLanguage: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
      availableDefinitions: undefined,
      selectedDefinitionGuid: '',
      newName: '',
      newSku: '',
      errorMsg: '',
      isLoading: false,
    }
  },
  computed: {
    ...mapGetters(['definitionsByGuid']),
    definitionPlaceholder () {
      return `${capitalize(this.$t('global.select'))} ${this.translatedDefinition}`
    },
    translatedDefinition () {
      return this.$t('global.definition')
    },
    definitionLabel () {
      return capitalize(this.translatedDefinition)
    },
    nameLabel () {
      return `${capitalize(this.$t('createProduct.internalName'))}`
    },
    skuLabel () {
      return `${this.$t('createProduct.sku')}`
    },
    createProductLabel () {
      return `${this.$t('createProduct.createProduct')}`
    },
    enabled () {
      return this.newName.trim().length > 0 && this.selectedDefinitionGuid.length > 0 && this.newSku.trim().length > 0
    },
    dialogTitle () {
      return `${this.$t('createProduct.title')}`
    },
  },
  async mounted () {
    if (!this.compact) this.availableDefinitions = await this.loadDefinitions()
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails']),
    async loadDefinitions () {
      const definitions = await productService.fetchDefinitionsListAsync()
      const result = definitions.data.map(definition => ({ guid: definition.guid, label: definition.name }))

      return result
    },
    hideDialog () {
      if (!this.isLoading) {
        this.$emit('hideDialog')
      }
    },
    async createNewProduct () {
      if (this.isLoading) {
        return
      }
      this.isLoading = true
      this.successfullyInserted = false

      const payload = {
        name: this.newName,
        sku: this.newSku,
        categoryGuid: this.categoryGuid,
        definitionGuid: this.selectedDefinitionGuid,
      }
      try {
        const result = await this.axios.post('products', payload)
        this.$emit('productCreated')
        this.getCreatedProduct(result.data.Guid)
      } catch (err) {
        this.isLoading = false
        if (err.response.status === 400) {
          this.errorMsg = err.response.data.message
        }
      }
    },
    async getCreatedProduct (productGuid) {
      const productResult = await this.axios.get(`products/${productGuid}/${this.contentLanguage}`)
      this.isLoading = false
      const mappedResult = this.getFieldsExceptOf([productResult.data], '')
      const mergedProduct = mergeAllData(this.definitionsByGuid, mappedResult)
      this.hideDialog()
      this.$emit('addProductToListAndOpenItsDetails', mergedProduct[0])
    },
    updateName (name) {
      this.newName = name
      this.errorMessage = ''
    },
    updateSku (sku) {
      this.newSku = sku
      this.errorMessage = ''
    },
    getFieldsExceptOf (definition, field) {
      const fieldsKeys = Object.keys(definition).filter(x => x !== field)
      const fieldsObj = fieldsKeys.reduce((obj, key) => ({ ...obj, [key]: definition[key] }), {})
      fieldsKeys.forEach(key => {
        fieldsObj[key].key = key
      })
      return Object.values(fieldsObj)
    },
  },

}
</script>

<style lang="stylus" scoped>
.create-product__definition-picker
  margin-bottom 24px

.create-product__input:not(:last-child)
  margin-bottom 24px

.create-product__error
  width 100%
  height 50px
  padding 6px 16px  6px 0
  error-text()
</style>
