<template>
  <div class="create-variant">
    <uc-modal
      v-if="isDialogOpen"
      :title="dialogTitle"
      @hide="hideDialog">
      <div class="create-variant__input">
        <uc-text-input
          ref="input"
          :label="nameLabel"
          :value="newName"
          :readonly="isLoading"
          @input="updateName" />
      </div>
      <div class="create-variant__input">
        <uc-text-input
          ref="input"
          :label="variantSkuLabel"
          :value="newVariantSku"
          :readonly="isLoading"
          @input="updateVariantSku" />
        <div
          v-show="errorMsg.length"
          class="create-variant__error">
          <span>{{ errorMsg }}</span>
        </div>
      </div>
      <template #footer>
        <uc-button
          type="submit"
          :disabled="!enabled"
          @click="createVariant">
          {{ createVariantLabel }}
          <template
            v-if="isLoading"
            #after>
            <loading-spinner />
          </template>
        </uc-button>
        <uc-button
          theme="text"
          @click="hideDialog">
          {{ $tc('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </div>
</template>

<script>
import UcButton from '@/components/forms/uc-button/uc-button'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import capitalize from '@/utils/capitalize'
import UcModal from '@/components/widgets/uc-modal'
import { mergeAllData } from '@/services/utils/merge-definitions-and-data.js'
import LoadingSpinner from '@/components/widgets/loading-spinner'
import { createNamespacedHelpers, mapGetters } from 'vuex'

const detailsStore = createNamespacedHelpers('details')

export default {
  name: 'CreateVariant',
  components: {
    UcTextInput,
    UcModal,
    UcButton,
    LoadingSpinner,
  },
  props: {
    isDialogOpen: {
      type: Boolean,
      default: false,
    },
    contentLanguage: {
      type: String,
      default: '',
    },
    currentProductGuid: {
      type: String,
      default: '',
    },
    productGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      newName: '',
      newVariantSku: '',
      errorMsg: '',
      isLoading: false,
    }
  },
  computed: {
    ...mapGetters(['definitionsByGuid']),
    nameLabel () {
      return `${capitalize(this.$t('createVariant.internalName'))}`
    },
    variantSkuLabel () {
      return `${this.$t('createVariant.variantSku')}`
    },
    createVariantLabel () {
      return `${this.$t('createVariant.createVariant')}`
    },
    enabled () {
      return this.newName.trim().length > 0 && this.newVariantSku.trim().length > 0
    },
    dialogTitle () {
      return `${this.$t('createVariant.title')}`
    },
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails']),
    hideDialog () {
      if (!this.isLoading) {
        this.$emit('hideDialog')
      }
    },
    async createVariant () {
      if (this.isLoading) {
        return
      }
      this.isLoading = true
      this.successfullyInserted = false

      const payload = {
        name: this.newName,
        variantSku: this.newVariantSku,
      }
      try {
        const result = await this.axios.post(`products/${this.productGuid}/variants`, payload)
        this.getVariant(result.data.Guid)
      } catch (err) {
        this.isLoading = false
        if (err.response.status === 400) {
          this.errorMsg = err.response.data.message
        }
      }
    },
    async getVariant (productGuid) {
      const variantResult = await this.axios.get(`products/${productGuid}/${this.contentLanguage}`)
      this.isLoading = false
      const mappedResult = this.getFieldsExceptOf([variantResult.data], '')
      const mergedProduct = mergeAllData(this.definitionsByGuid, mappedResult)
      this.hideDialog()
      this.$emit('addProductVariantToListAndOpenItsDetails', mergedProduct[0])
    },
    updateName (name) {
      this.newName = name
      this.errorMessage = ''
    },
    updateVariantSku (variantSku) {
      this.newVariantSku = variantSku
      this.errorMessage = ''
    },
    getFieldsExceptOf (definition, field) {
      const fieldsKeys = Object.keys(definition).filter(x => x !== field)
      const fieldsObj = fieldsKeys.reduce((obj, key) => ({ ...obj, [key]: definition[key] }), {})
      fieldsKeys.forEach(key => {
        fieldsObj[key].key = key
      })
      return Object.values(fieldsObj)
    },
  },

}
</script>

<style lang="stylus" scoped>
.create-variant__definition-picker
  margin-bottom 24px

.create-variant__input:not(:last-child)
  margin-bottom 24px

.create-variant__error
  width 100%
  height 50px
  padding 6px 16px  6px 0
  error-text()
</style>
