import { EntityService, EntityType } from './_entity.service.js'

export class DiscountService extends EntityService {
  constructor (httpClient) {
    super(EntityType.CRITERION, httpClient)
  }

  get _definitionUrl () {
    return `${this._rootUrl}/discounts/definitions/full`
  }

  get _definitionListUrl () {
    return `${this._rootUrl}/discounts/definitions`
  }

  fetchEntityAsync ({ parentGuid, guid, cultureCode }) {
    const url = `${this._rootUrl}/${parentGuid}/discounts/${guid}/${cultureCode}`
    return super.fetchEntityAsync({ url })
  }

  fetchEntitiesAsync ({ parentGuid, cultureCode }) {
    const url = `${this._rootUrl}/${parentGuid}/discounts/${cultureCode}`
    return super.fetchEntitiesAsync({ url })
  }

  createEntityAsync ({ definitionGuid, parentGuid }) {
    const url = `${this._rootUrl}/${parentGuid}/discounts`
    return this._httpClient.post(url, { definitionGuid })
  }

  updateEntityAsync ({ parentGuid, guid, cultureCode, payload }) {
    const url = `${this._rootUrl}/${parentGuid}/discounts/${guid}/${cultureCode}`
    return this._httpClient.patch(url, payload)
  }

  deleteEntityAsync ({ parentGuid, guid }) {
    const url = `${this._rootUrl}/${parentGuid}/discounts/${guid}`
    return this._httpClient.delete(url)
  }
}
