import Sortable, { AutoScroll, OnSpill } from 'sortablejs/modular/sortable.core.esm.js'
import { reorder } from '@/utils/array.utils.js'

Sortable.mount(new AutoScroll())
Sortable.mount(OnSpill)

const ComponentEvent = {
  INPUT: 'input',
  END: 'end',
}

export default {
  data () {
    return {
      sortable: null,
    }
  },
  props: {
    // Sortablejs options: https://github.com/SortableJS/Sortable
    options: {
      type: Object,
      default: () => ({}),
    },
    selector: {
      type: String,
      default: '',
    },
    value: {
      type: Array,
      required: true,
    },
  },
  watch: {
    options: {
      deep: true,
      handler (values) {
        for (const [key, value] of Object.entries(values)) {
          this.sortable.option(key, value)
        }
      },
    },
  },
  mounted () {
    const listElement = this.selector ? this.$el.querySelector(this.selector) : this.$el

    if (!listElement) {
      // eslint-disable-next-line
      return console.error(`[use-sorting] The element corresponding to the provided selector was not found.`)
    }

    this.sortable = Sortable.create(listElement, {
      scroll: true, // enable autoscroll
      revertOnSpill: true, // enable revert on spill
      forceFallback: true, // Fix bug in Safari: https://github.com/SortableJS/Sortable/issues/1571
      ...this.options,
      onUpdate: this.onUpdate,
      onEnd: this.onEnd,
    })

    this.$on('hook:destroyed', () => this.sortable.destroy())
  },
  methods: {
    onUpdate (event) {
      this.$emit(ComponentEvent.INPUT, reorder(this.value, event.oldIndex, event.newIndex))
    },
    onEnd (event) {
      this.$emit(ComponentEvent.END, { oldIndex: event.oldIndex, newIndex: event.newIndex })
    },
  },
  render () {
    if (!('default' in this.$scopedSlots)) {
      // eslint-disable-next-line
      return console.error(`[use-sorting] No root node found. Please ensure that the component is wrapping a node.`)
    }

    return this.$scopedSlots.default()
  },
}
