<template>
  <div
    class="uc-navigator-node"
    data-test="uc-navigator-node"
    :class="cssClassObject"
    @click="click">
    <slot />
  </div>
</template>

<script>
const ComponentEvent = {
  CLICK: 'click',
}

export default {
  name: 'UcNavigatorNode',
  props: {
    selected: {
      type: Boolean,
      default: false,
    },
    selectable: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    cssClassObject () {
      return {
        'is-selected': this.selected,
        'is-selectable': this.selectable,
      }
    },
  },
  methods: {
    click () {
      this.$emit(ComponentEvent.CLICK)
    },
  },
}
</script>

<style scoped lang="stylus">
.uc-navigator-node
  position relative
  display flex
  flex 1
  user-select none

  &::after
    position absolute
    top 0
    left 0
    display block
    box-sizing border-box
    width 100%
    height 100%
    border-radius $base-border-radius
    content ''
    pointer-events none

  &.is-selectable:hover::after
    border $base-border-width $base-border-style $base-color-brand

  &.is-selectable:active::after
  &.is-selected::after
    border 2 * $base-border-width $base-border-style $base-color-brand

.is-selectable
  cursor pointer
</style>
