import { EntityService, EntityType } from './_entity.service.js'

export class PromotionService extends EntityService {
  constructor (httpClient) {
    super(EntityType.PROMOTION, httpClient)
  }

  createEntityAsync ({ name, definitionGuid, campaignGuid }) {
    return super.createEntityAsync({ name, definitionGuid, params: { campaignGuid } })
  }

  fetchEntitiesAsync ({ parentGuid, cultureCode, params }) {
    const url = `campaigns/${parentGuid}/promotions/${cultureCode}`
    return super.fetchEntitiesAsync({ url, params })
  }

  updateEntityAsync ({ promotionGuid, cultureCode, payload }) {
    const url = `${this._rootUrl}/${promotionGuid}/${cultureCode}`
    return this._httpClient.patch(url, payload)
  }
}
