<template>
  <div class="promotion-edit">
    <transition name="fade">
      <!-- State: Idle -->
      <details-layout
        v-if="isIdle"
        @back-click="closePromotion">
        <generic-details @deleted="closePromotion" />
      </details-layout>

      <!-- State: Initializing -->
      <div
        v-if="isInitializing"
        class="promotion-edit__state">
        <loading-indicator is-loading />
      </div>

      <!-- State: Error -->
      <div
        v-else-if="hasError"
        class="promotion-edit__state">
        <p>{{ errorMessage }}</p>
      </div>
    </transition>
  </div>
</template>

<script>
import { createNamespacedHelpers, mapMutations } from 'vuex'
import DetailsLayout from '@/layouts/details-layout'
import GenericDetails from '@/components/generic/details-view/generic-details'
import { promotionService } from '@/services'
import LoadingIndicator from '@/components/widgets/loading-indicator'

const detailsStore = createNamespacedHelpers('details')

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  ERROR_NOT_FOUND: 'errorNotFound',
  ERROR_UNAUTHORIZED: 'errorUnauthorized',
  ERROR_UNKNOWN: 'errorUnknown',
}

export default {
  name: 'PromotionEdit',
  components: {
    DetailsLayout,
    GenericDetails,
    LoadingIndicator,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    campaignGuid: {
      type: String,
      required: true,
    },
    promotionGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
    }
  },
  computed: {
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    hasError () {
      return this.state === ComponentState.ERROR_NOT_FOUND ||
             this.state === ComponentState.ERROR_UNAUTHORIZED ||
             this.state === ComponentState.ERROR_UNKNOWN
    },
    errorMessage () {
      const errorMessages = {
        [ComponentState.ERROR_NOT_FOUND]: this.$t('promotionEdit.promotionNotFoundError'),
        [ComponentState.ERROR_UNAUTHORIZED]: this.$t('promotionEdit.promotionUnauthorizedError'),
        [ComponentState.ERROR_UNKNOWN]: this.$t('promotionEdit.promotionUnknownError'),
      }

      return errorMessages[this.state] || ''
    },

  },
  async created () {
    try {
      const entity = (await promotionService.fetchEntityAsync({ guid: this.promotionGuid, cultureCode: this.contentLanguage })).data
      this.setDetails({ details: entity })
      this.state = ComponentState.IDLE
    } catch (e) {
      if (e.response && e.response.status) {
        switch (e.response.status) {
          case 404:
            this.state = ComponentState.ERROR_NOT_FOUND
            break
          case 401:
            this.state = ComponentState.ERROR_UNAUTHORIZED
            break
          default:
            this.state = ComponentState.ERROR_UNKNOWN
        }
      } else {
        throw e
      }
    }
  },
  methods: {
    ...mapMutations(['clearUndos']),
    ...detailsStore.mapMutations(['setDetails']),
    closePromotion () {
      this.clearUndos()
      this.$router.back()
    },
  },
}
</script>
<style lang="stylus" scoped>
.promotion-edit__state
  position fixed
  top 0
  left 0
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  height 100vh
</style>
