<template>
  <div
    class="uc-order-line-list"
    :class="cssClassObject">
    <!-- Table of orderLines -->
    <uc-data-grid-selectable
      v-model="selection"
      v-bind="{ columns, rows }"
      row-key="guid"
      :disable-infinite-scroll="hasLoadedAllItems"
      :disable-selection-when="() => !editable"
      @next="onNext">
      <!-- Customise columns to contain editors -->
      <template
        v-for="column in columns"
        #[column.key]="{ cell, row }">
        <div :key="column.key">
          <component
            :is="getPropertyEditorComponent(cell.editor)"
            :key="column.key"
            v-truncate
            :compact="column.readonly"
            no-currency-code
            :name="column.key"
            :language="contentLanguage"
            :definition="{ ...cell, readonly: column.readonly, label:'' }"
            @change="updateProperty(row.guid, $event)" />
        </div>
      </template>

      <!-- Skus column -->
      <template #skus="{ cell }">
        <div class="column-skus">
          <!-- Sku -->
          <div class="column-skus__sku">
            <component
              :is="getPropertyEditorComponent(cell.sku.editor)"
              v-truncate
              compact
              :name="cell.label"
              :language="contentLanguage"
              :definition="{ ...cell.sku, readonly: true }" />
          </div>
          <!-- Variant Sku -->
          <div
            v-if="cell.variantSku.value"
            class="column-skus__variant-sku">
            <component
              :is="getPropertyEditorComponent(cell.variantSku.editor)"
              v-truncate
              compact
              :name="cell.label"
              :language="contentLanguage"
              :definition="{ ...cell.variantSku, readonly: true }" />
          </div>
        </div>
      </template>

      <!-- Totals column -->
      <template #totals="{ cell }">
        <div class="column-totals">
          <!-- Total -->
          <div class="column-totals__total">
            <component
              :is="getPropertyEditorComponent(cell.total.editor)"
              v-truncate
              compact
              no-currency-code
              :name="cell.label"
              :language="contentLanguage"
              :definition="{ ...cell.total, readonly: true }" />
          </div>
          <!-- Tax -->
          <small class="column-totals__tax">
            {{ $t('columns.tax') }}
            <component
              :is="getPropertyEditorComponent(cell.tax.editor)"
              v-truncate
              compact
              no-currency-code
              :name="cell.label"
              :language="contentLanguage"
              :definition="{ ...cell.tax, readonly: true }" />
            {{ $t('perUnit') }}
          </small>
        </div>
      </template>
    </uc-data-grid-selectable>

    <!-- Actions -->
    <transition name="fade-in-up">
      <div
        v-if="!isEmptySelection"
        class="uc-order-line-list__actions-footer">
        <!-- Remove button -->
        <uc-button @click="selectOrderLines">
          {{ `${$t('global.remove')} (${selection.length})` }}
        </uc-button>
      </div>
    </transition>
  </div>
</template>

<i18n>
{
  "en": {
    "columns": {
      "name": "Name",
      "skus": "@:columns.sku / @:columns.variantSku",
      "sku": "Product",
      "variantSku": "Variant SKU",
      "listPrice": "Unit price",
      "quantity": "Quantity",
      "total": "Total",
      "tax": "Tax"
    },
    "perUnit": "/ unit"
  }
}
</i18n>

<script>
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcButton } from '@/components/forms/uc-button/'

const ComponentEvent = {
  NEXT: 'next',
  SELECT: 'select',
  CHANGE: 'change',
}

export default {
  name: 'UcOrderLineList',
  components: {
    UcDataGridSelectable,
    UcButton,
  },
  props: {
    currency: {
      type: String,
      required: true,
    },
    orderLines: {
      type: Array,
      required: true,
    },
    totalCount: {
      type: Number,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  data () {
    return {
      selection: [],
    }
  },
  computed: {
    columns () {
      return [
        {
          key: 'name',
          value: this.$t('columns.name'),
          cellStyle: {
            padding: '0 24px 0 0',
          },
          readonly: true,
        },
        {
          key: 'skus',
          value: `${this.$t('columns.skus')}`,
          cellStyle: {
            padding: '0 24px 0 0',
          },
          readonly: true,
        },
        {
          key: 'quantity',
          value: this.$t('columns.quantity'),
          width: 88,
          cellStyle: {
            padding: '0 24px 0 0',
          },
          readonly: !this.editable,
        },
        {
          key: 'listPrice',
          value: this.$t('columns.listPrice'),
          width: 130,
          cellStyle: {
            padding: '0 24px 0 0',
          },
          readonly: !this.editable,
        },
        {
          key: 'totals',
          value: this.$t('columns.total'),
          width: 160,
          cellStyle: {
            padding: '0 24px 0 0',
            textAlign: 'right',
          },
          readonly: true,
        },
      ]
    },
    rows () {
      return this.orderLines.map(orderLine => {
        const { name, sku, variantSku, listPrice, quantity, total, tax } = orderLine
        const skus = { sku, variantSku, editor: 'ShortText' }
        const totals = { total, tax, editor: 'ShortText' }
        const guid = orderLine.meta?.entityGuid
        return { name, listPrice, quantity, skus, totals, guid }
      })
    },
    cssClassObject () {
      return {
        'is-max-height': this.orderLines.length > 9,
      }
    },
    hasLoadedAllItems () {
      return this.orderLines.length === this.totalCount
    },
    isEmptySelection () {
      return this.selection.length === 0
    },
  },
  methods: {
    getPropertyEditorComponent,
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
    updateProperty (orderLineGuid, payload) {
      this.$emit(ComponentEvent.CHANGE, orderLineGuid, payload)
    },
    selectOrderLines () {
      this.$emit(ComponentEvent.SELECT, this.selection)
      this.selection = []
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-order-line-list
  position relative

  &.is-max-height
    height $base-unit * 61
    border-bottom $base-border

.column-skus,
.column-totals
  display flex
  flex-direction column

.column-totals
  align-items flex-end

.column-totals__tax
  display grid
  grid-auto-flow column
  grid-template-columns min-content min-content
  gap $base-unit * 0.5

.column-totals__tax,
.column-skus__variant-sku
  margin-bottom $base-unit * -2
  secondary-details-text()

.uc-order-line-list__actions-footer
  position absolute
  bottom $base-unit * 4
  z-index $base-z-index * 2
  display flex
  align-items center
  justify-content center
  width 100%
  pointer-events none
</style>
