<template>
  <div class="order-lines">
    <fields-section paddingless>
      <!-- Heading -->
      <template #heading>
        <div class="order-lines__header">
          <span class="order-lines__header-heading">
            {{ $tc('heading', totalCount) }}
          </span>
          <span
            v-show="isIdle && !isEmpty"
            class="order-lines__header-currency">
            ({{ currency }})
          </span>
        </div>
      </template>

      <template #actions>
        <uc-button
          v-if="editable"
          :size="buttonSizeSmall"
          @click="toggleModal">
          {{ $t('global.add') }}
        </uc-button>
      </template>

      <uc-section-wrapper
        v-bind="{ state, errorMessage }"
        :empty-message="$t('emptyMessage')">
        <uc-order-line-list
          v-bind="{
            currency,
            orderLines,
            totalCount,
            contentLanguage,
            editable,
          }"
          @select="deleteOrderLinesAsync"
          @next="requestNextPage"
          @change="updateProperty" />

        <!-- Totals -->
        <fields-section-footer v-if="parent.total">
          <div class="order-lines__footer">
            <uc-order-line-totals
              :order="parent"
              :content-language="contentLanguage" />
          </div>
        </fields-section-footer>
      </uc-section-wrapper>

      <!-- Notifications -->
      <div
        v-if="notifications.length > 0"
        class="order-lines__notifications">
        <div
          v-for="(notification, index) in notifications"
          :key="index"
          class="order-lines__notifications-item">
          <uc-snackbar
            v-bind="notification"
            @click="clearNotification(index)"
            @hide="clearFiniteNotifications" />
        </div>
      </div>
    </fields-section>

    <!-- Delete confirmation dialog -->
    <uc-warning-dialog
      v-if="isDeleting"
      ref="deleteConfirmationDialog"
      :title="$tc('deleteConfirmationDialog.title', itemsForDeletionCount)"
      :text="$tc('deleteConfirmationDialog.text', itemsForDeletionCount)"
      :proceed-button-text="$t('deleteConfirmationDialog.proceed')"
      :cancel-button-text="$t('deleteConfirmationDialog.cancel')" />

    <!-- Add orderlines modal -->
    <uc-modal
      v-if="isModalOpen"
      :title="$t('modalHeading')"
      :size="modalSize"
      @hide="toggleModal">
      <uc-order-line-picker
        v-bind="{ contentLanguage, orderGuid: parentGuid }"
        @add="addOrderlinesAsync" />
    </uc-modal>
  </div>
</template>

<i18n>
{
  "en": {
    "heading": "Order lines | Order lines ({count}) | Order lines ({count})",
    "modalHeading": "Add order lines",
    "emptyMessage": "No order lines have been added to this order yet. Click the button to get started.",
    "deleteConfirmationDialog": {
      "title": " | Remove order line | Remove order lines",
      "text": " | You are about to remove an order line. \n Do you want to proceed? | You are about to remove {count} order lines. \n Do you want to proceed?",
      "proceed": "@:global.proceed",
      "cancel": "@:global.cancel"
    },
    "notifications": {
      "add": {
        "success": " | 1 order line was added successfully. | {count} order lines were added successfully.",
        "notFound": " | 1 order line was not added since the selected product did not exist | {count} order lines were not added since the selected products did not exist.",
        "error": " | 1 order line was not added because the selected product did not have a price for the selected price group. | {count} order lines were not added because the selected products did not have prices for the selected price group."
      },
      "delete": {
        "success": " | 1 order line was removed successfully. | {count} order lines were removed successfully.",
        "notFound": " | 1 order line was not removed since it was not found on the order | {count} order lines were not removed since they were not found on the order."
      }
    }
  }
}
</i18n>
<script>
import { orderLineService } from '@/services/'
import { FieldsSection, FieldsSectionFooter } from '@/components/sections/fields-section/'
import { UcSectionWrapper, UcSectionWrapperState } from '@/components/generic/uc-section-wrapper/'
import { UcSnackbar, UcSnackbarTheme, UcSnackbarDuration } from '@/components/widgets/uc-snackbar/'
import { UcOrderLineList } from './components/uc-order-line-list/'
import { UcWarningDialog, UcWarningDialogEvent } from '@/components/widgets/uc-warning-dialog/'
import { UcButton, UcButtonSize } from '@/components/forms/uc-button/'
import { UcModal, UcModalSize } from '@/components/widgets/uc-modal/'
import { expectComponentEventAsync } from '@/utils/vue.utils.js'
import { UcOrderLinePicker } from './components/uc-order-line-picker/'
import { UcOrderLineTotals } from './components/uc-order-line-totals/'
import { ApiStatusCodes } from '@/constants.js'

const ComponentEvent = {
  CHANGE: 'change',
}


export default {
  name: 'OrderLines',
  components: {
    UcOrderLineList,
    UcSectionWrapper,
    UcSnackbar,
    FieldsSection,
    UcWarningDialog,
    UcButton,
    UcOrderLinePicker,
    UcModal,
    FieldsSectionFooter,
    UcOrderLineTotals,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    parentGuid: {
      type: String,
      default: '',
    },
    editable: {
      type: Boolean,
      default: true,
    },
    parent: {
      type: Object,
      required: true,
    },
  },
  data () {
    return {
      state: UcSectionWrapperState.LOADING,
      errorMessage: '',
      currency: '',
      orderLines: [],
      totalCount: 0,
      startAt: 0,
      currentPage: 1,
      pageSize: 18,
      itemsForDeletionCount: 0,
      isModalOpen: false,
      notifications: [],
    }
  },
  computed: {
    isInitializing () {
      return this.state === UcSectionWrapperState.LOADING
    },
    isIdle () {
      return this.state === UcSectionWrapperState.IDLE
    },
    isError () {
      return this.state === UcSectionWrapperState.ERROR
    },
    isEmpty () {
      return this.orderLines.length === 0
    },
    isDeleting () {
      return this.state === UcSectionWrapperState.DELETING
    },
    buttonSizeSmall: () => UcButtonSize.SMALL,
    modalSize: () => UcModalSize.LG,
  },
  async created () {
    await this.fetchOrderLinesAsync()
  },
  methods: {
    async fetchOrderLinesAsync (startAt = 0, currentPage = 1) {
      try {
        const response = await orderLineService.fetchManyAsync({
          startAt,
          limit: this.pageSize,
          orderGuid: this.parentGuid || this.$route?.params?.orderGuid,
          cultureCode: this.contentLanguage,
          priceGroupGuid: this.priceGroupGuid,
        })

        const orderLines = response.data
        this.orderLines = currentPage > 1 ? [...this.orderLines, ...orderLines] : orderLines
        this.currency = response.headers['x-currency']
        this.totalCount = Number(response.headers['x-total-count'])
        this.currentPage = currentPage
        this.state = this.isEmpty ? UcSectionWrapperState.EMPTY : UcSectionWrapperState.IDLE
      } catch (error) {
        this.state = UcSectionWrapperState.ERROR
        this.errorMessage = error?.response?.data?.message || this.$t('global.errorMessage')
      }
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1

      await this.fetchOrderLinesAsync(startAt, page)
    },
    async deleteOrderLinesAsync (orderLines) {
      this.state = UcSectionWrapperState.DELETING
      this.itemsForDeletionCount = orderLines.length
      const guids = orderLines.map(({ guid }) => guid)

      await this.$nextTick()

      try {
        await expectComponentEventAsync({
          component: this.$refs.deleteConfirmationDialog,
          resolveEvent: UcWarningDialogEvent.PROCEED,
          rejectEvent: UcWarningDialogEvent.CANCEL,
        })

        try {
          const response = await orderLineService.deleteManyAsync({ guids, orderGuid: this.parentGuid })
          this.showDeleteNotifications(response.data)
          this.orderLines = this.orderLines.filter(orderLine => !guids.includes(orderLine.meta.entityGuid))
          this.totalCount -= guids.length
          // TODO: Add notification sc-14829
          this.state = this.isEmpty ? UcSectionWrapperState.EMPTY : UcSectionWrapperState.IDLE
          this.$emit(ComponentEvent.CHANGE)
        } catch (error) {
          this.showErrorNotification(error?.response?.data?.message)
          this.state = UcSectionWrapperState.IDLE
        }
      } catch (error) {
        this.state = UcSectionWrapperState.IDLE
      } finally {
        this.itemsForDeletionCount = 0
      }
    },
    toggleModal () {
      this.isModalOpen = !this.isModalOpen
    },
    async addOrderlinesAsync (rows, priceGroup) {
      this.state = UcSectionWrapperState.LOADING

      try {
        const guids = rows.map(row => row.meta?.entityGuid || row.guid)
        const response = await orderLineService.createManyAsync({ guids, cultureCode: this.contentLanguage, orderGuid: this.parentGuid, priceGroupGuid: priceGroup.value })
        this.state = UcSectionWrapperState.IDLE
        this.$emit(ComponentEvent.CHANGE)
        this.showAddNotifications(response.data)
      } catch (error) {
        this.state = UcSectionWrapperState.ERROR
        this.showErrorNotification(error?.response?.data?.message)
      }

      await this.fetchOrderLinesAsync(priceGroup)
      this.toggleModal()
    },
    async updateProperty (orderLineGuid, payload) {
      try {
        await orderLineService.updateOneAsync({
          orderGuid: this.parentGuid,
          orderLineGuid: orderLineGuid,
          cultureCode: this.contentLanguage,
          payload: payload.save,
        })

        const orderLine = (await orderLineService.fetchOneAsync({
          orderGuid: this.parentGuid,
          orderLineGuid: orderLineGuid,
          cultureCode: this.contentLanguage,
        })).data

        const index = this.orderLines.findIndex(orderline => orderline.meta.entityGuid === orderLineGuid)
        this.orderLines.splice(index, 1, orderLine)
        this.$emit(ComponentEvent.CHANGE)
      } catch (error) {
        this.showErrorNotification(error?.response?.data?.message)
      }
    },
    // Methods related to showing notifications. TODO: Move to separate component
    createNotificationMessage (message, count) {
      return this.$tc(message, count)
    },
    filterItemsByStatusCode (items, statusCode) {
      const matchesStatusCode = product => product.statusCode === statusCode
      return items.filter(matchesStatusCode)
    },
    showAddNotifications (items) {
      const numberOfItems = {
        [ApiStatusCodes.OK]: this.filterItemsByStatusCode(items, ApiStatusCodes.OK).length,
        [ApiStatusCodes.NOT_FOUND]: this.filterItemsByStatusCode(items, ApiStatusCodes.NOT_FOUND).length,
        [ApiStatusCodes.ERROR]: this.filterItemsByStatusCode(items, ApiStatusCodes.ERROR).length,
      }

      const message = {
        [ApiStatusCodes.OK]: this.createNotificationMessage('notifications.add.success', numberOfItems[ApiStatusCodes.OK]),
        [ApiStatusCodes.NOT_FOUND]: this.createNotificationMessage('notifications.add.notFound', numberOfItems[ApiStatusCodes.NOT_FOUND]),
        [ApiStatusCodes.ERROR]: this.createNotificationMessage('notifications.add.error', numberOfItems[ApiStatusCodes.ERROR]),
      }

      const hasErrorItems = numberOfItems[ApiStatusCodes.NOT_FOUND] || numberOfItems[ApiStatusCodes.ERROR]
      const hasAddedItems = numberOfItems[ApiStatusCodes.OK]

      if (hasErrorItems) {
        const errorMessage = `${message[ApiStatusCodes.NOT_FOUND]} ${message[ApiStatusCodes.ERROR]}`
        this.showErrorNotification(errorMessage)
      }

      if (hasAddedItems) {
        const notificationMessage = `${message[ApiStatusCodes.OK]}`
        const theme = numberOfItems[ApiStatusCodes.OK] === 0 ? UcSnackbarTheme.NEUTRAL : UcSnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showDeleteNotifications (items) {
      const numberOfItems = {
        [ApiStatusCodes.OK]: this.filterItemsByStatusCode(items, ApiStatusCodes.OK).length,
        [ApiStatusCodes.NOT_FOUND]: this.filterItemsByStatusCode(items, ApiStatusCodes.NOT_FOUND).length,
      }

      const message = {
        [ApiStatusCodes.OK]: this.createNotificationMessage('notifications.delete.success', numberOfItems[ApiStatusCodes.OK]),
        [ApiStatusCodes.NOT_FOUND]: this.createNotificationMessage('notifications.delete.notFound', numberOfItems[ApiStatusCodes.NOT_FOUND]),
      }

      const hasErrorItems = numberOfItems[ApiStatusCodes.NOT_FOUND]
      const hasDeletedItems = numberOfItems[ApiStatusCodes.OK]

      if (hasErrorItems) {
        const errorMessage = `${message[ApiStatusCodes.NOT_FOUND]}`
        this.showErrorNotification(errorMessage)
      }

      if (hasDeletedItems) {
        const notificationMessage = `${message[ApiStatusCodes.OK]}`
        const theme = numberOfItems[ApiStatusCodes.OK] === 0 ? UcSnackbarTheme.NEUTRAL : UcSnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showNotification ({ text, theme, duration = UcSnackbarDuration.SHORT }) {
      const newNotification = {
        text,
        dismissable: duration === UcSnackbarDuration.INFINITE,
        duration,
        theme,
      }

      this.notifications = [...this.notifications, newNotification]
    },
    showErrorNotification (text) {
      this.showNotification({ text, theme: UcSnackbarTheme.ERROR, duration: UcSnackbarDuration.INFINITE })
    },
    clearFiniteNotifications () {
      this.notifications = this.notifications.filter(notification => notification.duration === UcSnackbarDuration.INFINITE)
    },
    clearNotification (index) {
      this.notifications = this.notifications.filter((_notification, i) => i !== index)
    },
  },
}
</script>
<style lang="stylus" scoped>
.order-lines
  position relative
  width 100%

.order-lines__header
  display flex
  flex-direction row

.order-lines__header-currency
  margin-left $base-unit
  color $base-color-text-secondary
  font-weight normal

.order-lines__footer
  display flex
  align-items center
  justify-content flex-end
  height 100%
  padding $base-unit * 3

.order-lines__notifications
  position absolute
  top $base-unit * 16
  z-index 2
  width 100%
  padding $base-unit * 2

.order-lines__notifications-item + .order-lines__notifications-item
  margin-top $base-unit
</style>
