import { rest } from 'msw'

const RESPONSE_DELAY = 200

const defaultResponse = {
  cms: 'Umbraco',
  cmsVersion: '8.0.0.0',
  UcommerceVersion: '9.6.1.22032',
  currentCultureCode: 'en-US',
  languages: [
    {
      name: 'English (United Kingdom)',
      cultureCode: 'en-GB',
    },
    {
      name: 'English (United States)',
      cultureCode: 'en-US',
    },
  ],
  currentUserFirstName: 'Ucommerce',
}

export const systemHandlers = [
  rest.get('/ucommerceapi/v2/system', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(defaultResponse),
    )
  }),
]
