<template>
  <div
    class="uc-product-editor"
    :class="cssClassObject">
    <!-- Add button -->
    <uc-button
      :theme="buttonTheme"
      block
      @click="toggleModal">
      {{ translations.add }}
    </uc-button>

    <!-- Product editor -->
    <div
      v-show="products.length > 0"
      class="uc-product-editor__editor">
      <uc-data-grid-selectable
        v-model="selectedProducts"
        v-bind="{ columns }"
        :rows="products"
        row-key="guid"
        :disable-infinite-scroll="hasLoadedAllItems"
        @next="requestNextPage">
        <template #image="{ cell }">
          <div class="uc-product-editor__image">
            <uc-image
              :src="cell"
              :fit="imageFit" />
          </div>
        </template>
      </uc-data-grid-selectable>

      <transition name="fade-in-up">
        <div
          v-show="selectedProducts.length > 0"
          class="uc-product-editor__button">
          <uc-button
            floating
            @click="deleteProductsAsync">
            {{ translations.remove }}
          </uc-button>
        </div>
      </transition>

      <!-- Loading spinner -->
      <uc-overlay v-if="isLoading">
        <div class="uc-product-editor__loading-indicator">
          <loading-indicator :is-loading="isLoading" />
        </div>
      </uc-overlay>

      <!-- Notifications -->
      <template v-if="notifications.length > 0">
        <div
          v-for="(notification, index) in notifications"
          :key="index"
          class="uc-product-editor__notifications">
          <uc-snackbar
            v-bind="notification"
            @click="clearNotification(index)"
            @hide="clearFiniteNotifications" />
        </div>
      </template>
    </div>

    <!-- Product picker -->
    <uc-modal
      v-if="isModalOpen"
      :title="translations.heading"
      :size="modalSize"
      @hide="toggleModal">
      <uc-product-picker
        v-bind="{ language }"
        @add="addProducts" />
    </uc-modal>
  </div>
</template>

<script>
import http from '@/services/http.js'
import UcButton, { UcButtonTheme } from '@/components/forms/uc-button/'
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcModal, UcModalSize } from '@/components/widgets/uc-modal/'
import { UcImage, UcImageFitProp } from '@/components/generic/uc-image/'
import { UcSnackbar, UcSnackbarTheme, UcSnackbarDuration } from '@/components/widgets/uc-snackbar/'
import { UcOverlay } from '@/components/widgets/uc-overlay/'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import { UcProductPicker } from '@/components/generic/uc-product-picker/'
import { ApiStatusCodes } from '@/constants.js'
import { ProductService } from './product.service.js'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  LOADING: 'loading',
  ERROR: 'error',
}

const apiStatusCodes = {
  ADDED: ApiStatusCodes.OK,
  UNAUTHORIZED: ApiStatusCodes.NOT_PERMITTED,
  NOT_FOUND: ApiStatusCodes.NOT_FOUND,
  DUPLICATE: ApiStatusCodes.ALREADY_EXIST,
}

export default {
  name: 'UcProductEditor',
  components: {
    UcButton,
    UcProductPicker,
    UcModal,
    UcDataGridSelectable,
    UcImage,
    UcSnackbar,
    UcOverlay,
    LoadingIndicator,
  },
  props: {
    language: {
      type: String,
      required: true,
    },
    promotionGuid: {
      type: String,
      required: true,
    },
    entityGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      isModalOpen: false,
      products: [],
      selectedProducts: [],
      notifications: [],
      state: ComponentState.INITIALIZING,
      productService: null,
      totalCount: 0,
      pageSize: 18,
      currentPage: 1,
    }
  },
  computed: {
    buttonTheme: () => UcButtonTheme.SECONDARY,
    imageFit: () => UcImageFitProp.CONTAIN,
    modalSize: () => UcModalSize.LG,
    cssClassObject () {
      return {
        'is-max-height': this.products.length > 6,
      }
    },
    hasLoadedAllItems () {
      return this.products.length === this.totalCount
    },
    columns () {
      return [
        { key: 'image', value: this.$t('global.columns.image'), width: '90px' },
        { key: 'name', value: this.$t('global.columns.name') },
        { key: 'sku', value: this.$t('global.columns.sku') },
        { key: 'variantSku', value: this.$t('global.columns.variantSku') },
      ]
    },
    translations () {
      return {
        add: this.$t('ucProductEditor.add'),
        remove: `${this.$t('global.remove')} (${this.selectedProducts.length})`,
        heading: this.$t('ucProductEditor.heading'),
      }
    },
    isLoading () {
      return this.state === ComponentState.LOADING
    },
  },
  async created () {
    this.productService = new ProductService(http, this.promotionGuid, this.entityGuid)
    await this.fetchProductsAsync()
  },
  methods: {
    toggleModal () {
      this.isModalOpen = !this.isModalOpen
    },
    async fetchProductsAsync (startAt = 0, currentPage = 1) {
      this.state = ComponentState.LOADING

      try {
        const response = await this.productService.fetchManyAsync({ startAt, limit: this.pageSize, cultureCode: this.language })
        const products = response.data.map(item => ({
          image: item.primaryImage?.url,
          name: item.name?.value,
          sku: item.sku?.value,
          variantSku: item.variantSku?.value,
          guid: item.meta?.entityGuid,
        }))

        this.products = currentPage > 1 ? [...this.products, ...products] : products
        this.totalCount = Number(response.headers['x-total-count'])
        this.currentPage = currentPage
        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
      }
    },
    async deleteProductsAsync () {
      this.state = ComponentState.LOADING

      try {
        const guids = this.selectedProducts.map(product => product.guid)
        await this.productService.deleteManyAsync({ guids })

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
        // Following should be handled: 400, 401 and 404
      }

      await this.fetchProductsAsync()
      this.selectedProducts = []
    },
    async addProducts (rows) {
      this.state = ComponentState.LOADING

      try {
        const guids = rows.map(row => row.meta?.entityGuid || row.guid)
        const response = await this.productService.createManyAsync({ guids, cultureCode: this.language })
        this.showProductsNotification(response.data)

        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle http request errors
        // Following should be handled: 304 and 400
      }

      await this.fetchProductsAsync()
      this.toggleModal()
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1

      await this.fetchProductsAsync(startAt, page)
    },

    // Methods related to showing notifications. TODO: Move to separate component
    createNotificationMessage (message, count) {
      return this.$tc(message, count, { count })
    },
    filterProductsByStatusCode (products, statusCode) {
      const matchesStatusCode = product => product.statusCode === statusCode
      return products.filter(matchesStatusCode)
    },
    showProductsNotification (products) {
      const numberOfProducts = {
        [apiStatusCodes.ADDED]: this.filterProductsByStatusCode(products, apiStatusCodes.ADDED).length,
        [apiStatusCodes.DUPLICATE]: this.filterProductsByStatusCode(products, apiStatusCodes.DUPLICATE).length,
        [apiStatusCodes.NOT_FOUND]: this.filterProductsByStatusCode(products, apiStatusCodes.NOT_FOUND).length,
        [apiStatusCodes.UNAUTHORIZED]: this.filterProductsByStatusCode(products, apiStatusCodes.UNAUTHORIZED).length,
      }

      const message = {
        [apiStatusCodes.ADDED]: this.createNotificationMessage('ucProductEditor.messages.success', numberOfProducts[apiStatusCodes.ADDED]),
        [apiStatusCodes.DUPLICATE]: this.createNotificationMessage('ucProductEditor.messages.alreadyExist', numberOfProducts[apiStatusCodes.DUPLICATE]),
        [apiStatusCodes.NOT_FOUND]: this.createNotificationMessage('ucProductEditor.messages.notFound', numberOfProducts[apiStatusCodes.NOT_FOUND]),
        [apiStatusCodes.UNAUTHORIZED]: this.createNotificationMessage('ucProductEditor.messages.unauthorized', numberOfProducts[apiStatusCodes.UNAUTHORIZED]),
      }

      const hasErrors = numberOfProducts[apiStatusCodes.NOT_FOUND] || numberOfProducts[apiStatusCodes.UNAUTHORIZED]
      const hasProducts = numberOfProducts[apiStatusCodes.ADDED] || numberOfProducts[apiStatusCodes.DUPLICATE]

      if (hasErrors) {
        const errorMessage = `${message[apiStatusCodes.NOT_FOUND]} ${message[apiStatusCodes.UNAUTHORIZED]}`
        this.showErrorNotification(errorMessage)
      }

      if (hasProducts) {
        const notificationMessage = `${message[apiStatusCodes.ADDED]} ${message[apiStatusCodes.DUPLICATE]}`
        const theme = numberOfProducts[apiStatusCodes.ADDED] === 0 ? UcSnackbarTheme.NEUTRAL : UcSnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showNotification ({ text, theme, duration = UcSnackbarDuration.SHORT }) {
      const newNotification = {
        text,
        dismissable: duration === UcSnackbarDuration.INFINITE,
        duration,
        theme,
      }

      this.notifications = [...this.notifications, newNotification]
    },
    showErrorNotification (text) {
      this.showNotification({ text, theme: UcSnackbarTheme.ERROR, duration: UcSnackbarDuration.INFINITE })
    },
    clearFiniteNotifications () {
      this.notifications = this.notifications.filter(notification => notification.duration === UcSnackbarDuration.INFINITE)
    },
    clearNotification (index) {
      this.notifications = this.notifications.filter((_notification, i) => i !== index)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-product-editor__editor
  position relative
  display flex
  justify-content center
  margin-top $base-unit * 3
  border $base-border
  border-bottom none

  .is-max-height &
    height $base-unit * 60
    border-bottom $base-border

.uc-product-editor__button
  position absolute
  bottom $base-unit * 4
  z-index 1

.uc-product-editor__image
  display flex

.uc-product-editor__notifications
  position absolute
  top $base-unit * 5
  width 100%
  padding $base-unit * 2

.uc-product-editor__notifications ~ .uc-product-editor__notifications
  top $base-unit * 12
</style>
