<template>
  <div class="uri-whitelist">
    <fields-section
      paddingless
      :heading="$tc('heading', resultCount)">
      <template #actions>
        <uc-button
          :size="buttonSizeSmall"
          @click="addUriAsync">
          {{ $t('new') }}
        </uc-button>
      </template>
      <uc-section-wrapper
        :empty-message="$t('emptyMessage')"
        v-bind="{ state, errorMessage }">
        <uc-uri-list
          v-bind="{ uris, contentLanguage, totalCount: resultCount}"
          @next="requestNextPage"
          @delete="deleteUrisAsync" />
      </uc-section-wrapper>
    </fields-section>
    <!-- Delete confirmation dialog -->
    <uc-warning-dialog
      v-if="isDeleting"
      ref="deleteConfirmationDialog"
      :title="$tc('deleteConfirmationDialog.title', itemsForDeletion.length)"
      :text="$tc('deleteConfirmationDialog.text', itemsForDeletion.length)"
      :proceed-button-text="$t('deleteConfirmationDialog.proceed')"
      :cancel-button-text="$t('deleteConfirmationDialog.cancel')" />
    <!-- Create new uri modal -->
    <uri-whitelist-create
      v-if="isCreating"
      v-bind="{ storeGuid, contentLanguage }"
      ref="addUriDialog" />
  </div>
</template>

<i18n>
{
  "en": {
    "heading": "URI Whitelist | URI Whitelist ({count}) | URI Whitelist ({count})",
    "emptyMessage": "No whitelisted URIs have been added yet. Click the button to get started.",
    "new": "Add",
    "deleteConfirmationDialog": {
      "title": " | Remove URI | Remove {count} URIs",
      "text": " | You are about to remove a URI. \n Do you want to proceed? | You are about to remove {count} URIs. \n Do you want to proceed?",
      "proceed": "@:global.proceed",
      "cancel": "@:global.cancel"
    },
    "notifications": {
      "add": {
        "success": " | 1 URI was added successfully. | {count} URIs were added successfully."
      },
      "delete": {
        "success": " | 1 URI was removed successfully. | {count} URIs were removed successfully."
      }
    }
  }
}
</i18n>

<script>
import { UcUriList } from './components/uc-uri-list/'
import { uriWhitelistService } from '@/services/'
import { FieldsSection } from '@/components/sections/fields-section/'
import { UcSectionWrapper, UcSectionWrapperState } from '@/components/generic/uc-section-wrapper'
import { UcButton, UcButtonSize } from '@/components/forms/uc-button/'
import { expectComponentEventAsync } from '@/utils/vue.utils.js'
import { UriWhitelistCreate, UriWhitelistEvent } from './components/uri-whitelist-create/'
import { UcWarningDialog, UcWarningDialogEvent } from '@/components/widgets/uc-warning-dialog/'

export default {
  name: 'UriWhitelist',
  components: {
    UriWhitelistCreate,
    UcSectionWrapper,
    UcUriList,
    FieldsSection,
    UcButton,
    UcWarningDialog,
  },
  props: {
    storeGuid: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      state: UcSectionWrapperState.LOADING,
      uris: [],
      totalCount: 0,
      resultCount: 0,
      startAt: 0,
      currentPage: 1,
      pageSize: 10,
      errorMessage: '',
      itemsForDeletion: [],
    }
  },
  computed: {
    buttonSizeSmall: () => UcButtonSize.SMALL,
    isIdle () {
      return this.state === UcSectionWrapperState.IDLE
    },
    isEmpty () {
      return this.uris.length === 0
    },
    isDeleting () {
      return this.state === UcSectionWrapperState.DELETING
    },
    isCreating () {
      return this.state === UcSectionWrapperState.CREATING
    },
  },
  watch: {
    storeGuid: {
      handler: function () {
        this.fetchUrisAsync()
      },
      immediate: true,
    },
  },
  methods: {
    async fetchUrisAsync (startAt = 0, currentPage = 1) {
      try {
        const response = await uriWhitelistService.fetchManyAsync({
          storeGuid: this.storeGuid,
          cultureCode: this.contentLanguage,
          startAt,
          limit: this.pageSize,
        })

        const uris = response.data
        this.uris = currentPage > 1 ? [...this.uris, ...uris] : uris
        this.totalCount = Number(response.headers['x-total-count'])
        this.resultCount = Number(response.headers['x-result-count'])
        this.currentPage = currentPage
        this.state = this.isEmpty ? UcSectionWrapperState.EMPTY : UcSectionWrapperState.IDLE
      } catch (error) {
        this.errorMessage = error?.response?.data?.message || this.$t('global.errorMessage')
        this.state = UcSectionWrapperState.ERROR
      }
    },
    async addUriAsync () {
      this.state = UcSectionWrapperState.CREATING
      await this.$nextTick()
      try {
        await expectComponentEventAsync({
          component: this.$refs.addUriDialog,
          resolveEvent: UriWhitelistEvent.CREATE,
          rejectEvent: UriWhitelistEvent.CANCEL,
        })
        await this.fetchUrisAsync()
        this.state = UcSectionWrapperState.IDLE
      } catch (e) {
        this.state = UcSectionWrapperState.IDLE
      }
    },
    async deleteUrisAsync (uris) {
      this.state = UcSectionWrapperState.DELETING
      this.itemsForDeletion = uris
      await this.$nextTick()
      try {
        await expectComponentEventAsync({
          component: this.$refs.deleteConfirmationDialog,
          resolveEvent: UcWarningDialogEvent.PROCEED,
          rejectEvent: UcWarningDialogEvent.CANCEL,
        })
        try {
          const guids = uris.map(({ guid }) => guid)
          await uriWhitelistService.deleteManyAsync({ guids, storeGuid: this.storeGuid, cultureCode: this.cultureCode })
          // this.showDeleteNotifications(response.data)
          // this.uris = this.uris.filter(uri => !guids.includes(uri.meta.entityGuid))
          this.uris = this.uris.filter(uri => !guids.includes(uri.guid.value))
          this.resultCount -= guids.length
          this.state = this.isEmpty ? UcSectionWrapperState.EMPTY : UcSectionWrapperState.IDLE
        } catch (error) {
          // this.showErrorNotification(error?.response?.data?.message)
          this.state = UcSectionWrapperState.IDLE
        }
      } catch (e) {
        this.state = UcSectionWrapperState.IDLE
      } finally {
        this.itemsForDeletion = []
      }
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1

      await this.fetchUrisAsync(startAt, page)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uri-whitelist
  position relative
  width 100%
  user-select none
</style>
