import { EntityService, EntityType } from './_entity.service.js'

export class ClientKeysService extends EntityService {
  constructor (httpClient) {
    super(EntityType.CLIENT_KEYS, httpClient)
  }

  fetchOneAsync ({ storeGuid, cultureCode }) {
    const url = `/settings/api-access/${storeGuid}/${cultureCode}`
    return super.fetchEntityAsync({ url })
  }

  updateOneAsync ({ storeGuid, cultureCode, payload }) {
    const url = `/settings/api-access/${storeGuid}/${cultureCode}`
    return this._httpClient.patch(url, payload)
  }
}
