<template>
  <div class="order-discounts">
    <fields-section paddingless>
      <!-- Add discount button-->
      <template #actions>
        <uc-button
          v-if="editable"
          :size="buttonSizeSmall"
          @click="openModal">
          {{ $t('new') }}
        </uc-button>
      </template>
      <!-- Heading -->
      <template #heading>
        <div class="order-discounts__header">
          <span class="order-discounts__header-heading">
            {{ $tc('heading', resultCount) }}
          </span>
          <span
            v-show="isIdle && !isEmpty"
            class="order-discounts__header-currency">
            ({{ currency }})
          </span>
        </div>
      </template>

      <uc-section-wrapper
        v-bind="{ state, errorMessage }"
        :empty-message="$t('emptyState')">
        <!-- State: Idle -->
        <uc-order-discount-list
          v-bind="{
            currency,
            discounts,
            totalCount: resultCount,
            contentLanguage,
            editable
          }"
          @next="requestNextPage"
          @select="deleteDiscountsAsync" />

        <fields-section-footer v-if="parent.discountTotal">
          <div class="order-discounts__footer">
            <span class="order-discounts__footer-content">
              <p class="order-discounts__label">{{ parent.discountTotal.label.toUpperCase() }}:</p>
              <component
                :is="getPropertyEditorComponent(parent.discountTotal.editor)"
                no-currency-code
                compact
                :language="contentLanguage"
                :definition="parent.discountTotal" />
            </span>
          </div>
        </fields-section-footer>
      </uc-section-wrapper>

      <!-- Notifications -->
      <div
        v-if="notifications.length > 0"
        class="order-discounts__notifications">
        <div
          v-for="(notification, index) in notifications"
          :key="index"
          class="order-discounts__notifications-item">
          <uc-snackbar
            v-bind="notification"
            @click="clearNotification(index)"
            @hide="clearFiniteNotifications" />
        </div>
      </div>
    </fields-section>
    <!-- Delete confirmation dialog -->
    <uc-warning-dialog
      v-if="isDeleting"
      ref="deleteConfirmationDialog"
      :title="$tc('deleteConfirmationDialog.title', itemsForDeletion.length)"
      :text="$tc('deleteConfirmationDialog.text', itemsForDeletion.length)"
      :proceed-button-text="$t('deleteConfirmationDialog.proceed')"
      :cancel-button-text="$t('deleteConfirmationDialog.cancel')" />
    <!-- Create new discount modal -->
    <order-discounts-create-modal
      v-if="isModalOpen"
      :content-language="contentLanguage"
      :parent-guid="parentGuid"
      @created="onCreated"
      @cancel="closeModal" />
  </div>
</template>

<i18n>
{
  "en": {
    "heading": "Discounts | Discounts ({count}) | Discounts ({count})",
    "emptyState": "No discounts have been added to this order yet. Click the button to get started.",
    "new": "Add",
    "emptyMessage": "No discounts have been added to this order yet. Click the button to get started.",
    "deleteConfirmationDialog": {
      "title": " | Remove discount | Remove {count} discounts",
      "text": " | You are about to remove a discount. \n Do you want to proceed? | You are about to remove {count} discounts. \n Do you want to proceed?",
      "proceed": "@:global.proceed",
      "cancel": "@:global.cancel"
    },
    "notifications": {
      "add": {
        "success": " | 1 discount was added successfully. | {count} discounts were added successfully."
      },
      "delete": {
        "success": " | 1 discount was removed successfully. | {count} discounts were removed successfully.",
        "notFound": " | 1 discount was not removed since it was not found on the order | {count} discounts were not removed since they were not found on the order."
      }
    }
  }
}
</i18n>
<script>
import { getPropertyEditorComponent } from '@/components/editors/'
import { FieldsSection, FieldsSectionFooter } from '@/components/sections/fields-section/'
import { UcOrderDiscountList } from './components/uc-order-discount-list/'
import { UcSectionWrapper, UcSectionWrapperState } from '@/components/generic/uc-section-wrapper/'
import { UcSnackbar, UcSnackbarTheme, UcSnackbarDuration } from '@/components/widgets/uc-snackbar/'
import { UcButton, UcButtonSize } from '@/components/forms/uc-button/'
import { OrderDiscountsCreateModal } from './components/order-discounts-create-modal/'
import { UcWarningDialog, UcWarningDialogEvent } from '@/components/widgets/uc-warning-dialog/'
import { expectComponentEventAsync } from '@/utils/vue.utils.js'
import { orderDiscountService } from '@/services/'
import { ApiStatusCodes } from '@/constants.js'

const ComponentEvents = {
  CHANGE: 'change',
}

export default {
  name: 'OrderDiscounts',
  components: {
    UcOrderDiscountList,
    FieldsSection,
    FieldsSectionFooter,
    UcSectionWrapper,
    UcSnackbar,
    UcButton,
    OrderDiscountsCreateModal,
    UcWarningDialog,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    parentGuid: {
      type: String,
      default: '',
    },
    parent: {
      type: Object,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  data () {
    return {
      state: UcSectionWrapperState.LOADING,
      errorMessage: '',
      currency: '',
      discounts: [],
      totalCount: 0,
      resultCount: 0,
      startAt: 0,
      currentPage: 1,
      pageSize: 18,
      isModalOpen: false,
      itemsForDeletion: [],
      notifications: [],
    }
  },
  computed: {
    buttonSizeSmall: () => UcButtonSize.SMALL,
    isIdle () {
      return this.state === UcSectionWrapperState.IDLE
    },
    isEmpty () {
      return this.discounts.length === 0
    },
    isDeleting () {
      return this.state === UcSectionWrapperState.DELETING
    },
  },
  async created () {
    await this.fetchDiscountsAsync()
  },
  methods: {
    getPropertyEditorComponent,
    async fetchDiscountsAsync (startAt = 0, currentPage = 1) {
      try {
        const response = await orderDiscountService.fetchManyAsync({
          orderGuid: this.parentGuid,
          cultureCode: this.contentLanguage,
          startAt,
          limit: this.pageSize,
        })

        const discounts = response.data
        this.discounts = currentPage > 1 ? [...this.discounts, ...discounts] : discounts
        this.currency = response.headers['x-currency']
        this.totalCount = Number(response.headers['x-total-count'])
        this.resultCount = Number(response.headers['x-result-count'])
        this.currentPage = currentPage
        // TODO: Add notification sc-14829
        this.state = this.isEmpty ? UcSectionWrapperState.EMPTY : UcSectionWrapperState.IDLE
      } catch (error) {
        // TODO: Add notification sc-14829
        this.state = UcSectionWrapperState.ERROR
        if (error?.response?.data?.message) {
          this.errorMessage = error.response.data.message
        } else {
          throw error
        }
      }
    },
    onCreated () {
      this.fetchDiscountsAsync()
      this.closeModal()
      this.$emit(ComponentEvents.CHANGE)
      this.showSuccessNotification(this.$tc('notifications.add.success', 1))
    },
    closeModal () {
      this.isModalOpen = false
    },
    openModal () {
      this.isModalOpen = true
    },
    async requestNextPage () {
      const startAt = this.currentPage * this.pageSize
      const page = this.currentPage + 1

      await this.fetchDiscountsAsync(startAt, page)
    },
    async deleteDiscountsAsync (payload) {
      this.state = UcSectionWrapperState.DELETING
      this.itemsForDeletion = payload
      const guids = payload.map(({ guid }) => guid)
      await this.$nextTick()
      try {
        await expectComponentEventAsync({
          component: this.$refs.deleteConfirmationDialog,
          resolveEvent: UcWarningDialogEvent.PROCEED,
          rejectEvent: UcWarningDialogEvent.CANCEL,
        })
        try {
          const response = await orderDiscountService.deleteManyAsync({ guids, orderGuid: this.parentGuid })
          this.showDeleteNotifications(response.data)
          this.discounts = this.discounts.filter(discount => !guids.includes(discount.meta.entityGuid))
          this.resultCount -= guids.length
          this.state = this.isEmpty ? UcSectionWrapperState.EMPTY : UcSectionWrapperState.IDLE
          this.$emit(ComponentEvents.CHANGE)
        } catch (error) {
          this.showErrorNotification(error?.response?.data?.message)
          this.state = UcSectionWrapperState.IDLE
        }
      } catch (e) {
        this.state = UcSectionWrapperState.IDLE
      } finally {
        this.itemsForDeletion = []
      }
    },
    // Methods related to showing notifications. TODO: Move to separate component
    createNotificationMessage (message, count) {
      return this.$tc(message, count)
    },
    filterItemsByStatusCode (items, statusCode) {
      const matchesStatusCode = product => product.statusCode === statusCode
      return items.filter(matchesStatusCode)
    },
    showDeleteNotifications (items) {
      const numberOfItems = {
        [ApiStatusCodes.OK]: this.filterItemsByStatusCode(items, ApiStatusCodes.OK).length,
        [ApiStatusCodes.NOT_FOUND]: this.filterItemsByStatusCode(items, ApiStatusCodes.NOT_FOUND).length,
      }

      const message = {
        [ApiStatusCodes.OK]: this.createNotificationMessage('notifications.delete.success', numberOfItems[ApiStatusCodes.OK]),
        [ApiStatusCodes.NOT_FOUND]: this.createNotificationMessage('notifications.delete.notFound', numberOfItems[ApiStatusCodes.NOT_FOUND]),
      }

      const hasErrorItems = numberOfItems[ApiStatusCodes.NOT_FOUND]
      const hasDeletedItems = numberOfItems[ApiStatusCodes.OK]

      if (hasErrorItems) {
        const errorMessage = `${message[ApiStatusCodes.NOT_FOUND]}`
        this.showErrorNotification(errorMessage)
      }

      if (hasDeletedItems) {
        const notificationMessage = `${message[ApiStatusCodes.OK]}`
        const theme = numberOfItems[ApiStatusCodes.OK] === 0 ? UcSnackbarTheme.NEUTRAL : UcSnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showNotification ({ text, theme, duration = UcSnackbarDuration.SHORT }) {
      const newNotification = {
        text,
        dismissable: duration === UcSnackbarDuration.INFINITE,
        duration,
        theme,
      }

      this.notifications = [...this.notifications, newNotification]
    },
    showErrorNotification (text) {
      this.showNotification({ text, theme: UcSnackbarTheme.ERROR, duration: UcSnackbarDuration.INFINITE })
    },
    showSuccessNotification (text) {
      this.showNotification({ text, theme: UcSnackbarTheme.SUCCESS, duration: UcSnackbarDuration.SHORT })
    },
    clearFiniteNotifications () {
      this.notifications = this.notifications.filter(notification => notification.duration === UcSnackbarDuration.INFINITE)
    },
    clearNotification (index) {
      this.notifications = this.notifications.filter((_notification, i) => i !== index)
    },
  },
}
</script>
<style lang="stylus" scoped>
$min-height = $base-unit * 24

.order-discounts
  position relative
  width 100%

.order-discounts__header
  display flex
  flex-direction row

.order-discounts__header-currency
  margin-left $base-unit
  color $base-color-text-secondary
  font-weight normal

.order-discounts__state
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  min-height $min-height
  secondary-body-text()

.order-discounts__footer
  display flex
  align-items center
  justify-content flex-end
  height 100%
  padding $base-unit * 3

.order-discounts__footer-content
  display flex

.order-discounts__label
  margin 0
  margin-right $base-unit
  padding 0

// When list hits max-height, we need to take scrollbar into consideration, when aligning
.is-max-height ~ .fields-section-footer .order-discounts__footer-content
  margin-right 6px

.order-discounts__notifications
  position absolute
  top $base-unit * 16
  z-index 2
  width 100%
  padding $base-unit * 2

.order-discounts__notifications-item + .order-discounts__notifications-item
  margin-top $base-unit
</style>
