const getters = {
  details: state => state.details,
  currentDetails: state => state.details[state.details.length - 1],
  priceGroups: state => state.priceGroups.map(x => {
    return {
      text: x.name,
      value: x.guid,
    }
  }),
  validationErrors: state => state.validationErrors,
}

export default getters
