<template>
  <div
    class="property-editor-dialog"
    data-test="property-editor-dialog">
    <uc-modal
      :title="header"
      @hide="hideDialog">
      <component
        :is="definition.editor"
        ref="editor"
        :language="selectedContentLanguage"
        v-bind="{ definition, name, entityGuid }"
        data-test="property-input-field"
        @onInput="captureSaveCommand"
        @change="captureSaveCommand" />
      <template #footer>
        <uc-button
          type="submit"
          :disabled="!saveCommand"
          @keyup.enter="captureSaveCommand"
          @click="submitCommand">
          {{ $t('global.done') }}
          <template
            v-if="isLoading"
            #after>
            <loading-spinner />
          </template>
        </uc-button>
        <uc-button
          theme="text"
          @click="hideDialog">
          {{ $tc('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </div>
</template>

<script>
import UcModal from '@/components/widgets/uc-modal'
import Editors from '@/components/editors'
import LoadingSpinner from '@/components/widgets/loading-spinner'
import UcButton from '@/components/forms/uc-button'
import { createNamespacedHelpers, mapActions } from 'vuex'
const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')
const persistedStore = createNamespacedHelpers('persisted')

export default {
  name: 'PropertyEditorDialog',
  components: {
    UcModal,
    UcButton,
    LoadingSpinner,
    ...Editors,
  },
  props: {
    definition: {
      type: Object,
      default () {
        return {}
      },
    },
    name: {
      type: String,
      default: '',
    },
    customHeader: {
      type: String,
      default: undefined,
    },
    entityGuid: {
      type: String,
      default: null,
    },
  },
  data () {
    return {
      saveCommand: undefined,
      isValid: true,
      isLoading: false,
    }
  },
  computed: {
    ...persistedStore.mapGetters(['selectedContentLanguage']),
    ...detailsStore.mapGetters(['currentDetails', 'validationErrors']),
    header () {
      return this.customHeader ? this.customHeader : `${this.$t('global.change')} ${this.name}`
    },
  },
  mounted () {
    this.$nextTick(function () {
      this.focusInput()
    })
    window.addEventListener('keyup', this.keyHandler)
  },
  destroyed () {
    window.removeEventListener('keyup', this.keyHandler)
  },
  methods: {
    ...detailsStore.mapActions(['changeDefinition']),
    ...builderStore.mapMutations(['setCurrentNodeName']),
    ...mapActions(['save']),
    focusInput () {
      if (this.$refs.editor && this.$refs.editor.$el.querySelectorAll('input')[0]) {
        this.$refs.editor.$el.querySelectorAll('input')[0].focus()
      }
    },
    hideDialog () {
      this.$emit('hideDialog')
    },
    captureSaveCommand (saveCommand) {
      this.isValid = true
      this.saveCommand = saveCommand

      if (this.definition.validation.required && !saveCommand.save[this.name].value && !saveCommand.save[this.name].selected) {
        this.saveCommand = null
      }
    },
    async submitCommand () {
      this.isLoading = true
      await this.saveChange(this.saveCommand)
      this.isLoading = false

      if (this.currentDetails[this.name].errors) {
        this.focusInput()
        this.isValid = false
        return
      }
      const isCategoryOrCatalog = this.saveCommand.meta.nodeType === 'productCategory' || this.saveCommand.meta.nodeType === 'productCatalog'
      const shouldUpdateNodeName = isCategoryOrCatalog && Object.prototype.hasOwnProperty.call(this.saveCommand.save, 'name')

      if (shouldUpdateNodeName) {
        this.setCurrentNodeName(this.saveCommand.save.name.value)
        this.isValid = true
      }

      this.$emit('change')
      this.hideDialog()
    },
    async saveChange (command) {
      this.enrichCommand(command)
      await this.save(command)
    },
    enrichCommand (command) {
      const { meta } = command
      meta.contentLanguage = this.selectedContentLanguage
      meta.nodeType = this.currentDetails.meta.nodeType
      meta.nodeGuid = this.currentDetails.meta.entityGuid
    },
    keyHandler (e) {
      if (e.key === 'Enter' && this.saveCommand && this.isValid) {
        this.submitCommand()
      }
    },
  },
}
</script>
