<template>
  <div class="uc-order-line-picker">
    <uc-stepper @beforeLeave="onBeforeLeaveStep">
      <!-- Back button -->
      <template #top-left="{ previous, step }">
        <transition name="fade">
          <uc-button
            v-show="step > 1"
            :theme="button.link"
            :size="button.small"
            @click="previous">
            {{ $t('back') }}
            <template #before>
              <arrow-back-icon />
            </template>
          </uc-button>
        </transition>
      </template>

      <!-- Step 1-->
      <template #step-1="{ next }">
        <p
          v-truncate
          class="uc-order-line-picker__sub-heading">
          {{ $t('subheadingStep1') }}
        </p>

        <!-- Button: Show selection -->
        <div class="uc-order-line-picker__preview-button">
          <uc-button
            :size="button.small"
            :disabled="selection.length === 0"
            :theme="button.ghost"
            @click="next">
            {{ $tc('previewButtonLabel', selection.length) }}
          </uc-button>
        </div>

        <!-- Product search -->
        <div class="uc-order-line-picker__step">
          <!-- Price group select -->
          <div class="uc-order-line-picker__price-groups">
            <uc-price-group-select
              v-model="selectedPriceGroup"
              v-bind="{ contentLanguage, orderGuid }" />
          </div>
          <product-search
            v-bind="{ contentLanguage }"
            id="OrderLinePicker"
            v-model="selection"
            :select-button-text="$t('add')"
            :columns="productSearchColumns"
            :disable-button="selection.length === 0 || !hasSelectedPriceGroup"
            show-variants
            @selection="onSelection" />
        </div>
      </template>

      <!-- Step 2 -->
      <template #step-2>
        <div class="uc-order-line-picker__step">
          <p
            v-truncate
            class="uc-order-line-picker__sub-heading">
            {{ $t('subheadingStep2') }}
          </p>

          <!-- Price group select -->
          <uc-price-group-select
            v-model="selectedPriceGroup"
            v-bind="{ contentLanguage, orderGuid }" />

          <div class="uc-order-line-picker__data-grid">
            <h2 class="uc-order-line-picker__header">
              {{ $tc('heading', selectedItems.length) }}
            </h2>

            <!-- Selected products -->
            <uc-data-grid-selectable
              v-model="selectedItems"
              :columns="columns"
              :rows="rows"
              row-key="guid"
              disable-infinite-scroll>
              <template #image="{ cell }">
                <div class="uc-order-line-picker__image">
                  <uc-image
                    :src="cell"
                    :fit="imageFit" />
                </div>
              </template>
            </uc-data-grid-selectable>

            <!-- Add button -->
            <transition name="fade-in-up">
              <div
                v-show="selection.length > 0"
                class="uc-order-line-picker__add-button">
                <uc-button
                  floating
                  :disabled="selectedItems.length === 0 || !hasSelectedPriceGroup"
                  @click="onSelection(selectedItems)">
                  {{ $tc('addButtonLabel', selectedItems.length) }}
                </uc-button>
              </div>
            </transition>
          </div>
        </div>
      </template>
    </uc-stepper>
  </div>
</template>

<i18n>
{
  "en": {
      "back": "@:global.back",
      "add": "@:global.add",
      "previewButtonLabel": "@:global.selected • {count}",
      "addButtonLabel": "@:global.add • {count}",
      "heading": "Selected products • {count}",
      "subheadingStep1": "Search items to add to the order",
      "subheadingStep2": "Manage the selected products"
    }
}
</i18n>

<script>
import ProductSearch from '@/apps/products/components/product-search/'
import { UcButton, UcButtonTheme, UcButtonSize } from '@/components/forms/uc-button/'
import UcStepper from '@/components/generic/uc-stepper/'
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcImage, UcImageFitProp } from '@/components/generic/uc-image/'
import { ArrowBack16Normal as ArrowBackIcon } from '@/assets/icons/'
import { isEmpty } from '@/utils/object.utils.js'
import { UcPriceGroupSelect } from '../uc-price-group-select/'

const ComponentEvent = {
  ADD: 'add',
}

export default {
  name: 'UcOrderLinePicker',
  components: {
    UcStepper,
    UcButton,
    ProductSearch,
    ArrowBackIcon,
    UcDataGridSelectable,
    UcImage,
    UcPriceGroupSelect,
  },
  props: {
    contentLanguage: {
      type: String,
      required: true,
    },
    orderGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      rows: [],
      selectedItems: [],
      selection: [],
      selectedPriceGroup: {},
    }
  },
  computed: {
    imageFit: () => UcImageFitProp.CONTAIN,
    button: () => ({
      small: UcButtonSize.SMALL,
      ghost: UcButtonTheme.GHOST,
      link: UcButtonTheme.LINK,
    }),
    productSearchColumns: () => ({
      primaryImage: { width: '90px' },
      name: { width: '192px' },
      sku: { width: '150px' },
      variantSku: { width: '150px' },
    }),
    columns () {
      return [
        { key: 'image', value: this.$t('global.columns.image'), width: '90px' },
        { key: 'name', value: this.$t('global.columns.name') },
        { key: 'sku', value: this.$t('global.columns.sku') },
        { key: 'variantSku', value: this.$t('global.columns.variantSku') },
      ]
    },
    hasSelectedPriceGroup () {
      return !isEmpty(this.selectedPriceGroup)
    },
  },
  methods: {
    onSelection (rows) {
      this.$emit(ComponentEvent.ADD, rows, this.selectedPriceGroup)
    },
    onBeforeLeaveStep (to) {
      if (to === 1) {
        const findSelectedItemsByGuid = selected => this.selectedItems.find(selectedItem => selected.meta.entityGuid === selectedItem.guid)
        this.selection = this.selection.filter(findSelectedItemsByGuid)
      }

      if (to === 2) {
        this.rows = this.selection.map(item => ({
          image: item.primaryImage?.url,
          name: item.name?.value,
          sku: item.sku?.value,
          variantSku: item.variantSku?.value,
          guid: item.meta?.entityGuid,
        }))

        this.selectedItems = [...this.rows]
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-order-line-picker
  display flex
  flex-direction column
  flex-grow 1
  height 'calc(100vh - %s)' % ($base-unit * 66)
  min-height $base-unit * 70

.uc-order-line-picker__preview-button
  align-self flex-end
  margin-bottom $base-unit * 2

.uc-order-line-picker__step
  display flex
  flex-direction column
  align-items center
  width 100%
  height 100%

.uc-order-line-picker__price-groups
  z-index 106 // To overlay the product-search list :-/;
  width 100%
  margin-bottom $base-unit * 2

.uc-order-line-picker__sub-heading
  secondary-details-text()
  max-width 100%
  margin 0
  margin-bottom $base-unit * 2
  text-align center

.uc-order-line-picker__data-grid
  display flex
  flex-direction column
  align-items center
  height 100%
  margin-top $base-unit * 2
  border $base-border
  overflow hidden

.uc-order-line-picker__header
  align-self flex-start
  margin $base-unit * 3
  primary-heading-2()

.uc-order-line-picker__image
  display flex

.uc-order-line-picker__add-button
  position absolute
  bottom $base-unit * 5
  z-index 1
</style>
