<template>
  <div
    class="uc-promo-code-editor-selection"
    :class="cssClassObject">
    <uc-data-grid-selectable
      v-model="selection"
      v-bind="{ columns, rows }"
      row-key="guid"
      :disable-infinite-scroll="hasLoadedAllItems"
      @next="onNext"
      @input="onChange" />

    <!-- Actions -->
    <transition name="fade-in-up">
      <div
        v-if="!isEmptySelection"
        class="uc-promo-code-editor-selection__actions-footer">
        <!-- Remove -->
        <uc-button
          :theme="ucButtonTheme.PRIMARY"
          @click="onRemove">
          {{ $tc('global.remove') }} ({{ selection.length }})
        </uc-button>
      </div>
    </transition>
  </div>
</template>

<script>
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import { UcButton, UcButtonTheme } from '@/components/forms/uc-button/'

const ComponentEvent = {
  REMOVE: 'remove',
  NEXT: 'next',
}

export default {
  name: 'UcPromoCodeSelection',
  components: {
    UcDataGridSelectable,
    UcButton,
  },
  props: {
    rows: {
      type: Array,
      required: true,
    },
    hasLoadedAllItems: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      selection: [],
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-max-height': this.rows.length > 6,
      }
    },
    ucButtonTheme () {
      return UcButtonTheme
    },
    columns () {
      return [
        {
          key: 'code',
          value: this.$t('ucPromoCodeEditor.code'),
        },
        {
          key: 'maxUses',
          value: this.$t('ucPromoCodeEditor.maxUses'),
        },
        {
          key: 'used',
          value: this.$t('ucPromoCodeEditor.used'),
        },
      ]
    },
    isEmptySelection () {
      return this.selection.length === 0
    },
  },
  methods: {
    onChange (value) {
      this.selection = value
    },
    onRemove () {
      this.$emit(ComponentEvent.REMOVE, this.selection)
      this.selection = []
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-promo-code-editor-selection
  position relative
  height $base-unit * 49 // 392px
  background $base-color-white

  .is-max-height
    height $base-unit * 56 // 448px

.uc-promo-code-editor-selection__actions-footer
  position absolute
  bottom $base-unit * 4
  display flex
  align-items center
  justify-content center
  width 100%
  pointer-events none
</style>
