<template>
  <div class="products-search">
    <!-- Product search -->
    <uc-search
      v-model.trim="searchTerm"
      :focus="isSearchFocused" />

    <!-- Search results -->
    <div class="products-search__results">
      <fields-section paddingless>
        <list
          :id="listId"
          :ref="listId"
          v-bind="{ nodeType, dataUrl, defaultColumnsConfiguration }"
          :header="$tc('list.products')"
          @sortBy="onSortBy"
          @row-clicked="openEntityEditor"
          @filter="onPriceFilter">
          <template #actionsHeader>
            <uc-button
              size="sm"
              @click="toggleDialog">
              {{ $t('list.newButton') }}
            </uc-button>
          </template>
        </list>
      </fields-section>
    </div>
  </div>
</template>

<script>
import List from '@/components/generic/generic-list/list'
import FieldsSection from '@/components/sections/fields-section'
import UcSearch from '@/components/forms/uc-search'
import UcButton from '@/components/forms/uc-button'
import { EntityType } from '@/constants.js'

export default {
  name: 'ProductsSearch',
  components: {
    FieldsSection,
    List,
    UcSearch,
    UcButton,
  },
  props: {
    listId: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
    isSearchFocused: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      searchTerm: '',
      sortBy: '',
      priceGroups: [],
    }
  },
  computed: {
    nodeType () {
      return EntityType.PRODUCT
    },
    defaultColumnsConfiguration () {
      return {
        primaryImage: {
          width: '90px',
        },
        sku: {},
        name: {
          width: '250px',
        },
        definition: {},
        'eur 15 pct': {
          width: '360px',
        },
      }
    },
    dataUrl () {
      let url = `/products/${this.contentLanguage}`

      const params = new URLSearchParams()
      params.set('searchTerm', this.searchTerm)
      params.set('sort', this.sortBy)

      if (this.priceGroups.length) {
        this.priceGroups.forEach(pg => params.append('priceGroups', pg))
      } else {
        params.set('priceGroups', '')
      }

      url = `${url}?${params.toString()}`
      return url
    },
  },
  methods: {
    onPriceFilter (priceColumns) {
      if (priceColumns) {
        this.priceGroups = priceColumns.map(priceColumn => priceColumn.guid)
      } else {
        this.priceGroups = []
      }
    },
    onSortBy (sortBy) {
      this.sortBy = sortBy
    },
    openEntityEditor (row) {
      this.$emit('open-entity-editor', row)
    },
    closeEntityEditor () {
      this.$emit('close-entity-editor')
    },
    toggleDialog () {
      this.$emit('toggle-dialog')
    },
    addItems (items) {
      this.$refs[this.listId].addItems(items)
    },
    removeItems (items) {
      this.$refs[this.listId].removeItems(items)
    },
  },
}
</script>

<style lang="stylus" scoped>
.products-search__results
  margin-top 4 * $base-unit
</style>
