import * as types from './mutation-types'
import { getEntityService } from '@/services'
import { mergeWith } from '@/utils/object.utils'

const actions = {
  async changeSelectedContentLanguage ({ commit, state, getters }, contentLanguage) {
    // Language was changed, so we request product details for the product in the new contentLanguage.
    const nodeType = state.details[state.details.length - 1].meta.nodeType
    const entityGuid = state.details[state.details.length - 1].meta.entityGuid
    const entityService = getEntityService(nodeType)
    let entity = (await entityService.fetchEntityAsync({ guid: entityGuid, cultureCode: contentLanguage })).data

    function customizer (_objValue, srcValue, key) {
      if (key === 'selected' && srcValue?.length === 0) {
        return []
      }

      if (key === 'value' && srcValue === undefined) {
        return ''
      }
    }

    // Only overwrite what is new when language is changed, and keep common data e.g prices
    entity = mergeWith({}, getters.currentDetails, entity, customizer)
    commit(types.SET_DETAILS, { details: entity, shouldUpdateLastElement: true })
  },
}

export default actions
