<template>
  <transition name="fade">
    <!-- State: idle -->
    <div
      v-if="isIdle || isDeleting"
      class="uc-entity-list uc-entity-list--idle">
      <!-- Entity factory -->
      <uc-select
        :placeholder="$t('ucEntityList.addNew', { entity: entityTypeName })"
        :options="definitionList"
        :selected="selectedOption"
        @selected="createEntityAsync($event.guid)" />

      <!-- Entity list -->
      <div class="uc-entity-list__item">
        <uc-fieldset
          v-for="entity in entities"
          :key="entity.meta.entityGuid"
          :legend="entity.definition.value">
          <template #topRight>
            <uc-fieldset-button @click="deleteEntityAsync(entity.meta.entityGuid)" />
          </template>

          <uc-entity
            v-bind="{ entity, promotionGuid, contentLanguage }"
            @change="updateEntityPropertyAsync(entity.meta.entityGuid, $event)" />
        </uc-fieldset>
      </div>

      <!-- Delete confirmation dialog -->
      <uc-warning-dialog
        v-if="isDeleting"
        ref="deleteConfirmationDialog"
        :title="$t('ucEntityList.deleteConfirmationDialog.title', { label: entityTypeName })"
        :text="$t('ucEntityList.deleteConfirmationDialog.text', { entity: `a ${entityTypeName}` })"
        :proceed-button-text="$t('ucEntityList.deleteConfirmationDialog.proceed')"
        :cancel-button-text="$t('ucEntityList.deleteConfirmationDialog.cancel')" />
    </div>

    <!-- State: initializing -->
    <div
      v-else-if="isInitializing"
      class="uc-entity-list uc-entity-list--initializing">
      <loading-indicator is-loading />
    </div>
  </transition>
</template>

<script>
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'
import { UcSelect } from '@/components/forms/uc-select/'
import { UcFieldset, UcFieldsetButton } from '@/components/forms/uc-fieldset/'
import { UcWarningDialog, UcWarningDialogEvent } from '@/components/widgets/uc-warning-dialog/'

import { validateAgainstEnum } from '@/utils/validate-against/'
import { expectComponentEventAsync } from '@/utils/vue.utils.js'
import { nodeTypeSingular, EntityType } from '@/constants.js'

import { UcEntity } from '../uc-entity/'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  DELETING: 'deleting',
}

export default {
  name: 'UcEntityList',
  components: {
    UcEntity,
    UcSelect,
    UcWarningDialog,
    UcFieldset,
    UcFieldsetButton,
    LoadingIndicator,
  },
  inject: ['entityService'],
  props: {
    // TODO(ch12695): Should be removed once we have sub-resource links in place
    promotionGuid: {
      type: String,
      required: true,
    },
    contentLanguage: {
      type: String,
      required: true,
    },
    entityType: {
      type: String,
      required: true,
      validate: validateAgainstEnum(EntityType),
    },
  },
  data () {
    return {
      state: ComponentState.INITIALIZING,
      definitionList: null,
      entities: null,
      selectedOption: null,
    }
  },
  computed: {
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    isDeleting () {
      return this.state === ComponentState.DELETING
    },
    isInitializing () {
      return this.state === ComponentState.INITIALIZING
    },
    entityTypeName () {
      return nodeTypeSingular[this.entityType]
    },
  },
  async created () {
    const toSelectOption = definition => ({ guid: definition.guid, label: definition.name })
    this.definitionList = (await this.entityService.fetchDefinitionsListAsync()).data.map(toSelectOption)
    this.entities = (await this.entityService.fetchEntitiesAsync({ parentGuid: this.promotionGuid, cultureCode: this.contentLanguage })).data
    this.state = ComponentState.IDLE
  },
  methods: {
    async createEntityAsync (definitionGuid) {
      this.selectedOption = definitionGuid
      const createResponse = await this.entityService.createEntityAsync({ definitionGuid, parentGuid: this.promotionGuid })
      const getResponse = await this.entityService.fetchEntityAsync({ guid: createResponse.data.guid, parentGuid: this.promotionGuid, cultureCode: this.contentLanguage })

      this.entities.unshift(getResponse.data)
      this.selectedOption = null
    },
    async deleteEntityAsync (guid) {
      this.state = ComponentState.DELETING
      await this.$nextTick()

      try {
        await expectComponentEventAsync({
          component: this.$refs.deleteConfirmationDialog,
          resolveEvent: UcWarningDialogEvent.PROCEED,
          rejectEvent: UcWarningDialogEvent.CANCEL,
        })

        try {
          await this.entityService.deleteEntityAsync({ parentGuid: this.promotionGuid, guid })
          this.entities = this.entities.filter(entity => entity.meta.entityGuid !== guid)
          this.state = ComponentState.IDLE
        } catch (e) {
          // TODO(ch10038): We need a way to handle http 500 request errors
          console.error(e)
          this.state = ComponentState.IDLE
        }
      } catch (e) {
        this.state = ComponentState.IDLE
      }
    },
    updateEntityPropertyAsync (guid, saveCommand) {
      // TODO: Should be wired up with undos
      this.entityService.updateEntityAsync({
        guid,
        parentGuid: this.promotionGuid,
        cultureCode: this.contentLanguage,
        payload: saveCommand.save,
      })
    },
  },
}
</script>
<style lang="stylus" scoped>
.uc-entity-list
  min-height 6 * $base-unit

.uc-entity-list--idle,
.uc-entity-list__item
  display grid
  row-gap 3 * $base-unit

.uc-entity-list--initializing
  display flex
  flex-grow 1
  align-items center
  justify-content center
</style>
