<template>
  <dl
    class="uc-dl"
    :class="cssClassObject">
    <slot />
  </dl>
</template>

<script>

export default {
  name: 'UcDl',
  props: {
    oneColumn: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    cssClassObject () {
      return {
        'one-column': this.oneColumn,
      }
    },
  },
}
</script>

<style scoped lang="stylus">
.uc-dl
  display grid
  grid-gap (1.5 * $base-unit) (3 * $base-unit)
  grid-template-columns fit-content(25 * $base-unit) 1fr
  margin 0

  &.one-column
    grid-gap 3 * $base-unit
    grid-template-columns: 1fr

</style>
