import { rest } from 'msw'
import {
  stores,
  storesShallow,
  storeDefinitions,
  storeDefinitionsShallow,
} from '@/mocks/entities/stores/'

const RESPONSE_DELAY = 200

export const rootStoreHandlers = [
  rest.get('/ucommerceapi/v2/stores/shallow', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(storesShallow),
    )
  }),

  rest.get('/ucommerceapi/v2/stores/:cultureCode', (req, res, ctx) => {
    const params = req.url.searchParams
    const startAt = Number(params.get('startAt'))
    const limit = Number(params.get('limit'))

    const response = stores.slice(startAt, startAt + limit)

    return res(
      ctx.set('x-total-count', stores.length),
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(response),
    )
  }),

  rest.get('/ucommerceapi/v2/stores/definitions', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(storeDefinitionsShallow),
    )
  }),

  rest.get('/ucommerceapi/v2/stores/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(storeDefinitions),
    )
  }),
]
