<template>
  <div class="uc-section-wrapper">
    <template v-if="!hasError">
      <!-- State: Loading -->
      <slot
        v-if="isLoading"
        name="loading">
        <div
          data-test="loading"
          class="uc-section-wrapper__container">
          <loading-indicator is-loading />
        </div>
      </slot>

      <!-- State: Empty -->
      <slot
        v-if="isEmpty"
        name="empty">
        <div
          data-test="empty"
          class="uc-section-wrapper__container">
          <p class="uc-section-wrapper__secondary-text">
            {{ emptyMessage || $t('emptyState') }}
          </p>
        </div>
      </slot>

      <!-- State: Idle -->
      <slot v-if="isIdle || isDeleting || isCreating" />
    </template>

    <!-- State: Error -->
    <slot
      v-if="hasError"
      name="error">
      <div
        data-test="error"
        class="uc-section-wrapper__container">
        <p class="uc-section-wrapper__secondary-text">
          {{ errorMessage || $t('global.errorMessage') }}
        </p>
      </div>
    </slot>
  </div>
</template>

<i18n>
{
  "en": {
    "emptyState": "No data is available."
  }
}
</i18n>

<script>
import { validateAgainstEnum } from '@/utils/validate-against/'
import { LoadingIndicator } from '@/components/widgets/loading-indicator/'

export const ComponentState = {
  LOADING: 'loading',
  IDLE: 'idle',
  EMPTY: 'empty',
  ERROR: 'error',
  DELETING: 'deleting',
  CREATING: 'creating',
}

export default {
  name: 'UcSectionWrapper',
  components: {
    LoadingIndicator,
  },
  props: {
    state: {
      type: String,
      default: ComponentState.IDLE,
      validator: validateAgainstEnum(ComponentState),
    },
    errorMessage: {
      type: String,
      default: '',
    },
    emptyMessage: {
      type: String,
      default: '',
    },
  },
  computed: {
    isLoading () {
      return this.state === ComponentState.LOADING
    },
    isIdle () {
      return this.state === ComponentState.IDLE
    },
    hasError () {
      return this.state === ComponentState.ERROR
    },
    isEmpty () {
      return this.state === ComponentState.EMPTY
    },
    isDeleting () {
      return this.state === ComponentState.DELETING
    },
    isCreating () {
      return this.state === ComponentState.CREATING
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-section-wrapper__container
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  min-height $base-unit * 18

.uc-section-wrapper__secondary-text
  secondary-body-text()
</style>
