<template>
  <div
    class="uc-price-editor"
    :class="cssClassObject">
    <editor-wrapper
      v-bind="{ compact, compactValue, errors }"
      :required="isRequired">
      <div class="uc-price-editor__input">
        <input-group
          v-bind="{ label, hasErrors }"
          :readonly="isReadonly">
          <currency-input
            ref="currencyInput"
            v-model="inputValue"
            :currency="null"
            :locale="language"
            :precision="4"
            @change="onBlur" />
          <template #after>
            <input-group-cap
              v-if="!noCurrencyCode"
              :size="size">
              {{ currency }}
            </input-group-cap>
          </template>
        </input-group>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import editorMixin from '../editor-mixin'
import { CurrencyInput } from 'vue-currency-input'
import { InputGroup, InputGroupCap, SizeProp } from '@/components/forms/input-group'

export default {
  name: 'PriceEditor',
  components: {
    InputGroup,
    InputGroupCap,
    CurrencyInput,
  },
  mixins: [editorMixin],
  props: {
    noCurrencyCode: {
      type: Boolean,
      default: false,
    },
    currencyIsoCode: {
      validator: prop => typeof prop === 'string' || prop === undefined,
      default: undefined,
    },
  },
  data () {
    return {
      inputValue: null,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-price-editor--readonly': this.isReadonly,
      }
    },
    formattedPrice () {
      const formattingOptions = {
        maximumFractionDigits: 4,
        minimumFractionDigits: 2,
        ...(!this.noCurrencyCode && this.currency) && { style: 'currency' },
        ...(!this.noCurrencyCode && this.currency) && { currency: this.currency },
      }

      return new Intl.NumberFormat(this.language, formattingOptions).format(this.value)
    },
    compactValue () {
      return this.value ? this.formattedPrice : ''
    },
    size () {
      return SizeProp.MD
    },
    currency () {
      return this.definition.currencyIsoCode || this.currencyIsoCode
    },
  },
  watch: {
    value: {
      immediate: true,
      handler (newValue) {
        this.inputValue = newValue >= 0 ? newValue : null
      },
    },
  },
  mounted () {
    if (this.compact) {
      return
    }

    this.$refs.currencyInput.$el.placeholder = this.placeholder
  },
  methods: {
    onBlur (newPrice) {
      const newValue = newPrice >= 0 ? newPrice : undefined
      const oldValue = this.value >= 0 ? this.value : undefined
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null) && newValue === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue,
        priceGroupGuid: this.guid,
        minimumQuantity: 1,
      })
    },
  },
}
</script>
