<template>
  <div class="uc-image-picker-selection">
    <use-sorting
      :value="items"
      selector=".uc-list"
      :options="{
        ghostClass: 'uc-image-picker-selection__ghost',
      }"
      @input="$emit('update-sort', $event)">
      <uc-list-selectable
        item-key="guid"
        :value="value"
        :items="items"
        @input="$emit('input', $event)">
        <template #default="{ item }">
          <div class="uc-image-picker-selection__item">
            <div class="uc-image-picker-selection__item__image">
              <uc-image
                v-if="isImage(item.nodeType)"
                :src="item.url"
                :alt="item.name"
                fit="scaleDown" />
              <file-icon v-else-if="isFile(item.nodeType)" />
              <folder-icon v-else />
            </div>
            <div class="uc-image-picker-selection__item__meta">
              <div class="uc-image-picker-selection__item__meta__name">
                {{ item.name }}
              </div>
              <div
                v-if="item.url"
                class="uc-image-picker-selection__item__meta__url">
                {{ item.url }}
              </div>
            </div>
          </div>
        </template>
        <template
          v-if="$scopedSlots.actions"
          #actions>
          <slot name="actions" />
        </template>
      </uc-list-selectable>
    </use-sorting>
  </div>
</template>

<script>
import UcListSelectable from '@/components/generic/uc-list-selectable'
import UcImage from '@/components/generic/uc-image'
import FolderIcon from '@/assets/illustrations/folder-image'
import FileIcon from '@/assets/illustrations/file-image'
import Constants from '@/constants.js'
import UseSorting from '@/components/wrappers/use-sorting'

export default {
  name: 'UcImagePickerSelection',
  components: {
    UcListSelectable,
    UcImage,
    FolderIcon,
    FileIcon,
    UseSorting,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
    value: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    isFile (nodeType) {
      return nodeType.toLowerCase() === Constants.NodeType.File
    },
    isImage (nodeType) {
      return nodeType.toLowerCase() === Constants.NodeType.Image
    },
  },
}
</script>
<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
.uc-image-picker-selection__ghost
  opacity 0.1 !important
</style>

<style lang="stylus" scoped>
.uc-image-picker-selection
  height 100%

.uc-image-picker-selection__item
  display flex
  align-items center
  cursor move

.uc-image-picker-selection__item__image
  display flex
  align-items center
  justify-content center
  width $base-unit * 10
  height $base-unit * 8
  margin-right $base-unit * 3
  border $base-border
  border-radius $base-border-radius
  overflow hidden

.uc-image-picker-selection__item__meta
  padding-right $base-unit * 3

.uc-image-picker-selection__item__meta__name
  height $base-unit * 3

.uc-image-picker-selection__item__meta__url
  secondary-petit-text()

</style>
