<template>
  <div class="uc-promo-code-editor-summary">
    <uc-data-grid v-bind="{ columns, rows }">
      <template #download="{ cell }">
        <a
          class="uc-promo-code-editor-summary__download"
          :href="cell"
          download
          data-test="download-link">
          <download-icon />
        </a>
      </template>
    </uc-data-grid>
  </div>
</template>

<script>
import { UcDataGrid } from '@/components/generic/uc-data-grid/'
import { Download24Thick as DownloadIcon } from '@/assets/icons/'

export default {
  name: 'UcPromoCodeEditorSummary',
  components: {
    UcDataGrid,
    DownloadIcon,
  },
  props: {
    downloadUrl: {
      type: String,
      required: true,
    },
    totalMaxUses: {
      type: Number,
      required: true,
    },
    totalUsed: {
      type: Number,
      required: true,
    },
  },
  computed: {
    columns () {
      return [
        {
          key: 'totalMaxUses',
          value: this.$t('ucPromoCodeEditor.totalMaxUses'),
        },
        {
          key: 'totalUsed',
          value: this.$t('ucPromoCodeEditor.totalUsed'),
        },
        {
          key: 'left',
          value: this.$t('ucPromoCodeEditor.left'),
        },
        {
          key: 'download',
          value: this.$t('ucPromoCodeEditor.download'),
        },
      ]
    },
    rows () {
      const { totalMaxUses, totalUsed } = this
      return [
        {
          totalMaxUses,
          totalUsed,
          left: totalMaxUses - totalUsed,
          download: this.downloadUrl,
        },
      ]
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-promo-code-editor-summary__download
  color $base-color-navy
</style>
