let handledIds = []
const bundleThreshold = 90000

export const processNewChunk = rawFeed => {
  const filteredFeed = rawFeed.filter(x => handledIds.indexOf(x.id) === -1) // remove already processed feed items
  if (!filteredFeed.length) return filteredFeed
  const transformedFeed = filteredFeed.map(x => { // Transform dates in the array
    const y = x
    y.created = new Date(y.created)
    y.bundle = []
    y.showBundle = false
    return y
  })
  handledIds = handledIds.concat(transformedFeed.map(x => x.id)) // add new ids to handled ids
  const processedFeed = transformedFeed.reduce((processed, element, index) => { // create bundles
    if (index === 0) {
      processed.push(element)
      return processed
    }
    let i = processed.length
    while (i--) {
      const processing = processed[i]
      if (processing.type === element.type) {
        if (Math.abs(processing.created - element.created) < bundleThreshold) {
          processing.bundle.push(element)
          return processed
        }
      }
    }
    processed.push(element)
    return processed
  }, [])

  return processedFeed
}
