import Vue from 'vue'
import App from '@/App.vue'
import store from '@/store'
import Axios from 'axios'
import Router from 'vue-router'
import PortalVue from 'portal-vue'

// Plugins
import truncateTooltip from '@/utils/truncate-tooltip.js'
import { Format, locales } from '@/utils/date-formatter.js'
import customScrollBar from '@/utils/custom-scroll-bar.js'
import i18n from '@/plugins/vue-i18n.js'
import { VTooltip, tooltipOptions } from '@/plugins/v-tooltip.js'
import * as Apps from '@/apps/'
import '@/styles/main.styl'

Vue.config.productionTip = false

// Setup Axios as http handler for Vue (including license interceptor)
const axiosInstance = Axios.create({
  baseURL: '/ucommerceapi/v2/',
})
axiosInstance.defaults.headers.post['Content-Type'] = 'application/json'
axiosInstance.defaults.headers.get['Content-Type'] = 'application/json'
Vue.prototype.axios = axiosInstance

// Filters
Vue.filter('format', function (value, format, locale) {
  if (!value) return ''
  value = value.toString()
  return Format(value, format, locales[locale])
})

// Directives
Vue.directive('truncate', truncateTooltip)
Vue.directive('custom-scroll-bar', customScrollBar)

// Plugins
Vue.use(VTooltip, tooltipOptions)
Vue.use(PortalVue)

// Mock Service Worker
if (process.env.VUE_APP_DATA_SRC === 'mock') {
  const { worker } = require('./mocks/browser')
  worker.start({
    serviceWorker: {
      url: '/umbraco/ucommerce/Vue/mockServiceWorker.js',
    },
  })
}

// Router
Vue.use(Router)

const router = new Router()
router.beforeResolve((to, _from, next) => {
  // Angular router url encodes location hash values (/ becomes %2F) which invalidates routing matching, so we decode those paths
  const isUrlEncodedPath = to.path.indexOf('%') !== -1
  isUrlEncodedPath ? next(decodeURIComponent(to.path)) : next()
})

new Vue({
  router,
  store,
  i18n,
  render: h => h(App),
}).$mount('#app')

const registeredApps = []

function registerApp (app) {
  if (registeredApps.includes(app)) {
    return
  }

  app({ store, router })
}

function registerApps (apps) {
  Object.keys(apps).forEach(key => registerApp(apps[key]))
}

registerApps(Apps)
