<template>
  <section
    class="fields-section"
    :class="cssClassObject"
    @click="$emit('click')">
    <header
      v-if="showHeader"
      class="fields-section__header"
      data-test="fields-section-header">
      <h2
        v-if="heading || $slots.heading"
        v-truncate
        class="fields-section__heading">
        <slot name="heading">
          {{ heading }}
        </slot>
      </h2>

      <div
        v-if="$slots.actions"
        class="fields-section__actions">
        <slot name="actions" />
      </div>
    </header>
    <div
      class="fields-section__body"
      :v-custom-scroll-bar="scrollable">
      <slot />
    </div>
    <slot name="footer" />
  </section>
</template>

<script>
export default {
  props: {
    heading: {
      type: String,
      default: '',
    },
    scrollable: {
      type: Boolean,
      default: false,
    },
    noHeading: {
      type: Boolean,
      default: false,
    },
    paddingless: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    showHeader () {
      return !this.noHeading && (this.heading || this.$slots.heading || this.$slots.actions)
    },
    cssClassObject () {
      return {
        'fields-section--scrollable': this.scrollable,
        'fields-section--paddingless': this.paddingless,
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
$header-height = 80px

.fields-section
  margin 0 auto
  background-color $base-color-white
  box-shadow $base-box-shadow

.fields-section + .fields-section
  margin-top 2.5 * $base-unit

.fields-section__header
  display flex
  align-items center
  justify-content space-between
  height $header-height
  padding 3 * $base-unit
  border-bottom $base-border-width $base-border-style $base-color-gray-200

.fields-section__body
  padding 4 * $base-unit 3 * $base-unit

.fields-section__heading
  margin 0
  primary-heading-2()
  user-select none

.fields-section__actions
  display flex
  justify-content flex-end

.fields-section--scrollable .fields-section__body
  max-height 575px
  overflow-y auto

.fields-section--paddingless
  .fields-section__body
    padding 0
</style>
