import { makeDefinitionsByDefinitionGuid } from '@/services/utils/merge-definitions-and-data.js'

const getters = {
  systemInfo: state => state.systemInfo,
  languages (state) {
    if (state.systemInfo.languages.length === 0) return state.systemInfo.languages
    return state.systemInfo.languages.map(x => {
      return {
        text: x.name,
        value: x.cultureCode,
      }
    })
  },
  uiLanguage: state => state.uiLanguage,
  undos: state => state.undos,
  errorMessage: state => state.errorMessage,
  definitionsByGuid: state => makeDefinitionsByDefinitionGuid(state.definitions),
  isProductFamily (state, getters) {
    const currentDetails = state.details.details[state.details.details.length - 1]
    if (currentDetails.meta.nodeType !== 'product') {
      return false
    }

    return Object.keys(getters.definitionsByGuid[currentDetails.meta.definition.guid])
      // variantSku is returned as being "isVariantProperty". Not filtering it out explicitly will mean that all products are returned as isProductFamily = true
      .some(key => key !== 'variantSku' &&
        getters.definitionsByGuid[currentDetails.meta.definition.guid][key].isVariantProperty)
  },
}

export default getters
