<template>
  <div class="uc-image-picker--multi-select">
    <uc-stepper @beforeLeave="onBeforeLeaveStep">
      <!-- Back button -->
      <template #top-left="{ previous, step}">
        <transition name="fade">
          <div
            v-show="step > 1"
            class="uc-image-picker--multi-select__back">
            <uc-button
              theme="link"
              size="sm"
              data-test="previous"
              @click="previous">
              {{ $tc('global.back') }}
              <template #before>
                <arrow-back-icon />
              </template>
            </uc-button>
          </div>
        </transition>
      </template>

      <!-- Step 1: Navigator -->
      <template #step-1="{ next }">
        <div class="uc-image-picker--multi-select__picker">
          <uc-image-picker
            v-bind="{ cms, nodes, isLoading, isEmptyRootState, selectedNodesGuids }"
            @openNode="getData"
            @selectNode="selectNode">
            <template #topRight>
              <!-- Button: Select all -->
              <div class="uc-image-picker--multi-select__picker__action">
                <uc-button
                  v-if="nodes.length > 0"
                  theme="ghost"
                  size="sm"
                  data-test="ucImagePickerSelectAllButton"
                  @click="selectAllClick">
                  {{ isEverythingUnderCurrentBranchSelected ? $t('global.deselect') : $t('global.select') }}
                  <template #after>
                    <div class="uc-image-picker-multi__icon">
                      <uc-button-round
                        size="s"
                        theme="ghost">
                        <component :is="isEverythingUnderCurrentBranchSelected ? 'MinusIcon' : 'PlusIcon'" />
                      </uc-button-round>
                    </div>
                  </template>
                </uc-button>
              </div>

              <!-- Button: Show selection -->
              <div class="uc-image-picker--multi-select__picker__action">
                <uc-button
                  size="sm"
                  :disabled="selectedNodes.length === 0"
                  @click="next">
                  {{ selectedButtonLabel }}
                </uc-button>
              </div>
            </template>
          </uc-image-picker>
        </div>
      </template>

      <!-- Step 2: Selection -->
      <template #step-2>
        <div
          v-custom-scroll-bar
          class="uc-image-picker--multi-select__selection">
          <div class="uc-image-picker--multi-select__selection__list">
            <div class="uc-image-picker--multi-select__selection__list__header">
              {{ $t('ucImagePicker.selectedMedia') }} • {{ selectedNodes.length }}
            </div>
            <div class="uc-image-picker--multi-select__selection__list__body">
              <uc-image-picker-selection
                v-model="selectedNodes"
                :items="listItems"
                @update-sort="onUpdateSort" />
            </div>
          </div>
        </div>
      </template>
    </uc-stepper>

    <div
      v-if="!isEmptyRootState"
      class="uc-image-picker__submit">
      <uc-button
        :disabled="required && selectedNodes.length === 0"
        floating
        data-test="ucImagePickerAddMediaButton"
        @click="submitSelection">
        {{ confirmButtonLabel }}
      </uc-button>
    </div>
  </div>
</template>

<script>
import UcImagePicker from '@/components/generic/uc-image-picker/uc-image-picker'
import { getSelectedMediaByGuids } from '@/components/editors/uc-image-picker-editor/uc-image-picker/uc-image-picker.http'
import UcImagePickerSelection from './components/uc-image-picker-selection'
import intersection from 'lodash.intersection'
import { mapGetters } from 'vuex'
import UcButton from '@/components/forms/uc-button/uc-button'
import UcButtonRound from '@/components/forms/uc-button-round'
import UcStepper from '@/components/generic/uc-stepper'
import { ArrowBack16Normal as ArrowBackIcon, Dual16Normal as MinusIcon, Add16Normal as PlusIcon } from '@/assets/icons'
import { toggleObjectInArray, addObjectToArray, removeObjectFromArray, getGuidFromNode } from '@/components/generic/uc-image-picker/uc-image-picker.utils'

const ComponentEvent = {
  NODES_SELECTED: 'nodesSelected',
}

export default {
  name: 'ImagePickerMultiSelect',
  components: {
    UcButton,
    UcImagePicker,
    UcImagePickerSelection,
    UcStepper,
    ArrowBackIcon,
    UcButtonRound,
    MinusIcon,
    PlusIcon,
  },
  props: {
    selected: {
      type: Array,
      required: true,
    },
    contentType: {
      type: String,
      required: true,
    },
    nodes: {
      type: Array,
      required: true,
    },
    getData: {
      type: Function,
      required: true,
    },
    isLoading: {
      type: Boolean,
      default: () => false,
    },
    isEmptyRootState: {
      type: Boolean,
      default: () => false,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      selectedNodes: [],
      listItems: [],
    }
  },
  computed: {
    ...mapGetters(['systemInfo']),
    cms () {
      return this.systemInfo.cms
    },
    selectedNodesGuids () {
      return this.selectedNodes.map(getGuidFromNode)
    },
    isEverythingUnderCurrentBranchSelected () {
      const currentBranchAllGuids = this.nodes.map(getGuidFromNode)
      const currentBranchSelectedGuids = intersection(currentBranchAllGuids, this.selectedNodesGuids)
      return currentBranchSelectedGuids.length === currentBranchAllGuids.length
    },
    confirmButtonLabel () {
      return `${this.$t('global.done')} (${this.selectedNodes.length})`
    },
    selectedButtonLabel () {
      return `${this.$t('global.selected')} • ${this.selectedNodes.length}`
    },
  },
  async mounted () {
    const result = await getSelectedMediaByGuids(this.contentType, this.selected)
    this.selectedNodes = result.data
  },
  methods: {
    onBeforeLeaveStep (to) {
      if (to === 2) {
        this.listItems = [...this.selectedNodes]
      }
    },
    onChange (saveCommand) {
      this.$emit('change', saveCommand)
    },
    selectNode (node) {
      toggleObjectInArray(this.selectedNodes, node)
    },
    selectAllClick () {
      this.isEverythingUnderCurrentBranchSelected
        ? this.removeAllItemsFromArray()
        : this.addAllMissingItemsToArray()
    },
    removeAllItemsFromArray () {
      this.nodes.forEach(elem => removeObjectFromArray(this.selectedNodes, elem))
    },
    addAllMissingItemsToArray () {
      this.nodes.forEach(elem => addObjectToArray(this.selectedNodes, elem))
    },
    submitSelection () {
      this.$emit(ComponentEvent.NODES_SELECTED, this.selectedNodes)
    },
    onUpdateSort (items) {
      this.selectedNodes = intersection(items, this.selectedNodes)
      this.listItems = items
    },
  },
}
</script>

<style lang="stylus" scoped>
$panel-horizontal-padding = $base-unit * 15
$list-header-height = 9 * $base-unit

.uc-image-picker--multi-select
  display flex
  flex-direction column
  height 100%

.uc-image-picker__wrapper
  position relative

.uc-image-picker__submit
  position absolute
  bottom $base-unit * 5
  left 50%
  z-index 1
  transform translateX(-50%)

.uc-image-picker--multi-select__picker
  flex 1 0 auto
  width 100%
  height 0

.uc-image-picker--multi-select__picker__action
  padding-right $base-unit
  padding-left $base-unit

.uc-image-picker--multi-select__selection
  width 100%
  height 100%
  margin-top $base-unit * 8
  overflow-y scroll

.uc-image-picker-multi__icon
  margin-left $base-unit
  pointer-events none

.uc-image-picker--multi-select__selection__list
  position relative
  width 100%

.uc-image-picker--multi-select__selection__list__header
  position fixed
  z-index 1
  width calc(100% - 6px) // 100% minus scrollbar width
  height $list-header-height
  padding $base-unit * 3
  border $base-border
  background-color $base-color-white
  primary-heading-2()

.uc-image-picker--multi-select__selection__list__body
  position absolute
  top $list-header-height
  left 0
  width 100%
  border $base-border
  border-top 0
  border-bottom 0
</style>
