<template>
  <div
    v-if="languages"
    class="language-selector"
    data-test="language-selector">
    <uc-selector
      v-model="selected"
      :items="languages"
      :search-keys="['text']"
      item-key="value">
      <template #default="{ item }">
        {{ item.text }}
      </template>
    </uc-selector>
  </div>
</template>

<script>
import UcSelector from '@/components/generic/uc-selector'
import { mapGetters, createNamespacedHelpers } from 'vuex'

const persistedStore = createNamespacedHelpers('persisted')

export default {
  name: 'LanguageSelector',
  components: {
    UcSelector,
  },
  computed: {
    ...mapGetters(['languages']),
    ...persistedStore.mapGetters(['selectedContentLanguageName']),
    selected: {
      get () {
        return this.selectedContentLanguageName
      },
      set (language) {
        this.setSelectedContentLanguage(language.value)
      },
    },
  },
  methods: {
    ...persistedStore.mapActions(['setSelectedContentLanguage']),
  },
}
</script>
