import { EntityService, EntityType } from './_entity.service.js'

export class OrderService extends EntityService {
  constructor (httpClient) {
    super(EntityType.ORDER, httpClient)
  }

  createEntityAsync ({ name, definitionGuid, cultureCode, orderNumber, orderStatus, store, currency }) {
    return super.createEntityAsync({ name, definitionGuid, cultureCode, params: { orderNumber, orderStatus, store, currency } })
  }

  updateStatusOfOrders ({ cultureCode, guids, payload }) {
    const params = {
      guids,
    }
    return this._httpClient.patch(`${this._rootUrl}/${cultureCode}`, payload, {
      params,
      paramsSerializer: (params) => {
        // to avoid having [] inside each param key
        return params.guids.map(guid => `guids=${guid}`).join('&')
      },
    })
  }

  fetchStatusesAsync ({ cultureCode, params }) {
    return this._httpClient.get(`${this._rootUrl}/statuses/${cultureCode}`, { params })
  }

  fetchAvailableStatusesForOrderAsync ({ orderGuid, cultureCode, params }) {
    return this._httpClient.get(`${this._rootUrl}/${orderGuid}/statuses/${cultureCode}`, { params })
  }

  fetchPriceGroupsAsync ({ orderGuid, cultureCode, startAt, limit, searchTerm }) {
    const url = `${this._rootUrl}/${orderGuid}/priceGroups/${cultureCode}`
    return this._httpClient.get(url, {
      params: {
        startAt,
        limit,
        searchTerm,
      },
    })
  }
}
