<template>
  <use-steps
    :steps="stepCount"
    @next="onChangeStep($event, 'next')"
    @previous="onChangeStep($event, 'previous')">
    <template #default="{ step, next, previous, previousStep }">
      <section class="uc-stepper">
        <div class="uc-stepper__top-left">
          <slot
            name="top-left"
            v-bind="{next, previous, step, stepCount}" />
        </div>
        <slot
          name="before"
          v-bind="{next, previous, step, stepCount}" />
        <div class="uc-stepper__body">
          <transition-group
            tag="div"
            :name="currentAnimation"
            @beforeLeave="onBeforeLeave(step, previousStep)"
            @leave="onLeave(step, previousStep)"
            @afterLeave="onAfterLeave(step, previousStep)"
            @beforeEnter="onBeforeEnter(step, previousStep)"
            @enter="onEnter(step, previousStep)"
            @afterEnter="onAfterEnter(step, previousStep)">
            <section
              v-for="(stepSlotName, slotIndex) in renderSteps(step)"
              v-show="slotIndex === (step - 1)"
              :key="stepSlotName"
              class="uc-stepper__body__panel"
              data-test="panel">
              <slot
                :name="stepSlotName"
                v-bind="{next, previous, step, stepCount}" />
            </section>
          </transition-group>
        </div>
        <slot
          name="after"
          v-bind="{next, previous, step, stepCount}" />
      </section>
    </template>
  </use-steps>
</template>

<script>
import UseSteps from '@/components/wrappers/use-steps'

import { clamp } from '@/utils/number.utils'

const ComponentEvent = {
  CHANGE: 'change',
  BEFORE_LEAVE: 'beforeLeave',
  LEAVE: 'leave',
  AFTER_LEAVE: 'afterLeave',
  BEFORE_ENTER: 'beforeEnter',
  ENTER: 'enter',
  AFTER_ENTER: 'afterEnter',
}

export default {
  components: {
    UseSteps,
  },
  props: {
    slotPrefix: {
      type: String,
      default: 'step-',
    },
    renderStepsBefore: {
      type: Number,
      default: Infinity,
    },
    renderStepsAfter: {
      type: Number,
      default: Infinity,
    },
  },
  data () {
    return {
      currentAnimation: 'next',
    }
  },
  computed: {
    stepSlotNames () {
      return Object.keys(this.$scopedSlots).filter(slotName =>
        slotName.startsWith(this.slotPrefix),
      )
    },
    stepCount () {
      return this.stepSlotNames.length
    },
  },
  methods: {
    renderSteps (step) {
      const left = clamp(step - this.renderStepsBefore, 1, this.stepCount) - 1
      const right = clamp(step + this.renderStepsAfter, 1, this.stepCount)
      return this.stepSlotNames.slice(left, right)
    },
    onChangeStep (step, direction) {
      this.currentAnimation = direction
      this.$emit(ComponentEvent.CHANGE, step)
    },
    onBeforeLeave (to, from) {
      this.$emit(ComponentEvent.BEFORE_LEAVE, to, from)
    },
    onLeave (to, from) {
      this.$emit(ComponentEvent.LEAVE, to, from)
    },
    onAfterLeave (to, from) {
      this.$emit(ComponentEvent.AFTER_LEAVE, to, from)
    },
    onBeforeEnter (to, from) {
      this.$emit(ComponentEvent.BEFORE_ENTER, to, from)
    },
    onEnter (to, from) {
      this.$emit(ComponentEvent.ENTER, to, from)
    },
    onAfterEnter (to, from) {
      this.$emit(ComponentEvent.AFTER_ENTER, to, from)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-stepper
  position relative
  display flex
  flex-direction column
  height 100%
  overflow hidden

.uc-stepper__top-left
  position absolute
  top 0
  left 0
  z-index 1

.uc-stepper__body
  position relative
  display flex
  flex-grow 1
  height 100%

.uc-stepper__body__panel
  position absolute
  display flex
  flex-direction column
  align-items center
  width 100%
  height 100%
  // To create a viewport for fixed header inside: https://stackoverflow.com/a/38796408
  transform translateX(0)

.next-leave-active,
.next-enter-active,
.previous-leave-active,
.previous-enter-active
  transition all 500ms

.next-enter
  transform translate(100%, 0)

.next-leave-to
  transform translate(-100%, 0)

.previous-enter
  transform translate(-100%, 0)

.previous-leave-to
  transform translate(100%, 0)
</style>
