<template>
  <div class="menu-container">
    <div
      class="menu-trigger"
      @click="toggleMenu">
      <coq-icon />
    </div>
    <nav
      :class="{'show': showMenu}"
      class="dropdown-menu">
      <span class="dropdown__label">{{ $t('global.shownFeed') }}</span>
      <ul>
        <li
          v-for="(menuItem, key, index) in mutableMenuItems"
          :key="index"
          :class="{muted: menuItem.muted}"
          @click="toggleFeed($event, key)">
          <hide-icon v-if="menuItem.muted" />
          <span>{{ $t(`feed.types.${menuItem.type}`) }}</span>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import HideIcon from '@/assets/icons/legacy/not-visible.vue'
import CoqIcon from '@/assets/icons/legacy/coq.vue'
import { mapGetters } from 'vuex'
export default {
  name: 'SettingsMenu',
  components: {
    HideIcon,
    CoqIcon,
  },
  computed: {
    ...mapGetters(['feedTypes', 'showMenu']),
    mutableMenuItems () {
      const mutableFeedtypes = {}
      Object.keys(this.feedTypes).forEach(type => {
        if (this.feedTypes[type].mutable) mutableFeedtypes[type] = this.feedTypes[type]
      })
      return mutableFeedtypes
    },
  },
  methods: {
    toggleFeed (event, type) {
      event.stopPropagation()
      this.$store.dispatch('toggleFeedType', type)
    },
    toggleMenu () {
      this.$store.dispatch('showMenu', !this.showMenu)
      if (this.showMenu) {
        window.setTimeout(() => {
          window.addEventListener('click', this.closeMenuHandler)
        }, 10)
      }
    },
    closeMenuHandler (event) {
      event.stopPropagation()
      this.$store.dispatch('showMenu', false)
      window.removeEventListener('click', this.closeMenuHandler)
    },
  },
}
</script>

<style lang="stylus" scoped>
.menu-container
  position absolute
  right 0
  z-index 100
  margin-left auto

.menu-trigger
  display flex
  width 26px
  height 26px
  border-radius 26px
  cursor pointer
  transition background-color 200ms

  svg
    width 80%
    height 80%
    margin auto
    fill rgba(0,0,0,0.2)

  &:hover
    background-color #e2e2e2

.dropdown-menu
  position absolute
  top 100%
  right 0
  z-index 20
  display inline-block
  min-width 130px
  padding 10px 20px 8px 40px
  background-color #fff
  box-shadow 2px 2px 6px rgba(0,0,0,0.3)
  transform translate(-11px, -11px) scale(0)
  transform-origin right top
  transition transform 200ms $base-ease-in-out-circ
  pointer-events none

  ul
    margin 0 -20px 10px -40px
    padding 0
    list-style none

  li
    position relative
    display flex
    padding 4px 20px 4px 40px
    white-space nowrap
    cursor pointer

    svg
      position absolute
      top 7px
      left 10px
      width 16px
      height 16px
      fill rgba(0,0,0,0.3)

    &:hover
      background-color rgba(0,0,0,0.03)

    &.selected
      cursor default
      pointer-events none

    &.muted
      color $legacy-color-dark-gray-50

      svg
        fill $legacy-color-dark-gray-50

  &.show
    transform translate(0, 0) scale(1)
    pointer-events all

.dropdown__label
  display inline-block
  margin-bottom 6px
  color $legacy-color-dark-gray-50
  font-size 0.8125rem

</style>
