<!-- This editor breaks editor contract (options are statically defined), do not take inspiration from this "editor" -->
<template>
  <div>
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedValue"
      :required="isRequired">
      <uc-time-frame
        :placeholder="label"
        :readonly="isReadonly"
        :options="options"
        :custom-date="isCustomDate"
        :selected="selectedOption"
        :start-date="from"
        :end-date="to"
        @date-selected="dateSelected"
        @time-frame-selected="timeFrameSelected" />
    </editor-wrapper>
  </div>
</template>

<script>
import { subDays, startOfDay, endOfDay, endOfToday } from 'date-fns'
import { isEmpty } from '@/utils/object.utils.js'
import { translateDateToTimeFrame } from './date.utils.js'
import { TODAY, THIS_WEEK, THIS_MONTH, LAST_THREE_MONTHS, THIS_YEAR, CUSTOM, DATE_OPTIONS } from './date.utils.options.js'

import EditorWrapper from '@/components/editors/editor-wrapper.vue'
import { UcTimeFrame } from '@/components/forms/uc-time-frame/'
import editorMixin from '@/components/editors/editor-mixin.js'

export default {
  name: 'UcTimeFrameEditor',
  components: { UcTimeFrame, EditorWrapper },
  mixins: [editorMixin],
  data () {
    return {
      isCustomDate: false,
      clearCalendar: false,
    }
  },
  computed: {
    formattedValue () {
      if (isEmpty(this.selectedOption)) {
        return ''
      }
      return this.selectedOption
    },
    placeholder () {
      return this.definition.label || this.$t('global.selectPlaceholder')
    },
    options () {
      return DATE_OPTIONS
    },
    from () {
      return !isEmpty(this.value) ? this.value.from : null
    },
    to () {
      return !isEmpty(this.value) ? this.value.to : null
    },
    selectedOption () {
      return !isEmpty(this.value) ? translateDateToTimeFrame(this.to, this.from).guid : undefined
    },
  },
  watch: {
    selectedOption: {
      immediate: true,
      handler (option) {
        this.isCustomDate = option === CUSTOM.guid
      },
    },
  },
  methods: {
    dateSelected (event) {
      const from = event.startDate ? startOfDay(new Date(event.startDate))?.toISOString() : null
      const to = event.endDate ? endOfDay(new Date(event.endDate))?.toISOString() : null
      this.onOptionChange({ from: from, to: to })
    },
    convertTimeFrameToDate (timeFrame = CUSTOM.guid, now) {
      const mapping = {
        [TODAY.guid]: { function: subDays, amount: 0 },
        [THIS_WEEK.guid]: { function: subDays, amount: 7 },
        [THIS_MONTH.guid]: { function: subDays, amount: 30 },
        [LAST_THREE_MONTHS.guid]: { function: subDays, amount: 90 },
        [THIS_YEAR.guid]: { function: subDays, amount: 365 },
      }
      const amount = mapping[timeFrame]?.amount

      return mapping[timeFrame]?.function(now, amount) || undefined
    },
    timeFrameSelected (event) {
      this.isCustomDate = event.guid === CUSTOM.guid

      if (!this.isCustomDate) {
        const timeFrame = this.options.find(x => x.guid === event.guid)
        const to = endOfToday().toISOString()
        const from = startOfDay(this.convertTimeFrameToDate(timeFrame.guid, to)).toISOString()
        this.onOptionChange({ from: from, to: to })
      }
    },
    onOptionChange (event) {
      this.onChange({
        label: this.placeholder,
        newValue: event,
        valueFieldName: 'value',
      })
    },

  },
}
</script>
