<template>
  <transition
    :name="transitionName"
    @after-enter="onAfterEnter"
    @after-leave="onAfterLeave">
    <div
      v-if="text && isVisible"
      class="uc-snackbar"
      :class="cssClassObject"
      @click="onClick">
      {{ text }}
    </div>
  </transition>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

export const ThemeProp = {
  NEUTRAL: 'neutral',
  SUCCESS: 'success',
  ERROR: 'error',
}

export const DurationProp = {
  SHORT: 'short',
  LONG: 'long',
  INFINITE: 'infinite',
}

export const AnchorProp = {
  TOP: 'top',
  BOTTOM: 'bottom',
}

export const ComponentEvent = {
  SHOW: 'show',
  HIDE: 'hide',
  CLICK: 'click',
}

const durationToMillisecondMap = {
  [DurationProp.SHORT]: 5000,
  [DurationProp.LONG]: 10000,
}

const anchorToTransitionNameMap = {
  [AnchorProp.TOP]: 'fade-in-down',
  [AnchorProp.BOTTOM]: 'fade-in-up',
}

export default {
  name: 'UcSnackbar',
  props: {
    text: {
      type: String,
      default: undefined,
    },
    dismissable: {
      type: Boolean,
      default: false,
    },
    theme: {
      type: String,
      default: ThemeProp.NEUTRAL,
      validator: validateAgainstEnum(ThemeProp),
    },
    duration: {
      type: String,
      default: DurationProp.SHORT,
      validator: validateAgainstEnum(DurationProp),
    },
    anchor: {
      type: String,
      default: AnchorProp.TOP,
      validator: validateAgainstEnum(AnchorProp),
    },
  },
  data () {
    return {
      isVisible: false,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-snackbar--theme-neutral': this.theme === ThemeProp.NEUTRAL,
        'uc-snackbar--theme-success': this.theme === ThemeProp.SUCCESS,
        'uc-snackbar--theme-error': this.theme === ThemeProp.ERROR,
        'is-clickable': this.isClickable,
      }
    },
    transitionName () {
      return anchorToTransitionNameMap[this.anchor]
    },
    durationInMilliseconds () {
      return durationToMillisecondMap[this.duration]
    },
    isClickable () {
      return ComponentEvent.CLICK in this.$listeners || this.dismissable
    },
  },
  mounted () {
    this.show()
  },
  methods: {
    show () {
      this.isVisible = true

      if (this.duration !== DurationProp.INFINITE) {
        const timerId = window.setTimeout(this.hide, this.durationInMilliseconds)
        this.$on('hook:destroyed', () => clearTimeout(timerId))
      }
    },
    hide () {
      this.isVisible = false
    },
    onClick (e) {
      if (this.dismissable) {
        this.hide()
      }

      this.$emit(ComponentEvent.CLICK, e)
    },
    onAfterEnter () {
      this.$emit(ComponentEvent.SHOW)
    },
    onAfterLeave () {
      this.$emit(ComponentEvent.HIDE)
    },
  },
}
</script>

<style lang="stylus" scoped>
$background-opacity = 0.85

.uc-snackbar
  padding $base-unit * 2
  border-radius 3px
  color $base-color-white
  text-align center
  user-select none

// modifier: theme-neutral
.uc-snackbar--theme-neutral
  background-color alpha($base-color-gray-700, $background-opacity)

// modifier: theme-success
.uc-snackbar--theme-success
  background-color alpha($base-color-success, $background-opacity)

.uc-snackbar--theme-error
  background-color alpha($base-color-error, $background-opacity)

// state: is-clickable
.uc-snackbar.is-clickable
  cursor pointer

</style>
