/**
 * @description
 * This function takes a Vue component and returns a `Promise` that is:
 * - resolved when the `resolveEvent` is emitted
 * - rejected when the `rejectEvent` is emitted
 * - rejected when the `timeout` is reached
 *
 * @param  {Object} options
 * @param  {Object} options.component       - The component that will emit the events to listen for
 * @param  {String} options.resolveEvent    - The event that will resolve the promise
 * @param  {String} [options.rejectEvent]   - The event that will reject the promise
 * @param  {Number} [options.timeout]       - The duration in miliseconds before the promise is rejected automatically
 * @returns {Promise}
 */
export function expectComponentEventAsync ({ component, resolveEvent, rejectEvent, timeout }) {
  if (component === undefined) {
    return console.error('[expectComponentEventAsync] a Vue component is required')
  }

  if (!component._isVue) {
    return console.error('[expectComponentEventAsync] the provided component is not a Vue component')
  }

  if (resolveEvent === undefined) {
    return console.error('[expectComponentEventAsync] a resolveEvent is required')
  }

  return new Promise((resolve, reject) => {
    function cleanup () {
      component.$off(resolveEvent, resolveListener)
      component.$off(rejectEvent, rejectListener)
    }

    const resolveListener = e => cleanup(resolve(e))
    const rejectListener = e => cleanup(reject(new Error(e)))

    component.$once(resolveEvent, resolveListener)
    component.$once(rejectEvent, rejectListener)

    if (timeout) {
      setTimeout(() => cleanup(reject(new Error('Timed out'))), timeout)
    }
  })
}
