<template>
  <div
    class="uc-image"
    data-test="image"
    :class="cssClassObject">
    <transition
      name="fade"
      mode="out-in">
      <div
        v-if="isLoading && $slots.loading"
        class="uc-image__status uc-image__status--loading"
        data-test="image-status-loading">
        <slot name="loading" />
      </div>

      <div
        v-else-if="isFailed && $slots.failed"
        class="uc-image__status uc-image__status--failed"
        data-test="image-status-failed">
        <slot name="failed" />
      </div>

      <img
        v-else-if="isLoaded"
        ref="image"
        class="uc-image__file"
        data-test="image-file"
        :src="src"
        :alt="alt"
        draggable="false">
    </transition>
  </div>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

export const FitProp = {
  NONE: 'none',
  CONTAIN: 'contain',
  COVER: 'cover',
  FILL: 'fill',
  SCALE_DOWN: 'scaleDown',
}

const State = {
  PENDING: 'pending',
  LOADING: 'loading',
  LOADED: 'loaded',
  FAILED: 'failed',
}

export default {
  name: 'UcImage',
  props: {
    src: {
      type: [String, null],
      required: true,
    },
    alt: {
      type: String,
      default: '',
    },
    fit: {
      type: String,
      default: FitProp.NONE,
      validator: validateAgainstEnum(FitProp),
    },
    delayBeforeShowingLoadingSlot: {
      type: Number,
      default: 500,
    },
  },
  data () {
    return {
      state: State.PENDING,
      loadingSlotTimerId: undefined,
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-image--fit-contain': this.fit === FitProp.CONTAIN,
        'uc-image--fit-cover': this.fit === FitProp.COVER,
        'uc-image--fit-fill': this.fit === FitProp.FILL,
        'uc-image--fit-scale-down': this.fit === FitProp.SCALE_DOWN,
      }
    },
    isLoading () {
      return this.state === State.LOADING
    },
    isLoaded () {
      return this.state === State.LOADED
    },
    isFailed () {
      return this.state === State.FAILED
    },
  },
  mounted () {
    this.$nextTick(this.loadImage)
  },
  destroyed () {
    clearTimeout(this.loadingSlotTimerId)
  },
  methods: {
    loadImage () {
      const image = new Image()

      this.loadingSlotTimerId = setTimeout(this.onLoading, this.delayBeforeShowingLoadingSlot)

      image.onload = this.onLoaded
      image.onerror = this.onError
      image.src = this.src
    },
    onLoading () {
      this.state = State.LOADING
    },
    onLoaded () {
      clearTimeout(this.loadingSlotTimerId)
      this.state = State.LOADED
    },
    onError () {
      clearTimeout(this.loadingSlotTimerId)
      this.state = State.FAILED
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-image
  position relative
  display inline-flex
  align-items center
  justify-content center

.uc-image__file
  max-width: 100%
  max-height: 100%

.uc-image__status
  display flex
  align-items center
  justify-content center

// fit: contain, cover, fill, scale-down
.uc-image--fit-contain,
.uc-image--fit-cover,
.uc-image--fit-fill,
.uc-image--fit-scale-down
  width: 100%
  height: 100%

  .uc-image__file
    width: 100%
    height: 100%

// fit: contain
.uc-image--fit-contain
  .uc-image__file
    object-fit: contain

// fit: cover
.uc-image--fit-cover
  .uc-image__file
    object-fit: cover

// fit: fill
.uc-image--fit-fill
  .uc-image__file
    object-fit: fill

// fit: scale-down
.uc-image--fit-scale-down
  .uc-image__file
    object-fit: scale-down

</style>
