import { localStore } from './storage'
import Constants from '@/constants.js'

export default {
  getLocalStorageData (currentList) {
    const enums = Constants.GenericList
    const key = currentList.localStorageKey

    return {
      defaultOrder: localStore.getJSON(`${key}-${enums.DefaultOrder}`) || [],
      currentOrder: localStore.getJSON(`${key}-${enums.CurrentOrder}`) || [],
      configuration: localStore.getJSON(`${key}-${enums.Configuration}`) || {},
    }
  },
  setLocalStorageKeyData (currentList, keySuffix, data) {
    const key = currentList.localStorageKey
    localStore.set(`${key}-${keySuffix}`, JSON.stringify(data))
  },
  saveColumnOrder (currentList) {
    const columnsToShow = currentList.columns.filter(column => column.show).map(column => column.key)

    if (columnsToShow.length === 0) {
      return
    }

    if (currentList.localStorageKey) {
      this.setLocalStorageKeyData(currentList, Constants.GenericList.CurrentOrder, columnsToShow)
    }
  },
  setDefaultColumnOrder (currentList, defaultColumnNames) {
    let defaultColumns = defaultColumnNames
    const DEFAULT_COLUMNS_COUNT = 10

    if (defaultColumnNames.length === 0) {
      defaultColumns = currentList.columns.slice(0, DEFAULT_COLUMNS_COUNT).map(column => column.key)
    }

    this.setLocalStorageKeyData(currentList, Constants.GenericList.DefaultOrder, defaultColumns)
  },
  mergeColumns (currentList, columnNames) {
    const columnsToHide = currentList.columns.filter(column => !columnNames.includes(column.key))
    let columnsToShow = currentList.columns.filter(column => columnNames.includes(column.key))
    columnsToShow = this._orderColumns(columnsToShow, columnNames)
    return [...columnsToShow, ...columnsToHide]
  },
  _orderColumns (columns, columnNames) {
    const orderedColumns = []

    columnNames.forEach(columnName => {
      const column = columns.find(column => column.key === columnName)

      if (column) {
        orderedColumns.push(column)
      }
    })

    return orderedColumns
  },
}
