<template>
  <div
    class="uc-category-picker-selection"
    :class="cssClassObject">
    <uc-data-grid-selectable
      :value="selection"
      v-bind="{ columns, rows }"
      row-key="guid"
      :disable-infinite-scroll="hasLoadedAllItems"
      headingless
      @next="onNext"
      @deselect="onDeselect"
      @select="onSelect">
      <template #category="{ cell }">
        <!-- Category item -->
        <uc-node-row>
          <uc-node-title>
            {{ cell.name }}
          </uc-node-title>
          <uc-node-subtitle>
            {{ cell.path }}
          </uc-node-subtitle>
        </uc-node-row>
      </template>
    </uc-data-grid-selectable>
  </div>
</template>

<script>
import { UcDataGridSelectable } from '@/components/generic/uc-data-grid-selectable/'
import {
  UcNodeRow,
  UcNodeTitle,
  UcNodeSubtitle,
} from '@/components/generic/uc-node-row/'
import { difference } from '@/utils/array.utils'

const ComponentEvent = {
  CHANGE: 'change',
  NEXT: 'next',
}

export default {
  name: 'UcCategoryPickerSelection',
  components: {
    UcDataGridSelectable,
    UcNodeRow,
    UcNodeTitle,
    UcNodeSubtitle,
  },
  props: {
    rows: {
      type: Array,
      required: true,
    },
    hasLoadedAllItems: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      deselectedItems: [],
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-max-height': this.rows.length > 6,
      }
    },
    columns () {
      return [
        {
          key: 'category',
          value: this.$t('ucCategoryEditor.category'),
        },
      ]
    },
    selection () {
      return difference(this.rows, this.deselectedItems)
    },
  },
  methods: {
    onDeselect (value) {
      this.deselectedItems = [...this.deselectedItems, value]
      this.$emit(ComponentEvent.CHANGE, this.deselectedItems)
    },
    onSelect (value) {
      this.deselectedItems = difference(this.deselectedItems, [value])
      this.$emit(ComponentEvent.CHANGE, this.deselectedItems)
    },
    onNext () {
      this.$emit(ComponentEvent.NEXT)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-category-picker-selection
  position relative
  border-top $base-border
  background $base-color-white

  &.is-max-height
    height $base-unit * 60
    border-bottom $base-border
</style>
