<template functional>
  <div
    v-if="props.items.length > 0"
    class="uc-grid"
    :class="$options.methods.getCssClassObject(props)"
    :style="{ '--uc-grid--column-count': props.columns }">
    <slot
      v-for="item in props.items"
      :item="item" />
  </div>
</template>

<script>
import { validateAgainstEnum } from '@/utils/validate-against'

export const GridGapProp = {
  SM: 'sm',
  MD: 'md',
  LG: 'lg',
}

export default {
  name: 'UcGrid',
  props: {
    items: {
      type: Array,
      required: true,
    },
    columns: {
      type: Number,
      default: 0,
    },
    gridGap: {
      type: String,
      default: GridGapProp.MD,
      validator: validateAgainstEnum(GridGapProp),
    },
  },
  methods: {
    getCssClassObject (props) {
      return {
        'uc-grid--grid-gap-sm': props.gridGap === GridGapProp.SM,
        'uc-grid--grid-gap-md': props.gridGap === GridGapProp.MD,
        'uc-grid--grid-gap-lg': props.gridGap === GridGapProp.LG,
      }
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-grid--grid-gap-sm
  --uc-grid--column-gap $base-unit

.uc-grid--grid-gap-md
  --uc-grid--column-gap 2 *$base-unit

.uc-grid--grid-gap-lg
  --uc-grid--column-gap 3 * $base-unit

.uc-grid
  --uc-grid--column-count auto-fill
  --uc-grid--column-width minmax(0, 1fr)

  display grid
  grid-gap var(--uc-grid--column-gap)
  grid-template-columns repeat(var(--uc-grid--column-count), var(--uc-grid--column-width))

</style>
