<template>
  <div
    class="payment-method-navigator"
    data-test="payment-method-navigator">
    <uc-navigator
      v-bind="{ nodes, totalNodeCount, scrollTo, path }"
      :is-loading="loading"
      @requestNextPage="onRequestNextPage">
      <template #default="props">
        <div class="payment-method-navigator__view">
          <uc-navigator-view-grid-spacious
            :nodes="props.nodes"
            grid-gap="lg">
            <template #default="{ node }">
              <payment-method-navigator-leaf-node
                :node="node"
                :size="nodeSize"
                @click="onClick(node)" />
            </template>
          </uc-navigator-view-grid-spacious>
        </div>
      </template>
      <template #trigger>
        <div class="payment-method-navigator__loading">
          <loading-indicator is-loading />
        </div>
      </template>
    </uc-navigator>
  </div>
</template>

<script>
import { UcNavigator, UcNavigatorViewGridSpacious } from '@/components/generic/uc-navigator'
import PaymentMethodNavigatorLeafNode, { SizeProp } from './components/payment-method-navigator-leaf-node'
import LoadingIndicator from '@/components/widgets/loading-indicator'

const ComponentEvent = {
  SELECT: 'select',
  REQUEST_NEXT_PAGE: 'requestNextPage',
}

export default {
  name: 'PaymentMethodNavigator',
  components: {
    UcNavigator,
    PaymentMethodNavigatorLeafNode,
    UcNavigatorViewGridSpacious,
    LoadingIndicator,
  },
  props: {
    nodes: {
      type: Array,
      required: true,
    },
    totalNodeCount: {
      type: Number,
      required: true,
    },
    scrollTo: {
      type: Array,
      default: () => [],
      validator: (value) => value.length <= 2 && value.every(entry => Number.isInteger(entry)),
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      nodeSize: this.nodes.length > 4 ? SizeProp.MD : SizeProp.LG,
      path: [],
    }
  },
  methods: {
    onClick (item) {
      this.$emit(ComponentEvent.SELECT, item)
    },
    onRequestNextPage () {
      this.$emit(ComponentEvent.REQUEST_NEXT_PAGE)
    },
  },
}
</script>

<style lang="stylus" scoped>
.payment-method-navigator
  width 100%
  height 100%

.payment-method-navigator__view
  margin 3 * $base-unit

.payment-method-navigator__loading
  display flex
  align-items center
  justify-content center
  width 100%
  height 4 * $base-unit
  padding-bottom 3 * $base-unit
</style>
