<template>
  <div class="uc-numeric-input">
    <input-group
      v-bind="{ label, readonly, hasErrors }">
      <input
        ref="input"
        v-model="inputValue"
        type="number"
        :disabled="readonly"
        :placeholder="label"
        :step="step"
        @input="onInput"
        @blur="onBlur">
      <template #after>
        <slot name="after" />
      </template>
    </input-group>
  </div>
</template>

<script>
import { InputGroup } from '@/components/forms/input-group/'

export default {
  name: 'UcNumericInput',
  components: {
    InputGroup,
  },
  props: {
    label: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    value: {
      type: Number,
      default: null,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    hasErrors: {
      type: Boolean,
      default: false,
    },
    step: {
      type: String,
      default: 'any',
    },
  },
  data () {
    return {
      inputValue: undefined,
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (value) {
        this.inputValue = value
      },
    },
  },
  methods: {
    onBlur (event) {
      this.$emit('blur', event)
    },
    onInput (event) {
      this.$emit('input', event.target.value || null)
    },
    focus () {
      this.$nextTick(() => this.$refs.input.focus())
    },
  },
}
</script>

<style lang="stylus" scoped>
// for Firefox
input[type=number]
  appearance textfield

// for Chrome
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button
  margin 0
  appearance none
</style>
