import { EntityService, EntityType } from './_entity.service.js'

export class OrderPaymentService extends EntityService {
  constructor (httpClient) {
    super(EntityType.ORDER, httpClient)
  }

  get _definitionUrl () {
    return `${this._rootUrl}/payments/definitions/full`
  }

  fetchManyAsync ({ parentGuid, cultureCode, startAt, limit, searchTerm }) {
    const url = `${this._rootUrl}/${parentGuid}/payments/${cultureCode}`
    return super.fetchEntitiesAsync({
      url,
      params: {
        startAt,
        limit,
        searchTerm,
      },
    })
  }
}
