<template>
  <form
    class="promotion-create"
    @submit.prevent="createPromotion">
    <uc-modal
      :title="$t('promotionCreate.title')"
      @hide="closeDialog">
      <!-- Definition -->
      <div class="promotion-create__field">
        <uc-select
          :options="availableDefinitions"
          :selected="selectedDefinitionGuid"
          :placeholder="definitionPlaceholderText"
          :readonly="isPosting"
          @selected="selectedDefinitionGuid = $event.guid">
          <template #pending>
            <loading-indicator is-loading />
          </template>
        </uc-select>
      </div>

      <!-- Name -->
      <div class="promotion-create__field">
        <uc-text-input
          ref="input"
          v-model.trim="newName"
          :label="nameLabelText"
          :readonly="isPosting" />
        <div
          v-show="errorMessage.length"
          class="promotion-create__error"
          data-test="promotion-create-error">
          {{ errorMessage }}
        </div>
      </div>

      <!-- Submit -->
      <template #footer>
        <uc-button
          :disabled="!isEnabled"
          theme="primary"
          type="submit"
          @click="createPromotion">
          {{ $t('promotionCreate.createPromotion') }}
          <template
            v-if="isPosting"
            #after>
            <loading-spinner is-loading />
          </template>
        </uc-button>
        <uc-button
          theme="text"
          @click="closeDialog">
          {{ $tc('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </form>
</template>

<script>
import UcModal from '@/components/widgets/uc-modal'
import UcSelect from '@/components/forms/uc-select'
import UcButton from '@/components/forms/uc-button/uc-button'
import { UcTextInput } from '@/components/forms/uc-text-input/'
import LoadingSpinner from '@/components/widgets/loading-spinner'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import capitalize from '@/utils/capitalize'
import { promotionService } from '@/services'

const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  POSTING: 'posting',
  ERROR: 'error',
}

export default {
  name: 'PromotionCreate',
  components: {
    UcModal,
    UcSelect,
    UcTextInput,
    UcButton,
    LoadingSpinner,
    LoadingIndicator,
  },
  props: {
    campaignGuid: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      availableDefinitions: undefined,
      selectedDefinitionGuid: '',
      newName: '',
      errorMessage: '',
      state: ComponentState.INITIALIZING,
    }
  },
  computed: {
    definitionPlaceholderText () {
      return `${capitalize(this.$t('global.select'))} ${this.$t('global.definition')}`
    },
    nameLabelText () {
      return `${capitalize(this.$t('promotionCreate.promotionName'))}`
    },
    isEnabled () {
      return this.newName.length > 0 && this.selectedDefinitionGuid.length > 0
    },
    isPosting () {
      return this.state === ComponentState.POSTING
    },
  },
  async created () {
    this.availableDefinitions = (await promotionService.fetchDefinitionsListAsync()).data.map(definition => ({ guid: definition.guid, label: definition.name }))
    this.state = ComponentState.IDLE
  },
  methods: {
    async createPromotion () {
      if (this.isPosting) {
        return
      }

      const payload = {
        name: this.newName,
        definitionGuid: this.selectedDefinitionGuid,
        campaignGuid: this.campaignGuid,
      }

      try {
        this.state = ComponentState.POSTING
        const createPromotionResponse = await promotionService.createEntityAsync(payload)
        this.state = ComponentState.IDLE

        this.$router.push({
          name: 'promotion-edit',
          params: {
            promotionGuid: createPromotionResponse.data.guid,
            campaignGuid: this.campaignGuid,
          },
        })
      } catch (e) {
        this.state = ComponentState.ERROR
        if (e.response.data.message) {
          this.errorMessage = e.response.data.message
        } else {
          throw e
        }
      }
    },
    closeDialog () {
      this.$router.replace({ name: 'campaign-edit' })
    },
  },
}
</script>

<style lang="stylus" scoped>
.promotion-create__field:not(:last-of-type)
  margin-bottom $base-unit * 3

.promotion-create__error
  padding $base-unit $base-unit * 2  $base-unit 0
  error-text()
</style>
