/**
 * Need to use our own debounce function, since debounce and
 * lodash.debounce rely on Date.now(), which breaks the tests
 */
export default function (fn, delay) {
  let timer = null
  return function () {
    const context = this
    const args = arguments
    clearTimeout(timer)
    timer = setTimeout(function () {
      fn.apply(context, args)
    }, delay)
  }
}
