const ComponentState = {
  INITIALIZING: 'initializing',
  IDLE: 'idle',
  LOADING: 'loading',
  ERROR: 'error',
}

export default {
  name: 'UseDataProvider',
  props: {
    services: {
      type: Object,
      required: true,
    },
    language: {
      type: String,
      required: true,
    },
    pageSize: {
      type: Number,
      default: 36,
    },
    mapEntityUrl: {
      type: Function,
      default: () => ({}),
    },
    mapEntity: {
      type: Function,
      default: () => ({}),
    },
  },
  data () {
    return {
      data: [],
      state: ComponentState.INITIALIZING,
      totalNodeCount: 0,
    }
  },
  computed: {
    isLoading () {
      return this.state === ComponentState.LOADING
    },
  },
  methods: {
    async getData (payload) {
      this.state = ComponentState.LOADING

      const { node, page } = payload
      const cultureCode = this.language
      const params = {
        startAt: this.pageSize * page,
        limit: this.pageSize,
        includePath: true,
      }

      try {
        const service = this.services[node.nodeType]
        const url = this.mapEntityUrl(node)

        const response = await service.fetchEntitiesAsync({ cultureCode, params, url })
        const entities = response.data.map(this.mapEntity)

        this.data = page === 0
          ? [...entities]
          : [...this.data, ...entities]

        this.totalNodeCount = Number(response.headers['x-total-count'])
        this.state = ComponentState.IDLE
      } catch (error) {
        console.error(error)
        this.state = ComponentState.ERROR
        // TODO(ch10038): We need a way to handle request errors
      }
    },
  },
  render () {
    return this.$scopedSlots.default({
      data: this.data,
      getData: this.getData,
      isLoading: this.isLoading,
      totalNodeCount: this.totalNodeCount,
    })
  },
}
