<template>
  <div
    ref="body"
    class="list-table-container">
    <slot name="categoryLoader" />
    <section
      ref="draggableContainer"
      v-custom-scroll-bar
      class="draggable-container">
      <div class="list-table__content-wrapper">
        <div
          v-if="selectableRows"
          class="column column--sticky">
          <div
            class="sticky cell column_header handle"
            :style="headerStyle">
            <div
              v-truncate
              class="header-label" />
          </div>
          <div class="column-rows">
            <div
              v-for="row in list.rows"
              :key="row.meta.entityGuid"
              class="cell cell__select"
              :style="cellStyle"
              data-test="cell"
              @click="toggleRow(row)">
              <uc-checkbox
                class="cell__content"
                :guid="row.meta.entityGuid"
                :checked="isSelected(row)"
                :readonly="false"
                :compact="false" />
            </div>
          </div>
        </div>
        <draggable
          ref="draggable"
          v-model="shownColumns"
          handle=".handle"
          animation="150"
          class="draggable">
          <div
            v-for="column in shownColumns"
            :key="column.index"
            ref="columnRefs"
            class="column"
            :style="defaultColumnStyle"
            :data-key="column.key"
            data-test="list-column"
            @dragstart="startDrag"
            @dragend="toggleDragged">
            <div
              class="sticky cell column_header handle"
              :style="headerStyle"
              :class="{sorted : isSortedByDescending(column.key) || isSortedByAscending(column.key)}"
              @click="sortBy(column.key)"
              @mouseenter="hoveredColumn = column"
              @mouseleave="hoveredColumn = null">
              <div
                v-truncate
                class="header-label">
                {{ column.label }}
              </div>
              <sortable-sorted-descending
                v-if="isSortedByDescending(column.key)"
                class="sortable" />
              <sortable-sorted-ascending
                v-if="isSortedByAscending(column.key)"
                class="sortable" />
              <resize-handler
                v-show="!resizeObj.headerRef && hoveredColumn && hoveredColumn.key === column.key"
                class="resize-handler"
                :size="resizeObj.resizeWidth"
                :min-size="resizeObj.minWidth"
                :is-vertical="false"
                @resizeMousedown="onMousedown(column.key)"
                @emitDelta="onMousemoveDebounced($event, 250)"
                @resizeMouseup="onMouseup(column.key, 'width')" />
            </div>
            <div
              v-show="list.rows.length > 0"
              class="column-rows">
              <div
                v-for="row in list.rows"
                :key="row.meta.entityId"
                class="cell"
                :style="cellStyle"
                data-test="cell"
                @click="onRowClick(row)">
                <template v-if="row[column.key]">
                  <component
                    :is="column.editor"
                    class="cell__content"
                    :name="column.label"
                    compact
                    :language="contentLanguage"
                    :currency-iso-code="column.currencyIsoCode"
                    :definition="{...row[column.key], readonly: readonly}" />
                </template>
              </div>
            </div>
          </div>
          <div
            key="spacerColumnRight"
            ref="filler"
            :style="fillerStyle">
            <div
              class="sticky cell filler column_header"
              :style="headerStyle">
              <slot />
            </div>
            <div class="column-rows">
              <div
                v-for="row in list.rows"
                :key="row.meta.entityGuid"
                class="cell filler"
                :style="cellStyle"
                @click="onRowClick(row)" />
            </div>
          </div>
        </draggable>
      </div>
      <use-intersection-observer
        :root="$refs.draggableContainer"
        root-margin="100%"
        @enter="loadMoreEntries">
        <div class="list-table__footer">
          <loading-indicator
            :is-loading="isLoadingItems" />
        </div>
      </use-intersection-observer>
    </section>
    <section
      v-show="resizeObj.headerRef"
      class="resize-box"
      :style="resizeBoxStyle">
      <div
        class="resize-indicator"
        :style="resizeIndicatorStyle" />
    </section>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import debounce from 'debounce'
import ResizeHandler from '@/components/generic/resize-handler'
import Editors from '@/components/editors'
import SortableSortedAscending from '@/assets/icons/legacy/sortable-sorted-ascending'
import SortableSortedDescending from '@/assets/icons/legacy/sortable-sorted-descending'
import LoadingIndicator from '@/components/widgets/loading-indicator'
import UseIntersectionObserver from '@/components/wrappers/use-intersection-observer'
import UcCheckbox from '@/components/forms/uc-checkbox'

import Helpers from '@/utils/generic-list-helpers'
import Constants from '@/constants.js'

export default {
  name: 'ListTable',
  components: {
    draggable,
    ResizeHandler,
    ...Editors,
    SortableSortedAscending,
    SortableSortedDescending,
    LoadingIndicator,
    UseIntersectionObserver,
    UcCheckbox,
  },
  props: {
    list: {
      type: Object,
      required: true,
    },
    readonly: {
      type: Boolean,
      default: true,
    },
    totalCount: {
      type: Number,
      required: true,
    },
    isLoadingItems: {
      type: Boolean,
      required: true,
    },
    selectableRows: {
      type: Boolean,
      default: false,
    },
    selected: {
      type: Array,
      default: () => [],
    },
    contentLanguage: {
      type: String,
      required: true,
    },
  },
  data () {
    return {
      sortedBy: '',
      hoveredColumn: undefined,
      resizeObj: {
        minWidth: 100,
        defaultWidth: 150,
        widthsApplied: false,
        headerRef: null,
        resizeWidth: 0,
        resizeOffset: 0,
        fillerWidth: 0,
        containerDefaultWidth: 1000,
      },
      rowHeight: 70,
      headerHeight: 50,
    }
  },
  computed: {
    shownColumns: {
      get () {
        return this.list.columns.filter(column => column.show)
      },
      set (shownColumns) {
        const hiddenColumns = this.list.columns.filter(column => !column.show)
        const resultColumns = [...shownColumns, ...hiddenColumns]
        this.$emit('columnsDragged', resultColumns)
        this.$forceUpdate()
      },
    },
    headerStyle () {
      return { height: `${this.headerHeight}px` }
    },
    cellStyle () {
      return { height: `${this.rowHeight}px` }
    },
    loadingIndicatorStyle () {
      return {
        position: 'absolute',
        top: `${this.loadingIndicatorObj.offsetTop}px`,
        left: `${this.loadingIndicatorObj.offsetLeft}px`,
      }
    },

    remainingItemsStyle () {
      const items = (this.totalCount - this.list.rows.length === 0) ? 0 : 2
      const height = `${this.rowHeight * items}px`
      return { height, border: 'none' }
    },
    defaultColumnStyle () {
      return { width: `${this.resizeObj.defaultWidth}px` }
    },
    fillerStyle () {
      return { width: `${this.resizeObj.fillerWidth}px` }
    },

    resizeBoxStyle () {
      if (!this.resizeObj.headerRef) return {}
      const height = Math.floor(this.resizeObj.headerRef.getBoundingClientRect().height)
      return {
        height: `${height}px`,
        width: `${this.resizeObj.resizeWidth}px`,
        left: `${this.resizeObj.resizeOffset}px`,
      }
    },
    resizeIndicatorStyle () {
      if (!this.resizeObj.headerRef) return {}
      const height = this.$refs.draggable.$el.getBoundingClientRect().height
      return { height: `${height}px` }
    },
  },
  watch: {
    shownColumns (newVal, oldVal) {
      if (!this.resizeObj.widthsApplied) return
      this.$nextTick(() => {
        this.resizeFiller()
        // Scroll horizontally to the end if new column was added
        if (newVal.length > oldVal.length) {
          this.$refs.draggableContainer.scrollLeft = Math.floor(this.getDraggableRect().width)
          this.resetScrollPosition()
        }
      })
    },
  },
  methods: {
    saveColumnClientConfiguration ({ currentList, columnKey, fieldName, fieldValue }) {
      const dict = Helpers.getLocalStorageData(currentList).configuration
      if (!dict[columnKey]) {
        dict[columnKey] = {}
      }
      dict[columnKey][fieldName] = fieldValue
      Helpers.setLocalStorageKeyData(currentList, Constants.GenericList.Configuration, dict)
    },
    getColumnsClientConfiguration (currentList) {
      return Helpers.getLocalStorageData(currentList).configuration
    },
    sortBy (columnKey) {
      if (columnKey === this.sortedBy) {
        const invertedColumnKey = this.toColumnKeyDescending(columnKey)
        this.$emit('sortBy', invertedColumnKey)
        this.sortedBy = invertedColumnKey
      } else {
        if (this.sortedBy === this.toColumnKeyDescending(columnKey)) {
          this.$emit('sortBy', '')
          this.sortedBy = ''
        } else {
          this.$emit('sortBy', columnKey)
          this.sortedBy = columnKey
        }
      }
      this.resetScrollPosition()
    },
    toColumnKeyDescending (columnKey) {
      return `-${columnKey}`
    },
    isSortedByDescending (columnKey) {
      return this.toColumnKeyDescending(columnKey) === this.sortedBy
    },
    isSortedByAscending (columnKey) {
      return columnKey === this.sortedBy
    },
    isNotSortedBy (columnKey) {
      return !this.isSortedByAscending(columnKey) && !this.isSortedByDescending(columnKey)
    },
    startDrag (e) {
      e.dataTransfer.setDragImage(new Image(), 0, 0)
      this.toggleDragged(e)
    },
    toggleDragged (e) {
      const rows = e.target.childNodes
      rows.forEach(row => row.classList.toggle('dragged_column_row'))
      rows[0].classList.remove('dragged_column_row')
      rows[0].classList.toggle('dragged_column_header')
    },
    resetScrollPosition () {
      this.$refs.draggableContainer.style.scrollBehavior = 'auto'
      this.$refs.draggableContainer.scrollTop = 0
      this.$refs.draggableContainer.style.scrollBehavior = ''
    },

    // --- Layout configuration helpers
    async initColumnsConfiguration (defaultConfiguration, fieldName) {
      if (this.resizeObj.widthsApplied) return
      this.resizeObj.widthsApplied = true
      this.resizeObj.containerDefaultWidth = Math.floor(this.getDraggableRect().width)
      const clientConfiguration = await this.getColumnsClientConfiguration(this.list)
      this.applyConfiguration(defaultConfiguration, fieldName)
      this.applyConfiguration(clientConfiguration, fieldName)
      this.resizeFiller()
    },
    applyConfiguration (configuration, fieldName) {
      const currentList = this.list
      Object.keys(configuration).forEach(columnKey => {
        const header = this.$refs.columnRefs.find(header => header.dataset.key === columnKey)
        if (!header) return

        let fieldValue = configuration[columnKey][fieldName]
        header.style.width = fieldValue
        if (fieldValue && header.getBoundingClientRect().width > 0) {
          fieldValue = `${Math.floor(header.getBoundingClientRect().width)}px`
          this.saveColumnClientConfiguration({ currentList, columnKey, fieldName, fieldValue })
        }
      })
    },

    // --- Column resizing helpers
    resizeFiller () {
      const containerDefaultWidth = this.resizeObj.containerDefaultWidth
      const containerWidth = Math.floor(this.getDraggableRect().width)
      const headersWidth = this.calcHeadersWidth()
      let result = 0

      // Filler column taking up remainder of container
      const fillerWidth = containerWidth - headersWidth

      if (fillerWidth > 0) {
        result = fillerWidth
      }
      // Filler column shrinks when resized header approaches container end
      if (headersWidth + fillerWidth > containerDefaultWidth && containerDefaultWidth !== 0) {
        result = containerDefaultWidth - headersWidth
      }
      // No filler column when resized header ends beyond container
      if (headersWidth > containerDefaultWidth && containerDefaultWidth !== 0) {
        result = 0
      }
      this.resizeObj.fillerWidth = result
    },

    getDraggableContainerRect () {
      return this.$refs.draggableContainer.getBoundingClientRect()
    },
    getDraggableRect () {
      return this.$refs.draggable.$el.getBoundingClientRect()
    },
    calcHeadersWidth () {
      if (this.$refs.columnRefs.length === 0) return 0
      return this.$refs.columnRefs
        .map(header => header.getBoundingClientRect().width)
        .reduce((sum, width) => sum + Math.floor(width))
    },
    calcHeaderWidth () {
      if (!this.resizeObj.headerRef) return 0
      return Math.floor(this.resizeObj.headerRef.getBoundingClientRect().width)
    },
    calcResizeOffset () {
      if (!this.resizeObj.headerRef) return 0
      const offset = this.resizeObj.headerRef.getBoundingClientRect().left - this.$refs.body.getBoundingClientRect().left
      return Math.floor(offset)
    },

    // --- Column resizing handlers
    onMousemoveDebounced (event, delay) {
      debounce(this.onMousemove(event), delay)
    },
    onMousedown (columnKey) {
      this.resizeObj.headerRef = this.$refs.columnRefs.find(header => header.dataset.key === columnKey)
      this.resizeObj.resizeWidth = this.calcHeaderWidth()
      this.resizeObj.resizeOffset = this.calcResizeOffset()
    },
    onMousemove (newWidth) {
      if (!this.resizeObj.headerRef) return
      this.resizeObj.resizeWidth += newWidth
    },
    onMouseup (columnKey, fieldName) {
      if (!this.resizeObj.headerRef) return
      const header = this.resizeObj.headerRef
      const currentList = this.list
      const fieldValue = header.style.width = `${this.resizeObj.resizeWidth}px`
      this.saveColumnClientConfiguration({ currentList, columnKey, fieldName, fieldValue })
      this.resizeFiller()
      this.resizeObj.headerRef = null
    },
    loadMoreEntries () {
      this.$emit('needsMoreData')
    },
    toggleRow (row) {
      this.$emit('toggleRow', row)
    },
    isSelected (row) {
      return this.selected.some(selected => selected.meta.entityGuid === row.meta.entityGuid)
    },
    onRowClick (row) {
      if (this.selectableRows) {
        this.toggleRow(row)
      }

      this.$emit('rowClick', row)
    },
  },
}
</script>

<style lang="stylus" scoped>
@keyframes fadein
  from { opacity: 0.3 }

  to   { opacity: 1 }

.list-table-container
  position relative
  height 100%
  overflow hidden
  background-color $base-color-white

  .draggable-container
    position relative
    height 100%
    min-height 260px // Including list header this will be the approx height of empty-state
    max-height 648px
    overflow auto
    scroll-behavior smooth

  .draggable-container::-webkit-scrollbar-track:vertical
    border-radius 0
    background linear-gradient(180deg, rgba(247,247,249,1) 50px, rgba(255,255,255,0) 50px)

  .draggable
    position relative
    display flex
    width 100%

  .sticky
    position sticky
    top 0
    z-index 100

  .column
    position relative

  .column--sticky
    position sticky
    left 0
    z-index 101

  .column_header.sorted
    text-decoration underline
    text-decoration-color $base-color-text-secondary

  .cell.column_header
    padding-right $base-unit
    border none
    background $base-background-color
    cursor pointer

  .cell
    display flex
    align-items center
    padding 0 $base-unit * 3
    border-bottom $base-border-width $base-border-style $base-color-gray-200
    cursor pointer
    animation fadein 1s
    primary-body-text()

    &.cell__select
      display flex
      align-items center
      justify-content center
      width 70px
      height 70px

    > div:not(.sort-icon-container):not(.settings-container):not(.header-label)
      overflow hidden
      white-space nowrap
      text-overflow ellipsis

    &.filler
      padding 0

  .cell__content
    pointer-events none

  .dragged_column_header
    background-color $base-color-white--selected

  .dragged_column_row
    background-color $base-color-white--selected

.resize-box
  position absolute
  top 0
  z-index 101

.resize-handler
  position absolute
  right 4px

.resize-indicator
  position absolute
  right 0
  width 2px
  background $base-color-text-secondary

.header-label
  secondary-details-text()

.sortable
  width 12px
  height 20px
  margin-left 6px

.remaining.items.placeholder.cell
  border none

.list-table__footer
  display flex
  align-items center
  justify-content center
  height $base-unit * 16
  padding-bottom $base-unit * 5

.list-table__content-wrapper
  display flex
</style>
