export function generateMockCatalogs (count = 100) {
  const sequence = [...Array(count).keys()]
  const catalogs = sequence.map(_ => {
    const guid = `mock-${faker.datatype.uuid()}`
    const name = faker.lorem.word()
    return {
      guid,
      name,
      label: name,
    }
  })
  return catalogs
}
