<template>
  <uc-navigator
    v-bind="{ nodes, isLoading, totalNodeCount }"
    :path.sync="path"
    @navigate="openNode"
    @requestPage="requestPage">
    <template #topLeft="navigator">
      <uc-breadcrumbs
        :items="navigator.path"
        @click="navigator.navigate" />
    </template>
    <template #topRight="navigator">
      <slot
        name="topRight"
        v-bind="navigator" />
    </template>

    <template #default="navigator">
      <uc-navigator-view-grid-spacious :nodes="nodes">
        <template #default="{ node }">
          <uc-navigator-grid-node
            :children-count="node.childrenCount"
            :selectable="isNodeSelectable(node.nodeType)"
            :selected="isNodeSelected(node.guid)"
            @select="selectNode(node)"
            @open="navigator.navigate(node)">
            {{ node.name }}
          </uc-navigator-grid-node>
        </template>
      </uc-navigator-view-grid-spacious>
    </template>
  </uc-navigator>
</template>

<script>
import { UcNavigator, UcNavigatorViewGridSpacious, UcNavigatorGridNode } from '@/components/generic/uc-navigator/'
import { UcBreadcrumbs } from '@/components/widgets/uc-breadcrumbs/'

const ComponentEvent = {
  REQUEST_PAGE: 'requestPage',
  SELECT_NODE: 'selectNode',
  OPEN_NODE: 'openNode',
}

export default {
  name: 'UcEntityNavigator',
  components: {
    UcNavigator,
    UcNavigatorViewGridSpacious,
    UcNavigatorGridNode,
    UcBreadcrumbs,
  },
  props: {
    nodes: {
      type: Array,
      required: true,
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    selection: {
      type: Array,
      default: () => [],
    },
    blacklistedNodeTypes: {
      type: Array,
      default: () => [],
    },
    totalNodeCount: {
      type: Number,
      default: 0,
    },
  },
  data () {
    return {
      path: [
        {
          name: 'Stores',
          nodeType: 'root',
        },
      ],
    }
  },
  computed: {
    selectedNodesGuids () {
      return this.selection.map(node => node.guid)
    },
  },
  methods: {
    isNodeSelectable (nodeType) {
      return !this.blacklistedNodeTypes.includes(nodeType)
    },
    isNodeSelected (guid) {
      return this.selectedNodesGuids.includes(guid)
    },
    openNode (node) {
      const payload = {
        node,
        page: 0,
      }
      this.$emit(ComponentEvent.OPEN_NODE, payload)
    },
    selectNode (node) {
      if (this.isNodeSelectable(node.nodeType)) {
        this.$emit(ComponentEvent.SELECT_NODE, node)
      }
    },
    requestPage (payload) {
      this.$emit(ComponentEvent.REQUEST_PAGE, payload)
    },
  },
}
</script>
