<template>
  <div class="uc-filter">
    <!-- Header -->
    <header
      v-if="title"
      data-test="filter-header"
      class="uc-filter__header">
      <h1
        v-truncate
        class="uc-filter__heading">
        {{ title }}
      </h1>
    </header>

    <!-- Body/content -->
    <ul
      v-custom-scroll-bar
      class="uc-filter__body">
      <li
        v-for="(section, key) in sections"
        :key="section.guid"
        data-test="filter-section">
        <h2
          v-truncate
          class="uc-filter__sub-heading">
          {{ section.label }}
        </h2>
        <div class="uc-filter__content">
          <component
            :is="getPropertyEditorComponent(section.editor)"
            :key="section.guid"
            no-label
            :name="key"
            :definition="section"
            :language="contentLanguage"
            @change="onChange" />
        </div>
      </li>
    </ul>

    <!-- Footer -->
    <div class="uc-filter__footer">
      <uc-button
        data-test="clear-button"
        :theme="buttonThemeText"
        :size="buttonSizeSmall"
        @click="onClear">
        {{ $t('buttonClearText') }}
      </uc-button>
      <uc-button
        data-test="filter-button"
        :size="buttonSizeSmall"
        @click="onFilter">
        {{ $t('buttonApplyText') }}
      </uc-button>
    </div>
  </div>
</template>

<i18n>
{
  "en": {
    "title": "Filter",
    "buttonClearText": "Clear",
    "buttonApplyText": "Apply"
  },
  "da": {
  },
  "sv": {
  },
  "de": {
  }
}
</i18n>

<script>
import { getPropertyEditorComponent } from '@/components/editors/'
import { UcButton, UcButtonSize, UcButtonTheme } from '@/components/forms/uc-button'

const ComponentEvent = {
  CLEAR: 'clear',
  FILTER: 'filter',
  CHANGE: 'change',
}

export default {
  name: 'UcFilter',
  components: {
    UcButton,
  },
  props: {
    title: {
      type: String,
      default () {
        return this.$t('title')
      },
    },
    sections: {
      type: Object, // Object of defintion objects
      required: true,
    },
    contentLanguage: {
      type: String,
      default: '',
    },
  },
  computed: {
    buttonThemeText: () => UcButtonTheme.TEXT,
    buttonSizeSmall: () => UcButtonSize.SMALL,
  },
  methods: {
    getPropertyEditorComponent,
    onClear () {
      this.$emit(ComponentEvent.CLEAR)
    },
    onFilter () {
      this.$emit(ComponentEvent.FILTER)
    },
    onChange (payload) {
      this.$emit(ComponentEvent.CHANGE, payload)
    },
  },
}
</script>

<style lang="stylus" scoped>
.uc-filter
  position relative
  width $base-unit * 48
  border $base-border
  border-radius $base-border-radius
  background-color $base-color-white
  box-shadow $base-box-shadow

.uc-filter__header
  position sticky
  padding $base-unit * 2 $base-unit * 3
  border-bottom $base-border

.uc-filter__heading
  primary-body-text($base-font-weight-bold)
  margin 0

.uc-filter__sub-heading
  primary-body-text($base-font-weight-semibold)
  height $base-unit * 5
  margin 0
  padding $base-unit $base-unit * 3
  background-color $base-color-gray-50

.uc-filter__body
  max-height $base-unit * 62
  margin 0
  padding 0
  overflow-y auto
  list-style none

.uc-filter__content
  padding $base-unit * 2 $base-unit * 3

.uc-filter__footer
  display grid
  grid-template-columns min-content min-content
  gap $base-unit * 3
  justify-content end
  padding $base-unit * 3
  box-shadow $base-box-shadow-top
</style>
