<template>
  <div class="warning-dialog">
    <uc-modal
      persistent
      :title="title"
      @hide="$emit('cancel')">
      <p class="warning-dialog__content">
        {{ text }}
      </p>
      <template #footer>
        <uc-button
          ref="proceed"
          @click="proceed">
          {{ proceedButtonText }}
        </uc-button>
        <uc-button
          ref="cancel"
          theme="text"
          @click="cancel">
          {{ cancelButtonText }}
        </uc-button>
      </template>
    </uc-modal>
  </div>
</template>

<script>
import UcModal from '@/components/widgets/uc-modal'
import UcButton from '@/components/forms/uc-button/uc-button'

export const ComponentEvent = {
  PROCEED: 'proceed',
  CANCEL: 'cancel',
}

export default {
  name: 'UcWarningDialog',
  components: {
    UcButton,
    UcModal,
  },
  props: {
    text: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
    proceedButtonText: {
      type: String,
      default: function () { return this.$tc('global.proceed') },
    },
    cancelButtonText: {
      type: String,
      default: function () { return this.$tc('global.cancel') },
    },
  },
  methods: {
    proceed () {
      this.$emit(ComponentEvent.PROCEED)
    },
    cancel () {
      this.$emit(ComponentEvent.CANCEL)
    },
  },
}
</script>

<style lang="stylus" scoped>
.warning-dialog__content
  secondary-details-text()
  margin 0
  // Add the possibility to add \n to the string for several lines
  white-space pre-line
  text-align center
</style>
