import { EntityService } from './_entity.service.js'
export { EntityType } from './_entity.service'

/**
 * Catalogs and Categories are different from other entities:
 * - They come with definitions baked in so there's no endpoint for getting definitions
 * - The URL for getting definition list is different than from other entities
 */
export class LegacyEntityService extends EntityService {
  fetchDefinitionsListAsync () {
    return this._httpClient.get(`${this._rootUrl}/definitions`)
  }

  fetchDefinitionsAsync () {
    console.error(`[legacy-entity-service] Method not implementet for: ${this._nodeType}`)
  }

  fetchEntityAsync ({ guid, cultureCode }) {
    return this._httpClient.get(`${this._rootUrl}/${guid}/${cultureCode}`)
  }

  fetchEntitiesAsync ({ cultureCode, params, url }) {
    url = url || `${this._rootUrl}/${cultureCode}`
    return this._httpClient.get(url, { params })
  }
}
