import { rest } from 'msw'
import { criterionTemplates, criteria, criterionDefinitions, criterionShallowDefinitions } from '@/mocks/entities/criteria/'
import mockGuid from '@/utils/mock-guid.js'

const RESPONSE_DELAY = 200

export const rootCriterionHandlers = [
  rest.get('/ucommerceapi/v2/promotions/criteria/definitions', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(criterionShallowDefinitions),
    )
  }),
  rest.get('/ucommerceapi/v2/promotions/criteria/definitions/full', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(criterionDefinitions),
    )
  }),
  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criterionGuid/:cultureCode', (req, res, ctx) => {
    const criterion = JSON.parse(JSON.stringify(criterionTemplates.find(newCriteria => newCriteria.meta.entityGuid === req.params.criterionGuid)))
    criterion.meta.entityGuid = mockGuid()

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(criterion),
    )
  }),
  rest.patch('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:discountGuid/:cultureCode', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
  rest.delete('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:criteriaGuid', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
    )
  }),
  rest.get('/ucommerceapi/v2/promotions/:promotionGuid/criteria/:cultureCode', (_req, res, ctx) => {
    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json(criteria),
    )
  }),
  rest.post('/ucommerceapi/v2/promotions/:promotionGuid/criteria', (req, res, ctx) => {
    const guid = criterionTemplates.find(criterionTemplate => criterionTemplate.meta.definition.guid === req.body.definitionGuid).meta.entityGuid

    return res(
      ctx.delay(RESPONSE_DELAY),
      ctx.status(200),
      ctx.json({ guid }),
    )
  }),
]
