export class CatalogCriterionService {
  constructor (httpClient, promotionGuid, criterionGuid) {
    this._httpClient = httpClient
    this._promotionGuid = promotionGuid
    this._criterionGuid = criterionGuid
  }

  get _rootUrl () {
    return `/promotions/${this._promotionGuid}/criteria/${this._criterionGuid}`
  }

  fetchManyAsync ({ startAt, limit, searchTerm, cultureCode }) {
    const url = `${this._rootUrl}/catalogs/${cultureCode}`
    const payload = {
      params: { startAt, limit, searchTerm },
    }
    return this._httpClient.get(url, payload)
  }

  patchOneAsync ({ catalog, cultureCode }) {
    const url = `${this._rootUrl}/${cultureCode}`
    const payload = {
      catalog: {
        selected: [
          catalog.guid,
        ],
      },
    }
    return this._httpClient.patch(url, payload)
  }

  fetchSelectedAsync ({ cultureCode }) {
    const url = `${this._rootUrl}/catalogs/selected/${cultureCode}`
    return this._httpClient.get(url)
  }
}
