export class OrderLineDiscountService {
  constructor (httpClient, promotionGuid, discountGuid) {
    this._httpClient = httpClient
    this._promotionGuid = promotionGuid
    this._discountGuid = discountGuid
  }

  get _rootUrl () {
    return `/promotions/${this._promotionGuid}/discounts/${this._discountGuid}`
  }

  fetchManyAsync ({ startAt, limit, searchTerm, cultureCode }) {
    const url = `${this._rootUrl}/orderlines/${cultureCode}`
    const payload = {
      params: { startAt, limit, searchTerm },
    }
    return this._httpClient.get(url, payload)
  }

  patchOneAsync ({ orderLine, cultureCode }) {
    const url = `${this._rootUrl}/${cultureCode}`
    const payload = {
      orderLine: {
        selected: [
          orderLine.guid,
        ],
      },
    }

    return this._httpClient.patch(url, payload)
  }

  fetchSelectedAsync ({ cultureCode }) {
    const url = `${this._rootUrl}/orderlines/selected/${cultureCode}`
    return this._httpClient.get(url)
  }
}
