<template>
  <uc-navigator
    v-bind="{ nodes, isLoading }"
    :path.sync="path"
    @navigate="openNode">
    <template #topLeft="navigator">
      <uc-breadcrumbs
        :items="navigator.path"
        @click="navigator.navigate" />
    </template>
    <template #topRight="navigator">
      <div class="content-navigator__actions">
        <slot
          name="topRight"
          v-bind="navigator" />
      </div>
    </template>

    <template #default="navigator">
      <!-- View -->
      <uc-navigator-view-grid-spacious :nodes="navigator.nodes">
        <template #default="{ node }">
          <content-navigator-grid-node
            :node="node"
            :selected="isNodeSelected(node.guid)"
            @select="selectNode(node)"
            @open="navigator.navigate(node)" />
        </template>
      </uc-navigator-view-grid-spacious>
    </template>
  </uc-navigator>
</template>

<script>
import { UcNavigator, UcNavigatorViewGridSpacious } from '@/components/generic/uc-navigator'
import UcBreadcrumbs from '@/components/widgets/uc-breadcrumbs/uc-breadcrumbs'
import ContentNavigatorGridNode from '../content-navigator-grid-node'

const ComponentEvent = {
  SELECT_NODE: 'selectNode',
  OPEN_NODE: 'openNode',
}

export default {
  name: 'ContentNavigator',
  components: {
    UcNavigator,
    UcNavigatorViewGridSpacious,
    UcBreadcrumbs,
    ContentNavigatorGridNode,
  },
  props: {
    nodes: {
      type: Array,
      required: true,
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    selectedNodesGuids: {
      type: Array,
      default: () => [],
    },
  },
  data () {
    return {
      path: [
        {
          name: this.$t('ucContentPicker.item'),
        },
      ],
    }
  },
  methods: {
    isNodeSelected (guid) {
      return this.selectedNodesGuids.includes(guid)
    },
    openNode (node) {
      this.$emit(ComponentEvent.OPEN_NODE, node)
    },
    selectNode (node) {
      this.$emit(ComponentEvent.SELECT_NODE, node)
    },
  },
}
</script>

<style lang="stylus" scoped>
.content-navigator__actions
  display flex
  flex-direction row
</style>
